/* **********************************************************************
 * 회원 > 마이페이지 > 홈 Route
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';

import { Home } from '../../components/mypage';
import PointRoute from './PointRoute';

const Commi = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/mypage/home"
        render={(props) => <Home {...props} />}
      />
      <AuthRoute
        path="/mypage/home/point"
        render={(props) => <PointRoute {...props} />}
      />
    </Switch>
  );
};

export default Commi;