import React, { useCallback, useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
import { useDispatch } from 'react-redux';

// 개발자 Coponent
import _axios from '../../../utils/axios';
import AccurateDetail from './AccurateDetail';
// css
import '../../../css/kendo.css';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const AccurateInfo = (props) => {
  const dispatch = useDispatch();
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,
    memId: '',
    memName: '',
    dueDate: '',
    commission: 0,
    otherDescript: '',
    otherAmount: 0,
    totalAmount: 0,
    taxAmount: 0,
    accurateDate: '',
    accurateStatus: '',
    statusName: '',
    taxAssort: '',
    taxOptions: [],
    success: false,
    message: null,
    visibleDialog: false,
  });
  const _getData = useCallback(async () => {
    const url = '/api/admin/commission/get_accurate_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      memId: data.memId,
      memName: data.memName,
      dueDate: data.dueDate,
      commission: data.commission,
      otherDescript: data.otherDescript,
      otherAmount: data.otherAmount,
      totalAmount: data.totalAmount,
      taxAmount: data.taxAmount,
      accurateAmount: data.accurateAmount,
      accurateDate: data.accurateDate,
      accurateStatus: data.accurateStatus,
      statusName: data.statusName,
      taxOptions: data.taxOptions,
      taxAssort: { code: data.taxAssort, name: data.taxName },
    });
  }, [state]);

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/admin/commission/set_accurate_update.php';
    const { message } = await _axios(url, state);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    //dispatch(stackRequest('S', '정산관리'));
    _getData();
  }, [dispatch]);

  // const { isLoading, isSearch, loadData, searchAble } = state;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '600px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>수수료정산 〉 정산서목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memId"
                  value={state.memId}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memName"
                  value={state.memName}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산기간</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="dueDate"
                  value={state.dueDate}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>수수료</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commission"
                  value={state.commission}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>

              <Td width={tdWidth}>
                <Label width={labelWidth}>가감내용</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="otherDescript"
                  value={state.otherDescript}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가감금액</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="otherAmount"
                  value={state.otherAmount}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>합계금액</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="totalAmount"
                  value={state.totalAmount}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산상태</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="statusName"
                  value={state.statusName}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="accurateDate"
                  value={state.accurateDate}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}

      <Wrap>
        <AccurateDetail idx={state.idx} />
      </Wrap>
    </Container>
  );
};

export default AccurateInfo;
