import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const BbsManagerList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '게시판관리',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchKey: '',
      searchValue: '',
      discountType: '',
      typeOptions: [],
      useYn: '',
      useOptions: [],
      pageSize,
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/bbs/get_bbs_manager_list.php';
    const params = {
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
      searchValue: state.searchAble.searchValue,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      searchAble: {
        ...state.searchAble,
        searchOptions: response.searchOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          {type: 'text',width: '84%',name: 'searchValue',value: searchAble.searchValue,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [{type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},],
      };

  const searchFields = isMobile ? [searchValue] : [searchValue];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',width: '10%',align: 'center',},
        {type: 'text',title: '메뉴명',name: 'menuName',width: '50%',align: 'left',},
        {type: 'text',title: '나열순서',name: 'sortNo',width: '20%',align: 'center',},
        {type: 'text',title: '사용여부',name: 'menuStatus',width: '20%',align: 'center',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '3%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '10%',align: 'center',},
        {type: 'text',title: '게시판코드',name: 'bbsCode',width: '25%',align: 'center',},
        {type: 'text',title: '게시판명',name: 'title',width: '25%',align: 'center',},
        {type: 'text',title: '썸네일',name: 'thumbYn',width: '12%',align: 'center',},
        {type: 'text',title: '댓글',name: 'replyYn',width: '20%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>게시판관리 〉 목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={true}
            isDeleteButton={false}
            infoPath="/admin/bbsManager/info"
            deleteUrl="/api/admin/setting/set_menu_delete.php"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            infoPath="/admin/bbsManager/info"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default BbsManagerList;
