/*
설명: 로그인 화면
- 2021.06.01 안예린 로그인 리뉴얼
*/
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation } from 'react-router';
// 개발자 라이브러리
import { idSave, init, loginRequest } from '../../redux/actions/auth';
import CmsCheckAlert from './CmsCheckAlert';
//css
import '../../styles/kendo.css';
import {
  Container,
  Wrap,
  LogoWrap,
  FindIDPW,
  FindIDPWButton,
  LoginModal,
} from '../../styles/account';
//input & button
import CommonButton from '../common/Button';
import CommonInput from '../common/CommonInput';
//icon
import LoginLogo from '../../images/account/logo_9930.svg';
import warning from '../../images/account/warning.svg';

const Login = (props) => {
  const [error, setError] = useState('');
  const [red, setRed] = useState(false);
  const dispatch = useDispatch();
  const auth = useSelector((store) => store.auth);
  const storageData = localStorage.getItem('user');
  const saveId =
    storageData === null || storageData === '' ? '' : atob(storageData);
  const location = useLocation();
  const isCertify = location?.state?.isCertify;
  const isCertifyPW = location?.state?.isCertifyPW;

  const [state, setState] = useState({
    userPw: '',
    userId: saveId,
    idSave: saveId,
    idCheck: saveId !== '' ? true : false,
    isCmsCheck: false,
    isEyesOn: false,
    userInfo: [],
    success: false,
    message: null,
    visibleDialog: false,
  });

  // 입력값이 변할 때
  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
    setRed(false);
    setError('');
  };

  // 로그인 버튼 클릭
  const _handleSubmit = (e) => {
    e.preventDefault();

    if (state.idCheck) {
      dispatch(idSave(btoa(state.userId)));
    }

    dispatch(loginRequest(state.userId, state.userPw));
  };

  //엔터 키
  const _handleEnterPress = (e) => {
    e.preventDefault();

    if (e.charCode === 13) {
      dispatch(loginRequest(state.userId, state.userPw));
    }
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(async () => {
    const loginStatus = await auth.login.status;
    // 로그인 정상
    if (loginStatus === 'SUCCESS') {
      setRed(false);
      setError('');
      // 정산계좌 미등록
      if (auth.authStatus.userInfo.infoStatus === '0') {
        setState({
          ...state,
          success: true,
          visibleDialog: true,
          message: auth.authStatus.userInfo.message,
        });

        setTimeout(() => {
          setState({
            ...state,
            success: false,
          });

          props.history.push({
            pathname: '/mypage/myinfo/update',
          });
        }, 1000);

        // 구독료 미납
      } else if (auth.authStatus.userInfo.infoStatus === '2') {
        setState({
          ...state,
          isCmsCheck: true,
        });

        // 정상
      } else if (auth.authStatus.userInfo.infoStatus === '9') {
        setState({
          ...state,
          success: true,
          visibleDialog: true,
          message: auth.authStatus.userInfo.message,
        });

        setTimeout(() => {
          setState({
            ...state,
            success: false,
          });

          props.history.push({
            pathname:
              auth.authStatus.userAuth === 'A' ||
              auth.authStatus.userAuth === 'D'
                ? '/admin/home'
                : '/mypage/home',
          });
        }, 1000);
      }

      // 로그인실패
    } else if (loginStatus === 'FAILURE') {
      setRed(true);
      setError('입력하신 정보가 올바르지 않습니다.');

      setTimeout(() => {
        setState({
          ...state,
          success: false,
        });
        dispatch(init());
      }, 1000);
    }
  }, [auth.login.status]);

  const TogglePw = () => {
    const temp = document.getElementById('typepass');
    if (temp.type === 'password') {
      temp.type = 'text';
    } else if (temp.type === 'text') {
      temp.type = 'password';
    }
    setState({
      ...state,
      isEyesOn: !state.isEyesOn,
    });
  };

  return (
    <Container>
      {state.isCmsCheck && <CmsCheckAlert props={props} />}
      <Wrap>
        <LogoWrap
          onClick={() =>
            props.history.push({
              pathname: '/',
            })
          }
        >
          <img src={LoginLogo} alt="logo"></img>
        </LogoWrap>
        <LoginModal display={isCertify ? 'block' : 'none'}>
          <h4>아이디가 전송되었습니다.</h4>
          <p>
            스파이더플랫폼 공식 카카오톡으로 아이디를 안내해
            <br />
            드렸습니다.
          </p>
        </LoginModal>
        <LoginModal display={isCertifyPW ? 'block' : 'none'}>
          <h4>비밀번호가 변경되었습니다.</h4>
        </LoginModal>
        <form onSubmit={_handleSubmit}>
          <CommonInput
            className="input-id"
            inputtype="text"
            width="100%"
            height="60px"
            value={state.userId}
            maxLength={20}
            autoComplete="off"
            name="userId"
            onChange={_handleInputChange}
            required={true}
            placeholder="아이디를 입력해주세요"
            validityStyles={false}
            titlename="아이디"
          />
          <CommonInput
            idName="typepass"
            kind={red ? 'is-active' : ''}
            inputtype="password"
            name="userPw"
            value={state.userPw}
            maxLength={20}
            onChange={_handleInputChange}
            onKeyPress={() => _handleEnterPress}
            required={true}
            titlename="비밀번호"
            width="100%"
            height="60px"
            placeholder="비밀번호를 입력해주세요."
            src={red ? warning : ''}
            onClickHandler={TogglePw}
            error={error}
            validityStyles={false}
            autoComplete="off"
          />

          <CommonButton
            type={'submit'}
            className="loginAnchor"
            kind="wideBtn_01"
          >
            로그인
          </CommonButton>
        </form>
        <FindIDPW>
          <FindIDPWButton to="/account/findID">아이디찾기 | </FindIDPWButton>
          <FindIDPWButton to="/account/findPW"> 비밀번호찾기</FindIDPWButton>
        </FindIDPW>
        <CommonButton
          className="loginAnchor"
          onClickHandler={() => {
            props.history.push({
              pathname: '/account/checkRecomandID',
            });
          }}
          kind="wideBtn_02"
        >
          회원가입
        </CommonButton>
      </Wrap>
    </Container>
  );
};

export default Login;
