import React from "react";
import styled from 'styled-components';
// 개발자 라이브러리
import MypageRoute from "../../routes/MypageRoute";

export default function MobileLayout() {
  return (
    <Container>
      <MypageRoute />
    </Container>
  );
}

const Container = styled.div`
  width: 100%;
  height: 100%;
`;
