/*
  설명: CMS 미납료 납부 페이지
  - 2021.05.18. 안예린, import 수정
*/
import React, { useEffect, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { useDispatch, useSelector } from 'react-redux';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import styled from 'styled-components';
// 개발자 Component
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
// css
import { SwiperMiddleLabel } from '../../../css/common-component';
import '../../../css/kendo.css';
// icon & images
import PersonIcon from '@material-ui/icons/Person';

const MyInfoUnpaid = () => {
  const headerInfo = {
    isBackground: false, // 백그라운드
    isBugger: false, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: 'CMS납부',
  };

  const dispatch = useDispatch();
  const { userId } = useSelector((store) => store.auth.login);

  const [isAccountCheckAlert, setIsAccountCheckAlert] = useState({
    show: false,
    content: '',
  });

  const [sendUnpaidButton, setSendUnpaidButton] = useState(true);

  const [state, setState] = useState({
    // user정보
    userId: userId,
    userName: '',
    photo: '',
    // 미납내역
    unpaidList: [],
    unpaidSum: 0,
    // Header
    isBackground: true, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '내정보',
  });

  // slideModal
  const [isComplete, setIsComplete] = useState({
    show: false,
    content: '',
  });

  const _getData = async () => {
    // 사진, 유저이름,유저 아이디
    const url = '/api/member/get_my_info.php';

    const params = {
      userId: state.userId,
    };

    const { data } = await _axios(url, params);

    const url2 = '/api/member/myInfo/get_cms_unpaid.php';

    const params2 = {
      userId: state.userId,
    };
    const unpaidList = await _axios(url2, params2);
    setState({
      ...state,
      // user정보 state
      userName: data.userName,
      photo: data.photo,

      // 미납료 납부 state
      unpaidList: unpaidList.data,
      unpaidSum: unpaidList.data.map(
        (v) => (state.unpaidSum = state.unpaidSum + Number(v.payAmount)),
      ),
    });
  };

  // 미납신청 버튼
  const _setUnpaidPay = async () => {
    const url = '/api/member/myInfo/set_cms_unpaid_pay.php';
    const params = {
      userId: state.userId,
    };

    const { result, message } = await _axios(url, params);

    setIsAccountCheckAlert({
      show: true,
      result: result,
      content: message,
    });

    setSendUnpaidButton(false);
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    _getData();
  }, []);

  if (isMobile) {
    return (
      <StyledInfoUpdate>
        <CMSprofile>
          <div className="cmsprofile-img">
            {!state.photo ? (
              <PersonIcon style={{ fontSize: '2.5em' }} />
            ) : (
              <img src={state.photo} />
            )}
          </div>
          <div className="cmsprofile-txt">
            <p className="cmsprofile-txt-name">{state.userName}</p>
            <p className="cmsprofile-txt-num">{state.userId}</p>
          </div>
        </CMSprofile>

        <CMSInner>
          {/* 등록된 CMS 정보 */}
          <StyledMyInfo>
            <StyledTitleCss>미납료 납부 하기</StyledTitleCss>
            <StyledListBox>
              <div style={{ width: '30%' }}>No.</div>
              <div style={{ width: '35%' }} className="unpaid-line">
                미납월
              </div>
              <div style={{ width: '35%' }} className="unpaid-line">
                금액
              </div>
            </StyledListBox>

            {state.unpaidList.map((list) => (
              <StyledListBox key={list.idx}>
                <div style={{ width: '30%' }}>{list.no}</div>
                <div style={{ width: '35%', justifyContent: 'space-between' }}>
                  {list.payMonth}
                </div>
                <div style={{ width: '35%', justifyContent: 'space-between' }}>
                  {list.payAmount}
                </div>
              </StyledListBox>
            ))}

            <StyledListBox>
              <div style={{ width: '30%' }}>합계</div>
              <div
                style={{ width: '35%', justifyContent: 'space-between' }}
              ></div>
              <div style={{ width: '35%', justifyContent: 'space-between' }}>
                {state.unpaidSum[state.unpaidSum.length - 1]}
              </div>
            </StyledListBox>
            {sendUnpaidButton ? (
              <MyinfoButton onClick={_setUnpaidPay} className="myinfo-button">
                입력된 CMS 결제 정보로 납부를 신청합니다.
              </MyinfoButton>
            ) : (
              <MyinfoButton
                style={{ backgroundColor: '#979797' }}
                className="myinfo-button"
              >
                입력된 CMS 결제 정보로 납부를 신청합니다.
              </MyinfoButton>
            )}
            <p style={{ marginTop: '2em' }} className="unpaid-txt">
              위 납부 신청 버튼을 클릭하시면 미납된 개월 수 <br />
              만큼 자동 결제를 진행합니다. <br />
              신용카드인 경우 즉시 결제 진행되며, <br />
              CMS계좌인 경우 2~3일이 소요될 수 있습니다.
            </p>
          </StyledMyInfo>
        </CMSInner>

        {isAccountCheckAlert.show && (
          <StyledDialog
            // id="bonusModal"
            width={'256px'}
            height={'auto'}
            title={' '}
            onClose={() => {
              isAccountCheckAlert.show
                ? setIsAccountCheckAlert({ show: false })
                : setIsAccountCheckAlert({ show: true });
            }}
          >
            <SwiperMiddleLabel
              width="216px"
              height="auto"
              margin="0px auto 0px auto"
              style={{
                color: '#000f34',
                fontSize: '15px',
                textAlign: 'center',
              }}
            >
              {isAccountCheckAlert.content}
            </SwiperMiddleLabel>
            <DialogActionsBar>
              <button
                className="k-button"
                onClick={() => {
                  isAccountCheckAlert.show
                    ? setIsAccountCheckAlert({ show: false })
                    : setIsAccountCheckAlert({ show: true });
                }}
              >
                확인
              </button>
            </DialogActionsBar>
          </StyledDialog>
        )}
      </StyledInfoUpdate>
    );
  }
};

const CMSprofile = styled.div`
  height: 8em;
  display: flex;
  align-items: center;
  margin-bottom: 3em;
  padding: 2em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  background-color: #000f34;

  .cmsprofile-img {
    width: 5em;
    height: 5em;
    margin-right: 1.5em;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .cmsprofile-txt {
    color: white;
    padding: 0.2em;

    p {
      padding-top: 0.4em;
      margin-bottom: 0;
    }

    /* .cmsprofile-txt-name */
    &-name {
      font-weight: 700;
    }
  }
`;

// 회원수정 스타일 컴포넌트
const StyledInfoUpdate = styled.div`
  width: 100%;
  padding: 0 0 9em;
  background-color: #f2f2f2;

  .k-maskedtextbox .k-textbox {
    border: solid 1px #c7c7c7;
    transform: translateY(0px);
    height: 100%;

    ::placeholder {
      padding: 0.5em;
      font-size: 14px;
    }
  }
`;

const CMSInner = styled.div`
  padding: 0 2em;
`;

// 등록된 CMS 정보
const StyledMyInfo = styled.section`
  position: relative;
  width: 100%;

  div {
    padding-left: 0.5em;
  }

  p {
    color: #000f34;
    line-height: 2em;
  }
`;

const StyledTitleCss = styled.div`
  font-size: 12px;
  color: #8f92a1;
`;

const StyledListBox = styled.div`
  display: flex;
  font-size: 16px;
  padding: 1.2em 0 0.6em;
  color: #171717;
  border-bottom: 1px solid #d6d6d6;

  .unpaid-line:before {
    position: absolute;
    display: block;
    content: '';
    transform: translate(-10px, 1px);
    width: 2px;
    height: 17px;
    background-color: #d6d6d6;
  }
`;

// modal
const StyledDialog = styled(Dialog)`
  .k-dialog {
    border-top-right-radius: 22px;
    border-top-left-radius: 22px;
    border-bottom-right-radius: 22px;
    border-bottom-left-radius: 22px;
  }

  .k-window-titlebar {
    padding: 0px;
  }

  .k-dialog-content {
    margin-bottom: 30px;
  }
  .k-dialog-titlebar {
    background-color: transparent;
    border: none;
  }

  .k-dialog-button-layout-stretched {
    background-color: #000f34;
    color: #fff;
    border-bottom-right-radius: 22px;
    border-bottom-left-radius: 22px;
  }

  .k-button {
    background-color: #000f34;
    color: #fff;
    height: 59px;
  }
`;

const MyinfoButton = styled.button`
  display: flex;
  margin: 2.5em 0 3em;
  padding: 1em;
  color: white;
  border: none;
  background-color: #004aff;
  box-shadow: 0 20px 50px 0 rgba(0, 0, 0, 0.3);
  outline: none;
`;

const CMSbutton = styled.button`
  font-size: 14px;
  outline: none;
  border: none;
  padding: 0.5em 2em 0.6em;
  margin-top: 1em;
  background-color: #000f34;
  color: white;
  border-radius: 8px;
  display: flex;
  position: absolute;
  right: 0;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
`;

export default MyInfoUnpaid;
