import React, { useEffect } from 'react';
import { Route, Switch, useLocation } from 'react-router-dom';

import {
  RecommandJoin,
  CheckRecomandID,
  JoinInsertInfo,
  JoinComplete,
  FindID,
  FindPW,
  ChangePW,
} from '../components/account';

const AccountRoutes = () => {
  const { pathname } = useLocation();

  // 페이지이동시 스크롤 상단
  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };

  useEffect(() => {
    scrollToTop();
  }, [pathname]);

  return (
    <React.Fragment>
      <Switch>
        <Route path="/account/recommandJoin" component={RecommandJoin} />
        <Route path="/account/checkRecomandID" component={CheckRecomandID} />
        <Route path="/account/joinInsertInfo" component={JoinInsertInfo} />
        <Route path="/account/joinComplete" component={JoinComplete} />
        <Route path="/account/findID" component={FindID} />
        <Route path="/account/findPW" component={FindPW} />
        <Route path="/account/changePW" component={ChangePW} />
      </Switch>
    </React.Fragment>
  );
};

export default AccountRoutes;