import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { logoutRequest } from '../../../redux/actions/auth';
import WebHeader from './web/WebHeader';
import MobileHeader from './mobile/MobileHeader';
import _isMobile from '../../../utils/isMobile';

const Header = () => {
  const dispatch = useDispatch();
  const isMobile = _isMobile();
  const headerStatus = useSelector((store) => store.header.headerStatus);

  const _handleLogout = () => {
    dispatch(logoutRequest());
  };

  // 타이머 초기값
  const MINUTES = 30;
  const [minutes, setMinutes] = useState(MINUTES);
  const [seconds, setSeconds] = useState(0);
  /*
  // 타이머
  useEffect(() => {
    const countdown = setInterval(() => {
      if (parseInt(seconds) > 0) {
        setSeconds(parseInt(seconds) - 1);
      }
      if (parseInt(seconds) === 0) {
        if (parseInt(minutes) === 0) {
          clearInterval(countdown);
          _handleLogout();
        } else {
          setMinutes(parseInt(minutes) - 1);
          setSeconds(59);
        }
      }
    }, 1000);
    return () => clearInterval(countdown);
  }, [minutes, seconds]);

  // screen이 바뀌면 초기값이 재설정된다.
  useEffect(() => {
    setMinutes(MINUTES);
    setSeconds(0);
  }, [headerStatus.screenName]);
*/
  if (
    window.location.pathname === '/login' ||
    window.location.pathname === '/account/findID' ||
    window.location.pathname === '/account/findPW' ||
    window.location.pathname === '/account/changePW'
  )
    return null;
  return (
    <React.Fragment>
      {isMobile ? <MobileHeader /> : <WebHeader />}
    </React.Fragment>
  );
};

export default Header;
