import React, { useEffect, useState } from 'react';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
import { SquareButton } from '../../../css/common-component';
import _isMobile from '../../../utils/isMobile';
// css
import {
  Container,
  DropDownList,
  Grid,
  Popup,
  SmallLabel,
  Td,
  Th,
} from '../../../css/commiBest';

const CommiTelecom = (props) => {
  const isMobile = _isMobile();
  const [mounted, setMounted] = useState(true);
  const [state, setState] = useState({
    show: true,
    data: [],
  });

  const _onGoBack = () => {
    props.history.goBack();
  };
  const _getData = async (telecom = 'S') => {
    const url = '/api/member/hp/get_commiTelecom.php';

    const { data, telecomOptions } = await _axios(url, { telecom: telecom });

    return { data, telecomOptions };
  };
  useEffect(() => {
    _getData().then((response) => {
      if (response && mounted) {
        setState({
          ...state,
          telecomOptions: response.telecomOptions,
          data: response.data,
        });
      }
    });
    return () => {
      setMounted(false);
    };
  }, []);

  const handleChange = (event) => {
    let telecom = event.target.value.code;

    _getData(telecom).then((response) => {
      if (response) {
        setState({
          ...state,
          data: response.data,
        });
      }
    });
  };

  const onClick = () => {
    setState({ ...state, show: false });
  };

  const { data, telecomOptions } = state;
  const items = [];

  for (const [index, value] of data.entries()) {
    items.push(
      <div
        className="row"
        style={{ borderBottom: 'solid 0.3px #707070' }}
        key={index}
      >
        <Td className="col-3" style={{ color: '#404040' }}>
          <span className="name">{value.modelName}</span>
        </Td>
        <Td className="col" style={{ color: '#404040' }}>
          {value.priceNew}
        </Td>
        <Td className="col" style={{ color: '#404040' }}>
          {value.priceMnp}
        </Td>
        <Td className="col" style={{ color: '#404040' }}>
          {value.priceChange}
        </Td>
      </div>,
    );
  }
  return (
    <>
      <Container>
        <SmallLabel>통신사별 수수료 정보</SmallLabel>

        <Grid className="contaner">
          <div
            className="row"
            style={{ borderBottom: 'solid 1px #707070', paddingBottom: '12px' }}
          >
            <div className="col-3" style={{ padding: 0 }} onClick={onClick}>
              <DropDownList
                defaultValue={{ name: 'SKT', code: 'S' }}
                data={telecomOptions}
                textField="name"
                dataItemKey="code"
                onChange={handleChange}
              ></DropDownList>
              {''}
              {isMobile && (
                <Popup
                  show={state.show}
                  popupClass={'popup-content'}
                  offset={{ top: 170, left: 21 }}
                >
                  <div onClick={onClick}>
                    통신사별 수수료를 조회 할 수 있습니다.
                  </div>
                </Popup>
              )}
            </div>

            <Th className="col align-self-center"> 신규 </Th>
            <Th className="col align-self-center"> 번호이동 </Th>
            <Th className="col align-self-center"> 기기변경 </Th>
          </div>

          {items}
        </Grid>

        <SquareButton
          width="auto"
          height="54px"
          borderRadius="5px"
          color="#fff"
          boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
          border="solid 1px #e2e2e2"
          backgroundColor="#000f34"
          margin="20px auto"
          onClick={() => _onGoBack()}
        >
          최다 수수료 모델 정보 보기
        </SquareButton>
      </Container>
    </>
  );
};

export default CommiTelecom;
