import React from 'react';
import styled from 'styled-components';

const CmsCheckAlert = ({ props }) => {
  return (
    <StyledCmsCheckDiv>
      <section className="section">
        <div>
          <h1>CMS 정기 납부 정보</h1>
          <h3>미납된 CMS 납부 정보가 있습니다.</h3>
        </div>

        <button
          onClick={() => {
            props.history.push('/mypage/myinfo/cms');
          }}
        >
          확인
        </button>
      </section>
    </StyledCmsCheckDiv>
  );
};

const StyledCmsCheckDiv = styled.div`
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  display: flex;
  top: 0;
  justify-content: center;
  align-items: center;
  .section {
    background-color: #ffffff;
    border-radius: 4px;
    border: solid 1px rgba(0, 0, 0, 0.2);
    width: 85%;
    height: 22%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    div {
      width: 80%;
      margin-bottom: 2em;
    }
    button {
      width: 45%;
      height: 20%;
      background-color: rgba(0, 0, 0, 0.2);
      border: none;
      border-radius: 4px;
    }
  }
`;

export default CmsCheckAlert;
