/*
  설명: 링크 생성 페이지
  - 2021.05.18. 안예린, import수정
*/
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { RadioButton } from '@progress/kendo-react-inputs';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../redux/actions/header';
import _axios from '../../utils/axios';
import _axiosUrl from '../../utils/axiosUrl';
// css
import {
  Container,
  NextButton,
  SquareButton,
} from '../../css/common-component';

export default function ShareLink(props) {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: false, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '다이렉트보험',
  };

  const userId = useSelector((store) => store.auth.authStatus.userInfo.userId);
  const [selectedValue, setSelectedValue] = useState('yes');
  const [link, setLink] = useState('');
  const [isCopy, setIsCopy] = useState(false);

  const [data, setData] = useState();
  const [state, setState] = useState({
    isLoading: true,
    isSearch: false,
    isBatchDelete: false,
    isExcel: false,
    isExport: false,
    loadData: [],
    excelData: [],
    success: false,
    message: null,
    visibleDialog: false,
    confirmMessage: null,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    page: 0,
    pageCount: 1,
    pageable: {
      buttonCount: 10,
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      pageSize: 5,
      searchKey: '',
      searchValue: '',
      searchOptions: [],
      memId: userId,
      minDate: '',
      maxDate: '',
      requestStatus: '',
      statusOptions: [],
    },
  });
  const dispatch = useDispatch();

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const creactShortLink = useCallback(async () => {
    const bitly_url = 'https://api-ssl.bitly.com/v4/shorten';
    const long_url = `http://spiderplatform.co.kr/insu/apply1/?memId=${userId}`;
    const token = 'e58e936d6e0871655d674c279d1d47802ac43faf';
    const response = await _axiosUrl(
      bitly_url,
      { long_url: long_url },
      {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    );
    const { link } = response;
    setLink(link);
  }, [userId]);

  const _getMemberInfo = useCallback(async () => {
    // const url = '/api/partner/chabot/get_member_info.php';
    const url = '/api/partner/insu/get_member_info.php';
    const params = {
      memId: userId,
    };

    const { data } = await _axios(url, params);
    if (data.insuId === null) {
      setData('1');
    } else {
      setData('0');
      creactShortLink();
    }
  }, [creactShortLink, userId]);

  useEffect(() => {
    _getMemberInfo(userId);
  }, [_getMemberInfo, userId]);

  const _getInSuData = async () => {
    const url = '/api/admin/insu/get_request_list.php';
    const params = {
      searchKey: state.searchAble.searchKey,
      searchValue: state.searchAble.searchValue,
      memId: state.searchAble.memId,
      requestStatus: state.searchAble.requestStatus,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
      rows: state.searchAble.pageSize,
      page: state.page,
    };

    const response = await _axios(url, params);

    let items = state.loadData.slice();
    items = items.concat(response.data);

    setState((prevState) => ({
      isLoading: false,
      loadData: items,
      page: state.page + 1,
      rowTotal: response.rowTotal,
      pageCount: response.pageCount,
      searchAble: {
        ...prevState.searchAble,
        searchOptions: response.searchOptions,
        statusOptions: response.statusOptions,
      },
    }));
  };

  const insuRegist = async () => {
    const url = '/api/partner/chabot/set_account_regist.php';
    //const url = '/api/partner/insu/set_account_regist.php';

    const params = {
      memId: userId,
    };
    const { result, message } = await _axios(url, params);

    if (result === '0') {
      setData(result);
      creactShortLink();
      _getInSuData();
    }
  };
  const ontoggle = () => {
    if (selectedValue === 'yes') {
      insuRegist();
    }
  };

  const handleChange = useCallback(
    (e) => {
      setSelectedValue(e.value);
    },
    [setSelectedValue],
  );

  if (data === '0') {
    return (
      <Container
        className="page d-flex flex-wrap justify-content-start"
        style={{ paddingTop: '50px' }}
      >

        <StyledLinkWrap>
          <h1>자동차 다이렉트 보험</h1>
          <div className="title">상담요청 링크</div>
          <div className="subTitle">
            소비자에게 링크를 전달하여 상담요청을 진행할 수 있습니다.
          </div>
          <div className="d-flex align-items-center">
            <input
              style={{
                width: '235px',
                height: '33px',
                margin: '10px 7px 0px 0px',
                border: 'solid 1px #707070',
                backgroundColor: '#ffffff',
                color: '#707070',
                display: 'flex',
                alignItems: 'center',
              }}
              value={link}
              disabled={true}
            />
            <CopyToClipboard text={link} onCopy={() => setIsCopy(true)}>
              <NextButton
                width="91px"
                height="33px"
                margin="10px 0 0 0"
                style={{
                  display: 'flex',
                  fontSize: '14px',
                  justifyContent: 'center',
                  alignItems: 'center',
                  borderRadius: '0',
                }}
              >
                링크 복사
              </NextButton>
            </CopyToClipboard>
          </div>
          {isCopy ? (
            <div style={{ color: '#374057', textAlign: 'center' }}>
              복사되었습니다.
            </div>
          ) : null}
        </StyledLinkWrap>
        {/* <ParentWrap padding="23px 9px 39px 11px" height="auto"></ParentWrap> */}
        {/*
        <div
          style={{
            fontSize: '15px',
            color: '#000f34',
            marginLeft: '20px',
            width: 'auto',
          }}
        >
          자동차 다이렉트 보험 신청 목록
        </div>
        */}
      </Container>
    );
  } else {
    return (
      <Container
        className="page d-flex flex-wrap justify-content-start"
        style={{
          paddingTop: '74px',
          paddingLeft: '20px',
          paddingRight: '15px',
          backgroundColor: '#f8f8f8',
        }}
      >
        <div
          style={{
            fontSize: '15px',
            color: '#000f34',
            marginBottom: '83px',
            width: 'auto',
          }}
        >
          자동차 다이렉트 보험
        </div>
        <StyledInfoWrap>
          <div className="title">제 3자 정보 제공 동의(필수)</div>
          <div className="subTitle">
            {`
차봇 모빌리티(주) 회원 가입을 위해 아래와 같이 개인정보가 활용됩니다.

제공받는 자 : 차봇 모빌리티(주)
제공목적 : (주)차봇모빌리티 보험 영업을 위한 회원 가입
제공항목 : 이름, 연락처
보유기간 : 탈퇴시까지

본 동의는 차봇 모빌리티(주) 영업자 가입을 의미하며 동의하지 않으실 경우 이용에 제약이 있습니다.
`}
          </div>
        </StyledInfoWrap>
        <div style={{ fontSize: '15px', color: '#707070' }}>
          개인정보 수집 이용에 동의하시겠습니까?(필수)
        </div>

        <StyledRadioWrap style={{ textAlign: 'right', width: '100%' }}>
          <RadioButton
            name="group1"
            value="yes"
            checked={selectedValue === 'yes'}
            label="예"
            onChange={handleChange}
          />
          <RadioButton
            name="group1"
            value="no"
            checked={selectedValue === 'no'}
            label="아니오"
            onChange={handleChange}
          />
        </StyledRadioWrap>

        <SquareButton
          width="256px"
          height="54px"
          borderRadius="5px"
          color="#fff"
          border="solid 1px #e2e2e2"
          backgroundColor="#000f34"
          margin="20px auto"
          style={{ fontWeight: 'normal', marginTop: '89px' }}
          onClick={() => ontoggle()}
          disabled={selectedValue === 'no'}
        >
          확인
        </SquareButton>
      </Container>
    );
  }
}

const StyledLinkWrap = styled.div`
  width: 100%;
  padding: 11px 21px 10px;
  margin-bottom: 44px;

  h1 {
    margin-bottom: 2em;
  }
  
  .title {
    color: #000f34;
  }
  .subTitle {
    color: #4b4b4b;
    font-size: 12px;
  }
`;

const StyledInfoWrap = styled.div`
  width: 100%;
  padding-bottom: 4rem;
  border-bottom: 1px solid #d9d9d9;
  margin-bottom: 12px;
  .title {
    color: #000f34;
  }
  .subTitle {
    color: #4b4b4b;
    font-size: 15px;
    white-space: break-spaces;
  }
`;

const StyledRadioWrap = styled.div`
  .k-radio:checked {
    border-color: #000f34;
    color: #000f34;
    background-color: #fff;
  }

  .k-radio:checked:focus {
    box-shadow: none;
  }
`;
