import React from 'react';
import _isMobile from '../../../../utils/isMobile';
import PointHomeMobile from './PointHomeMobile';
import PointHomeWeb from './PointHomeWeb';

const PointHome = () => {
  const isMobile = _isMobile();

  return (
    <>
      {isMobile ? <PointHomeMobile /> : <PointHomeWeb />}
    </>
  );
};

export default PointHome;