/* **********************************************************************
 * 관리자페이지 > 포인트관리 > 포인트목록
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import LoginRoute from '../../components/common/AuthRoute';
import PointList from '../../components/admin/point/PointList';
import PointInfo from '../../components/admin/point/PointInfo';
import CommiList from '../../components/admin/point/CommiList';
import CommiInfo from '../../components/admin/point/CommiInfo';
import CommiAccurateList from '../../components/admin/point/AccurateList';
import CommiAccurateInfo from '../../components/admin/point/AccurateInfo';
import CashRequestList from '../../components/admin/point/CashRequestList';
import CashRequestInfo from '../../components/admin/point/CashRequestInfo';
import CashRequestExcelUp from '../../components/admin/point/CashRequestExcelUp';

const Point = () => {
  return (
    <Switch>
      <LoginRoute
        exact
        path="/admin/point"
        render={(props) => <PointList {...props} />}
      />
      <LoginRoute
        path="/admin/point/info"
        render={(props) => <PointInfo {...props} />}
      />
      <LoginRoute
        exact      
        path="/admin/point/commi"
        render={(props) => <CommiList {...props} />}
      />
      <LoginRoute
        path="/admin/point/commi/info"
        render={(props) => <CommiInfo {...props} />}
      />  
      <LoginRoute
        exact      
        path="/admin/point/accurate"
        render={(props) => <CommiAccurateList {...props} />}
      />
      <LoginRoute
        path="/admin/point/accurate/info"
        render={(props) => <CommiAccurateInfo {...props} />}
      /> 
      <LoginRoute
        exact      
        path="/admin/point/request"
        render={(props) => <CashRequestList {...props} />}
      />
      <LoginRoute
        path="/admin/point/request/info"
        render={(props) => <CashRequestInfo {...props} />}
      />
      <LoginRoute
        path="/admin/point/request/excelUp"
        render={(props) => <CashRequestExcelUp {...props} />}
      />
    </Switch>
  );
};

export default Point;
