/*
  설명: 관리자 Info 페이지
  - 2021.05.27. 안예린 userId 경로 수정 , import 정리
*/
import React, { useEffect, useState } from 'react';
import { Input } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { useDispatch, useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const MyInfo = (props) => {
  const headerInfo = {
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: false, // Back 버튼
    titleName: '',
  };
  const dispatch = useDispatch();
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);
  const [state, setState] = useState({
    isLoading: true,
    userId: userInfo.userId,
    userPw: '',
    userName: '',
    phone: '',
    email: '',
    useYn: '',
    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/admin/get_my_info.php';
    const params = {
      userId: state.userId,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      userPw: data.userPw,
      userName: data.userName,
      phone: data.phone,
      email: data.email,
      useYn: data.useYn,
    });
  };

  const _setData = async () => {
    const url = '/api/admin/set_my_info_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _toggleDialog = () => {
    setState({ ...state, visibleDialog: !state.visibleDialog });
  };

  useEffect(() => {
    _getData();
  }, []);
  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const userNameMessage = "'회원명'을 입력해주세요!";
  const userPwMessage = "'비밀번호'를 입력해주세요!";

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '600px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form
        style={{ display: 'flex', justifyContent: 'center' }}
        onSubmit={_handleSubmit}
      >
        <Wrap width={'98%'}>
          <Title>내정보 〉 상세정보</Title>
          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="userId"
                  value={state.userId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  disabled={true}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="userName"
                  value={state.userName}
                  style={{ width: '74%' }}
                  maxLength={10}
                  required={true}
                  validationMessage={userNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>비밀번호</Label>
                <Input
                  validityStyles={false}
                  type="password"
                  name="userPw"
                  value={state.userPw}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={userPwMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>연락처</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="phone"
                  value={state.phone}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>이메일</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="email"
                  value={state.email}
                  style={{ width: '74%' }}
                  maxLength={30}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() =>
                  setState({
                    ...state,
                    isSave: true,
                  })
                }
              >
                저장하기
              </Button>
            )}

            <Button
              style={{ marginLeft: '10px' }}
              type={'button'}
              onClick={() => props.history.goBack()}
            >
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default MyInfo;
