/* 설명 : 비밀번호 찾는 페이지
-2021.06.07 안예린 페이지 리뉴얼
*/
import React, { useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import Countdown from 'react-countdown';
// 개발자 라이브러리
import _axios from '../../utils/axios';
// css
import { HpNoRoundInput, RoundButton2 } from '../../styles/common-component';
import '../../css/kendo.css';
import {
  Container,
  Wrap,
  LogoWrap,
  FindIDPW,
  FindIDPWButton,
  LoginModal,
  HpNoRoundInputBox,
  WarningIcon,
  CountdownWrap,
} from '../../styles/account';

import CommonInput from '../common/CommonInput';
import CommonButton from '../common/Button';
// images & icons
import LoginLogo from '../../images/account/logo_9930.svg';
import warning from '../../images/account/warning.svg';

const FindPW = (props) => {
  const [redCertifyNo, setRedCertifyNo] = useState(false);
  const [red, setRed] = useState(false);
  const [error, setError] = useState('');
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);
  const [state, setState] = useState({
    userId: userInfo.userId,
    userPw: '',
    userName: '',
    hpNo: '',
    success: false,
    message: null,
    visibleDialog: false,
    isCertify: false,
    certifyNo: '',
    inputCertifyNo: '',
    date: Date.now() + 180000,
    isTimeOut: false,
  });
  const countDownRef = useRef();

  //인증번호 받기
  const _getCertifyNo = async () => {
    const url = '/api/account/get_certify_no.php';
    const params = {
      assort: 'P',
      userId: state.userId,
      hpNo: state.hpNo,
    };
    const response = await _axios(url, params);
    if (response.result === '0') {
      countDownRef.current.start();
      setState({
        ...state,
        isCertify: true,
        isTimeOut: false,
        certifyNo: response.certifyNo,
      });
    } else {
      setState({
        ...state,
        success: true,
        visibleDialog: true,
      });
      setRed(true);
      setError('정보가 올바르지 않습니다 .');
    }
  };

  const _handleInputChange = (e) => {
    setRed(false);
    setRedCertifyNo(false);
    setError(false);
    setState({
      ...state,
      message: '',
      [e.target.name]: e.target.value,
    });
    setError('');
    setRed(false);
  };

  const _handleSubmit = (e) => {
    setError('');
    e.preventDefault();
    _getCertifyNo();
  };

  const _onVaildCertifyNo = () => {
    // eslint-disable-next-line eqeqeq
    if (state.certifyNo == state.inputCertifyNo) {
      props.history.push({
        pathname: '/account/changePW',
        userId: state.userId,
      });
    } else {
      setState({
        ...state,
        success: true,
        message: '인증번호가 다릅니다. 다시 확인해주세요',
        visibleDialog: true,
      });
      setRed(false);
      setRedCertifyNo(true);
    }
  };

  // Renderer callback with condition
  const renderer = ({ minutes, seconds, completed }) => {
    // Render a countdown
    return (
      <span>
        {minutes}:{seconds}
      </span>
    );
  };

  const _onTimeComplete = () => {
    setState({ ...state, isTimeOut: true, date: Date.now() + 180000 });
  };
  const hpNoMessage = '휴대폰번호를 입력해 주세요';
  const memNameMessage = '이름을 입력해 주세요';
  const certifyMessage = '인증번호를 입력해 주세요';

  return (
    <Container>
      <Wrap>
        <LogoWrap
          onClick={() =>
            props.history.push({
              pathname: '/',
            })
          }
        >
          <img src={LoginLogo} alt="logo"></img>
        </LogoWrap>
        <form onSubmit={_handleSubmit}>
          <LoginModal display={state.isGetData ? 'block' : 'none'}>
            <h4>아이디가 전송되었습니다.</h4>
            <p>
              스파이더플랫폼 공식 카카오톡으로 아이디를 안내해
              <br />
              드렸습니다.
            </p>
          </LoginModal>
          <CommonInput
            inputtype="text"
            width="100%"
            height="60px"
            value={state.userId}
            autoComplete="off"
            name="userId"
            titlename="아이디"
            placeholder="아이디를 입력해주세요."
            onChange={_handleInputChange}
            required={true}
            validationMessage={memNameMessage}
            validityStyles={false}
            disabled={state.isCertify}
          />
          <HpNoRoundInputBox className={red ? 'is-active' : ''}>
            <p>핸드폰 번호</p>
            <HpNoRoundInput
              className={red ? 'is-active findidinput' : 'findidinput'}
              type="text"
              value={state.hpNo}
              autoComplete="off"
              name="hpNo"
              placeholder="010-0000-0000"
              onChange={_handleInputChange}
              mask="000-0000-0000"
              required={true}
              width="100%"
              height="60px"
              margin="0 0 20px 0"
              validationMessage={hpNoMessage}
              validityStyles={false}
              borderdisable={state.isCertify}
              disabled={state.isCertify}
              error={state.message}
            ></HpNoRoundInput>
            <WarningIcon display={red ? 'block' : 'none'}>
              <a id="warning" className="warning">
                <img src={warning} />
              </a>
            </WarningIcon>
            <h1 className="error-red">{error}</h1>
          </HpNoRoundInputBox>
          {state.isTimeOut === true && (
            <RoundButton2
              display="none"
              width={'252px'}
              height={'48px'}
              color="#000f34"
              margin="0 0 0 0"
              bordershadow="0 0 10px 0 rgba(0, 0, 0, 0.4)"
              border="solid 2px #e2e2e2"
              onClick={() => _getCertifyNo()}
            >
              인증번호 재전송
            </RoundButton2>
          )}

          {state.isCertify === false && (
            <CommonButton type="submit" kind="wideBtn_01">
              인증번호 받기
            </CommonButton>
          )}

          {state.isCertify && state.isTimeOut === false && (
            <CommonInput
              kind={redCertifyNo ? 'is-active' : ''}
              inputtype="text"
              value={state.inputCertifyNo}
              autoComplete="off"
              titlename="인증번호"
              name="inputCertifyNo"
              placeholder="인증번호를 입력하세요."
              onChange={_handleInputChange}
              required={true}
              width="100%"
              height="60px"
              validationMessage={certifyMessage}
              validityStyles={false}
              error2={state.message}
            />
          )}

          <CountdownWrap display={state.isCertify ? 'block' : 'none'}>
            <Countdown
              ref={countDownRef}
              date={state.date}
              renderer={renderer}
              autoStart={false}
              onComplete={_onTimeComplete}
            />
          </CountdownWrap>

          {state.isCertify && (
            <CommonButton
              type="button"
              onClickHandler={() => _onVaildCertifyNo()}
              kind="btn_01"
              width="100%"
              height="44px"
            >
              완료
            </CommonButton>
          )}
        </form>
        <FindIDPW display={state.isCertify ? 'none' : 'flex'}>
          <FindIDPWButton to="/login">로그인 | </FindIDPWButton>
          <FindIDPWButton to="/account/findID"> 아이디찾기</FindIDPWButton>
        </FindIDPW>
        {state.isCertify && (
          <FindIDPW display={state.isCertify ? 'flex' : 'none'}>
            <FindIDPWButton to="/login">로그인 | </FindIDPWButton>
            <FindIDPWButton to="/account/findID"> 아이디찾기</FindIDPWButton>
          </FindIDPW>
        )}
      </Wrap>
    </Container>
  );
};

export default FindPW;
