import { Container, Col, Row } from 'react-bootstrap';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
// 개발자 component
import _axios from '../../../utils/axios';
import Notice from './Notice';
import PointStatus from './PointStatus';
import Service from './Service';
import HpRequestStatus from './HpRequestStatus';

const HomeWeb = () => {
  const date = new Date();
  const year = date.getFullYear();
  const month = date.getMonth() + 1;

  const authStatus = useSelector((store) => store.auth.authStatus);

  const [state, setState] = useState({
    isLoading: true,
    userId: authStatus.userInfo.userId,

    totalPoint: 0,
    monthPoint: 0,
    chartData: [],
    hpRequest: [],
    noticeData: []
  });

  const _getData = async () => {
    const url = '/api/member/get_mypage_home.php';
    const params = {
      userId: state.userId,
      page: 1,
      rows: 10,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      totalPoint: response.totalPoint,
      monthPoint: response.monthPoint,
      chartData: response.chartData,
      hpRequest: response.hpRequest,
      noticeData: response.noticeData,
    });
  };
  
  useEffect(() => {
    _getData();
  }, []);

  return (
    <Wrap>
      {state.isLoading ? (
        <div>Loading...</div>
      ) : (
        <StyledContainer className="ml-0 mr-0 p-0">
          <Row className="ml-0 mr-0 justify-content-between">
            <Col md={12} xl={6} className="p-0">
              <PointStatus
                totalPoint={state.totalPoint}
                monthPoint={state.monthPoint}
                chartData={state.chartData}
                currentMonth={year + '년 ' + month + '월'}
              />
              <Service />
            </Col>
    
            <Col md={12} xl={6} className="p-0">
              <HpRequestStatus 
                hpStatus_0={state.hpRequest.status_0}
                hpStatus_1={state.hpRequest.status_1}
                hpStatus_9={state.hpRequest.status_9}
              />

              <Notice 
                noticeData={state.noticeData}
              />
            </Col>
          </Row>
        </StyledContainer>
      )}
    </Wrap>
  );
};

export const Wrap = styled.section`
  width: 100%;
  height: 100%;
`;

const StyledContainer = styled(Container)`
  width: auto;
`;
export default HomeWeb;
