import React from 'react';
import { Route } from "react-router-dom";
import {
  ShareLink,
  ApplyGuide,
  Apply1,
  Apply2,
  Apply3,
  ApplyComplete,
} from '../components/insu';

export default function InsuRoute() {
  return (
    <React.Fragment>
      <Route
        exact
        path={`/insu/shareLink`}
        component={ShareLink}
      />
      <Route
        exact
        path={`/insu/applyGuide`}
        component={ApplyGuide}
      />
      <Route
        exact
        path={`/insu/apply1`}
        component={Apply1}
      />
      <Route
        exact
        path={`/insu/apply2`}
        component={Apply2}
      />
      <Route
        exact
        path={`/insu/apply3`}
        component={Apply3}
      />
      <Route
        exact
        path={`/insu/applyComplete`}
        component={ApplyComplete}
      />
    </React.Fragment>
  );
}
