import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import { 
  CommiBest,
  CommiTelecom,
  RequestList,
  RequestInfo
} from '../../components/mypage/hpRequest';

/* **********************************************************************
* 마이페이지 > 휴대폰신청 > 신청목록 Route
*********************************************************************** */
const HpRequest = () => {
  return (
    <Switch>
      <AuthRoute 
        exact
        path="/mypage/hpRequest" 
        render={ props => <RequestList {...props} /> } 
      />
      <AuthRoute 
        path="/mypage/hpRequest/info" 
        render={ props => <RequestInfo {...props} /> } 
      />
      <AuthRoute
        exact
        path="/mypage/hpRequest/commiBest"
        render={(props) => <CommiBest {...props} />}
      />
      <AuthRoute
        path="/mypage/hpRequest/commiTelecom"
        render={(props) => <CommiTelecom {...props} />}
      />      
    </Switch>
  );
}

export default HpRequest;