/*
  설명: 신청 완료 페이지
  - 2021.05.20. 안예린, import 수정
*/
import React, { useCallback, useEffect } from 'react';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import {
  BigLabel,
  Container,
  ParentWrap,
  RoundButton,
  SmallLabel,
} from '../../../css/common-component';

export default function Complete({ match, history }) {
  const dispatch = useDispatch();

  const _initHpStore = useCallback(() => {
    dispatch(actions.initHpRequest());
  }, [dispatch]);

  useEffect(() => {
    //dispatch(stackRequest('H', 'dd'));
    return () => {
      _initHpStore();
    };
  }, [_initHpStore, dispatch]);

  return (
    <Container className="page">
      <ParentWrap
        style={{
          display: 'flex',
          alignItems: 'center',
          flexDirection: 'column',
        }}
        padding="190px 39px 39px 43px"
      >
        <BigLabel textAlign="center" height="auto" style={{ marginTop: '1em' }}>
          신청이 완료되었습니다.
        </BigLabel>

        <SmallLabel
          height="16px"
          bold="normal"
          textAlign="center"
          margin="79px auto 179px auto"
          color="#404040"
        >
          곧, 상담사가 연락을 드리겠습니다. 감사합니다.
        </SmallLabel>

        <RoundButton
          width={'252px'}
          height={'48px'}
          color="#000f34"
          margin="0 0 1em 0"
          bordershadow="0 0 10px 0 rgba(0, 0, 0, 0.4)"
          border="solid 2px #e2e2e2"
          to="/mypage/hpRequest"
          onClick={() => _initHpStore()}
        >
          신청내역 확인
        </RoundButton>
      </ParentWrap>
    </Container>
  );
}
