/* **********************************************************************
 * 관리자페이지 > 회원관리 > 회원목록
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import MemberList from '../../components/admin/member/MemberList';
import MemberInfo from '../../components/admin/member/MemberInfo';
import Losmap from '../../components/admin/member/Losmap'; // Losmap

const Member = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/admin/member"
        render={(props) => <MemberList {...props} />}
      />
      <AuthRoute
        path="/admin/member/info"
        render={(props) => <MemberInfo {...props} />}
      />
      <AuthRoute
        path="/admin/member/losmap"
        render={(props) => <Losmap {...props} />}
      />      
    </Switch>
  );
};

export default Member;