/* **********************************************************************
 * 모바일홈 > 보너스 자세히 보기> 휴대폰 실적 포인트
 * 작성자: 이해영
 * 2021. 06. 07. 양민석. 포인트 월표시 개선
 *********************************************************************** */
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
//개발자 라이브러리
import _axios from '../../../../utils/axios';
import PointHomeChart from './PointHomeChart';
// css
import { Table, Td, Th, Tr } from '../../../../css/list';
import { PhoneButton, PhoneTable } from './Hp';
import theme from '../../../../styles/theme';
import { setHeader } from '../../../../redux/actions/header';

const Rental = () => {
  const headerInfo = {
    isBackground: false,
    isBugger: false,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '렌탈 실적 포인트',
  };
  const date = new Date();
  const todayMonth = date.getMonth();
  const [monthIndex, setMonthIndex] = useState(todayMonth);

  const dispatch = useDispatch();

  const { userId } = useSelector((store) => store.auth.login.userInfo);

  const [state, setState] = useState([]);

  const year = date.getFullYear();

  const _getData = useCallback(async () => {
    const url = '/api/member/get_point_rental.php';
    const params = {
      userId: userId,
      year: '2021',
    };

    const data = await _axios(url, params);
    setState(data);
  }, [userId]);

  useEffect(() => {
    _getData();
  }, [_getData]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);
  return (
    <CommiRentalInner>
      <RentalPoint>
        <div className="month-box">
          <p className="month-title">렌탈 포인트</p>
          <div className="month-sales">
            <p className="directly">뎁스판매</p>
            <p className="depth">직접판매</p>
          </div>
        </div>

        <div>
          <ul
            className="phone-month-list"
            style={{
              height: '80px',
            }}
          >
            {state.length !== 0 &&
              state.chartData.map((n, index) => (
                <PointHomeChart
                  key={index}
                  monthIndex={monthIndex}
                  setMonthIndex={setMonthIndex}
                  percent={n}
                  index={index}
                  rental={true}
                />
              ))}
          </ul>
        </div>

        <div className="phone-num">
          <ol>
            {state.length !== 0 &&
              state.chartData.map((n, index) => <li key={index}>{n.month}</li>)}
          </ol>
        </div>

        <div>
          <ul className="detail-list">
            <li className="detail-list-title">
              {state.length !== 0 &&
                state.chartData.map((n, index) => (
                  <div key={index}>
                    {parseInt(n.month) === monthIndex + 1 && (
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                        }}
                        key={index}
                      >
                        <p>
                          {year}년{n.month}월
                        </p>
                        <p>{n.point}</p>
                      </div>
                    )}
                  </div>
                ))}
            </li>
          </ul>
        </div>
      </RentalPoint>

      <PhoneTable>
        <h3>렌탈 판매 실적</h3>

        <Tr width={'100%'} height={'35px'}>
          <Th width={'28%'}>개통일자</Th>
          <Th width={'20.5%'}>고객명</Th>
          <Th width={'29.5%'}>구분</Th>
          <Th width={'22%'}>포인트</Th>
        </Tr>

        <Table width={'100%'} className="table">
          {state.length !== 0 &&
            state.listData.map((item, index) => (
              <div key={index}>
                {parseInt(item.month) === monthIndex + 1 && (
                  <Tr width={'100%'} height={'30px'} key={index}>
                    <Td width={'28%'}>{item.wdate}</Td>
                    <Td width={'20.5%'}>{item.custName}</Td>
                    <Td width={'29.5%'}>{item.assort}</Td>
                    <Td width={'22%'}>{item.point}</Td>
                  </Tr>
                )}
              </div>
            ))}
        </Table>
      </PhoneTable>

      <PhoneButton>
        <Link to="/mypage/commi">
          <button type="button">렌탈 신청 전체 목록</button>
        </Link>
      </PhoneButton>
    </CommiRentalInner>
  );
};

export default Rental;

export const CommiRentalInner = styled.section`
  width: 100%;
  padding: 2em 1.5em;
  background-color: #f8f8f8;

  ul {
    padding: 0;
  }
  li {
    position: relative;
    list-style: none;
  }
  p {
    margin: 0;
  }
`;

const RentalPoint = styled.section`
  padding: 2em 1.5em;
  margin-top: 0;
  background-color: ${theme.colors.white};
  border-radius: 3px;

  .month-box {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid ${theme.colors.grey2};
  }

  .month-title {
    ${theme.H3};
  }

  .month-sales {
    display: flex;
    padding-top: 1em;

    p {
      position: relative;
      ${theme.Button2};

      &:before {
        position: absolute;
        content: '';
        display: block;
        left: -19px;
        width: 12px;
        height: 12px;
        border-radius: 28px;
      }

      &.directly {
        margin-right: 3em;

        &:before {
          background: ${theme.colors.murstard};
        }
      }

      &.depth {
        &:before {
          background: ${theme.colors.royalblue};
        }
      }
    }
  }

  .phone-month {
    &-list {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
      margin: 1em 0 10px;

      li {
        position: relative;
        text-align: center;
      }
    }
  }

  .phone-num {
    ol {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
    }

    li {
      display: flex;
      justify-content: center;
      position: relative;
      width: 15.3px;
      color: ${theme.colors.grey1};
      ${theme.Button2};

      /* 삼각형 */
      &:before {
        position: absolute;
        display: block;
        content: '';
        top: 49px;
        left: 2px;
        width: 0;
        height: 0;
        border-top: 1.5em solid transparent;
        border-right: 1em solid transparent;
        border-bottom: 1.5em solid transparent;
        border-left: 1em solid transparent;
      }

      &.active {
        &:before {
          border-bottom: 1.5em solid ${theme.colors.boxGray};
        }
      }
    }
  }

  .detail-list {
    margin-top: 4em;
    padding: 1.5em;
    background-color: ${theme.colors.boxGray};

    &-title {
      display: flex;
      justify-content: space-between;
      ${theme.H5};
      display: flex;
      flex-direction: column;
    }
  }
`;
