import React, { useState } from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import {
  BigLabel,
  ParentWrap,
  SmallLabel,
  SquareButton,
} from '../../../css/common-component';

export default function FeeSetting({ history }) {
  const dispatch = useDispatch();
  const [color, setColor] = useState('#fff');
  const [fontColor, setFontColor] = useState('#000f34');
  //   const _initHpStore = () => {
  //     dispatch(actions.initHpRequest());
  //   };

  const _selectBanefitType = (marginPrice) => {
    setColor('#000f34');

    dispatch(actions.selectMarginPrice(marginPrice));
    history.push('/partnership/hp/currentTelecom');
  };
  return (
    <Container>
      <ParentWrap
        padding="0px 0px 0px"
        style={{ backgroundColor: 'transparent', borderColor: 'transparent' }}
      >
        <BigLabel
          textAlign="left"
          margin="0 auto 10px auto"
          width="100%"
          bold="normal"
        >
          수수료 설정
        </BigLabel>
        <SmallLabel
          width="100%"
          height="29px"
          fontsize="12px"
          textalign="left"
          margin={'0px auto 70px auto'}
          bold="normal"
          color="#4b4b4bs"
        >
          아래 설정된 수수료가 실제 내가 받는 수수료입니다.
          <br /> 나머지 수수료는 신청자에게 할인 또는 차액 지급됩니다.
        </SmallLabel>
        <div
          style={{
            display: 'flex',
            marginTop: '55px',
            marginBottom: '55px',
            justifyContent: 'center',
            flexWrap: 'wrap',
          }}
        >
          <StyledSquareButton
            width="118px"
            height="118px"
            margin="0 20px 20px 0"
            border="solid 1px #e2e2e2"
            backgroundColor={color}
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => {
              _selectBanefitType(50000);
            }}
          >
            <span
              style={{
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
                fontFamily: 'NanumGothic',
              }}
            >{`50,000원`}</span>
          </StyledSquareButton>
          <StyledSquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor={color}
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => {
              _selectBanefitType(70000);
            }}
          >
            <span
              style={{
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
                fontFamily: 'NanumGothic',
              }}
            >{`70,000원`}</span>
          </StyledSquareButton>
          <StyledSquareButton
            width="118px"
            height="118px"
            margin="0 20px 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor={color}
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => {
              _selectBanefitType(100000);
            }}
          >
            <span
              style={{
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
                fontFamily: 'NanumGothic',
              }}
            >{`100,000원`}</span>
          </StyledSquareButton>
          <StyledSquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor={color}
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => {
              _selectBanefitType(150000);
            }}
          >
            <span
              style={{
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
                fontFamily: 'NanumGothic',
              }}
            >{`150,000원`}</span>
          </StyledSquareButton>

          <StyledSquareButton
            width="258px"
            height="118px"
            margin="16px 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor={color}
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => {
              _selectBanefitType(0);
            }}
          >
            <span
              style={{
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
                fontFamily: 'NanumGothic',
              }}
            >{`FULL 마진`}</span>
          </StyledSquareButton>
        </div>
      </ParentWrap>
    </Container>
  );
}

export const Container = styled.div`
  display: flex;

  width: 100%;
  height: 100%;
  overflow-y: auto;

  padding: 131px 50px 74px 50px;
  background-color: #fafafa;
`;

export const StyledSquareButton = styled(SquareButton)`
  &:hover,
  &:focus {
    background-color: #000f34;
    color: #fff;
  }

  span:hover,
  span:focus {
    color: #fff;
  }
`;
