import React, { useCallback, useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const SalesInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    isDelete: false,
    idx: props.location.idx,
    mode: props.location.mode,
    sponsId: '',
    sponsName: '',
    memId: '',
    memName: '',
    price: '',
    assort: '',
    assortOptions: [],
    status: '',
    statusOptions: [],
    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = useCallback(async () => {
    const url = '/api/admin/sales/get_sales_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      leg: data.leg,
      sponsId: data.sponsId,
      sponsName: data.sponsName,
      memId: data.memId,
      memName: data.memName,
      price: data.price,
      assort: { code: data.assort, name: data.assortName },
      assortOptions: data.assortOptions,
      status: { code: data.status, name: data.statusName },
      statusOptions: data.statusOptions,
    });
  }, [state]);

  const _setData = async () => {
    const url = '/api/admin/sales/set_sales_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  // 매출 정보 삭제
  const _deleteData = useCallback(async () => {
    const url = '/api/admin/sales/set_sales_delete.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      isDelete: false,
      success: true,
      message: message,
      visibleDialog: true,
    });
  }, [state]);

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  useEffect(() => {
    if (state.isDelete) {
      _deleteData();
    }
  }, [state.isDelete]);

  const assortMessage = "'매출구분'을 선택해주세요!";
  const memIdMessage = "'회원ID'를 입력해주세요!";
  const memNameMessage = "'회원명'을 입력해주세요!";
  const priceMessage = "'매출금액'을 입력해주세요!";
  const statusMessage = "'정산상태'를 선택해주세요!";

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>매출원장 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>매출구분</Label>
                <ComboBox
                  style={{
                    width: '74%',
                    color: '#dcdcdc',
                    marginBottom: '2px',
                  }}
                  name="assort"
                  value={state.assort}
                  data={state.assortOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={assortMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>스폰서ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="sponsId"
                  value={state.sponsId}
                  style={{ width: '74%' }}
                  maxLength={100}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>스폰서명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="sponsName"
                  value={state.sponsName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memId"
                  value={state.memId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={memIdMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memName"
                  value={state.memName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={memNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>매출금액</Label>
                <Input
                  validityStyles={false}
                  type="number"
                  name="price"
                  value={state.price}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={priceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산상태</Label>
                <ComboBox
                  style={{ width: '74%', color: '#dcdcdc' }}
                  name="status"
                  value={state.status}
                  data={state.statusOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={statusMessage}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default SalesInfo;
