import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
// 개발자 라이브러리
import _axios from '../../../../utils/axios';

const MemberMenu = ({ onCloseMenu }) => {
  const history = useHistory();
  const authStatus = useSelector((store) => store.auth.authStatus);

  const [state, setState] = useState({
    userId: authStatus.userInfo.userId,
    isLoading: true,
    loadData: [],
  });

  const _getData = async () => {
    const url = '/api/account/get_menu.php';
    const params = {
      userId: state.userId,
    };

    await _axios(url, params).then((response) => {
      setState({
        ...state,
        loadData: response.data,
        isLoading: false,
      });
    });
  };

  useEffect(() => {
    if (state.isLoading) {
      _getData();
    }
  }, [state.isLoading]);

  return (
    <Container>
      {state.isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          <div
            onClick={() => {
              history.push('/mypage/myinfo');
              onCloseMenu({ menuOpen: false });
            }}
            style={{ paddingLeft: '10px' }}
          >
            내정보
          </div>
          {state.loadData.map((menu) => (
            <MainWrap key={menu.no}>
              <MenuName
                onClick={() => {
                  history.push(menu.linkTo);
                  onCloseMenu({ menuOpen: false });
                }}
              >
                {menu.menuName}
              </MenuName>

              {menu.children.length > 0 && (
                <SubWrap>
                  {menu.children.map((child) => (
                    <ChildName
                      key={child.no}
                      onClick={() => {
                        history.push(child.linkTo);
                        onCloseMenu({ menuOpen: false });
                      }}
                    >
                      {child.menuName}
                    </ChildName>
                  ))}
                </SubWrap>
              )}
            </MainWrap>
          ))}
        </Wrap>
      )}
    </Container>
  );
};

const Container = styled.div`
  padding-top: 105px;
  width: 100%;
  height: 100%;
`;

const Wrap = styled.div`
  width: 100%;
  height: 100%;
  padding-left: 20px;
  padding-top: 25px;
`;

const MainWrap = styled.div`
  width: 100%;
`;

const MenuName = styled.div`
  width: 100%;
  color: #666666;
  padding-left: 10px;
  padding-top: 20px;
`;

const SubWrap = styled.div`
  padding-left: 30px;
  padding-top: 20px;
`;

const ChildName = styled.div`
  width: 100%;
  height: 30px;
  color: #666666;
`;

export default MemberMenu;
