import React, { useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { Upload } from '@progress/kendo-react-upload';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
import { connect } from 'react-redux';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
import Axios from 'axios';
//import { scrollRequest, stackRequest } from '../../../redux/actions/auth';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const fileStatuses = [
  'UploadFailed',
  'Initial',
  'Selected',
  'Uploading',
  'Uploaded',
  'RemoveFailed',
  'Removing',
];

const SupportExcelUp = (props) => {
  const [state, setState] = useState({
    telecom: '',
    telecomOptions: [
      { code: 'S', name: 'SKT' },
      { code: 'K', name: 'KT' },
      { code: 'L', name: 'LGU+' },
      { code: 'A', name: '알뜰폰' },
    ],
    excelFile: '',

    success: false,
    message: null,
    visibleDialog: false,
    files: [],
    events: [],
    filePreviews: {},
  });

  const onFileChange = (event) => {
    setState({
      ...state,
      files: event.target.files[0],
    });
  };

  const uploadFileData = async () => {
    let formData = new FormData();
    formData.append('files', state.file);
    await Axios.post(
      'http://spiderplatform.co.kr/utils/excelSave.php',
      formData,
      {
        headers: { 'Content-Type': 'multipart/form-data;charset=utf-8;' },
      },
    )
      .then((response) => {})
      .catch((err) => {});
  };

  // 저장
  const _setData = async () => {
    uploadFileData();
    const params = {
      telecom: state.telecom,
      excelFile: state.files.length === 0 ? '' : state.files.name,
    };

    const url = '/api/hp/supportPrice/set_excel_update.php';
    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({ ...state, visibleDialog: !state.visibleDialog });
  };

  useEffect(() => {
    props.stackRequest('S', '엑셀등록');
  }, []);

  const telecomMessage = "'통신사'를 선택해주세요!";
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';
  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 공시지원가 〉 목록 〉 엑셀등록</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신사</Label>
                <ComboBox
                  style={{ width: '64.4%', color: '#dcdcdc' }}
                  name="telecom"
                  value={state.telecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={telecomMessage}
                />
              </Td>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>썸네일</Label>
                <div id="container">
                  <h4>{state.msg}</h4>
                  <input onChange={onFileChange} type="file"></input>
                </div>
              </Td>
            </Tr>
          </Table>
          <ButtonWrap>
            <Button
              type={'submit'}
              onClick={() => setState({ ...state, isSave: true })}
              primary={true}
            >
              저장하기
            </Button>
            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default SupportExcelUp;
