import styled from 'styled-components';

export const Container = styled.div`
  width: 100%;
`;

export const Wrap = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
`;

export const CardWrap = styled.div`
  display: flex;

  @media (max-width: 768px) {
    width: 100%;
    margin-top: 50px;
  }

  @media (min-width: 769px) {
    width: 50%;
  }
`;

export const Header = styled.div`
  width: 100%;
  line-height: 2em;
  font-size: 0.9em;
  color: #222222;
  font-weight: 600;
  background: #dcdcdc;
  padding-left: 10px;
`;

export const Body = styled.div`
  display: flex;
  flex-direction: column;
  color: #666666;
  background-color: #fff;
  font-size: 0.8em;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 10px;
`;

export const Row = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

export const CellWrap = styled.div`
  display: flex;
  flex-direction: row;
  width: 98%;
  height: 2em;
  border-bottom: 1px solid #dcdcdc;
`;

export const CellHeader = styled.div`
  color: #222222;
  padding-top: 0.3em;
`;

export const Cell = styled.div`
  width: 55%;
  line-height: 1.5em;
  color: #666666;
  padding-top: 0.3em;
`;
