import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import {
  Container,
  MenuButton,
  MenuButtonWrap,
  MenuImg,
} from '../../../css/common-component';
import { CurrentWrap } from './CurrentTelecom';
// icon & images
import attle_logo from '../../../images/sale-bt.svg';
import kt_logo from '../../../images/kt-logo.svg';
import lguplus_logo from '../../../images/lguflus-logo.svg';
import skt_logo from '../../../images/sk-logo.svg';

export default function ChangeTelecom(props) {
  const dispatch = useDispatch();
  const _changeTelecom = (telecom) => {
    dispatch(actions.changeTelecom(telecom));
    props.history.push('/partnership/hp/manufacturerType');
  };
  useEffect(() => {
  }, [dispatch]);

  return (
    <Container className="page" style={{ padding: '6em 2em'}}>
      <CurrentWrap>
        <h1>가입 희망 통신사</h1>
        <p className="carrier">이용하시고자 하시는 통신사를 선택하세요.</p>

        <MenuButtonWrap className="row">
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _changeTelecom('S');
            }}
          >
            <MenuImg src={skt_logo} alt="skt" />
          </MenuButton>
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _changeTelecom('L');
            }}
          >
            <MenuImg src={lguplus_logo} alt="lguplus" />
          </MenuButton>
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _changeTelecom('K');
            }}
          >
            <MenuImg src={kt_logo} alt="kt" />
          </MenuButton>
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _changeTelecom('A');
            }}
          >
            <MenuImg src={attle_logo} alt="etc" />
          </MenuButton>
        </MenuButtonWrap>
      </CurrentWrap>
    </Container>
  );
}
