import React, { useCallback, useEffect, useState } from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import { RadioButton } from '@progress/kendo-react-inputs';
// 개발자 라이브러리
import { insuCheck, marketingCheck } from '../../redux/reducers/insu';
// css
import { SquareButton } from '../../css/common-component';

export default function Apply3(props) {
  const dispatch = useDispatch();
  const [necessarySelectedValue, setNecessarySelectedValue] = useState('Y');
  const [selectedValue1, setSelectedValue1] = useState('Y');
  const [selectedValue2, setSelectedValue2] = useState('Y');

  const handleChange = useCallback((e) => {
    setNecessarySelectedValue(e.value);
  }, []);

  const handleChange1 = useCallback((e) => {
    setSelectedValue1(e.value);
  }, []);

  const handleChange2 = useCallback((e) => {
    setSelectedValue2(e.value);
  }, []);
  useEffect(() => {
    dispatch(insuCheck(true));
    return () => {
      // dispatch(insuCheck(false));
    };
  }, [dispatch]);

  const onMarketingCheck = () => {
    if (necessarySelectedValue === 'Y') {
      dispatch(marketingCheck(selectedValue1, selectedValue2));
    }
  };

  return (
    <>
      <StyledBackGroundWrap>
        <div className="title">개인정보 수집 및 이용 및 마케팅 동의</div>
      </StyledBackGroundWrap>

      <StyledContent>
        <div className="title">제 3자 정보 제공 동의(필수)</div>
        <br /> <br />
        <div className="info_apply">
          <div className="content">
            {`제공받는자 : 차봇 모빌리티(주)
제공목적 : 다이렉트자동차보험료 비교서비스 제공 및 만기 안내
제공항목 : 이름, 연락처, 거주지역, 만기정보, 차량번호(차대번호)
보유기간 : 동의일로부터 5년

제공받는자 : 현대해상화재보험㈜, 한화손해보험주식회사, ㈜KB손해보험, DB손해보험주식회사, 흥국화재해상보험주식회사, AXA손해보험㈜, MG손해보험주식회사
제공목적 : 다이렉트자동차보험료 산출
제공항목 : 이름, 연락처
보유기간 : 동의일로부터 5년

제공받는자 : ㈜리치앤코, ㈜인슈온
제공목적 : 보험상품에 대한 안내
제공항목 : 이름, 연락처
보유기간 : 동의일로부터 5년

제공받는자 : ㈜리치플래닛
제공목적 : 보험 및 재무상담 서비스 제공
제공항목 : 이름, 연락처
보유기간 : 동의일로부터 5년

제공받는자 : 굿카비즈 서비스에 등록된 GA 및 설계사
제공목적 : 기존 가입 및 상담 완료된 다이렉트자동차보험 갱신 관리 및 기타 보험상품 안내
제공항목 : 이름, 연락처, 자동차보험만기일자
보유기간 : 동의일로부터 5년

제공받는자 : ㈜오토데이터뱅크
제공목적 : 맞춤 다이렉트자동차보험사 추천을 위한 차량정보 조회
제공항목 : 이름, 연락처, 차량번호(차대번호)
보유기간 : 동의일로부터 5년`}
          </div>
        </div>
        <div className="line"></div>
        <div className="gray_text">
          개인정보 수집 이용에 동의하시겠습니까?(필수)
        </div>
        <StyledRadioWrap style={{ textAlign: 'right', width: '100%' }}>
          <RadioButton
            name="group1"
            value="Y"
            checked={necessarySelectedValue === 'Y'}
            label="예"
            onChange={handleChange}
          />
          <RadioButton
            name="group1"
            value="N"
            checked={necessarySelectedValue === 'N'}
            label="아니오"
            onChange={handleChange}
          />
        </StyledRadioWrap>
        <div className="title" style={{ marginTop: '32px' }}>
          홍보를 위한 제 3자 정보 제공 동의(선택)
        </div>
        <br /> <br />
        <div className="info_apply">
          <div className="content">
            {`제공받는자 : 드림프리덤(주)
제공목적 : 고객 혜택을 위한 드림프리덤(주)의 상품 또는 서비스 정보 제공
제공항목 : 이름, 연락처, 거주지역
보유기간 : 동의일로부터 5년`}
          </div>
        </div>
        <div className="line"></div>
        <div className="gray_text">홍보를 위한 제 3자 정보 제공 동의(선택)</div>
        <StyledRadioWrap style={{ textAlign: 'right', width: '100%' }}>
          <RadioButton
            name="group2"
            value="Y"
            checked={selectedValue1 === 'Y'}
            label="예"
            onChange={handleChange1}
          />
          <RadioButton
            name="group2"
            value="N"
            checked={selectedValue1 === 'N'}
            label="아니오"
            onChange={handleChange1}
          />
        </StyledRadioWrap>
        <div className="title" style={{ marginTop: '32px' }}>
          홍보를 위한 제 3자 정보 제공 동의(선택)
        </div>
        <br /> <br />
        <div className="info_apply">
          <div className="content">
            {`제공받는자 : 차봇 모빌리티(주)
제공목적 : 고객 혜택을 위한 차봇 모빌리티(주) 및 제 3자의 상품 또는 서비스 정보 제공
제공항목 : 이름, 연락처, 거주지역, 만기정보, 차량번호(차대번호)
보유기간 : 동의일로부터 5년주지역
보유기간 : 동의일로부터 5년`}
          </div>
        </div>
        <div className="line"></div>
        <div className="gray_text">홍보를 위한 제 3자 정보 제공 동의(선택)</div>
        <StyledRadioWrap style={{ textAlign: 'right', width: '100%' }}>
          <RadioButton
            name="group3"
            value="Y"
            checked={selectedValue2 === 'Y'}
            label="예"
            onChange={handleChange2}
          />
          <RadioButton
            name="group3"
            value="N"
            checked={selectedValue2 === 'N'}
            label="아니오"
            onChange={handleChange2}
          />
        </StyledRadioWrap>
        <SquareButton
          width="256px"
          height="54px"
          borderRadius="5px"
          color="#fff"
          border="solid 1px #e2e2e2"
          backgroundColor="#000f34"
          margin="20px auto"
          style={{ fontWeight: 'normal', marginTop: '39px' }}
          onClick={onMarketingCheck}
          disabled={necessarySelectedValue === 'N'}
        >
          확인
        </SquareButton>
      </StyledContent>

      {/* <StyledButtonWrap disabled={necessarySelectedValue === "no"} onClick={onMarketingCheck}>
        <div className="title">확인</div>
      </StyledButtonWrap> */}
    </>
  );
}

const StyledBackGroundWrap = styled.div`
  background-color: #000f34;

  /* Full height */
  height: 6vh;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  .title {
    font-weight: bold;
    padding: 15px 0 15px 0;
    font-size: 20px;
    text-align: center;
  }

  .content {
    font-size: 15px;
    text-align: center;
  }
`;

const StyledContent = styled.div`
  height: 100%;
  background-color: #f8f8f8;
  padding: 28px 10px 20px 10px;
  font-family: NanumGothic;
  color: #000f34;
  font-size: 15px;
  .info_apply {
    height: 89px;
    overflow-x: auto;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none; /* Chrome, Safari, Opera*/
    }
  }

  .title {
    font-weight: bold;
  }

  .content {
    font-size: 15px;
    font-weight: normal;
    white-space: pre-wrap;
  }

  .line {
    width: 100%;
    height: 0;
    margin: 19px 8px 12px 2px;
    border: solid 1px #d9d9d9;
  }

  .gray_text {
    color: #707070;
  }
`;

const StyledRadioWrap = styled.div`
  .k-radio:checked {
    border-color: #000f34;
    color: #000f34;
    background-color: #fff;
  }

  .k-radio:checked:focus {
    box-shadow: none;
  }
`;
