import React, { useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input, RadioGroup, Checkbox } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
import styled from 'styled-components';
// 개발자 Component
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const BbsManagerInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    isDelete: false,
    idx: props.location.idx,
    mode: props.location.mode,
    bbsCode: '',
    title: '',
    thumbYn: '',
    thumbOptions: [],
    replyYn: '',
    replyOptions: [],
    userAuth: [],
    authWrite: [],
    authView: [],
    authDelete: [],
    authReply: [],
    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/bbs/get_bbs_manager_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      bbsCode: data.bbsCode,
      title: data.title,
      thumbYn: data.thumbYn,
      thumbOptions: data.thumbOptions,
      replyYn: data.replyYn,
      replyOptions: data.replyOptions,
      userAuth: data.userAuth,
    });
  };

  // 삭제
  const _setDelete = async () => {
    const url = '/api/bbs/set_bbs_manager_delete.php';
    const params = {
      adminId: state.adminId,
      idx: state.idx,
    };

    const { result, message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
      isDelete: false,
    });

    setTimeout(() => {
      setState({ ...state, success: false });

      if (result === '0') {
        props.history.goBack();
      }
    }, 1000);
  };

  // 저장
  const _setData = async () => {
    const url = '/api/bbs/set_bbs_manager_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleInputChange = (e) => {
    const target = e.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    setState({ ...state, [name]: value });
  };

  const _handleAuthCheck = (e) => {
    const target = e.target;
    const index = target.tabIndex;
    const value = target.checked;
    const name = target.name;

    const { userAuth } = state;
    let find_index = userAuth.findIndex((element) => element.no === index);
    userAuth[find_index][name] = value;
  };

  const _handleThumbChange = (e) => {
    setState({ ...state, thumbYn: e.value });
  };

  const _handleReplyChange = (e) => {
    setState({ ...state, replyYn: e.value });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  useEffect(() => {
    if (state.isDelete) {
      _setDelete();
    }
  }, []);

  const bbsCodeMessage = "'게시판코드'를 입력해주세요!";
  const titleMessage = "'게시판명'을 입력해주세요!";

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>게시판관리 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="bbsCode"
                  value={state.bbsCode}
                  style={{ width: '74%' }}
                  maxLength={10}
                  required={true}
                  validationMessage={bbsCodeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>게시판명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="title"
                  value={state.title}
                  style={{ width: '74%' }}
                  maxLength={50}
                  required={true}
                  validationMessage={titleMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>썸네일</Label>
                <RadioGroup
                  name="telecom"
                  defaultValue={state.thumbYn}
                  data={state.thumbOptions}
                  value={state.thumbYn}
                  style={{ fontSize: '0.9em', marginTop: '5px' }}
                  onChange={_handleThumbChange}
                  layout="horizontal"
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>댓글</Label>
                <RadioGroup
                  name="replyYn"
                  defaultValue={state.thumbYn}
                  data={state.replyOptions}
                  value={state.replyYn}
                  style={{ fontSize: '0.9em', marginTop: '5px' }}
                  onChange={_handleReplyChange}
                  layout="horizontal"
                />
              </Td>
            </Tr>
          </Table>
          <div
            style={{
              marginTop: '5px',
              borderTop: '2px solid rgb(220, 220, 220)',
              borderLeft: '2px solid rgb(220, 220, 220)',
              borderRight: '2px solid rgb(220, 220, 220)',
            }}
          >
            {state.userAuth.map((row) => (
              <RowWrap key={row.no}>
                <Label
                  style={{
                    width: '12.5%',
                    borderRight: '2px solid rgb(220, 220, 220)',
                  }}
                >
                  {row.name}
                </Label>
                <CellWrap>
                  <Checkbox
                    tabIndex={row.no}
                    name="authWrite"
                    defaultChecked={row.authWrite}
                    onClick={_handleAuthCheck}
                  >
                    <label
                      style={{
                        fontSize: '12px',
                        marginLeft: '5px',
                        marginRight: '10px',
                      }}
                    >
                      쓰기
                    </label>
                  </Checkbox>
                  <Checkbox
                    tabIndex={row.no}
                    name="authView"
                    defaultChecked={row.authView}
                    onClick={_handleAuthCheck}
                  >
                    <label
                      style={{
                        fontSize: '12px',
                        marginLeft: '5px',
                        marginRight: '10px',
                      }}
                    >
                      읽기
                    </label>
                  </Checkbox>
                  <Checkbox
                    tabIndex={row.no}
                    name="authDelete"
                    defaultChecked={row.authDelete}
                    onClick={_handleAuthCheck}
                  >
                    <label
                      style={{
                        fontSize: '12px',
                        marginLeft: '5px',
                        marginRight: '10px',
                      }}
                    >
                      삭제
                    </label>
                  </Checkbox>
                  <Checkbox
                    tabIndex={row.no}
                    name="authReply"
                    defaultChecked={row.authReply}
                    onClick={_handleAuthCheck}
                  >
                    <label
                      style={{
                        fontSize: '12px',
                        marginLeft: '5px',
                        marginRight: '10px',
                      }}
                    >
                      댓글
                    </label>
                  </Checkbox>
                </CellWrap>
              </RowWrap>
            ))}
          </div>
          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

const RowWrap = styled.div`
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid rgb(220, 220, 220);
`;

const CellWrap = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export default BbsManagerInfo;
