import { SET_BOARD } from './BoardTypes';
import _axios from '../../utils/axios';

/* *************************** 게시판 정보 ************************************* */
export function setBoard(searchAble) {
  return (dispatch) => {
    dispatch({
      type: SET_BOARD,
      searchAble
    });
  };
}

/* *************************** 게시판 삭제 ************************************* */
export function setBoardDelete(url, params) {
  return () => {
    // API REQUEST
    return _axios(url, params).then((response) => {
      return response;
    });
  };
}
