/* **********************************************************************
 * 모바일홈 > 포인트 자세히 보기
 * 작성자: 이해영
 *********************************************************************** */
import React, { useState, useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../../../redux/actions/header';
import PointHomeChart from './PointHomeChart';
// css
import '../../../../styles/font.css';
import theme from '../../../../styles/theme';
// images
import circleBtn from '../../../../images/front/commi-circleBtn.svg';
import _axios from '../../../../utils/axios';

const PointHomeWeb = () => {
  const { userId } = useSelector((store) => store.auth.login.userInfo);
  const [data, setData] = useState([]);

  const headerInfo = {
    isBackground: false,
    isBugger: true,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '포인트정보',
  };

  const dispatch = useDispatch();
  const authStatus = useSelector((state) => state.auth.authStatus);

  const [month, setMonth] = useState(true);

  const [monthIndex, setMonthIndex] = useState(0);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const _getData = useCallback(async () => {
    const date = new Date();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();

    const url = '/api/member/get_point_home.php';
    const params = {
      userId: userId,
      year: String(year),
      month: String(0) + String(month - 1),
    };
    const data = await _axios(url, params);
    setData(data);
  }, [userId]);

  useEffect(() => {
    _getData();
  }, [_getData]);

  return (
    <Container>
      <BonusTitle>
        <h1>포인트</h1>
        <select>
          <option>2021</option>
        </select>
      </BonusTitle>

      <BonusPoint className="point">
        <div className="point-sum">
          <h1>합계</h1>
          <strong>{data?.totalPoint}원</strong>
        </div>
        <div>
          <ul>
            <li className="list-title">
              <p>실적 포인트</p>
              <p>{data?.yearData?.performance?.performanceSum}원</p>
            </li>
            <li className="list-sub">
              <p>
                휴대폰
                <i>
                  <Link to="/mypage/home/hp">
                    <img src={circleBtn} alt="" />
                  </Link>
                </i>
              </p>

              <p>{data?.yearData?.performance?.hp?.hp}원</p>
            </li>
            <li className="list-sub list-sub-item">
              <p>
                렌탈
                <i>
                  <Link to="/mypage/home/rental">
                    <img src={circleBtn} alt="" />
                  </Link>
                </i>
              </p>
              <p>{data?.yearData?.performance?.rental.rental}원</p>
              <ul className="list-sub-item-depth">
                <li className="list-desc first">
                  <p className="list-desc-pr">직접 판매</p>
                  <p>{data?.yearData?.performance?.rental?.detail?.direct}원</p>
                </li>
                <li className="list-desc">
                  <p className="list-desc-pr">뎁스 판매</p>
                  <p>{data?.yearData?.performance?.rental?.detail?.depth}원</p>
                </li>
              </ul>
            </li>
            <li className="list-sub">
              <p>
                자동차 다이렉트 보험
                <i>
                  <Link to="/mypage/home/insu">
                    <img src={circleBtn} alt="" />
                  </Link>
                </i>
              </p>
              <p>{data?.yearData?.performance?.insurance?.insurance}원</p>
            </li>
          </ul>
        </div>
        <div>
          <ul>
            <li className="list-title">
              <p>
                후원 포인트
                <i>
                  <Link to="/mypage/home/support">
                    <img src={circleBtn} alt="" />
                  </Link>
                </i>
              </p>
              <p>{data?.yearData?.support?.supportSum}원</p>
            </li>
            <li className="list-sub">
              <p>MD 유치</p>
              <p>{data?.yearData?.support?.invite}원</p>
            </li>
            <li className="list-sub mb">
              <p>월 구독료</p>
              <p>{data?.yearData?.support?.subscribe}원</p>
            </li>
          </ul>
        </div>
      </BonusPoint>

      <MonthPoint>
        <p className="month-title">월간 포인트</p>

        <div className="phone-month">
          <div>
            <ul
              style={{ height: '80px', marginTop: '2.5em' }}
              className="phone-month-list"
            >
              {data.length !== 0 &&
                data.chartData.map((n, index) => (
                  <PointHomeChart
                    monthIndex={monthIndex}
                    setMonthIndex={setMonthIndex}
                    percent={n.percent}
                    index={index}
                  />
                ))}
            </ul>
          </div>

          <div className="phone-num">
            <ol style={{ paddingLeft: '1px' }}>
              {data.length !== 0 &&
                data.chartData.map((n, index) => (
                  <li className={month && 'active'} key={index}>
                    {n.month}
                  </li>
                ))}
            </ol>
          </div>
          <div>
            <ul className="detail-list">
              <li className="detail-list-title">
                <p>2021년 {monthIndex + 1}월</p>
                {data.length !== 0 && <p>{data.chartData[monthIndex].point}</p>}
              </li>
              <li
                style={{ display: 'flex', flexDirection: 'column' }}
                className="detail-list-sub"
              >
                {data.length !== 0 &&
                  data.monthData.map((n, index) => (
                    <>
                      {parseInt(n.month) === monthIndex + 1 && (
                        <div
                          key={index}
                          style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            marginBottom: '12px',
                          }}
                        >
                          <p>{n.assort}</p>
                          <p>{n.point}</p>
                        </div>
                      )}
                    </>
                  ))}
              </li>
            </ul>
          </div>
        </div>
      </MonthPoint>

      <PointBtn>
        <h1>출금 가능 포인트 </h1>
        <div className="bonus-point">
          <strong>{data?.usableCash}</strong>
          <div className="bonus-button">
            <Link to="/mypage/home/withdraw">
              <button type="button">출금요청</button>
            </Link>
          </div>
        </div>
      </PointBtn>
    </Container>
  );
};

export default PointHomeWeb;

export const Container = styled.section`
  background-color: #f8f8f8;
  padding: 2em 1.5em 4em;
  width: 400px;

  h1 {
    margin: 0;
  }

  ul {
    padding: 0;
  }

  li {
    list-style: none;
    display: flex;
    justify-content: space-between;
  }

  p {
    margin: 0;
    font-family: NotoM;
  }

  strong {
    font-weight: NotoM;
  }
`;

export const BonusTitle = styled.section`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5em;
  margin-bottom: 0.4em;

  h1 {
    font-size: ${theme.fontSizes.H2};
    font-family: NotoB;
  }

  select {
    width: 93px;
    height: 32px;
    padding: 0 8px;
    border: 1px solid ${theme.colors.boxGray};
    background-color: ${theme.colors.white};
    box-sizing: content-box;
    font-family: NotoR;

    option {
      font-family: NotoR;
    }
  }
`;

export const BonusPoint = styled.section`
  background-color: ${theme.colors.white};
  padding: 2em 1.5em;
  width: 100%;

  .point {
    padding: 2em 1.5em;
    background-color: ${theme.colors.white};
    border-radius: 3px;

    &-sum {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid ${theme.colors.grey2};
      ${theme.H3};
    }
  }

  .list-title {
    margin-top: 2.5em;
    color: ${theme.colors.black1};
    border-bottom: 1px solid ${theme.colors.grey2};
    ${theme.H5};
  }

  .list-sub {
    align-items: center;
    margin: 1em 0;
    color: ${theme.colors.grey2};
    ${theme.Title};

    p {
      color: ${theme.colors.grey2};
      display: flex;
    }
  }

  .list-sub-item {
    position: relative;
    margin-bottom: 4em;

    &-depth {
      position: absolute;
      top: 100%;
      width: 100%;

      .first {
        margin-top: 0.5em;
      }

      .list-desc {
        padding-left: 2em;

        p {
          ${theme.SubTitle1};
        }
      }
    }
  }

  i {
    margin-left: 0.3em;
    display: inline-flex;

    a {
      display: inline-flex;
    }
  }
`;

export const MonthPoint = styled.section`
  padding: 2em 1.5em;
  margin-top: 3em;
  background-color: ${theme.colors.white};
  border-radius: 3px;

  .month-title {
    font-size: ${theme.fontSizes.H3};
    padding-bottom: 0.2em;
    border-bottom: 1px solid ${theme.colors.grey2};
  }

  .phone-month {
    &-list {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;

      li {
        position: relative;
        width: 15.3px;
        height: 70px;
        background: ${theme.colors.royalblue};
        text-align: center;
      }
    }
  }

  .phone-num {
    ol {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
    }

    li {
      justify-content: center;
      position: relative;
      width: 15.3px;
      margin-top: 5px;
      color: ${theme.colors.grey1};
      ${theme.Button2};

      /* 삼각형 */
      /* &:before {
        position: absolute;
        display: block;
        content: '';
        top: 49px;
        left: 2px;
        width: 0;
        height: 0;
        border-top: 1.5em solid transparent;
        border-right: 1em solid transparent;
        border-bottom: 1.5em solid transparent;
        border-left: 1em solid transparent;
      } */

      &.active {
        &:before {
          border-bottom: 1.5em solid ${theme.colors.boxGray};
        }
      }
    }
  }

  .detail-list {
    position: relative;
    margin-top: 4em;
    padding: 2em;
    border-radius: 3px;
    background-color: ${theme.colors.boxGray};

    &-title {
      p {
        font-size: ${theme.fontSizes.H5};
        color: ${theme.colors.black1};
      }
    }

    &-sub {
      margin-top: 1em;
      p {
        font-size: ${theme.fontSizes.SubTitle1};
        font-family: NotoR;
        color: ${theme.colors.grey2};
      }
    }
  }
`;

export const PointBtn = styled.div`
  margin-top: 2em;
  padding: 1em;
  background-color: ${theme.colors.white};
  ${theme.H3};

  h1 {
    border-bottom: 1px solid ${theme.colors.grey2};
  }

  .bonus-point {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.5em;
  }

  button {
    width: 121px;
    height: 32px;
    color: ${theme.colors.white};
    font-size: ${theme.fontSizes.Button1};
    font-family: NotoR;
    background-color: ${theme.colors.royalblue};
    border-style: none;
    border-radius: 3px;
  }
`;