import React, { useEffect, useState } from "react";
import { Upload } from "@progress/kendo-react-upload";
import { Button } from "@progress/kendo-react-buttons";
import { Dialog } from "@progress/kendo-react-dialogs";
// 개발자 라이브러리
import _axios from "../../../utils/axios";
// css
import "../../../css/kendo.css";
import {
  ButtonWrap,
  Container,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from "../../../css/list";

const fileStatuses = [
  "UploadFailed",
  "Initial",
  "Selected",
  "Uploading",
  "Uploaded",
  "RemoveFailed",
  "Removing",
];

const CashRequestExcelUp = (props) => {
  const [state, setState] = useState({
    excelFile: "",
    success: false,
    message: null,
    visibleDialog: false,
    files: [],
    events: [],
    filePreviews: {},
  });

  const onAdd = (event) => {
    const afterStateChange = () => {
      event.affectedFiles
        .filter((file) => !file.validationErrors)
        .forEach((file) => {
          const reader = new FileReader();
          reader.onloadend = (ev) => {
            setState({
              ...state,
              filePreviews: {
                ...state.filePreviews,
                [file.uid]: ev.target.result,
              },
            });
          };
          reader.readAsDataURL(file.getRawFile());
        });
    };

    setState(
      {
        ...state,
        files: event.newState,
        events: [
          ...state.events,
          `File selected: ${event.affectedFiles[0].name}`,
        ],
      },
      afterStateChange
    );
  };

  const onRemove = (event) => {
    const filePreviews = {
      ...state.filePreviews,
    };
    event.affectedFiles.forEach((file) => {
      delete filePreviews[file.uid];
    });
    setState({
      ...state,
      files: event.newState,
      events: [...state.events, `File removed: ${event.affectedFiles[0].name}`],
      filePreviews: filePreviews,
    });
  };

  const onProgress = (event) => {
    console.log("onProgress: ", event);
    setState({
      ...state,
      files: event.newState,
      events: [
        ...state.events,
        `On Progress: ${event.affectedFiles[0].progress} %`,
      ],
    });
  };

  const onStatusChange = (event) => {
    console.log("onStatusChange: ", event);
    let newFiles = event.newState;
    const file = {
      ...event.newState,
      name: event.response.response.saveName,
    };
    newFiles[0] = file;
    setState({
      ...state,
      files: newFiles,
      events: [
        ...state.events,
        `File '${file.name}' status changed to: ${fileStatuses[file.status]}`,
      ],
    });
    console.log(state);
  };
  // **************************** 파일 업로드 끝

  // 저장
  const _setData = async () => {
    const params = {
      telecom: state.telecom,
      excelFile: state.files.length === 0 ? "" : state.files[0].name,
    };

    const url = "/api/admin/point/set_excel_update.php";
    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
    });
  };

  return (
    <Container width={"100%"}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={"100%"}>
          <Title>포인트관리 〉 인출요청목록 〉 엑셀업로드</Title>

          <Table width={"100%"}>
            <Tr width={"100%"} direction={"column"}>
              <Td style={{ padding: "10px" }}>
                ※ 입금정보를 업로드합니다.
                <br />
                1. 출금요청목록에서 요청상태가 "처리중"인 정보를 다운로드
                받습니다.
                <br />
                2. 입금된 정보에 입금일자(YYYY-MM-DD)를 입력합니다.
                <br />
                &nbsp;&nbsp;&nbsp;(입금일자가 있는 정보만 등록되며, 다운로드된
                형식을 유지하여 주세요.)
                <br />
                3. 좌측의 "파일선택" 버튼을 클릭하여 수정완료된 엑셀파일을
                선택합니다.
                <br />
                4. "저장하기" 버튼을 클릭합니다.
              </Td>
              <Td>
                <Upload
                  batch={false}
                  multiple={false}
                  files={state.files}
                  onAdd={onAdd}
                  onRemove={onRemove}
                  onProgress={onProgress}
                  onStatusChange={onStatusChange}
                  withCredentials={false}
                  saveUrl={"http://spiderplatform.co.kr/utils/excelSave.php"}
                  removeUrl={
                    "http://spiderplatform.co.kr/utils/excelRemove.php"
                  }
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            <Button
              type={"submit"}
              onClick={() => setState({ ...state, isSave: true })}
              primary={true}
            >
              저장하기
            </Button>
            <Button type={"button"} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={"알림"} onClose={_toggleDialog}>
          <p style={{ margin: "25px", textAlign: "center" }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default CashRequestExcelUp;
