import styled from 'styled-components';

export const Container = styled.div`
  width: 100%;
  height: 100%;
`;

export const Wrap = styled.div`
  width: 100%;
  height: 100%;
`;

export const Title = styled.div`
  width: 100%;
  font-size: 0.9em;
  font-weight: 600;
  color: #666666;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 5px;
`;

export const Table = styled.div`
  width: 100%;
  height: 100%;
`;

export const Tr = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 1px;

  @media (max-width: 768px) {
    flex-direction: column;
  }

  @media (min-width: 769px) {
    flex-direction: row;
  }
`;

export const Td = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  font-size: 0.9em;
  color: #666666;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  
  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 50%;
  }
`;

export const Label = styled.div`
  width: 25%;
  font-size: 1em;
  color: rgb(13,34,75);
  padding-left: 3px;
  margin-right: 2px;
  margin-top: 5px;
`;

export const Table2 = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
`;

export const Tr2 = styled.div`
  width: 100%;
  padding: 1px;
`;

export const Td2 = styled.div`
  float: left;
  font-size: 0.9em;
  color: #666666;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 50%;
  }
`;

export const ButtonWrap = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin: 10px;
`;

export const SmallButton = styled.button`
  width: 100%;
  height: 30px;
  border: 1px solid #dcdcdc;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  background: #fff;
`;