import React, { useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const PointInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,
    memId: '',
    memName: '',
    assort: '',
    assortOptions: [],
    descript: '',
    point: 0,
    wdate: '',

    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/admin/point/get_point_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      memId: data.memId,
      memName: data.memName,
      assort: { code: data.assort, name: data.assortName },
      assortOptions: data.assortOptions,
      descript: data.descript,
      point: data.point,
      wdate: data.wdate,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/admin/point/set_point_update.php';
    const { message } = await _axios(url, state);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  // const { isLoading, isSearch, loadData, searchAble } = state;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>포인트관리 〉 포인트목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memId"
                  value={state.memId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memName"
                  value={state.memName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>구분</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="assort"
                  value={state.assort}
                  data={state.assortOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>적요</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="descript"
                  value={state.descript}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>포인트</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="point"
                  value={state.point}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>등록일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="wdate"
                  value={state.wdate}
                  style={{ width: '74%' }}
                  maxLength={20}
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default PointInfo;
