import React, { useState } from 'react';
import ImageUploading from 'react-images-uploading';
import Axios from 'axios';

// css
import styled from 'styled-components';

// 아이콘 , 이미지

const GoodsImgUpload = ({ state, setState, modelIndex }) => {
  const [images, setImages] = React.useState([]);
  const [imagesNum, setImagesNum] = useState(0);
  const [thumCheck, setThumCheck] = useState(modelIndex ? modelIndex : 0);

  const _photoUpload = async (fileInfo) => {
    let formData = new FormData();
    formData.append('files', fileInfo.file);

    await Axios.post(
      'http://spiderplatform.co.kr/utils/thumbSave.php',
      formData,
      {
        headers: { 'Content-Type': 'multipart/form-data;charset=utf-8;' },
      },
    );
  };

  const onChange = (imageList, addUpdateIndex, event) => {
    _photoUpload(imageList[imagesNum]);
    setImages(imageList);
    if (modelIndex) {
      state.models[thumCheck].modelThumb =
        'http://spiderplatform.co.kr/upload/thumbnail/' +
        imageList[imagesNum].file.name;
      setState({
        ...state,
        models: state.models,
      });
    } else {
      setState({
        ...state,
        thumbnail:
          'http://spiderplatform.co.kr/upload/thumbnail/' +
          imageList[imagesNum].file.name,
      });
    }

    setImagesNum(imagesNum + 1);
  };
  console.log(state);
  return (
    <div>
      <StyledMyPhoto>
        <ImageUploading
          multiple
          value={images}
          onChange={onChange}
          dataURLKey="data_url"
        >
          {({
            imageList,
            onImageUpload,
            onImageRemoveAll,
            onImageUpdate,
            onImageRemove,
            isDragging,
            dragProps,
          }) => (
            // write your building UI
            <div className="upload__image-wrapper" style={{ display: 'flex' }}>
              <button
                type="button"
                style={{ height: '100%' }}
                onClick={onImageUpload}
                {...dragProps}
              >
                썸네일 변경
              </button>
              &nbsp;
              <div className="image-item">
                {images.length !== 0 ? (
                  // <img src={images[0].data_url} alt="" width="100" />
                  <img
                    src={images[images.length - 1].data_url}
                    alt=""
                    width="60px"
                  />
                ) : modelIndex ? (
                  <img
                    src={
                      state.models.length !== 0 &&
                      state.models[thumCheck].modelThumb
                      // state.thumbnail !== '' && state.thumbnail
                    }
                    alt=""
                    width="60px"
                  />
                ) : (
                  <img
                    src={state.thumbnail !== '' && state.thumbnail}
                    alt=""
                    width="60px"
                  />
                )}
              </div>
            </div>
          )}
        </ImageUploading>

        <section style={{ marginLeft: '6em' }}>
          <div style={{ fontWeight: 'bold' }}>{state.userName}</div>
          <div>{state.userId}</div>
        </section>
      </StyledMyPhoto>
    </div>
  );
};

// 프로필사진 수정 스타일컴포넌트

const StyledMyPhoto = styled.section`
  button {
    border: 1px solid black;
  }
`;

export default GoodsImgUpload;
