/* **********************************************************************
 * 관리자페이지 > 휴대폰신청관리
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';

import RequestList from '../../components/hp/request/RequestList';
import RequestInfo from '../../components/hp/request/RequestInfo';
import ServiceList from '../../components/hp/addService/ServiceList';
import ServiceInfo from '../../components/hp/addService/ServiceInfo';
import AllianceCardList from '../../components/hp/allianceCard/AllianceCardList';
import AllianceCardInfo from '../../components/hp/allianceCard/AllianceCardInfo';
import ChargeList from '../../components/hp/charge/ChargeList';
import ChargeInfo from '../../components/hp/charge/ChargeInfo';
import CommiList from '../../components/hp/commi/CommiList';
import CommiInfo from '../../components/hp/commi/CommiInfo';
import CommiExcelUp from '../../components/hp/commi/CommiExcelUp';
import DiscountList from '../../components/hp/discount/DiscountList';
import DiscountInfo from '../../components/hp/discount/DiscountInfo';
import GoodsList from '../../components/hp/goods/GoodsList';
import GoodsInfo from '../../components/hp/goods/GoodsInfo';
import SupportList from '../../components/hp/supportPrice/SupportList';
import SupportInfo from '../../components/hp/supportPrice/SupportInfo';
import SupportExcelUp from '../../components/hp/supportPrice/SupportExcelUp';

const Hp = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/admin/hp/request"
        render={(props) => <RequestList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/request/info"
        render={(props) => <RequestInfo {...props} />}
      />
      <AuthRoute
        exact      
        path="/admin/hp/addService"
        render={(props) => <ServiceList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/addService/info"
        render={(props) => <ServiceInfo {...props} />}
      />
      <AuthRoute
        exact      
        path="/admin/hp/allianceCard"
        render={(props) => <AllianceCardList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/allianceCard/info"
        render={(props) => <AllianceCardInfo {...props} />}
      />
      <AuthRoute
        exact
        path="/admin/hp/charge"
        render={(props) => <ChargeList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/charge/info"
        render={(props) => <ChargeInfo {...props} />}
      />
      <AuthRoute
        exact        
        path="/admin/hp/commi"
        render={(props) => <CommiList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/commi/info"
        render={(props) => <CommiInfo {...props} />}
      />
      <AuthRoute
        path="/admin/hp/commi/excelUp"
        render={(props) => <CommiExcelUp {...props} />}
      />
      <AuthRoute
        exact        
        path="/admin/hp/discount"
        render={(props) => <DiscountList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/discount/info"
        render={(props) => <DiscountInfo {...props} />}
      />
      <AuthRoute
        exact        
        path="/admin/hp/goods"
        render={(props) => <GoodsList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/goods/info"
        render={(props) => <GoodsInfo {...props} />}
      />
      <AuthRoute
        exact        
        path="/admin/hp/supportPrice"
        render={(props) => <SupportList {...props} />}
      />
      <AuthRoute
        path="/admin/hp/supportPrice/info"
        render={(props) => <SupportInfo {...props} />}
      />
      <AuthRoute
        path="/admin/hp/supportPrice/excelUp"
        render={(props) => <SupportExcelUp {...props} />}
      />
    </Switch>
  );
};

export default Hp;