import _axios from "../../utils/axios";

export const initialState = {
  loading: false,
  result: "",
  message: "",
  outCash: "",
  registNo: "",
};

export const LOADING = "pointOut/LOADING";
export const SUCCESS = "pointOut/SUCCESS";
export const SUBMIT = "pointOut/SUBMIT";
export const INIT = "pointOut/INIT";

export const submitChangeAccount = (params) => async (dispatch, getState, { history }) => {
  dispatch({
    type: LOADING,
    loading: true,
  });
  
  const url = "/api/member/set_account_info_update.php";
  const { result, message } = await _axios(url, params);

  if (result === "0") {
    dispatch({
      type: SUCCESS,
      loading: false,
      result,
      message,
    });
    history.goBack();
  }
};

export const submitPointOut = (params) => async (dispatch) => {
  dispatch({
    type: LOADING,
    loading: true,
  });

  const url = "/api/member/set_pointOut_update.php";
  const { result, message } = await _axios(url, params);

  dispatch({
    type: SUCCESS,
    loading: false,
    result,
    message,
  });
};

export const submitRequest2 = (registNo) => async (dispatch, getState, { history }) => {
  dispatch({
    type: LOADING,
    loading: true,
  });

  const { outCash, memId, memName } = getState().pointOut;
  const url = "/api/member/set_pointOut_update.php";
  const params = { outCash, memId, memName, registNo };
  const { result, message } = await _axios(url, params);

  dispatch({
    type: SUCCESS,
    loading: false,
    result,
    message,
  });
};

export function initPointOut() {
  return {
    type: INIT,
  };
}

export default function pointOut(state = initialState, action) {
  switch (action.type) {
    case LOADING:
      return {
        ...state,
        loading: action.loading,
      };

    case SUCCESS:
      return {
        ...state,
        loading: action.loading,
        result: action.result,
        message: action.message,
      };

    case SUBMIT:
      return {
        ...state,
        outCash: action.outCash,
        // registNo: action.message,
        memId: action.memId,
        memName: action.memName,
      };

    case INIT:
      return {
        initialState,
      };

    default:
      return state;
  }
}
