import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import WebHeaderButton from './WebHeaderButton';
// css
import '../../../../styles/font.css';
import theme, { Anchor } from '../../../../styles/theme';
// icon
import logo from '../../../../images/header/logo_navy02.svg';

const WebHeader = () => {
  return (
    <Container>
      <Wrap>
        <Inner>
          <Link to="/" style={{ width: '123px', height: '36px' }}>
            <img
              src={logo}
              alt="스파이더플랫폼 로고"
              style={{ width: '100%' }}
            />
          </Link>
          <MenuWrap>
            <Ul>
              {/* <Anchor to="/" backgroundColor="transparent" border="transparent">
                홈
              </Anchor>
              <Anchor
                to="/siteInfo"
                backgroundColor="transparent"
                border="transparent"
                style={{ marginRight: 0 }}
              >
                스파이더플랫폼 소개
              </Anchor> */}
            </Ul>
          </MenuWrap>
          <WebHeaderButton />
        </Inner>
      </Wrap>
    </Container>
  );
};

const Container = styled.div`
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  width: 100%;
  height: 100px;
  padding: 0 2em;
  z-index: 9999;
  color: ${theme.colors.navy};
  font-size: ${theme.fontSizes.body1};
  background-color: ${theme.colors.boxGray};
  border-bottom: 1px solid rgba(169, 169, 169, 0.5);
`;

const Wrap = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
`;

const Inner = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1620px;
  margin: 0 auto;
`;

const MenuWrap = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 100%;
`;

const Ul = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  font-family: S-Core;
`;

export default WebHeader;
