import React, { useEffect, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Input, RadioGroup } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const CommitInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,

    telecom: '',
    telecomOptions: [],
    modelCode: '',
    modelOptions: [],
    priceNew: '',
    priceMnp: '',
    priceChange: '',
    useYn: '',
    useOptions: [],

    success: false,
    message: null,
    visibleDialog: false,
  });
  // 수수료정책 정보
  const _getData = async () => {
    const url = '/api/hp/commission/get_commit_info.php';
    const params = {
      idx: state.idx,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,

      telecom: { code: response.data.telecom, name: response.data.telecomName },
      modelCode: {
        code: response.data.modelCode,
        name: response.data.modelName,
      },
      priceNew: response.data.priceNew,
      priceMnp: response.data.priceMnp,
      priceChange: response.data.priceChange,
      useYn: response.data.useYn,

      telecomOptions: response.telecomOptions,
      modelOptions: response.modelOptions,
      useOptions: response.useOptions,
    });
  };

  // 수수료정책 저장
  const _setData = async () => {
    const url = '/api/hp/commission/set_commit_update.php';
    const params = {
      mode: state.mode,
      idx: state.idx,
      telecom: state.telecom,
      modelCode: state.modelCode,
      priceNew: state.priceNew,
      priceMnp: state.priceMnp,
      priceChange: state.priceChange,
      useYn: state.useYn,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleTextarea = (e) => {
    setState({ ...state, [e.target.element.current.name]: e.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, useYn: e.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const telecomMessage = "'통신사'를 선택해주세요!";
  const modelMessage = "'모델'을 선택해주세요!";
  const priceNewMessage = "'신규금액'를 입력해주세요!";
  const priceMnpMessage = "'번호이동금액'을 입력해주세요!";
  const priceChangeMessage = "'기기변경금액'을 입력해주세요!";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 수수료정책 〉 목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="telecom"
                  value={state.telecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={telecomMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>모델명</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="modelCode"
                  value={state.modelCode}
                  data={state.modelOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={modelMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신규</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="priceNew"
                  value={state.priceNew}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={priceNewMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>번호이동</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="priceMnp"
                  value={state.priceMnp}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={priceMnpMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>기기변경</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="priceChange"
                  value={state.priceChange}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={priceChangeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용여부</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    marginBottom: '1px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="useYn"
                    defaultValue={state.useYn}
                    data={state.useOptions}
                    value={state.useYn}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default CommitInfo;
