import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Input } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
// 개발자 라이브러리
import { _handleInputChange, _setDelete } from '../../../utils/board';
import _isMobile from '../../../utils/isMobile';
// css
import {
  Container,
  Wrap,
  Table,
  Tr,
  Td,
  Table2,
  Tr2,
  Td2,
  Label,
  ButtonWrap,
  SmallButton,
} from '../../../styles/board';

const SearchForm = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const isMobile = _isMobile();
  const state = props.state;
  const setState = props.setState;
  const searchFields = props.searchFields;
  const isInsertButton = props.isInsertButton;
  const isDeleteButton = props.isDeleteButton;
  const isExcelUpButton = props.isExcelUpButton;  
  const isExcelDownButton = props.isExcelDownButton;  
  const infoPath = props.infoPath;
  const excelUpPath = props.excelUpPath;  
  const deleteUrl = props.deleteUrl;
  
  // 추가하기
  const _handleInsert = (e) => {
    e.preventDefault();

    history.push({
      pathname: infoPath,
      idx: 0,
      mode: 'insert',
    });
  };

  // 엑셀업로드
  const _handleExcelUp = (e) => {
    e.preventDefault();

    history.push({
      pathname: excelUpPath,
    });
  };
  
  // 엑셀다운로드
  const _handleExceDown = () => {
    setState({
      ...state,
      isExcelDown: true,
    });
  };

  // 검색하기
  const _handleSubmit = (e) => {
    e.preventDefault();

    setState({
      ...state,
      isLoading: true,
      skip: 0,
      searchAble: {
        ...searchAble,
        page: 0,
      },
    });
  };

  useEffect(() => {
    if (state.isDelete) {
      _setDelete(dispatch, state, setState, deleteUrl);
    }
  }, [state.isDelete]);

  const { searchAble } = state;

  return (
    <Container>
      <Wrap>
        <Table background={'#f5f5f5'} border={'1px solid #dcdcdc'}>
          <Tr>
            {searchFields.map((field, index) => {
              return (
                <Td key={index}>
                  <Table2>
                    {field.title !== '' && <Label>{field.title}</Label>}
                    <Tr2>
                      {field.items.map((item, index) => {
                        return (
                          <Td2 key={index} style={{ width: item.width }}>
                            {item.type === 'comboBox' && (
                              <ComboBox
                                style={{
                                  width: '100%',
                                  height: '30px',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name={item.name}
                                value={item.value}
                                data={item.options}
                                dataItemKey="code"
                                textField="name"
                                required={false}
                                onChange={(e) =>
                                  _handleInputChange(state, setState, e)
                                }
                              />
                            )}

                            {item.type === 'text' && (
                              <Input
                                style={{ width: '100%' }}
                                type="text"
                                validityStyles={false}
                                name={item.name}
                                value={item.value}
                                required={false}
                                onChange={(e) =>
                                  _handleInputChange(state, setState, e)
                                }
                              />
                            )}

                            {item.type === 'date' && (
                              <Input
                                style={{ width: '100%' }}
                                type="date"
                                validityStyles={false}
                                name={item.name}
                                value={item.value}
                                required={false}
                                onChange={(e) =>
                                  _handleInputChange(state, setState, e)
                                }
                              />
                            )}

                            {item.type === 'submit' && (
                              <SmallButton onClick={_handleSubmit}>
                                검색
                              </SmallButton>
                            )}
                          </Td2>
                        );
                      })}
                    </Tr2>
                  </Table2>
                </Td>
              );
            })}
          </Tr>
        </Table>

        {!isMobile && (
          <ButtonWrap>
            <Input
              type="text"
              validityStyles={false}
              style={{ width: '40px' }}
              name="pageSize"
              value={searchAble.pageSize ? searchAble.pageSize : null}
              maxLength={3}
              required={false}
              onChange={(e) => _handleInputChange(state, setState, e)}
            />
            <span style={{ marginTop: '5px' }}>행</span>
            <Button
              style={{ marginLeft: '10px' }}
              onClick={_handleSubmit}
              primary={true}
            >
              검색하기
            </Button>

            {isInsertButton && (
              <Button style={{ marginLeft: '15px' }} onClick={_handleInsert}>
                등록하기
              </Button>
            )}

            {isExcelUpButton && (
              <Button style={{ marginLeft: '5px' }} onClick={_handleExcelUp}>
                엑셀업로드
              </Button>
            )}

            {isExcelDownButton && (
              <Button style={{ marginLeft: '5px' }} onClick={_handleExceDown}>
                엑셀다운로드
              </Button>
            )}

            {isDeleteButton && (
              <Button
                style={{ marginLeft: '4px' }}
                onClick={() => {
                  setState({
                    ...state,
                    confirmDialog: true,
                    confirmMessage: "'삭제'하시겠습니까?",
                  });
                }}
              >
                삭제하기
              </Button>
            )}
          </ButtonWrap>
        )}
      </Wrap>
    </Container>
  );
};

export default SearchForm;
