import React from 'react';
import _isMobile from '../../utils/isMobile';
import HomeMobile from './home/HomeMobile';
import HomeWeb from './home/HomeWeb';

const Home = () => {
  const isMobile = _isMobile();

  return (
    <>
      {isMobile ? <HomeMobile /> : <HomeWeb />}
    </>
  );
};

export default Home;
