import React from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
//개발자 라이브러리
import _axios from '../../../utils/axios';
//css
import styled from 'styled-components';
import theme from '../../../styles/theme';
//images
import mobile from '../../../images/mainAfterLogin/mobile.svg';
import tv from '../../../images/mainAfterLogin/tv.svg';
import car from '../../../images/mainAfterLogin/car.svg';

const Service = () => {
  const history = useHistory();
  const authStatus = useSelector((store) => store.auth.authStatus);

  const _goHpRequest = () => {
    history.push('/partnership/hp/currentTelecom');
  };

  const _goGajaRental = () => {
    window.open(
      `https://open.gajarental.com?token=${authStatus?.userInfo?.token}`,
    );
  };

  const _goInSu = () => {
    history.push('/mypage/insuShareLink');
  };

  return (
    <StyledContainer>
      <div className="titleWrap">
        <span>서비스</span>
      </div>

      <div className="d-flex justify-content-between">
        <div className="serviceWrap">
          <div>
            <img src={mobile} alt="" />
          </div>
          <p className="name">휴대폰 간편신청</p>

          <div
            className="button line"
            onClick={_goHpRequest}
          >
            바로신청
          </div>
          <div
            className="button"
            onClick={() =>
              history.push('/mypage/hpShareLink')
            }
          >
            링크신청
          </div>
          <div
            className="button"
            onClick={() => history.push('/mypage/hpRequest')}
          >
            신청목록
          </div>
        </div>

        <div className="serviceWrap">
          <div>
            <img src={tv} alt="" />
          </div>
          <p className="name">가자렌탈</p>

          <div
            className="button line"
            onClick={_goGajaRental}
          >
            사이트
          </div>
          <div className="button" onClick={() => history.push('/mypage/commi')}>
            신청목록
          </div>
          <div className="button none"></div>
        </div>

        <div className="serviceWrap car">
          <div>
            <img src={car} alt="" />
          </div>
          <p className="name word">자동차 다이렉트 보험</p>

          <div
            className="button line"
            onClick={_goInSu}
          >
            링크신청
          </div>
          <div
            className="button"
            onClick={() => history.push('/mypage/insuRequest')}
          >
            신청목록
          </div>
          <div className="button none"></div>
        </div>
      </div>
    </StyledContainer>
  );
};

export default Service;

const StyledContainer = styled.div`
  width: 100%;
  height: auto;
  margin-top: 30px;
  padding: 22px;
  background: #f1f2f2;
  .titleWrap {
    ${theme.H3};

    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;

    color: #0e2b64;
  }

  .hamburger {
    img {
      width: 100%;
      width: 28px;
      height: 24px;
    }
  }

  .serviceWrap {
    margin-top: 22px;
    width: 30%;
    height: 236px;
    padding: 22px;
    background-color: white;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    p {
      margin: 0;
    }

    img {
      width: 45px;
      height: 45px;
    }

    .name {
      ${theme.Title};
      text-align: center;
      color: ${theme.colors.navyBox};
    }

    &.car {
      padding: 22px 0px;
    }


    .button {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      width: 8.25em;
      height: 28px;

      background: #3c64b1;
      border-radius: 3px;

      ${theme.Button1};
      color: ${theme.colors.white};
      text-align: center;

      cursor: pointer; /* Auto Layout */

      &.line {
        margin-top: 10px;
      }

      &.line:before {
        position: absolute;
        display: block;
        content: '';
        top: -11px;
        width: 100%;
        border: 1px solid ${theme.colors.navyBox};
      }
    }

    .none {
      opacity: 0;
      cursor: auto;
    }
  }

`;
