import { DropDownList as KendoDropDownList } from "@progress/kendo-react-dropdowns";
import { Popup as KendoPopup } from "@progress/kendo-react-popup";
import styled from "styled-components";
export const SmallLabel = styled.div`
  width: 160px;
  height: 16px;
  font-family: NanumGothic;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: normal;
  text-align: left;
  color: #404040;
  margin: 45px 0 20px 0;
`;
export const Container = styled.div`
  background-color: #f8f8f8;
  padding: 0 20px;
  width:100%;
  .row {
    width: 100%;
    margin: 0 auto;
  }
`;

export const Grid = styled.div``;

export const Th = styled.div`
  font-family: NanumGothic;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: normal;
  text-align: center;
  color: #404040;
`;

export const Td = styled.div`
  font-family: NanumGothic;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.1;
  letter-spacing: normal;
  text-align: center;
  color: #000f34;
  margin: 13px 0 !important;

  padding: 0;

  .name {
    text-align: left;
    display: inline-block;
    width: 95px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .font {
    font-family: NanumGothic;
    font-size: 13px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.15;
    letter-spacing: normal;
    text-align: center;
    color: #000f34;
  }
`;

export const XsmallLabel = styled.div`
  font-family: NanumGothic;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.1;
  letter-spacing: normal;
  text-align: center;
  color: #000f34;
  margin: 20px 0;
`;

export const DropDownList = styled(KendoDropDownList)`
  width: 100%;
`;

export const Popup = styled(KendoPopup)`
  .popup-content {
    width: 295px;
    height: 38px;
    background-color: #ffffff;
    div {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
`;
