import React, { useEffect, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Input, RadioGroup, TextArea } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const ChargeInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,

    chargeCode: '',
    chargeName: '',
    chargePrice: '',
    discountPrice: '',
    imtAssort: '',
    imtOptions: [],
    telecom: '',
    telecomOptions: [],
    chargeExplain: '',
    useYn: '',
    useOptions: [],

    success: false,
    message: null,
    visibleDialog: false,
  });
  // 요금제 정보
  const _getData = async () => {
    const url = '/api/hp/charge/get_charge_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,

      chargeCode: data.chargeCode,
      chargeName: data.chargeName,
      chargePrice: data.chargePrice,
      discountPrice: data.discountPrice,
      imtAssort: data.imtAssort,
      imtOptions: data.imtOptions,
      telecom: { code: data.telecom, name: data.telecomName },
      telecomOptions: data.telecomOptions,
      chargeExplain: data.chargeExplain,
      useYn: data.useYn,
      useOptions: data.useOptions,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/hp/charge/set_charge_update.php';
    const params = {
      mode: state.mode,
      idx: state.idx,
      chargeCode: state.chargeCode,
      chargeName: state.chargeName,
      chargePrice: state.chargePrice,
      discountPrice: state.discountPrice,
      imtAssort: state.imtAssort,
      telecom: state.telecom,
      chargeExplain: state.chargeExplain,
      useYn: state.useYn,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleTextarea = (e) => {
    setState({ ...state, [e.target.element.current.name]: e.value });
  };

  // 통신망 체크
  const _handleImtChange = (e) => {
    setState({ ...state, imtAssort: e.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, useYn: e.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const chargeCodeMessage = "'요금제코드'를 입력해주세요!";
  const chargeNameMessage = "'요금제명'을 입력해주세요!";
  const chargePriceMessage = "'월요금'을 입력해주세요!";
  const discountPriceMessage = "'할인요금'을 입력해주세요!";
  const telecomMessage = "'통신사'를 선택해주세요!";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 요금제관리 〉 목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>요금제코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="chargeCode"
                  value={state.chargeCode}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={chargeCodeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>요금제명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="chargeName"
                  value={state.chargeName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={chargeNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>월요금</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="chargePrice"
                  value={state.chargePrice}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={chargePriceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인요금</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="discountPrice"
                  value={state.discountPrice}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={discountPriceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="telecom"
                  value={state.telecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={telecomMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신망</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="imtAssort"
                    defaultValue={state.imtAssort}
                    data={state.imtOptions}
                    value={state.imtAssort}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleImtChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용여부</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    marginBottom: '69px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="useYn"
                    defaultValue={state.useYn}
                    data={state.useOptions}
                    value={state.useYn}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>요금제설명</Label>
                <TextArea
                  name="chargeExplain"
                  value={state.chargeExplain}
                  style={{ width: '74%', height: '100px' }}
                  autoSize={true}
                  onChange={_handleTextarea}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default ChargeInfo;
