import React, { useEffect, useState } from 'react';
import { Input, RadioGroup } from '@progress/kendo-react-inputs';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const AdminInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,
    adminId: '',
    adminPw: '',
    adminName: '',
    phone: '',
    email: '',
    auth: '',
    authOptions: [],
    useYn: 'Y',
    useYnOptions: [],
    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/admin/setting/get_admin_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      adminId: data.adminId,
      adminPw: data.adminPw,
      adminName: data.adminName,
      phone: data.phone,
      email: data.email,
      auth: { code: data.auth, name: data.authName },
      authOptions: data.authOptions,
      useYn: data.useYn,
      useYnOptions: data.useYnOptions,
    });
  };

  const _setData = async () => {
    const url = '/api/admin/setting/set_admin_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _handleCheckChange = (e) => {
    setState({
      ...state,
      useYn: e.value,
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const adminNameMessage = "'관리자명'을 입력해주세요!";
  const adminIdMessage = "'관리자ID'를 입력해주세요!";
  const adminPwMessage = "'비밀번호'를 입력해주세요!";
  const adminAuthMessage = "'권한구분'을 선택해주세요!";

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form
        style={{ display: 'flex', justifyContent: 'center' }}
        onSubmit={_handleSubmit}
      >
        <Wrap width={'98%'}>
          <Title>관리자관리 〉상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>관리자명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="adminName"
                  value={state.adminName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={adminNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>관리자 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="adminId"
                  value={state.adminId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={adminIdMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>비밀번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="adminPw"
                  value={state.adminPw}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={adminPwMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>연락처</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="phone"
                  value={state.phone}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>이메일</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="email"
                  value={state.email}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>권한구분</Label>
                <ComboBox
                  style={{ width: '74%', color: '#dcdcdc' }}
                  name="auth"
                  value={state.auth}
                  data={state.authOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={adminAuthMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>현상태</Label>
                <RadioGroup
                  name="useYn"
                  defaultValue={state.useYn}
                  data={state.useYnOptions}
                  value={state.useYn}
                  style={{
                    width: '74%',
                    backgroundColor: 'white',
                    fontSize: '0.9em',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    fontSize: '13px',
                    marginTop: '1px',
                  }}
                  onChange={_handleCheckChange}
                  layout="horizontal"
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() =>
                  setState({
                    ...state,
                    isSave: true,
                  })
                }
              >
                저장하기
              </Button>
            )}

            <Button
              style={{ marginLeft: '10px' }}
              type={'button'}
              onClick={() => props.history.goBack()}
            >
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default AdminInfo;
