import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import CmsList from '../../components/admin/cms/CmsList';
import CmsInfo from '../../components/admin/cms/CmsInfo';
import CmsPayList from '../../components/admin/cms/CmsPayList';
import CmsPayInfo from '../../components/admin/cms/CmsPayInfo';
import CmsPayRequest from '../../components/admin/cms/CmsPayRequest'; // CMS출금신청
import CmsPayLogExcel from '../../components/admin/cms/CmsPayLogExcel';

const Cms = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/admin/cms"
        render={(props) => <CmsList {...props} />}
      />
      <AuthRoute
        path="/admin/cms/info"
        render={(props) => <CmsInfo {...props} />}
      />
      <AuthRoute
        exact
        path="/admin/cms/pay"
        render={(props) => <CmsPayList {...props} />}
      />
      <AuthRoute
        path="/admin/cms/pay/info"
        render={(props) => <CmsPayInfo {...props} />}
      />
      <AuthRoute
        path="/admin/cms/payRequest"
        render={(props) => <CmsPayRequest {...props} />}
      />  
      <AuthRoute
        path="/admin/cms/payLogExcel"
        render={(props) => <CmsPayLogExcel {...props} />}
      />           
    </Switch>
  );
};

export default Cms;