import React, { useCallback, useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../../redux/actions/header';
import {
  initPointOut,
  submitPointOut,
} from '../../../../redux/reducers/pointOut';
import _axios from '../../../../utils/axios';
// css
import { Table, Td, Th, Tr } from '../../../../css/list';
import theme from '../../../../styles/theme';
import styled from 'styled-components';
//images
import KakaoIcon from '../../../../images/front/commi-kakao.png';
import { getDataList } from '../../../../redux/actions/board';

const Withdraw = () => {
  const headerInfo = {
    isBackground: false,
    isBugger: false,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '포인트 출금신청',
  };

  const dispatch = useDispatch();
  const authStatus = useSelector((state) => state.auth.authStatus);
  const userId = authStatus.userInfo.userId;
  const userName = authStatus.userInfo.userName;

  const [data, setData] = useState();
  const [outCash, setOutCash] = useState();
  const [red, setRed] = useState(false);
  const [txt, setTxt] = useState(false);
  const [error, setError] = useState('');
  const RefInput = useRef();
  const RefButton = useRef();

  const [state, setState] = useState({
    isLoading: true,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 0,
      pageSize: 20,
      searchValue: '',
      memId: '',
      status: '',
      minDate: '',
      maxDate: '',
      statusOptions: [],
    },
  });

  // 출금 가능 포인트 input
  const onChange = (e) => {
    setOutCash(e.target.value);
  };

  // Modal
  const [btnToggle, setBtnToggle] = useState(false);

  // 출금요청
  const withdrawBtn = () => {
    const params = {
      memName: userName,
      memId: userId,
      outCash: outCash,
    };
    dispatch(submitPointOut(params));
    setBtnToggle(!btnToggle);
  };

  // 출금요청 > 닫기버튼
  const modalClose = () => {
    setBtnToggle(!btnToggle);
    setOutCash('');
    setState({
      ...state,
      isLoading: true,
    });
  };

  useEffect(() => {
    if (outCash === 0) {
      RefButton.current.disabled = true;
      RefInput.current.disabled = true;
      RefInput.current.style.color = `${theme.colors.grey3}`;
      RefButton.current.style.backgroundColor = '#A4AFC4';
    } else if (outCash >= 1 && outCash < 50000) {
      setRed(true);
      setError('최소 5만 포인트 이상 출금 가능합니다.');
      setTxt(true);
      RefButton.current.disabled = true;
      RefButton.current.style.backgroundColor = '#A4AFC4';
    } else if (outCash >= 50000) {
      RefButton.current.disabled = false;
      RefButton.current.style.backgroundColor = `${theme.colors.royalblue}`;
      setRed(false);
      setError('');
      setTxt(false);
    }
  }, [outCash]);

  const _getPointOut = useCallback(async () => {
    const url = '/api/member/get_pointOut_info.php';
    const params = {
      memId: userId,
    };

console.log(params);
    const { data } = await _axios(url, params);
    setData(data);
    const usableCash = data.usableCash.replace(/,/gi, '');
    setOutCash(parseInt(usableCash) < 0 ? 0 : parseInt(usableCash));
  }, [userId]);

  useEffect(() => {
    _getPointOut();
    return () => {
      dispatch(initPointOut());
    };
  }, [_getPointOut, dispatch]);

  useEffect(() => {
    dispatch(setHeader(headerInfo));
  }, []);

  const _getPointOUtList = useCallback(async () => {
    const url = '/api/member/get_cash_request_list.php';
    const params = {
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
      memId: userId,
      status: state.searchAble.status,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
    });
  }, [authStatus.userId, state]);

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getPointOUtList();
    }
  }, [_getPointOUtList, state.isLoading, state.isRestore]);

  return (
    <Container>
      {data === undefined ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          <WithdrawWrap>
            <div className="withdraw">
              <div className="withdraw-point">
                <h1>출금 가능 포인트</h1>
              </div>
              <WithdrawalApplication>
                <input
                  ref={RefInput}
                  type="number"
                  name="outCash"
                  placeholder="0"
                  onChange={onChange}
                  value={outCash || ''}
                  className={red ? 'is-active' : ''}
                />
                <WithdrawButton>
                  <button ref={RefButton} type="button" onClick={withdrawBtn}>
                    출금요청
                  </button>
                </WithdrawButton>
              </WithdrawalApplication>
            </div>

            <div className="withdraw-bank">
              <p className={txt ? 'is-active' : ''}>
                {data.bankName} {data.accountNo} {data.accountName}
              </p>
              <p className={txt ? 'is-active' : ''}>
                최소 5만 포인트 이상 출금 가능
              </p>
              {error && <span className="error">{error}</span>}
            </div>
          </WithdrawWrap>

          <WithdrawTable>
            <h3>출금 내역</h3>
            <Table width={'100%'}>
              <Tr width={'100%'} height={'35px'} background={'#fff'}>
                <Th width={'25%'}>요청일자</Th>
                <Th width={'24%'}>금액</Th>
                <Th width={'24%'}>은행명</Th>
                <Th width={'27%'}>입금일자</Th>
              </Tr>

              <Table className="table">
                {state.loadData?.map((item, index) => {
                  return (
                    <Tr key={index} width={'100%'} height={'30px'}>
                      <Td width={'25%'}>{item.wdate}</Td>
                      <Td width={'25%'}>{item.point}</Td>
                      <Td width={'25%'} className="table-ellipsis">
                        {data.bankName}
                      </Td>
                      <Td width={'25%'}>{item.paymentDate}</Td>
                    </Tr>
                  );
                })}
              </Table>
            </Table>
          </WithdrawTable>
        </Wrap>
      )}

      <WithdrawKaKao>
        <div>
          <a href="http://pf.kakao.com/_eGPTK" target="_blank" rel="noreferrer">
            <img src={KakaoIcon} />
          </a>
        </div>
      </WithdrawKaKao>

      {btnToggle && outCash >= 50000 && outCash < 10000000 ? (
        <WithdrawModal>
          <div className="modal-cover">
            <h1>출금 신청이 완료되었습니다.</h1>
            <p className="modal-txt01">
              입금은 출금 신청일로부터 3영업일 <br />
              이내에 입금드릴 예정이오니 이 점 <br />
              참고 부탁드리겠습니다.
            </p>
            <p className="modal-txt02">감사합니다.</p>
            <button type="button" onClick={modalClose}>
              닫기
            </button>
          </div>
        </WithdrawModal>
      ) : (
        ''
      )}
    </Container>
  );
};

export default Withdraw;

export const Container = styled.section`
  width: 100%;
  padding: 1em 1.5em 3em;
  background-color: #f8f8f8;

  ul {
    padding: 0;
  }

  li {
    position: relative;
    list-style: none;
  }

  p {
    margin: 0;
  }
`;

export const Wrap = styled.section`
  width: 100%;
  height: 100%;
`;

export const WithdrawWrap = styled.section`
  .withdraw-point {
    width: 100%;
    text-align: left;

    h1 {
      margin-bottom: 5px;
      ${theme.H3};
      border-bottom: 1px solid #000;
    }

    p {
      font-size: 14px;
    }
  }

  .withdraw {
    margin: 1em 0 0.3em;
    padding: 1.2em;
    background-color: #fff;

    &-money {
      flex: 60%;
      font-size: 14px;
    }

    &-won {
      flex: 0;
      font-size: 20px;
    }

    input {
      flex: 40%;
      width: 100%;
      height: 37px;
      padding-right: 1em;
      ${theme.H3};
      text-align: right;
      outline: none;
      border: none;
      background: #f1f2f2;

      ::placeholder {
        ${theme.H3};
        color: #a9a9a9;
        text-align: right;
      }
    }

    .is-active {
      ${theme.H3};
      color: red;
      text-align: right;
    }
  }

  .withdraw-bank {
    text-align: right;

    p {
      display: block;
      ${theme.Body1};
      color: #000;
    }

    .is-active {
      display: none;
    }
  }

  .error {
    ${theme.Body1};
    color: red;
  }
`;

export const WithdrawalApplication = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

export const WithdrawButton = styled.div`
  display: block;
  margin-left: 0.5em;
  text-align: right;

  button {
    width: 120px;
    height: 32px;
    font-size: 14px;
    color: #ffffff;
    background-color: #3c64b1;
    border-style: none;
    border-radius: 2px;
  }
`;

export const WithdrawTable = styled.section`
  margin-top: 3em;
  padding: 1.2em;
  background-color: #fff;

  h3 {
    margin-bottom: 20px;
    ${theme.H3};
    border-bottom: 1px solid #000;
  }

  .table {
    height: 180px;
    border-radius: 3px;
    overflow-y: scroll;

    .table-ellipsis {
      display: block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  ${Td} {
    ${theme.SubTitle2};
    color: #000;
    letter-spacing: -0.3px;
  }

  ${Th} {
    ${theme.SubTitle2};
    color: #000;
  }
`;

export const WithdrawKaKao = styled.div`
  margin-top: 3em;
  text-align: right;
`;

export const WithdrawModal = styled.aside`
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .modal-cover {
    position: fixed;
    top: 40%;
    left: 0%;
    right: 0%;
    bottom: 0%;
    width: 280px;
    height: 230px;
    margin: 0 auto;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0px 24px 38px rgba(0, 0, 0, 0.14),
      0px 9px 46px rgba(0, 0, 0, 0.12), 0px 11px 15px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
  }

  h1 {
    font-size: ${theme.fontSizes.H5};
    font-family: NotoM;
    font-weight: 500;
  }

  p {
    margin-top: 1.5em;
    font-size: ${theme.fontSizes.Body1};
    font-family: NotoR;
    font-weight: 500;
    color: ${theme.colors.grey1};
  }

  .modal-txt01 {
    line-height: 20px;
  }

  button {
    width: 100%;
    margin-top: 2em;
    font-size: ${theme.fontSizes.Body1};
    color: ${theme.colors.lapis};
    text-align: right;
    outline: none;
    border: none;
    background-color: transparent;
  }
`;
