import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { BrowserView, MobileView } from 'react-device-detect';
import styled from 'styled-components';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import CommonButton from '../common/Button';
// 개발자 라이브러리
import { setHeader } from '../../redux/actions/header';
import _axios from '../../utils/axios';
// css
import {
  Container,
  Loading,
  ParentWrap,
  SwiperMiddleLabel,
} from '../../css/common-component';
import theme from '../../styles/theme';

export default function CheckRecomandID({ history }) {
  const dispatch = useDispatch();

  const headerInfo = {
    isBackground: false,
    isBugger: false,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '회원가입',
  };

  const [inputs, setInputs] = useState({
    recommandId: '',
  });

  const [isComplete, setIsComplete] = useState({
    show: false,
    content: '',
  });

  const [isLoading, setIsLoading] = useState(false);
  const _handleInputChange = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  const _onCheckID = async () => {
    setIsLoading(true);
    const url = '/api/account/get_recommander_check.php';
    const data = await _axios(url, inputs);
    console.log(data);

    setIsComplete({ show: true, result: data.result, content: data.message });
    setIsLoading(false);
  };

  useEffect(() => {
    // header 설정
    dispatch(setHeader(headerInfo));
  }, []);

  return (
    <>
      <BrowserView>
        <Container
          style={{
            paddingTop: '82px',
          }}
          className="page"
        >
          <ParentWrap
            style={{
              width: 'auto',
              margin: '0px auto',
              padding: '1em 0 0',
              backgroundColor: 'transparent',
              border: 'none',
            }}
          >
            <StyledTitle>회원가입</StyledTitle>
            <StyledId>추천인 아이디 입력</StyledId>
            <StyledInputWrap>
              <input
                type="text"
                name="recommandId"
                placeholder="추천인 아이디를 입력하세요."
                className="input_name"
                onChange={_handleInputChange}
                required
                style={{ width: '100%', marginBottom: '15px' }}
              ></input>
              <CommonButton
                onClickHandler={_onCheckID}
                kind="wideBtn_01"
                type="button"
              >
                확인
              </CommonButton>
            </StyledInputWrap>
            {isComplete.show && (
              <StyledDialog
                width={'256px'}
                height={'auto'}
                title={' '}
                onClose={() => {
                  isComplete.show
                    ? setIsComplete({ show: false })
                    : setIsComplete({ show: true });
                }}
              >
                <SwiperMiddleLabel
                  width="216px"
                  height="auto"
                  margin="0px auto 0px auto"
                  style={{
                    color: '#000f34',
                    fontSize: '15px',
                    textAlign: 'center',
                  }}
                >
                  {isComplete.result === '0'
                    ? '확인되었습니다.'
                    : `입력하신 정보가 없습니다. 다시 입력하세요.`}
                </SwiperMiddleLabel>
                <DialogActionsBar>
                  {isComplete.result === '0' ? (
                    <button
                      className="k-button"
                      onClick={() => {
                        history.push({
                          pathname: '/account/joininsertInfo',
                          state: { recommandId: inputs.recommandId },
                        });
                        isComplete.show
                          ? setIsComplete({ show: false })
                          : setIsComplete({ show: true });
                      }}
                    >
                      다음
                    </button>
                  ) : (
                    <button
                      className="k-button"
                      onClick={() => {
                        isComplete.show
                          ? setIsComplete({ show: false })
                          : setIsComplete({ show: true });
                      }}
                    >
                      닫기
                    </button>
                  )}
                </DialogActionsBar>
              </StyledDialog>
            )}
          </ParentWrap>
          {isLoading && <Loading style={{ left: '35%' }}></Loading>}
        </Container>
      </BrowserView>
      <MobileView>
        <Container className="page">
          <ParentWrap
            height="100%"
            padding="0px"
            width="auto"
            style={{
              margin: '0 auto',
              backgroundColor: 'transparent',
              border: 'none',
            }}
          >
            <StyledTitle>회원가입</StyledTitle>
            <StyledId>추천인 아이디 입력</StyledId>
            <StyledInputWrap
            // style={{ width: '100%', margin: '0 auto' }}
            >
              <input
                type="text"
                name="recommandId"
                placeholder="추천인 아이디를 입력하세요."
                className="input_name"
                onChange={_handleInputChange}
                required
                style={{ width: '335px', marginBottom: '15px' }}
              ></input>
              <CommonButton
                onClickHandler={_onCheckID}
                kind="wideBtn_01"
                type="button"
              >
                확인
              </CommonButton>
            </StyledInputWrap>
            {isComplete.show && (
              <StyledDialog
                width={'256px'}
                height={'auto'}
                title={' '}
                onClose={() => {
                  isComplete.show
                    ? setIsComplete({ show: false })
                    : setIsComplete({ show: true });
                }}
              >
                <SwiperMiddleLabel
                  width="216px"
                  height="auto"
                  margin="0px auto 0px auto"
                  style={{
                    color: '#000f34',
                    fontSize: '15px',
                    textAlign: 'center',
                  }}
                >
                  {isComplete.result === '0'
                    ? '확인되었습니다.'
                    : `입력하신 정보가 없습니다.
                다시 입력하세요.`}
                </SwiperMiddleLabel>
                <DialogActionsBar>
                  {isComplete.result === '0' ? (
                    <button
                      className="k-button"
                      onClick={() => {
                        history.push({
                          pathname: '/account/insertInfo',
                          state: { recommandId: inputs.recommandId },
                        });
                        isComplete.show
                          ? setIsComplete({ show: false })
                          : setIsComplete({ show: true });
                      }}
                    >
                      다음
                    </button>
                  ) : (
                    <button
                      className="k-button"
                      onClick={() => {
                        isComplete.show
                          ? setIsComplete({ show: false })
                          : setIsComplete({ show: true });
                      }}
                    >
                      닫기
                    </button>
                  )}
                </DialogActionsBar>
              </StyledDialog>
            )}
          </ParentWrap>
          {isLoading && <Loading style={{ left: '35%' }}></Loading>}
        </Container>
      </MobileView>
    </>
  );
}

const StyledDialog = styled(Dialog)`
  .k-dialog {
    border-top-right-radius: 22px;
    border-top-left-radius: 22px;
    border-bottom-right-radius: 22px;
    border-bottom-left-radius: 22px;
  }

  .k-window-titlebar {
    padding: 0px;
  }

  .k-dialog-content {
    margin-bottom: 30px;
  }
  .k-dialog-titlebar {
    background-color: transparent;
    border: none;
  }

  .k-dialog-button-layout-stretched {
    background-color: #000f34;
    color: #fff;
    border-bottom-right-radius: 22px;
    border-bottom-left-radius: 22px;
  }

  .k-button {
    background-color: #000f34;
    color: #fff;
    height: 59px;
  }
`;

const StyledTitle = styled.div`
  margin-top: 2em;
  margin-bottom: 1em;
  font-size: ${theme.fontSizes.H1};
  font-family: NotoM;
  text-align: center;
`;

const StyledId = styled.div`
  font-size: ${theme.fontSizes.H4};
  font-family: NotoR;
  text-align: center;
`;

const StyledInputWrap = styled.div`
  margin-top: 50px;
  margin-bottom: 309px;

  .input_name {
    width: 60%;
    height: 44px;
    padding: 9px 0px 8px 11px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }
  .button {
    width: 35%;
    height: 34px;
    margin-bottom: 49px;
    border-radius: 8px;
    border: solid 1px #000;
    background-color: #000f34;
    color: #fff;
    text-align: center;
    float: right;
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;
