/*
설명: Mypage > Home > Mobile
- 2021.05.25 안예린
- 2021.05.28 박태수
*/
import React, { useState, useEffect } from 'react';
import { useHistory, Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import KakaoLinkList from '../../common/KakaoLinkList';
// css
import { SwiperMiddleLabel } from '../../../styles/common-component';
// icon & images
import mobile from '../../../images/mainAfterLogin/mobile.svg';
import tv from '../../../images/mainAfterLogin/tv.svg';
import car from '../../../images/mainAfterLogin/car.svg';
import back_arrow_btn from '../../../images/mainAfterLogin/back_arrow_btn.png';
import circleBtn from '../../../images/front/commi-circleBtn.svg';
import theme from '../../../styles/theme';

const HomeMobile = () => {
  const headerInfo = {
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: false, // Back 버튼
    titleName: '',
  };

  const dispatch = useDispatch();
  const history = useHistory();
  const authStatus = useSelector((store) => store.auth.authStatus);

  const [state, setState] = useState({
    isLoading: true,
    isGuide: false,
    userId: authStatus.userInfo.userId,

    totalPoint: 0,
    monthPoint: 0,
    chartData: [],
    hpRequest: [],
    noticeData: [],
  });

  const _getData = async () => {
    const url = '/api/member/get_mypage_home.php';
    const params = {
      userId: state.userId,
      page: 1,
      rows: 10,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      totalPoint: response.totalPoint,
      monthPoint: response.monthPoint,
      chartData: response.chartData,
      hpRequest: response.hpRequest,
      noticeData: response.noticeData,
    });
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    _getData();
  }, []);

  const _goGajaRental = () => {
    window.open(
      `https://open.gajarental.com?token=${authStatus?.userInfo?.token}`,
    );
  };

  const _toggleGuideDialog = () => {
    setState({
      isGuide: !state.isGuide,
    });
  };

  const _goHpRequest = () => {
    history.push('/partnership/hp/currentTelecom');
  };

  const _goInSu = () => {
    history.push('/mypage/insuShareLink');
  };

  const _goCommission = () => {
    const date = new Date();
    const year = date.getFullYear();
    const month = ('0' + (1 + date.getMonth())).slice(-2);
    const lastDate = new Date(year, month, 0);
    const lastDay = ('0' + lastDate.getDate()).slice(-2);

    history.push({
      pathname: '/mypage/home/point',
      minDate: year + '-' + month + '-01',
      maxDate: year + '-' + month + '-' + lastDay,
    });
  };

  const date = new Date();
  const year = date.getFullYear();
  const month = ('0' + (1 + date.getMonth())).slice(-2);

  return (
    <Container style={{ paddingTop: '2em' }}>
      <StyledContainer className="page">
        <StyledPointWrap>
          <div className="container">
            <div className="title">
              <div className="point">포인트</div>
              <p
                className=" d-flex justify-content-end align-items-center "
                onClick={_goCommission}
              >
                <span id="more_btn">자세히 보기</span>
                <i>
                  <Link to="/mypage/home/point">
                    <img src={circleBtn} alt="" />
                  </Link>
                </i>
              </p>
            </div>

            <div className="point_box">
              <p className=" d-flex justify-content-between">
                <span id="all">전체 누적 보너스</span>
                <span id="point">{state.totalPoint}원</span>{' '}
              </p>
              <p className=" d-flex justify-content-between">
                <span id="all">
                  {year}년 {month}월 누적 보너스
                </span>
                <span id="point">{state.monthPoint}원</span>{' '}
              </p>
            </div>
          </div>
        </StyledPointWrap>

        <StyledMainBody>
          <div className="container">
            <StyledTitle>휴대폰 간편신청 현황</StyledTitle>
            <StyledProgressWrap>
              <div 
                className="progress1" 
                onClick={() => {
                  history.push({
                    pathname: '/mypage/hpRequest',
                    requestStatus: {
                      code: '0',
                      name: '접수중',
                    },
                  });
                }}
              >
                <p id="child1">접수중</p>
                <p id="child2">{state.hpRequest.status_0}건</p>
              </div>
              <div className="line">
                <img src={back_arrow_btn}></img>
              </div>
              <div 
                className="progress1" 
                onClick={() => {
                  history.push({
                    pathname: '/mypage/hpRequest',
                    requestStatus: {
                      code: '1',
                      name: '처리중',
                    },
                  });
                }}
              >
                <p id="child1">처리중</p>
                <p id="child2">{state.hpRequest.status_1}건</p>
              </div>
              <div className="line">
                <img src={back_arrow_btn}></img>
              </div>
              <div 
                className="progress1" 
                onClick={() => {
                  history.push({
                    pathname: '/mypage/hpRequest',
                    requestStatus: {
                      code: '9',
                      name: '개통완료',
                    },
                  });
                }}
              >
                <p id="child1">개통완료</p>
                <p id="child2">{state.hpRequest.status_9}건</p>
              </div>
            </StyledProgressWrap>
          </div>
        </StyledMainBody>

        <StyledMainBody style={{ marginTop: '25px' }}>
          <div className="container">
            <StyledTitle>서비스</StyledTitle>
            <StyledMenuWrap>
              <StyledMenuButton>
                <p id="icon">
                  <img src={mobile}></img>
                </p>
                <div className="sub_wrap">
                  <p id="name">휴대폰 간편신청</p>
                  <p id="link">
                    <span onClick={_goHpRequest}>바로신청</span>
                    <span onClick={() => history.push('/mypage/hpShareLink')}>
                      링크신청
                    </span>
                    <span onClick={() => history.push('/mypage/hpRequest')}>
                      신청목록
                    </span>
                  </p>
                </div>
              </StyledMenuButton>

              <StyledMenuButton>
                <p id="icon">
                  <img src={tv}></img>
                </p>
                <div className="sub_wrap">
                  <p id="name">가자렌탈</p>
                  <p id="link">
                    <span onClick={_goGajaRental}>사이트</span>
                    <span 
                      onClick={() => {
                        history.push('/mypage/commi')
                      }}
                    >
                      신청목록
                    </span>
                  </p>
                </div>
              </StyledMenuButton>

              <StyledMenuButton>
                <p id="icon">
                  <img src={car}></img>
                </p>
                <div className="sub_wrap">
                  <p id="name">자동차 다이렉트 보험</p>
                  <p id="link">
                    <span onClick={_goInSu}>링크신청</span>
                    <span onClick={() => history.push('/mypage/insuRequest')}>
                      신청목록
                    </span>
                  </p>
                </div>
              </StyledMenuButton>
            </StyledMenuWrap>
          </div>

          {/* <NotiWrap className="row">
            <NotiImg
              src={noti_button}
              alt="공지사항"
              className="col-12"
            />
          </NotiWrap> */}

          {/* <img
            src={kakao_icon}
            alt=""
            style={{
              width: '45px',
              height: '48px',
              margin: '0px 254px 0 0',
              objectFit: 'contain',
            }}
            onClick={_kakaoChating}
          /> */}
        </StyledMainBody>

        <StyledMainBody>
          <div className="container">
            <div className="title">
              <div className="point">공지사항</div>
              <p className=" d-flex justify-content-end align-items-center">
                <span id="more_btn">전체보기</span>
                <i>
                  <img src={circleBtn} alt="" />
                  {/*
                  <Link to="/">
                    <img src={circleBtn} alt="" />
                  </Link>
                  */}
                </i>
              </p>
            </div>

            <div className="div2 ">
              {state.noticeData.map((data) => (
                <div key={data.idx}>
                  <p className="info_title">
                    <span>{data.subject}</span>
                    <span>{data.wdate}</span>
                  </p>
                </div>
              ))}
            </div>
          </div>
        </StyledMainBody>

        {state.isGuide && (
          <Dialog
            id="mainAfterLogin"
            width={'256px'}
            height={'auto'}
            title={'자동차 다이렉트 보험이란?'}
            onClose={_toggleGuideDialog}
          >
            <SwiperMiddleLabel
              width="216px"
              height="74px"
              margin="0 auto 0px auto"
              style={{
                color: '#000f34',
                fontSize: '15px',
                textAlign: 'left',
              }}
            >
              자동차 다이렉트 보험은 제휴된 국내 모든 자동차 보험사의 다이렉트
              자동차 보험을 쉽게 비교하여 이용하실 수 있습니다.
              <br /> <br />
              중계수수료가 없기 때문에 소비자에게는 저렴한 보험료를 이용할 수
              있어 이득이며, 홍보한 사람은 광고료를 수익으로 받을 수 있습니다.
            </SwiperMiddleLabel>
            <DialogActionsBar>
              <button className="k-button" onClick={() => _toggleGuideDialog()}>
                닫기
              </button>
            </DialogActionsBar>
          </Dialog>
        )}
      </StyledContainer>

      {/* 카카오톡 플러팅 버튼 */}
      <KakaoLinkList />
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  background: #f8f8f8;
  i {
    transform: translateY(1.5px);
  }
`;

const StyledContainer = styled.div`
  width: 100%;
  padding: 0 20px;
  padding-bottom: 30px;
  // margin-top: 56px;
  // margin-bottom: 30px;
`;

const StyledPointWrap = styled.div`
  width: 100%;
  background-color: ${theme.colors.white};
  .container {
    background-color: white;
    width: 100%;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
    border-radius: 3px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    border-bottom: 0.5px solid #a9a9a9;
    margin-bottom: 20px;
  }
  .point {
    display: flex;
    align-items: flex-end;
    font-size: ${theme.fontSizes.H4};
    font-family: NotoM;
    color: ${theme.colors.black1};
    line-height: 28px;
  }
  .point_box {
    padding-bottom: 30px;
    p {
      margin: 0 0 5px;
    }
    .line {
      margin: 8.5px 0 10px;
      background-color: #000;
    }
  }
  #all {
    display: flex;
    align-items: center;
    font-size: ${theme.fontSizes.Title};
    font-family: NotoM;
    line-height: 20px;
    color: ${theme.colors.black1};
  }
  #point {
    display: flex;
    align-items: center;
    font-size: ${theme.fontSizes.Title};
    font-family: NotoM;
    line-height: 20px;
    color: ${theme.colors.black1};
  }
  #more_btn {
    display: flex;
    align-items: center;
    margin-right: 5px;
    font-size: ${theme.fontSizes.Caption};
    font-family: NotoM;
    color: ${theme.colors.royalblue};
    text-align: right;
    text-transform: capitalize;
    line-height: 16px;
    letter-spacing: 0.2px;
  }
  #icon {
    align-items: center;
    justify-content: center;
    display: flex;
    border-radius: 100%;
    width: 15px;
    height: 15px;
    flex-grow: 0;
    margin: 0px 0 2px 5px;
    background-color: #3c64b1;
  }
`;

const StyledMainBody = styled.div`
  width: 100%;
  height: auto;
  background: #fff;
  margin-top: 30px;
  border-radius: 3px;
  .container {
    background-color: white;
    width: 100%;
    margin-top: 30px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
    border-radius: 3px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    border-bottom: 0.5px solid #a9a9a9;
    margin-bottom: 20px;
  }
  .point {
    display: flex;
    align-items: flex-end;
    font-size: ${theme.fontSizes.H4};
    font-family: NotoM;
    color: ${theme.colors.black1};
    line-height: 28px;
  }
  #more_btn {
    display: flex;
    align-items: center;
    margin-right: 5px;
    font-size: ${theme.fontSizes.Button1};
    font-family: NotoM;
    color: ${theme.colors.royalblue};
    text-align: right;
    text-transform: capitalize;
    line-height: 16px;
    letter-spacing: 0.2px;
  }
  #icon2 {
    align-items: center;
    justify-content: center;
    display: flex;
    border-radius: 100%;
    width: 15px;
    height: 15px;
    flex-grow: 0;
    margin: 0px 0 2px 5px;
    background-color: #3c64b1;
  }
  .div2 {
    padding-bottom: 30px;
  }
  .info_title {
    font-family: Noto Sans KR;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    /* identical to box height, or 143% */
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* black1 */
    color: #000000;
    margin-bottom: 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
`;

const StyledTitle = styled.div`
  display: flex;
  align-items: center;
  padding-top: 30px;
  font-size: ${theme.fontSizes.H4};
  font-family: NotoM;
  line-height: 28px;
  border-bottom: 0.5px solid #a9a9a9;
`;

const StyledMenuWrap = styled.div`
  width: 100%;
`;

const StyledMenuButton = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: auto;
  margin-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  border-radius: 4px;
  border-bottom: solid 1px #f1f2f2;
  background-color: #ffffff;
  p {
    margin: 0px;
  }
  .sub_wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 75px;
    width: 240px;
    margin-left: 27px;
    #name {
      color: ${theme.colors.black1};
      font-size: ${theme.fontSizes.Title};
      font-family: NotoM;
      line-height: 20px;
    }
    #link {
      display: flex;
      align-items: center;
      color: ${theme.colors.royalblue};
      font-size: ${theme.fontSizes.Body1};
      font-family: NotoR;
      text-align: center;
      line-height: 20px;
      span {
        padding-right: 16px;
      }
    }
  }
  #icon {
    display: flex;
    justify-content: center;
    width: 39px;
    height: 39px;
    border-radius: 78px;
  }
`;

const NotiImg = styled.img`
  width: 100%;
  padding: 0;
`;
const MenuImg = styled.img`
  width: 100%;
`;

const NotiWrap = styled.div`
  padding: 0;
`;

const StyledProgressWrap = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  align-items: center;
  padding-bottom: 30px;

  .progress1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0px 0 0;
    min-width: 67px;
    width: 67px;
    height: 67px;
    color: #ffffff;
    font-size: ${theme.fontSizes.SubTitle1};
    font-family: NotoR;
    color: ${theme.colors.white};
    line-height: 17px;
    text-align: center;
    background: ${theme.colors.royalblue};
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
    border-radius: 100%;
    p {
      margin: 0;
    }
  }
  .line {
    width: 11px;
    height: 15px;
    flex-grow: 0;
    img {
      width: 100%;
    }
  }
`;
export default HomeMobile;
