import React, { useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Progress } from 'semantic-ui-react';
import { useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
import { GridColumn as Column, Grid } from '@progress/kendo-react-grid';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import { Input, MaskedTextBox } from '@progress/kendo-react-inputs';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import 'semantic-ui-css/semantic.min.css';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const CmsPayRequest = () => {
  const authStatus = useSelector((store) => store.auth.authStatus);

  const [state, setState] = useState({
    adminId: authStatus.userId,
    isLoading: true,
    loadData: [],

    percent: 0,
    inc: 0,

    success: false,
    message: null,
    visibleDialog: false,
    confirmMessage: null,
    confirmDialog: false,
    // 페이징
    skip: 0,
    take: 10,
    rowTotal: 0,
    pageable: {
      buttonCount: 10,
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      memId: '',
      memAssort: '',
      assortOptions: [],
      paymentKind: '',
      kindOptions: [],
      payMonth: '',
      payDate: '',
    },
  });

  const _handleInputChange = (e) => {
    const { searchAble } = state;
    const value = e.target.value;

    setState({
      ...state,
      searchAble: {
        ...searchAble,
        [e.target.name]: value,
      },
    });
  };

  // 검색시작
  const _handleSubmit = (e) => {
    e.preventDefault();

    setState({ ...state, isLoading: true, loadData: [] });
  };

  const _getData = async () => {
    const url = '/api/admin/cms/get_pay_target_list.php';
    const params = {
      memId: state.searchAble.memId,
      memAssort: state.searchAble.memAssort,
      paymentKind: state.searchAble.paymentKind,
    };

    const response = await _axios(url, params);

    setState((prevState) => ({
      ...state,
      isLoading: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      inc: 100 / response.rowTotal,
      percent: 0,
      searchAble: {
        ...prevState.searchAble,
        assortOptions: response.assortOptions,
        kindOptions: response.kindOptions,
      },
    }));
  };

  // 페이지 클릭
  const _handlePageChange = (e) => {
    setState({ ...state, skip: e.page.skip, take: e.page.take });
  };

  // 다이얼로그 토글
  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  // ******************************** 출금신청 시작 *******************************
  // 출금신청시작
  const _requestSubmit = async (e) => {
    e.preventDefault();

    setState({
      ...state,
      confirmDialog: true,
      confirmMessage: "'출금신청'을 진행하시겠습니까?",
    });
  };

  // 출금신청API
  const _requestApi = async (i) => {
    if (state.loadData.length >= i) {
      const url = '/api/admin/cms/set_cms_pay.php';
      const params = {
        adminId: state.adminId,
        memId: state.loadData[i].memId,
        payMonth: state.searchAble.payMonth,
        payDate: state.searchAble.payDate,
      };

      await _axios(url, params).then(() => {
        setTimeout(() => {
          _increment();

          if (state.loadData.length > i + 1) {
            _requestApi(i + 1);
          } else {
            setState({
              ...state,
              success: true,
              message: "'출금신청'을 완료했습니다",
              visibleDialog: true,
            });

            setTimeout(() => {
              setState({ ...state, success: false });
            }, 3000);
          }
        }, 1000);
      });
    }
  };

  // 진행 Bar
  const _increment = () => {
    setState((prevState) => ({
      ...state,
      percent: prevState.percent >= 100 ? 0 : prevState.percent + state.inc,
    }));
  };
  // ******************************** 출금신청 끝 *******************************

  useEffect(() => {
    if (state.isLoading) {
      _getData();
    } else {
      _getData();
    }
  }, [state.isLoading]);

  const { isLoading, loadData, searchAble } = state;
  const conWidth = isMobile ? '100%' : '900px';
  const labelWidth = '25%';
  const payMonthMessage = "'해당년월'을 입력해주세요!";
  const payDateMessage = "'출금일자'를 입력해주세요!";

  return (
    <Container width={conWidth}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap width={'100%'}>
          <Title>CMS관리 〉 출금신청</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={'row'} background={'#fff'}>
              <Td width={'49%'} direction={'column'}>
                <Table width={'100%'} background={'#dcdcdc'}>
                  <Tr width={'100%'} direction={'column'}>
                    <Td width={'100%'} fontSize={'1em'}>
                      <Label width={labelWidth}>회원구분</Label>
                      <ComboBox
                        style={{
                          width: '74%',
                          borderBottom: '1px solid #dcdcdc',
                        }}
                        name="memAssort"
                        value={searchAble.memAssort}
                        data={searchAble.assortOptions}
                        dataItemKey="code"
                        textField="name"
                        onChange={_handleInputChange}
                        required={false}
                      />
                    </Td>
                    <Td width={'100%'} fontSize={'1em'}>
                      <Label width={labelWidth}>회원ID</Label>
                      <Input
                        validityStyles={false}
                        style={{ width: '74%' }}
                        type="text"
                        name="memId"
                        value={searchAble.memId}
                        maxLength={20}
                        required={false}
                        onChange={_handleInputChange}
                      />
                    </Td>
                    <Td width={'100%'} fontSize={'1em'}>
                      <Label width={labelWidth}>납부수단</Label>
                      <ComboBox
                        style={{
                          width: '74%',
                          borderBottom: '1px solid #dcdcdc',
                        }}
                        name="paymentKind"
                        value={searchAble.paymentKind}
                        data={searchAble.kindOptions}
                        dataItemKey="code"
                        textField="name"
                        onChange={_handleInputChange}
                        required={false}
                      />
                    </Td>
                  </Tr>
                </Table>

                <ButtonWrap>
                  <Button onClick={_handleSubmit}>검색하기</Button>
                </ButtonWrap>
              </Td>

              <Td
                width={'50%'}
                direction={'column'}
                style={{ marginLeft: '5px' }}
              >
                <form onSubmit={_requestSubmit}>
                  <Table width={'100%'}>
                    <Tr
                      width={'100%'}
                      direction={'column'}
                      background={'#dcdcdc'}
                    >
                      <Td width={'100%'} fontSize={'1em'}>
                        <Label width={labelWidth}>해당년월</Label>
                        <MaskedTextBox
                          name="payMonth"
                          value={searchAble.payMonth}
                          width={'74%'}
                          mask="0000-00"
                          required={true}
                          validationMessage={payMonthMessage}
                          onChange={_handleInputChange}
                        />
                      </Td>
                      <Td width={'100%'} fontSize={'1em'}>
                        <Label width={labelWidth}>출금일자</Label>
                        <Input
                          validityStyles={false}
                          style={{ width: '74%' }}
                          type="date"
                          name="payDate"
                          value={searchAble.payDate}
                          maxLength={10}
                          required={true}
                          validationMessage={payDateMessage}
                          onChange={_handleInputChange}
                        />
                      </Td>
                      <Td
                        style={{
                          width: '100%',
                          height: '30px',
                          background: '#fff',
                          padding: '8px',
                        }}
                      >
                        <div style={{ width: '100%' }}>
                          <Progress percent={state.percent} indicating />
                        </div>
                      </Td>
                    </Tr>
                  </Table>

                  <ButtonWrap>
                    <Button type={'submit'}>출금신청</Button>
                  </ButtonWrap>
                </form>
              </Td>
            </Tr>
          </Table>

          <Grid
            style={{
              width: '900px',
              height: '420px',
              fontSize: '0.9em',
              cursor: 'pointer',
            }}
            data={loadData.slice(state.skip, state.take + state.skip)}
            onPageChange={_handlePageChange}
            total={state.rowTotal}
            skip={state.skip}
            take={state.take}
            pageable={true}
          >
            <Column field="no" title="No" width={50} />
            <Column field="memAssort" title="구분" width={55} />
            <Column field="memId" title="회원ID" width={90} />
            <Column field="memName" title="회원명" />
            <Column field="paymentKind" title="납부수단" width={85} />
            <Column field="cmsStatus" title="CMS상태" width={85} />
            <Column field="wdate" title="가입일자" width={100} />
          </Grid>

          {state.success && state.visibleDialog && (
            <Dialog title={'알림'} onClose={_toggleDialog}>
              <p style={{ margin: '25px', textAlign: 'center' }}>
                {state.message}
              </p>
            </Dialog>
          )}

          {state.confirmDialog && (
            <Dialog title={'확인'} onClose={_toggleDialog}>
              <p style={{ margin: '25px', textAlign: 'center' }}>
                {state.confirmMessage}
              </p>
              <DialogActionsBar>
                <button className="k-button" onClick={_toggleDialog}>
                  아니오
                </button>
                <button
                  className="k-button"
                  onClick={() => {
                    setState({ ...state, confirmDialog: false });
                    _requestApi(0);
                  }}
                >
                  예
                </button>
              </DialogActionsBar>
            </Dialog>
          )}
        </Wrap>
      )}
    </Container>
  );
};

export default CmsPayRequest;
