/* **********************************************************************
  관리자페이지 > 환경설정 > 기초정보설정
  - 2021.05.19. 박태수
  - 2021.05.28. 양민석 : 스파이더2 리뉴얼작업
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Th,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const InitSetting = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    subsA: '',
    subsS: '',
    payA: '',
    payS: '',
    commitS: '',
    commitSA: '',
    commitSL: '',
    commitR: '',
    commitMA: '',
    commitMS: '',
    hpIntRate: '',
    cashOutRate: '',
    insuRate: '',

    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/admin/setting/get_setting_info.php';
    const params = {};

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      subsA: data.subsA,
      subsS: data.subsS,
      payA: data.payA,
      payS: data.payS,
      commitS: data.commitS,
      commitSA: data.commitSA,
      commitSL: data.commitSL,
      commitR: data.commitR,
      commitMA: data.commitMA,
      commitMS: data.commitMS,
      hpIntRate: data.hpIntRate,
      cashOutRate: data.cashOutRate,
      insuRate: data.insuRate,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/admin/setting/set_setting_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '900px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '30%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>환경설정 〉 기초정보설정</Title>

          <Table width={'100%'}>
            <Tr>
              <Th>개설비용</Th>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>플랫폼MD</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="subsA"
                  value={state.subsA}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>구독플랫폼</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="subsS"
                  value={state.subsS}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>

            <Tr>
              <Th>월 납입비용</Th>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>플랫폼MD</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="payA"
                  value={state.payA}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>구독플랫폼</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="payS"
                  value={state.payS}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>

            <Tr>
              <Th>수익플랜</Th>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>MD유치(기본)</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commitS"
                  value={state.commitS}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>MD유치(추가)</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commitSA"
                  value={state.commitSA}
                  style={{ width: '25%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
                <span style={{ width: '20%', height: '29px', padding: '5px' }}>
                  추가한도
                </span>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commitSL"
                  value={state.commitSL}
                  style={{ width: '24%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>월구독료(MD)</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commitMA"
                  value={state.commitMA}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>월구독료(판)</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commitMS"
                  value={state.commitMS}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>렌탈수수료(댑)</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commitR"
                  value={state.commitR}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>

            <Tr>
              <Th>휴대폰신청</Th>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할부이자율</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="hpIntRate"
                  value={state.hpIntRate}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>

            <Tr>
              <Th>포인트관리</Th>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>현급지급율</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="cashOutRate"
                  value={state.cashOutRate}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>

            <Tr>
              <Th>다이렉트보험</Th>
            </Tr>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>수수료율</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="insuRate"
                  value={state.insuRate}
                  style={{ width: '69%' }}
                  maxLength={20}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default InitSetting;
