import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const RequestList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '애드인슈 〉 신청목록',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchKey: '',
      searchValue: '',
      searchOptions: [],
      memId: '',
      minDate: '',
      maxDate: '',
      requestStatus: '',
      requestOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/hp/request/get_request_list.php';
    const params = {
      searchKey: state.searchAble.searchKey,
      searchValue: state.searchAble.searchValue,
      memId: state.searchAble.memId,
      requestStatus: state.searchAble.requestStatus,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        searchOptions: response.searchOptions,
        requestOptions: response.requestOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '검색어',
        items: [
          { type: 'text',width: '84%',name: 'searchValue',value: searchAble.searchValue },
          { type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [
          { type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue },
        ],
      };
  const useYn = {
    title: '회원ID',
    items: [
      { type: 'text', width: '100%', name: 'memId', value: searchAble.memId },
    ],
  };
  const requestStatus = {
    title: '신청상태',
    items: [
      { type: 'comboBox', width: '100%', name: 'requestStatus', value: searchAble.requestStatus, options: searchAble.requestOptions },
    ],
  };
  const writeDate = {
    title: '신청일자',
    items: [
      { type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate },
      { type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate },
    ],
  };
  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, useYn, requestStatus, writeDate];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',width: '10%',align: 'center',},
        {type: 'text',title: '회원ID',name: 'memId',width: '25%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '15%',align: 'center',},
        {type: 'text',title: '고객명',name: 'custName',width: '15%',align: 'center',},
        {type: 'text',title: '휴대폰번호',name: 'hpNo',width: '30%',align: 'center',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '5%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '5%',align: 'center',},
        {type: 'text',title: '회원ID',name: 'memId',width: '8%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '8%',align: 'center',},
        {type: 'text',title: '고객명',name: 'custName',width: '19%',align: 'center',},
        {type: 'text',title: '휴대폰번호',name: 'hpNo',width: '13%',align: 'center',},
        {type: 'text',title: '모델명',name: 'modelName',width: '20%',align: 'center',},
        {type: 'text',title: '신청상태',name: 'requestStatus',width: '7%',align: 'center',},
        {type: 'text',title: '신청일자',name: 'wdate',width: '15%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>애드인슈 〉 신청목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={false}
            isDeleteButton={true}
            deleteUrl="/api/hp/request/set_request_delete.php"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            infoPath="/admin/insuRequest/info"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default RequestList;
