import React from 'react';
import { useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import styled from 'styled-components';
// 개발자 라이브러리
import BurgerMenu from './BurgerMenu';
// css
import theme from '../../../../styles/theme';
// icon & images
import { ReactComponent as LogoNavy } from '../../../../images/header/logo_navy02.svg';
import { ReactComponent as LogoWhite } from '../../../../images/header/logo_white.svg';
import { ReactComponent as BackNavy } from '../../../../images/header/back_navy.svg';
import { ReactComponent as BackWhite } from '../../../../images/header/back_white.svg';

const MobileHeader = () => {
  const history = useHistory();
  const headerStatus = useSelector((store) => store.header.headerStatus);

  const color = headerStatus.isBackground
    ? theme.colors.lightGrey
    : theme.colors.navy;
  const background = headerStatus.isBackground
    ? theme.colors.navy
    : theme.colors.lightGrey;

  return (
    <Container style={{ background: background }}>
      <Wrap id="Bugger">
        <NaviWrap>
          {headerStatus.isLogo && (
            <Link to="/">
              {headerStatus.isBackground ? <LogoWhite /> : <LogoNavy />}
            </Link>
          )}
          <div
            style={{
              width: '26px',
              height: '26px',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            {headerStatus.isBackButton &&
              (headerStatus.isBackground ? (
                <BackWhite onClick={() => history.goBack()} />
              ) : (
                <BackNavy onClick={() => history.goBack()} />
              ))}
          </div>
          {headerStatus.titleName != '' && (
            <TitleName style={{ color: color }}>
              {headerStatus.titleName}
            </TitleName>
          )}
          {headerStatus.isBugger && (
            <BurgerMenu
              pageWrapId={'bugger-menu'}
              outerContainerId={'Bugger'}
            />
          )}
        </NaviWrap>
      </Wrap>
    </Container>
  );
};

const Container = styled.div`
  position: fixed;
  width: 100%;
  height: 56px;
  border-bottom: 1px solid ${theme.colors.boxGray};
  z-index: 999999;
`;

const Wrap = styled.div`
  position: fixed;
  display: flex;
  align-items: center;
  width: 100%;
  height: 56px;
  z-index: 21;
  .bm-overlay {
    top: 0;
  }
  .bm-burger-button {
    width: 34px;
    height: 34px;
    top: 11px;
    right: 16px;
  }
`;

const NaviWrap = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;

  a {
    svg {
      display: block;
      width: 72px;
      height: 21px;
    }
  }
`;

const TitleName = styled.div`
  align-self: center;
  margin-left: 10px;
  font-size: ${theme.fontSizes.Title};
  font-family: NotoM;
`;

export default MobileHeader;
