/* **********************************************************************
 * 관리자페이지 > 게시판관리 > 게시글목록 > 댓글목록
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Dialog } from '@progress/kendo-react-dialogs';
import styled from 'styled-components';
import renderHTML from 'react-render-html';
// 개발자 Component
import _axios from '../../../utils/axios';
import Editor from '../../common/board/Editor';
// icon
import DeleteIcon from '@material-ui/icons/Clear';

const BbsReply = (props) => {
  const auth = useSelector((store) => store.auth.login.userInfo);

  const [state, setState] = useState({
    memId: auth.userId,
    memName: auth.userName,
    parentIdx: props.parentIdx,
    isLoading: true,
    isDelete: false,
    loadData: [],
    rowTotal: 0,
    authReply: false,
    content: '',
    // Dialog
    success: false,
    message: null,
    visibleDialog: false,
    // Editor
    position: 'bottom',
    type: 'small',
  });

  const [content, setContent] = useState({
    content: ''
  });

  // load data
  const _getData = async () => {
    const url = "/api/bbs/get_bbs_reply_list.php";
    const params = {
      memId: state.memId,
      parentIdx: state.parentIdx,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      authReply: response.authReply,
    });
  };

  // 입력된 내용 저장
  const _setData = async () => {
    const url = '/api/bbs/set_bbs_reply_update.php';
    const params = {
      mode: 'insert',
      memId: state.memId,
      memName: state.memName,
      parentIdx: state.parentIdx,
      content: state.content,
    };
    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ 
        ...state, 
        isLoading: true,
        content: '',
        success: false 
      });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();

    setTimeout(() => {
      setState({ 
        ...state, 
        success: false 
      });
    }, 1000);
  };

  // 댓글삭제
  const _setDelete = async (idx) => {
    const url = "/api/bbs/set_bbs_reply_delete.php";
    const params = {
      parentIdx: state.parentIdx,      
      idx: idx,
    };
    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ 
        ...state, 
        isLoading: true,
        success: false 
      });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  // Editor input value change
  useEffect(() => {
    setState({
      ...state,
      content: content.content
    });
  }, [content]);

  useEffect(() => {
    if (state.isLoading) {
      _getData();
    }
  }, [state.isLoading]);

  useEffect(() => {
    _getData();
  }, []);

  const { isLoading, loadData } = state;

  return (
    <Container>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          <Title>댓글({state.rowTotal})</Title>

          {state.authReply &&
            <form onSubmit={_handleSubmit}>
              <EditorWrap>
                <Editor
                  style={{ width: "100%" }}
                  state={state}
                  content={state.content}
                  setContent={setContent}
                />
                <ButtonWrap>
                  <SmallButton
                    type={'submit'}
                  >
                    등록
                      </SmallButton>
                </ButtonWrap>
              </EditorWrap>
            </form>
          }
          
          <Table>
            {loadData.map((row, index) => (
              <Tr key={index}>
                <TdTop>
                  <TdLeft>
                    <TdName>
                      {row.memName}
                    </TdName>
                    <TdId>
                      [{row.memId}]
                    </TdId>
                  </TdLeft>
                  <TdRight>
                    {row.authDelete && 
                      <DeleteIcon
                        style={{ cursor: 'pointer' }}
                        onClick={() => _setDelete(row.idx)}
                      />
                    }
                  </TdRight>
                </TdTop>
                <TdContent>
                  {renderHTML(row.content)}
                </TdContent>
                <TdDate>
                  {row.wdate}
                </TdDate>
              </Tr>
            ))}
          </Table>
        </Wrap>
      )}

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>
            {state.message}
          </p>
        </Dialog>
      )}
    </Container>
  );
};

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

export const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  padding: 5px;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 800px;
  }
`;

export const EditorWrap = styled.div`
  width: 100%;
`;

export const ButtonWrap = styled.div`
  float: right;
  background: #dcdcdc;
  margin-top: 10px;
`;

export const SmallButton = styled.button`
  border: 1px solid #dcdcdc;
  padding-top: 3px;
  padding-bottom: 3px;
  cursor: pointer;
  background: #fff;
  color: red;
  width: 50px;
`;

export const Title = styled.div`
  width: 100%;
  font-size: 0.9em;
  font-weight: 600;
  color: #666666;
  margin-top: 5px;
  margin-bottom: 5px;
`;

export const Table = styled.div`
  width: 100%;
  padding: 0.2em;
  margin-bottom: 20px;
`;

export const Tr = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  margin-top: 5px;
  padding: 2px;
  border-bottom: 1px solid #f5f5f5;

  @media (max-width: 768px) {
    flex-direction: column;
  }

  @media (min-width: 769px) {
    flex-direction: row;
  }
`;

export const TdTop = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
`;

export const TdLeft = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
`;

export const TdName = styled.div`
  font-weight: bold;
  font-size: 0.9em;
  color: #666666;
`;

export const TdId = styled.div`
  font-size: 0.8em;
  color: #666666;
  margin-left: 2px;
`;

export const TdRight = styled.div`
  font-size: 0.8em;
  color: #666666;
  margin-left: 2px;
`;

export const TdContent = styled.div`
  font-size: 0.9em;
  color: #666666;
  margin-top: 0.5em;
  margin-bottom: 0.5em;

  img {
    width: 100%;
  }

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 70%;
  }
`;

export const TdDate = styled.div`
  width: 100%;
  font-size: 0.8em;
  color: #666666;
  text-align: right;
`;

export default BbsReply;
