import React, { useEffect } from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import { insuCheck } from '../../redux/reducers/insu';
// icon & images
import backArrow from '../../images/icn-back-dark.svg';

export default function ApplyGuide({ history }) {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(insuCheck(true));
    return () => {};
  }, [dispatch]);

  return (
    <>
      <StyledBackGroundWrap>
        <div className="back">
          <StyledBackArrowIcon
            src={backArrow}
            onClick={() => history.goBack()}
          />
        </div>

        <div className="title">
          본 서비스는
          <br /> 가입이 아닙니다.
        </div>
        <div className="divider"></div>
        <div className="content">
          본 서비스는 가입이 아니니 <br /> 안심하시고 신청하세요.
          <br /> <br /> 입력하신 정보로 AI가
          <br />
          고객님께 꼭 맞는 합리적인
          <br /> 자동차 보험료를 설계해 드리는
          <br /> 서비스를 제공드립니다.
        </div>

        <StyledSubmitButton onClick={() => history.push('/insu/apply2')}>
          다음
        </StyledSubmitButton>
      </StyledBackGroundWrap>
    </>
  );
}

const StyledBackGroundWrap = styled.div`
  height: 100vh;
  background-color: #87bcbf;
  color: #fff;
  .title {
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    font-weight: bold;
    margin-top: 90px;
  }
  .divider {
    width: 33.4px;
    height: 0;
    margin: 0 auto;
    margin-top: 10.5px;
    border-top: solid 1px #ffffff;
  }
  .content {
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    margin-top: 9.5px;
  }

  .button {
  }
`;

const StyledBackArrowIcon = styled.img`
  margin-left: 20px;
  margin-top: 50px;
  align-self: center;
`;

const StyledSubmitButton = styled.div`
  width: 178px;
  height: 50px;
  object-fit: contain;
  border-radius: 24px;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 273px;
  border: none;
  font-size: 14px;
  line-height: 35px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.4);
  background-color: #fff;
  color: #1b1c20;
`;
