import React from 'react';
import styled from 'styled-components';
// 개발자 라이브러리
import MypageRoute from '../../routes/MypageRoute';
import TreeMenu from '../../components/common/TreeMenu';
import Profile from '../../components/mypage/myinfo/MyProfile';

// css 스타일가이드
import theme from '../../styles/theme';

export default function WebLayout() {
  return (
    <Container>
      <Wrap>
        <LeftBody>
          <Profile pc={true} />
          <TreeMenu />
        </LeftBody>

        <RightBody>
          <MypageRoute />
        </RightBody>
      </Wrap>
    </Container>
  );
}

const Container = styled.div`
  display: flex;
  justify-content: center;
  max-width: 1400px;
  height: 100%;
  margin: 0 auto;
`;

const Wrap = styled.div`
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  width: 100%;
`;

const LeftBody = styled.div`
  flex-shrink: 0;
  width: 245px;
  margin-top: 100px;
  border-right: 1px solid #dcdcdc;
  background-color: ${theme.colors.boxGray};
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
`;

const RightBody = styled.div`
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin-top: 100px;
  padding: 30px;
  overflow-x: auto;
`;
