import React from 'react';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';

const ConfirmDialog = (props) => {
  const state = props.state;  
  const setState = props.setState;  

  // Dialog toggle
  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  return (
    <>
      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>
            {state.message}
          </p>
        </Dialog>
      )}

      {state.confirmDialog && (
        <Dialog title={'확인'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>
            {state.confirmMessage}
          </p>
          <DialogActionsBar>
            <button
              className="k-button"
              onClick={_toggleDialog}
            >
              아니오
            </button>
            <button
              className="k-button"
              onClick={() => {
                setState({
                  ...state,
                  confirmDialog: false,
                  isLoading: false,
                  isRestore: false,
                  isDelete: true,
                });
              }}
            >
              예
            </button>
          </DialogActionsBar>
        </Dialog>
      )}
    </>
  );
}

export default ConfirmDialog;
