/* **********************************************************************
 * 관리자페이지 > 매출관리 > 매출목록
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import SalesList from '../../components/admin/sales/SalesList';
import SalesInfo from '../../components/admin/sales/SalesInfo';

const Sales = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/admin/sales"
        render={(props) => <SalesList {...props} />}
      />
      <AuthRoute
        path="/admin/sales/info"
        render={(props) => <SalesInfo {...props} />}
      />
    </Switch>
  );
};

export default Sales;