export const submitHpRequest = (params) => async (dispatch) => {
  dispatch({
    type: types.LOADING,
    loading: true,
  });

  const url = '/api/hp/set_request.php';
  console.log(params);

  const response = await _axios(url, params);
  console.log(response);
  dispatch({
    type: types.SUBMIT_HPREQUEST,
    response: response,
  });
};

export const assort = (useTelecom, changeTelecom) => async (dispatch) => {
  //console.log(`useTelecom: ${useTelecom}, changeTelecom: ${changeTelecom}`);
  let sort;

  if (useTelecom === '') {
    sort = 'N';
  } else {
    if (changeTelecom === useTelecom) {
      sort = 'C';
    } else if (changeTelecom !== useTelecom) {
      sort = 'M';
    } else {
      sort = 'N';
    }
  }
  // console.log(`sort: ${sort}`);

  return dispatch({
    type: types.SET_ASSORT,
    assort: sort,
  });
};

export function initHpRequest() {
  return {
    type: types.INIT,
  };
}
