import React from 'react';
import styled from 'styled-components';

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 20px;
  border-bottom: 1px solid #dcdcdc;
  padding: 2px;
  font-size: 0.9em;
  color: #666666;
`;

// 회원 진행 로그
const LogList = (props) => {
  const { logData } = props;

  const logs = logData.map((log, index) => {
    return (
      <Row key={index}>
        <div style={{ width: '16%' }}>{log.adminId}</div>              
        <div style={{ width: '20%' }}>{log.adminName}</div>              
        <div style={{ width: '13%' }}>{log.logAssort}</div>              
        <div style={{ width: '13%' }}>{log.status}</div>              
        <div>{log.wdate}</div>              
      </Row>
    )
  });

  return logs;
}

export default LogList;