import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Input } from '@progress/kendo-react-inputs';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { useMedia } from 'react-media';
// 개발자 Component
import _axios from '../../../utils/axios';
import Editor from '../../common/board/Editor';
// css
import {
  Container,
  Title,
  Table,
  Tr,
  Td,
  Label,
  ButtonWrap,
} from '../../../css/list';

const BbsInfo = (props) => {
  const auth = useSelector((store) => store.auth.login.userInfo);
  const isMobile = useMedia({ query: '(max-width: 767px)' });
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const [state, setState] = useState({
    isLoading: false,

    memId: auth.userId,
    memName: auth.userName,
    mode: props.location.mode,
    idx: props.location.idx,
    
    bbsCode: '',
    subject: '',
    content: '',
    bbsOptions: [],
    // Dialog
    success: false,
    message: null,
    visibleDialog: false,
    // Editor
    isEditor: false,
    position: 'top',
    type: 'full',
  });

  const [content, setContent] = useState({
    content: ''
  });

  // 게시글 정보
  const _getData = async () => {
    const url = '/api/bbs/get_bbs_info.php';
    const params = {
      idx: state.idx,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isEditor: true,
      bbsCode: { code: response.data.bbsCode, name: response.data.bbsName },
      subject: response.data.subject,
      content: response.data.content,
      bbsOptions: response.bbsOptions,
    });
  };

  // 입력된 내용 저장
  const _setData = async () => {
    const url = '/api/bbs/set_bbs_update.php';
    const { message } = await _axios(url, state);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
      props.history.goBack();
    }, 1000);
  };

  // 입력값 변경
  const _handleInputChange = (e) => {
    setState({ 
      ...state, 
      [e.target.name]: e.target.value 
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    setState({
      ...state,
      content: content.content
    });
  }, [content]);

  // 
  useEffect(() => {
    //props.stackRequest('S', '게시글관리');
    if (!state.isLoading) {
      _getData();
    }
  }, []);

  return (
    <Container width={conWidth}>
      {state.isLoading ? (
        <div>Loading...</div>
      ) : (
        <form onSubmit={_handleSubmit}>
          <Title>게시글관리 〉 게시글등록</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>게시판구분</Label>
                <ComboBox
                  style={{
                    width: '74%',
                    borderBottom: '1px solid #dcdcdc',
                  }}
                  name="bbsCode"
                  value={state.bbsCode}
                  data={state.bbsOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>글 제목</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="subject"
                  value={state.subject}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>

            {state.isEditor &&
              <Tr width={'100%'}>
                <Td width={'100%'} direction={'column'}>
                  <Editor
                    state={state}
                    content={state.content}
                    setContent={setContent}
                  />
                </Td>
              </Tr>
            }
          </Table>

          <ButtonWrap>
            <Button
              primary={true}
              type={'submit'}
              onClick={() => setState({ ...state, isSave: true })}
            >
              저장하기
            </Button>

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </form>
      )}

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}      
    </Container>
  );
};

export default BbsInfo;

