/* **********************************************************************
 * 관리자페이지 > 환경설정 > SMS메세지관리
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import LoginRoute from '../../components/common/AuthRoute';
import SmsMessageList from '../../components/admin/smsMessage/SmsMessageList';
import SmsMessageInfo from '../../components/admin/smsMessage/SmsMessageInfo';

const SmsMessage = () => {
  return (
    <Switch>
      <LoginRoute
        exact
        path="/admin/smsMessage"
        render={(props) => <SmsMessageList {...props} />}
      />
      <LoginRoute
        path="/admin/smsMessage/info"
        render={(props) => <SmsMessageInfo {...props} />}
      />
    </Switch>
  );
};

export default SmsMessage;