import React, { useEffect, useState, useCallback } from 'react';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import { Input, TextArea } from '@progress/kendo-react-inputs';
import { useSelector } from 'react-redux';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { isMobile } from 'react-device-detect';
// 개발자 Component
import _axios from '../../../utils/axios';
import LogList from './LogList';
// css
import '../../../css/kendo.css';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const MemberInfo = (props) => {
  const authStatus = useSelector((store) => store.auth.authStatus);
  const [state, setState] = useState({
    isLoading: true,
    isDelete: false,
    idx: props.location.idx,
    mode: props.location.mode,
    adminId: authStatus.userId,
    sponsId: '',
    recommandId: '',
    memId: '',
    memName: '',
    memPw: '',
    hpNo: '',
    email: '',
    memAssort: '',
    assortOptions: [],
    contractStatus: '',
    contractOptions: [],
    cmsStatus: '',
    cmsOptions: [],
    joinPayStatus: '',
    joinPayOptions: [],
    clearStatus: '',
    clearOptions: [],
    memStatus: '',
    memOptions: [],
    cmsId: '',
    gajaId: '',
    registNo: '',
    accountName: '',
    accountNo: '',
    accountBank: '',
    comment: '',
    approvalDate: '',
    wdate: '',
    logData: [],

    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/admin/member/get_member_info.php';
    const params = {
      idx: state.idx,
    };

    const { data, logData } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isDelete: false,
      isMemAssort: false,
      isContractStatus: false,
      isCmsStatus: false,
      isClearStatus: false,
      isMemStatus: false,
      isSave: false,
      sponsId: data.sponsId,
      recommandId: data.recommandId,
      memId: data.memId,
      memName: data.memName,
      memPw: data.memPw,
      hpNo: data.hpNo,
      email: data.email,
      memAssort: { code: data.memAssort, name: data.memAssortName },
      assortOptions: data.assortOptions,
      contractStatus: {
        code: data.contractStatus,
        name: data.contractStatusName,
      },
      contractOptions: data.contractOptions,
      cmsStatus: { code: data.cmsStatus, name: data.cmsStatusName },
      cmsOptions: data.cmsOptions,
      joinPayStatus: { code: data.joinPayStatus, name: data.joinPayStatusName },
      joinPayOptions: data.joinPayOptions,
      clearStatus: { code: data.clearStatus, name: data.clearStatusName },
      clearOptions: data.clearOptions,
      memStatus: { code: data.memStatus, name: data.memStatusName },
      memOptions: data.memOptions,
      cmsId: data.cmsId,
      gajaId: data.gajaId,
      registNo: data.registNo,
      accountName: data.accountName,
      accountNo: data.accountNo,
      accountBank: data.accountBank,
      comment: data.comment,
      approvalDate: data.approvalDate,
      wdate: data.wdate,
      logData: logData,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/admin/member/set_member_update.php';
    const { message } = await _axios(url, state);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  // 삭제
  const _setDelete = useCallback(async () => {
    const url = '/api/admin/member/set_member_delete.php';
    const params = {
      adminId: state.adminId,
      idx: state.idx,
    };
    console.log(params);

    const { result, message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
      isDelete: false,
    });

    setTimeout(() => {
      setState({ ...state, success: false, isDelete: false });
      if (result === '0') {
        props.history.goBack();
      }
    }, 1000);
  }, [state]);

  // 회원탈퇴완료처리
  const _setStatusClosed = async (memId) => {
    const url = '/api/admin/member/set_status_closed.php';
    const params = {
      memId: memId,
      closedId: state.memId,
    };
    console.log(params);
    const response = await _axios(url, params);

    // 하위 뎁스 재구성
    if (response.result === '1') {
      _setStatusClosed(response.memId);
    } else {
      setState({
        ...state,
        isMemAssort: false,
        isContractStatus: false,
        isCmsStatus: false,
        isJoinPayStatus: false,
        isClearStatus: false,
        isMemStatus: false,
        isDelete: false,
        success: true,
        message: response.message,
        visibleDialog: true,
      });

      setTimeout(() => {
        setState({ ...state, success: false });
        props.history.goBack();
      }, 1000);
    }
  };

  // 진행상태 저장
  const _setStatusChange = async (mode, status) => {
    const url = '/api/admin/member/set_status_update.php';
    const params = {
      adminId: state.adminId,
      idx: state.idx,
      mode: mode,
      status: status,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    if (state.isMemAssort) {
      _setStatusChange('M', state.memAssort.code);
    } else if (state.isContractStatus) {
      _setStatusChange('E', state.contractStatus.code);
    } else if (state.isCmsStatus) {
      _setStatusChange('C', state.cmsStatus.code);
    } else if (state.isJoinPayStatus) {
      _setStatusChange('P', state.joinPayStatus.code);
    } else if (state.isClearStatus) {
      _setStatusChange('D', state.clearStatus.code);
    } else if (state.isMemStatus) {
      if (state.memStatus.code === '8') {
        // 회원탈퇴완료
        _setStatusClosed(state.memId);
      } else {
        _setStatusChange('A', state.memStatus.code);
      }
    } else if (state.isSave) {
      _setData();
    }

    setTimeout(() => {
      setState({
        ...state,
        success: false,
        isMemAssort: false,
        isContractStatus: false,
        isCmsStatus: false,
        isJoinPayStatus: false,
        isClearStatus: false,
        isMemStatus: false,
        isDelete: false,
      });
    }, 1000);

    // setTimeout(() => {
    //   setState({ ...state, success: false });
    // }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _handleTextarea = (e) => {
    setState({
      ...state,
      [e.target.element.current.name]: e.value,
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  useEffect(() => {
    if (state.isDelete) {
      _setDelete();
    }
  }, [state]);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '600px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>회원관리 〉 회원목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>스폰서</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="sponsId"
                  value={state.sponsId}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>추천인 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="recommandId"
                  value={state.recommandId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memId"
                  value={state.memId}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memName"
                  value={state.memName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>비밀번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memPw"
                  value={state.memPw}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>휴대폰번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="hpNo"
                  value={state.hpNo}
                  style={{ width: '74%' }}
                  maxLength={20}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>CMS ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="cmsId"
                  value={state.cmsId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가자 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="gajaId"
                  value={state.gajaId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>승인일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="approvalDate"
                  value={state.approvalDate}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가입일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="wdate"
                  value={state.wdate}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
            </Tr>
          </Table>

          <Title>정산계좌정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>주민번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="registNo"
                  value={state.registNo}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>예금주명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="accountName"
                  value={state.accountName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>계좌번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="accountNo"
                  value={state.accountNo}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>은행명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="accountBank"
                  value={state.accountBank}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>
          </Table>

          <Title>관리자 메모</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={'100%'}>
                <TextArea
                  name="comment"
                  value={state.comment}
                  style={{ width: '100%' }}
                  rows={3}
                  autoSize={true}
                  onChange={_handleTextarea}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
            <Button
              style={{ marginLeft: '10px' }}
              type={'button'}
              onClick={() => {
                setState({
                  ...state,
                  confirmDialog: true,
                });
              }}
            >
              삭제하기
            </Button>
          </ButtonWrap>

          <Title>상태정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가입구분</Label>
                <ComboBox
                  style={{ width: '50%', borderBottom: '1px solid #dcdcdc' }}
                  name="memAssort"
                  value={state.memAssort}
                  data={state.assortOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Button
                  type={'submit'}
                  onClick={() => setState({ ...state, isMemAssort: true })}
                >
                  상태변경
                </Button>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>계약상태</Label>
                <ComboBox
                  style={{ width: '50%', borderBottom: '1px solid #dcdcdc' }}
                  name="contractStatus"
                  value={state.contractStatus}
                  data={state.contractOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Button
                  type={'submit'}
                  onClick={() => setState({ ...state, isContractStatus: true })}
                >
                  상태변경
                </Button>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>CMS상태</Label>
                <ComboBox
                  style={{ width: '50%', borderBottom: '1px solid #dcdcdc' }}
                  name="cmsStatus"
                  value={state.cmsStatus}
                  data={state.cmsOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Button
                  type={'submit'}
                  onClick={() => setState({ ...state, isCmsStatus: true })}
                >
                  상태변경
                </Button>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가입비</Label>
                <ComboBox
                  style={{ width: '50%', borderBottom: '1px solid #dcdcdc' }}
                  name="joinPayStatus"
                  value={state.joinPayStatus}
                  data={state.joinPayOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Button
                  type={'submit'}
                  onClick={() => setState({ ...state, isJoinPayStatus: true })}
                >
                  상태변경
                </Button>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>구독료납부</Label>
                <ComboBox
                  style={{ width: '50%', borderBottom: '1px solid #dcdcdc' }}
                  name="clearStatus"
                  value={state.clearStatus}
                  data={state.clearOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Button
                  type={'submit'}
                  onClick={() => setState({ ...state, isClearStatus: true })}
                >
                  상태변경
                </Button>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원상태</Label>
                <ComboBox
                  style={{ width: '50%', borderBottom: '1px solid #dcdcdc' }}
                  name="memStatus"
                  value={state.memStatus}
                  data={state.memOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Button
                  type={'submit'}
                  onClick={() => setState({ ...state, isMemStatus: true })}
                >
                  상태변경
                </Button>
              </Td>
            </Tr>
          </Table>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}

      {state.confirmDialog && (
        <Dialog title={'확인'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>
            삭제하시겠습니까?
          </p>
          <DialogActionsBar>
            <button className="k-button" onClick={_toggleDialog}>
              아니오
            </button>
            <button
              className="k-button"
              onClick={() => {
                setState({
                  ...state,
                  confirmDialog: false,
                  isDelete: true,
                });
              }}
            >
              예
            </button>
          </DialogActionsBar>
        </Dialog>
      )}

      {!isMobile && (
        <Wrap style={{ marginTop: '30px' }}>
          <Title>회원상태 로그</Title>
          <LogList logData={state.logData} />
        </Wrap>
      )}
    </Container>
  );
};

export default MemberInfo;
