/*
  설명: 프로필 이미지 변경
  - 2021.05.18. 안예린, import 수정
  - 2021.06.01. 양민석, myprofile 웹 모바일 통합
*/
import React, { useEffect, useState, useCallback } from 'react';
import ImageUploading from 'react-images-uploading';
import styled from 'styled-components';
import Axios from 'axios';
import _axios from '../../../utils/axios';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

// css
import theme from '../../../styles/theme';

// icon & images
import cameraImg from '../../../images/myinfo/camera.svg';
import writeImg from '../../../images/myinfo/writeImg.svg';
import PersonIcon from '@material-ui/icons/Person';

const MyProfile = ({ pc }) => {
  const authStatus = useSelector((store) => store.auth.authStatus);
  // console.log(authStatus);
  const history = useHistory();

  // state
  const [photo, setPhoto] = useState([]);
  const [onImgUpload, setOnImgUpload] = useState(false);
  const [state, setState] = useState({
    isLoading: true,
    userId: authStatus.userId,
    photo: '',
    userName: authStatus.userInfo.userName,
    sendImg: false,
  });

  // 사진 정보 들고오는 함수
  const _getPotoData = useCallback(async () => {
    const url = '/api/member/get_my_info.php';
    const params = {
      userId: state.userId,
    };

    const { data } = await _axios(url, params);
    setState({
      ...state,
      photo: data.photo,
    });
  }, [state.userId]);

  // 서버에 사진 업로드 함수
  const _photoUpload = async (fileInfo) => {
    let formData = new FormData();
    formData.append('files', fileInfo.file);

    await Axios.post(
      'http://dev.spiderplatform.co.kr/utils/imageSave.php',
      formData,
      {
        headers: { 'Content-Type': 'multipart/form-data;charset=utf-8;' },
      },
    );
  };

  // 서버에 사진 경로 저장하는 함수
  const _photoData = async () => {
    const url = '/api/member/set_photo_update.php';
    const params = {
      userId: state.userId,
      photo:
        'http://spiderplatform.co.kr/upload/thumbnail/' + photo[0].file.name,
    };
    const { result, message } = await _axios(url, params);
    setState({
      ...state,
      sendImg: false,
      isLoading: true,
    });
  };

  // 사진 삭제하는 함수
  const _photoRemove = async () => {
    const url =
      'http://dev.spiderplatform.co.kr/api/member/set_photo_update.php';
    const params = {
      userId: state.userId,
      photo: null,
    };

    await Axios({
      method: 'post',
      url: url,
      data: params,
    }).then((response) => {
      setState({
        ...state,
        sendImg: false,
        isLoading: true,
      });
    });
    _getPotoData();
  };

  const onChange = (imageList, addUpdateIndex) => {
    _photoUpload(imageList[0]);
    setPhoto(imageList);
    setState({
      ...state,
      sendImg: true,
    });
    setOnImgUpload(false);
  };
  useEffect(() => {
    _getPotoData();
    if (state.sendImg) {
      _photoData();
    }
  }, [state.sendImg, state.photo]);

  if (pc) {
    return (
      <Container>
        <Wrap>
          <StyledProfile>
            <div className="whiteCycle">
              <ImageUploading multiple value={photo} onChange={onChange}>
                {({ onImageUpdate }) => (
                  <div className="upload__image-wrapper">
                    {onImgUpload && (
                      <div className="image-item__btn-wrapper">
                        <button onClick={() => onImageUpdate(0)}>
                          사진 변경
                        </button>
                        <button onClick={() => _photoRemove()}>
                          기본 이미지로 변경
                        </button>
                      </div>
                    )}
                    <div className="image-item">
                      {!state.photo ? (
                        <PersonIcon style={{ fontSize: '2.5em' }} />
                      ) : (
                        <img
                          style={{
                            width: '100%',
                            height: '100%',
                          }}
                          src={state.photo}
                          alt=""
                        />
                      )}
                    </div>
                  </div>
                )}
              </ImageUploading>
            </div>

            {authStatus.userAuth === 'M' ||
              (authStatus.userAuth === 'S' && (
                <div style={{ position: 'relative' }}>
                  <img
                    className="showimgbtn"
                    onClick={() => {
                      setOnImgUpload(!onImgUpload);
                    }}
                    src={writeImg}
                  ></img>
                </div>
              ))}

            <div
              style={{
                color: `${theme.colors.navyBox}`,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
              }}
            >
              <div className="name">{authStatus.userInfo.userName}님</div>
              <div className="id">회원번호 {authStatus.userId}</div>
              <div
                style={{
                  width: '116px',
                  height: '28px',
                  display: 'flex',
                  justifyContent: 'center',
                }}
                className="edit"
                onClick={() => {
                  authStatus.userAuth === 'M' || authStatus.userAuth === 'S'
                    ? history.push('/mypage/myinfo')
                    : history.push('/admin/adminInfo');
                }}
              >
                정보수정
              </div>
            </div>
          </StyledProfile>
        </Wrap>
      </Container>
    );
  } else {
    return (
      <div style={{ width: '100%' }}>
        <StyledMyPhoto>
          <img
            className="showimgbutton"
            style={{
              position: 'relative',
              zIndex: '20',
              left: '5.5em',
              top: '1.4em',
              width: '2.5em',
              color: 'black',
            }}
            onClick={() => {
              setOnImgUpload(!onImgUpload);
            }}
            src={cameraImg}
          ></img>

          <StyledImgUploading>
            <ImageUploading multiple value={photo} onChange={onChange}>
              {({ onImageUpdate }) => (
                <div className="upload__image-wrapper">
                  {onImgUpload && (
                    <div className="image-item__btn-wrapper">
                      <button onClick={() => onImageUpdate(0)}>
                        사진 변경
                      </button>
                      <button onClick={() => _photoRemove()}>
                        기본 이미지로 변경
                      </button>
                    </div>
                  )}
                  <div className="image-item">
                    {!state.photo ? (
                      <PersonIcon style={{ fontSize: '2.5em' }} />
                    ) : (
                      <img
                        style={{
                          width: '100%',
                          height: '100%',
                        }}
                        src={state.photo}
                        alt=""
                      />
                    )}
                  </div>
                </div>
              )}
            </ImageUploading>
          </StyledImgUploading>

          <section style={{ marginLeft: '6em' }}>
            <div style={{ fontWeight: 'bold' }} className="name">
              {state.userName}
            </div>
            <div className="body">{state.userId}</div>
          </section>
        </StyledMyPhoto>
      </div>
    );
  }
};

// pc
const Container = styled.div`
  height: 292px;
`;

const Wrap = styled.div``;

const StyledProfile = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  .showimgbtn {
    position: absolute;
    z-index: 20;
    left: 1em;
    top: -2.2em;
    width: 25px;
    height: 25px;
    cursor: pointer;
  }
  .image-item {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    overflow: hidden;
    background-color: ${theme.colors.lightGrey};
    color: ${theme.colors.black1};

    display: flex;
    justify-content: center;
    align-items: center;
  }
  .image-item__btn-wrapper {
    position: absolute;
    top: 15em;
    display: flex;
    flex-direction: column;
    border: solid 2px #707070;
  }
  button {
    outline: none;
    color: #404040;
    text-align: left;
    font-size: 1em;
    width: 10em;
    height: 2.3em;
    border: none;
  }
  .name {
    font-family: NotoM;
    font-style: normal;
    font-size: ${theme.fontSizes.H4};
    line-height: 36px;
    margin-bottom: 12px;
  }
  .id {
    font-family: NotoM;
    font-style: normal;

    font-size: ${theme.fontSizes.Title};
    line-height: 20px;
    /* or 133% */
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    text-align: center;
  }
  .whiteCycle {
    margin-top: 50px;
    position: 'relative';
    width: 80px;
    height: 80px;
    margin-bottom: 14px;
    overflow: 50%;
    overflow: hidden;
    background-color: ${theme.colors.lightGrey};
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 80px;
      height: 80px;
    }
  }

  .edit {
    margin-bottom: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: NotoM;
    font-size: ${theme.fontSizes.Button1};
    width: 100%;
    height: 28px;
    line-height: 26px;
    border: 1.5px solid #0e2b64;
    box-sizing: border-box;
    border-radius: 3px;

    cursor: pointer;
  }
`;

// 프로필사진 수정 스타일컴포넌트 ( 모바일 )
const StyledImgUploading = styled.div`
  position: absolute;
  z-index: '99';
  top: 7em;
  left: 5.5em;
  .image-item {
    width: 5em;
    height: 5em;
    border-radius: 50%;
    overflow: hidden;
    background-color: ${theme.colors.lightGrey};
    color: ${theme.colors.black1};
    position: absolute;
    bottom: 0.5em;
    left: -3.4em;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .image-item__btn-wrapper {
    position: absolute;
    display: flex;
    flex-direction: column;
    border: solid 2px #707070;
  }
  button {
    outline: none;
    color: #404040;
    text-align: left;
    font-size: 1em;
    width: 10em;
    height: 2.3em;
    border: none;
  }
  .ChangeImg:hover {
    background-color: 'red';
  }
`;

const StyledMyPhoto = styled.section`
  position: relative;
  height: 8em;
  background-color: ${theme.colors.lightGrey};
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  display: flex;
  align-items: center;
  color: ${theme.colors.black1};
  font-size: 14px;
  margin-bottom: 3em;
  .name {
    ${theme.H5}
  }
  .body {
    ${theme.Body1}
  }
`;

export default MyProfile;
