/*
  설명: 요금제 선택 페이지
  - 2021.05.20. 안예린, import 수정
*/
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
import * as actions from '../../../redux/actions/hp';
// css
import {
  BigLabel,
  Container,
  MiddleLabel,
  ParentWrap,
  SmallLabel,
  UnderLineInput,
  UnderLineInputWrap,
} from '../../../css/common-component';

export default function FeeSelect(props) {
  const dispatch = useDispatch();
  const chargeCode = useSelector((store) => store.hp.chargeCode);
  const { changeTelecom, modelCode } = useSelector((store) => store.hp);
  const [data, setData] = useState();

  const _getData = useCallback(async () => {
    const url = '/api/hp/get_charge_list.php';

    const params = { changeTelecom: changeTelecom, modelCode: modelCode };

    const { data } = await _axios(url, params);

    return data;
  }, [changeTelecom, modelCode]);

  useEffect(() => {
    let mounted = true;

    _getData().then((res) => {
      if (res && mounted) {
        setData(res);
      }
    });

    return () => (mounted = false);
  }, [_getData]);

  const _selectFee = (data) => {
    dispatch(actions.selectFee(data));
    props.history.push('/partnership/hp/discountType');
    // props.history.push("/partnership/hpRequest/insertInfo");
  };

  const [isModal, setIsModal] = useState(false);

  const _handleInputChange = (e) => {
    dispatch(
      actions.selectFee({
        chargeCode: e.target.value,
        chargeName: e.target.value,
      }),
    );
  };

  const _toggleDialog = () => {
    isModal ? setIsModal(false) : setIsModal(true);
  };
  const _handleSubmit = (e) => {
    if (chargeCode) {
      props.history.push('/partnership/hp/selectCard');
      // props.history.push("/partnership/hpRequest/insertInfo");
    } else {
      _toggleDialog();
    }
  };

  useEffect(() => {}, [chargeCode]);

  useEffect(() => {
    //dispatch(stackRequest('S', ''));
  }, [dispatch]);

  return (
    <Container className="page">
      <ParentWrap
        width="100%"
        padding="0"
        style={{ backgroundColor: '#fafafa' }}
      >
        <BigLabel textAlign="center" margin="0 auto 27px auto" width="187px">
          요금제
        </BigLabel>

        <SmallLabel
          width="267px"
          height="16px"
          bold="normal"
          color="#404040"
          textAlign="center"
          margin="27px auto 35px auto"
        >
          이용하실 요금제를 선택하세요.
        </SmallLabel>
        {data?.map((item) => (
          <div
            key={item.no}
            style={{
              padding: '27px 20px 26px 21px',
              backgroundColor: '#f2f2f2',
              marginBottom: '10px',
            }}
            onClick={() => {
              _selectFee({
                chargeCode: item?.chargeCode,
                chargeName: item?.chargeName,
              });
            }}
          >
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                marginBottom: '10px',
              }}
            >
              <MiddleLabel style={{ color: '#000000' }}>
                {' '}
                {item?.chargeName}
              </MiddleLabel>
              <span>월 {item?.chargePrice}원</span>
            </div>

            {item?.chargeExplain && (
              <div style={{ color: '#707070' }}>
                {item?.chargeExplain.split('\n').map((line, index) => {
                  return (
                    <span key={index}>
                      {line}
                      <br />
                    </span>
                  );
                })}
              </div>
            )}
          </div>
        ))}

        {isModal && (
          <Dialog
            title={'월 납부요금 선택'}
            id="hpModelOption"
            width={'256px'}
            height={'318px'}
          >
            <UnderLineInputWrap width="122px" height="auto" margin="0 auto">
              <UnderLineInput
                id="planInput"
                type="text"
                name="fee"
                value={chargeCode}
                validityStyles={false}
                maxLength={20}
                autoComplete="off"
                onChange={_handleInputChange}
                required={true}
                style={{
                  transform: 'translate(0px, -4px)',
                  textAlign: 'right',
                  fontSize: '25px',
                  width: '35%',
                }}
              ></UnderLineInput>
              <MiddleLabel
                width="82px"
                height="23px"
                style={{ transform: 'translateY(5px)', fontSize: '25px' }}
              >
                만원대
              </MiddleLabel>
            </UnderLineInputWrap>

            <DialogActionsBar id="hpModelOptionButton">
              <button className="k-button" onClick={_handleSubmit}>
                확인
              </button>
              <button className="k-button" onClick={_toggleDialog}>
                취소
              </button>
            </DialogActionsBar>
          </Dialog>
        )}
      </ParentWrap>
    </Container>
  );
}
