import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
import _axios from '../../../utils/axios';
// css
import {
  Container,
  MenuImg,
  MiddleLabel,
  ParentWrap,
  SquareButton,
} from '../../../css/common-component';

const CardItem = ({ item, history }) => {
  const dispatch = useDispatch();
  const [isModal, setIsModal] = useState(false);
  const {
    changeTelecom,
    assort,
    modelCode,
    capacityCode,
    chargeCode,
    discountType,

    marginPrice,
  } = useSelector((store) => store.hp);
  const _getBuyPrice = async (cardCode) => {
    const url = '/api/hp/get_factory_info.php';
    const params = {
      changeTelecom,
      assort,
      modelCode,
      capacityCode,
      chargeCode,
      discountType,
      cardCode: cardCode,
      marginPrice,
    };
    const data = await _axios(url, params);

    return data.buyPrice;
  };
  const _selectCardCode = async (cardCode) => {
    dispatch(actions.selectCardDiscount(cardCode));
    const buyPrce = await _getBuyPrice(cardCode);
    if (parseInt(buyPrce) === 0) {
      history.push('/partnership/hp/guideTable');
    } else {
      history.push('/partnership/hp/installment');
    }
  };

  return (
    <>
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          marginBottom: '75px',
        }}
      >
        <SquareButton
          width="118px"
          height="132px"
          border="1px solid #e9e9e9"
          // backgroundColor={isHover ? "#e2e2e2" : "#ffffff"}
          backgroundColor="#ffffff"
          boxShadow="none"
          borderRadius="0px"
        >
          <MenuImg src={item.thumbnail} alt={item.cardName} width="60%" />
        </SquareButton>

        <StyledRightWrap>
          <div className="name">{item.cardName}</div>
          <div className="smallPrice">{item.usePrice}원 이상 사용시</div>
          <div className="bigPrice">{item.discountPrice}원 할인/월</div>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <StyledWhiteButton onClick={() => setIsModal(!isModal)}>
              자세히보기
            </StyledWhiteButton>
            <StyledButton onClick={() => _selectCardCode(item.cardCode)}>
              적용하기
            </StyledButton>
          </div>
        </StyledRightWrap>
      </div>
      {isModal && (
        <StyledModal>
          <div className="closeButton" onClick={() => setIsModal(!isModal)}>
            X
          </div>
          <div className="name">{item.cardName}</div>
          {item.cardExplain.split('\n').map((line, index) => {
            return (
              <span key={index}>
                {line}
                <br />
              </span>
            );
          })}
        </StyledModal>
      )}
    </>
  );
};

React.memo(CardItem);

export default function SelectCard({ history }) {
  const dispatch = useDispatch();
  const [data, setData] = useState();

  const {
    changeTelecom,
    assort,
    modelCode,
    capacityCode,
    chargeCode,
    discountType,

    marginPrice,
  } = useSelector((store) => store.hp);

  useEffect(() => {
    //dispatch(stackRequest('S', ''));
  }, [dispatch]);

  const _getData = useCallback(async () => {
    const url = '/api/hp/get_card_list.php';

    const params = {
      changeTelecom: changeTelecom,
    };

    const { data } = await _axios(url, params);

    return data;
  }, [changeTelecom]);

  useEffect(() => {
    let mounted = true;

    _getData().then((res) => {
      if (res && mounted) {
        setData(res);
      }
    });

    return () => (mounted = false);
  }, [_getData, dispatch]);

  const _selectCardCode = async (cardCode) => {
    // const benefitPrice = await _getBanefitInfo(cardCode);
    dispatch(actions.selectCardDiscount(cardCode));
    const buyPrce = await _getBuyPrice(cardCode);
    // console.log(benefitPrice);
    // if (parseInt(benefitPrice) === 0) {
    if (parseInt(buyPrce) === 0) {
      history.push('/partnership/hp/guideTable');
    } else {
      history.push('/partnership/hp/installment');
    }
    // } else {
    //   history.push('/partnership/hp/banefitType');
    // }
  };

  const _getBanefitInfo = async (cardCode) => {
    const url = '/api/hp/get_benefit_info.php';
    const params = {
      changeTelecom,
      assort,
      modelCode,
      capacityCode,
      chargeCode,
      discountType,
      cardCode: cardCode,
      marginPrice,
    };
    const data = await _axios(url, params);

    dispatch(actions.benefitPrice(data.benefitPrice));
    return data.benefitPrice;
  };

  const _getBuyPrice = async (cardCode) => {
    const url = '/api/hp/get_factory_info.php';
    const params = {
      changeTelecom,
      assort,
      modelCode,
      capacityCode,
      chargeCode,
      discountType,
      cardCode: cardCode,
      marginPrice,
    };
    const data = await _axios(url, params);

    return data.buyPrice;
  };

  return (
    <Container
      style={{
        paddingTop: '82px',
        display: 'block',
        paddingBottom: '82px',
        paddingLeft: '20px',
        paddingRight: '22px',
      }}
      className="page"
    >
      <ParentWrap
        height="auto"
        padding="0"
        width="auto"
        style={{
          margin: '0 auto',
          backgroundColor: 'transparent',
          border: 'none',
        }}
      >
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            marginBottom: '46px',
          }}
        >
          <MiddleLabel
            textAlign="center"
            // margin="0 auto 61px auto"
            width="auto"
            style={{ color: '000f34', fontWeight: 'bold', fontSize: '18px' }}
          >
            제휴카드 할인 선택
          </MiddleLabel>
          <StyledButton onClick={() => _selectCardCode('')}>
            미선택
          </StyledButton>
        </div>

        {data?.map((item, index) => {
          return (
            <CardItem item={item} history={history} key={index}></CardItem>
          );
        })}
        {/* <SmallLabel
          width="100%"
          height="16px"
          textAlign="center"
          margin={'61px auto 55px auto'}
          bold="normal"
        >
          제휴카드 할인을 받으시겠습니까?
        </SmallLabel>

        <div
          style={{
            display: 'flex',
            marginTop: '55px',
            marginBottom: '55px',
            justifyContent: 'space-between',
          }}
        >
          <SquareButton
            width="118px"
            height="118px"
            margin="0 0px 0px 0"
            border="solid 1px #e2e2e2"
            backgroundColor="#000f34"
            color="#ffffff"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectYesOrNo('Y')}
          >
            예
          </SquareButton>
          <SquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor="#ffffff"
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectYesOrNo('N')}
          >
            아니오
          </SquareButton>
        </div>*/}
      </ParentWrap>
    </Container>
  );
}

const StyledButton = styled.div`
  width: 118px;
  height: 38px;
  padding: 11px 0px 12px;
  text-align: center;
  border-radius: 3px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #000f34;
  color: #fff;
  font-size: 13px;
  line-height: 15px;
`;

const StyledRightWrap = styled.div`
  width: 200px;
  .name {
    color: #000;
    font-size: 14px;
    margin-bottom: 18px;
  }
  .smallPrice {
    color: #000;
    font-size: 12px;
  }
  .bigPrice {
    color: #000;
    font-size: 17px;
    margin-bottom: 18px;
    text-align: right;
  }
`;

const StyledWhiteButton = styled.div`
  width: 72px;
  height: 25px;
  line-height: 14px;
  align-self: flex-end;
  /* margin: 33px 6px 0 13px; */
  padding: 6px 8px 5px;
  border-radius: 3px;
  border: solid 1px #e9e9e9;
  background-color: #ffffff;
  color: #000;
  text-align: center;
  font-size: 12px;
`;

const StyledModal = styled.div`
  transform: translateY(-37px);
  width: 100%;
  height: auto;
  padding: 19.5px 9px 132px 20px;
  background-color: #ffffff;

  border-top-right-radius: 5%;
  border-top-left-radius: 5%;
  .closeButton {
    font-size: 24px;
    color: #707070;
    text-align: right;
    margin-bottom: 14.5px;
  }
  .name {
    font-family: NanumGothic;
    font-size: 18px;
    color: #000;
    margin-bottom: 35px;
  }

  span {
    font-family: NanumGothic;
    font-size: 12px;
    color: #000;
  }
`;
