import React, { useEffect, useState } from 'react';
import {
  Input,
  RadioGroup,
} from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
import { connect } from 'react-redux';
import { useMedia } from 'react-media';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../styles/board';
// icon
import AddIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import RemoveIcon from '@material-ui/icons/RemoveCircleOutlineOutlined';

const MenuInfo = (props) => {
  const isMobile = useMedia({ query: '(max-width: 767px)' });
  const direction = isMobile ? 'column' : 'row';
  const containerWidth = isMobile ? '100%' : '600px';

  const [state, setState] = useState({
    isLoading: true,

    idx: props.location.idx,
    mode: props.location.mode,

    menuName: "",
    linkTo: "",
    iconName: "",
    parentIdx: "",
    authAssort: null,
    authOptions: [],
    sortNo: "",
    menuStatus: false,

    menus: [],
    authOptions: [],
    useOptions: [],
    useOptions2: [],

    success: false,
    message: null,
    visibleDialog: false,
  });

  // 메뉴 정보
  const _getData = async () => {
    const url = "/api/admin/setting/get_menu_info.php";
    const params = {
      idx: state.idx,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,

      menuName: response.data.menuName,
      linkTo: response.data.linkTo,
      iconName: response.data.iconName,
      authAssort: { code: response.data.authAssort, name: response.data.authName },
      sortNo: response.data.sortNo,
      menuStatus: response.data.menuStatus,

      menus: response.menus,
      authOptions: response.authOptions,
      useOptions: response.useOptions,
      useOptions2: response.useOptions2,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = "/api/admin/setting/set_menu_update.php";
    const params = {
      mode: state.mode,
      idx: state.idx,
      menuName: state.menuName,
      linkTo: state.linkTo,
      iconName: state.iconName,
      authAssort: state.authAssort,
      sortNo: state.sortNo,
      menuStatus: state.menuStatus,
      menus: state.menus,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  // input change
  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, menuStatus: e.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  // ***************** 서브 메뉴 정보 시작 ****************************
  const _handleMenuAdd = () => {
    const menu = {
      idx: 0,
      menuName: '',
      linkTo: '',
      iconName: '',
      authAssort: '',
      sortNo: '',
      menuStatus: '',
    };

    setState({ 
      ...state, 
      menus: [...state.menus, menu] 
    });
  };

  const _handleMenuRemove = (index) => {
    state.menus.splice(index, 1);
    setState({ 
      ...state, 
      menus: state.menus 
    });
  };

  const _handleMenuInputChange = (e, index) => {
    state.menus[index][e.target.name] = e.target.value;
    setState({ 
      ...state, 
      menus: state.menus 
    });
  };
  // ***************** 서브 메뉴 정보 끝 ****************************

  useEffect(() => {
    _getData();
  }, []);

  return (
    <Container width={containerWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap>
          <Title>환경설정 〉 메뉴목록 〉 상세정보</Title>
          <Table background={ '#f7f7f7'} border={ '1px solid #dcdcdc' }>            
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td>
                <Label>메뉴명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="menuName"
                  value={state.menuName}
                  style={{ width: '74%' }}
                  maxLength={50}
                  required={true}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td>
                <Label>Path Name</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="linkTo"
                  value={state.linkTo}
                  style={{ width: '74%' }}
                  maxLength={50}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td>
                <Label>아이콘명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="iconName"
                  value={state.iconName}
                  style={{ width: '74%' }}
                  maxLength={50}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td>
                <Label>나열순서</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="sortNo"
                  value={state.sortNo}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td>
                <Label>사용자</Label>
                <ComboBox
                  style={{ width: "74%", borderBottom: "1px solid #dcdcdc" }}
                  name="authAssort"
                  value={state.authAssort}
                  data={state.authOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                />
              </Td>
              <Td>
                <Label>사용여부</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    marginBottom: '1px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="menuStatus"
                    defaultValue={state.menuStatus}
                    data={state.useOptions2}
                    value={state.menuStatus}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
            </Tr>
          </Table>

          {state.menus.map((menu, index) => {
            return (
              <Wrap key={index}>
                <Table>
                  <Tr style={{ width: '100%', marginTop: '10px' }}>
                    <Td width={'60px'}>Menu. {index + 1}</Td>
                    <Td align={'flex-end'}>
                      <RemoveIcon
                        style={{ cursor: 'pointer', width: '20px', height: '20px' }}
                        onClick={() => _handleMenuRemove(index)}
                      />
                    </Td>
                  </Tr>
                </Table>

                <Table background={ '#f7f7f7'} border={ '1px solid #dcdcdc' }>  
                  <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
                    <Td>
                      <Label>메뉴명</Label>
                      <Input
                        validityStyles={false}
                        type="text"
                        name="menuName"
                        value={menu.menuName}
                        style={{ width: '74%' }}
                        maxLength={50}
                        required={true}
                        onChange={(e) => _handleMenuInputChange(e, index)}
                      />
                    </Td>
                    <Td>
                      <Label>Path Name</Label>
                      <Input
                        validityStyles={false}
                        type="text"
                        name="linkTo"
                        value={menu.linkTo}
                        style={{ width: '74%' }}
                        maxLength={50}
                        required={false}
                        onChange={(e) => _handleMenuInputChange(e, index)}
                      />
                    </Td>
                    <Td>
                      <Label>아이콘명</Label>
                      <Input
                        validityStyles={false}
                        type="text"
                        name="iconName"
                        value={menu.iconName}
                        style={{ width: '74%' }}
                        maxLength={50}
                        required={false}
                        onChange={(e) => _handleMenuInputChange(e, index)}
                      />
                    </Td>
                    <Td>
                      <Label>나열순서</Label>
                      <Input
                        validityStyles={false}
                        type="text"
                        name="sortNo"
                        value={menu.sortNo}
                        style={{ width: '74%' }}
                        maxLength={20}
                        required={false}
                        onChange={(e) => _handleMenuInputChange(e, index)}
                      />
                    </Td>

                    {/*
                    <Td valign={'flex-start'}>
                      <Label>사용여부</Label>
                      <ComboBox
                        style={{
                          width: '74%',
                          borderBottom: '1px solid #dcdcdc',
                        }}
                        name="menuStatus"
                        value={menu.menuStatus}
                        data={state.useOptions}
                        dataItemKey="code"
                        textField="name"
                        onChange={(e) => _handleMenuInputChange(e, index)}
                        required={false}
                      />
                    </Td>
                    */}
                  </Tr>
                </Table>
              </Wrap>
            );
          })}

          <Table style={{ marginTop: '5px' }}>
            <Tr width={'100%'}>
              <Td align={'flex-end'} style={{ width: '100%', fontWeight: '600' }}>
                <span style={{ width: '70px', marginTop: '2px' }}>메뉴 추가</span>
                <AddIcon
                  style={{ cursor: 'pointer', width: '20px', height: '20px' }}
                  onClick={(e) => _handleMenuAdd(e)}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            <Button
              primary={true}
              type={'submit'}
              onClick={() => setState({ ...state, isSave: true })}
            >
              저장하기
            </Button>
            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    status: state.auth.login,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(MenuInfo);
