import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import BbsList from '../../components/admin/bbs/BbsList';
import BbsInput from '../../components/admin/bbs/BbsInput';
import BbsView from '../../components/admin/bbs/BbsView';
import BbsManagerList from '../../components/admin/bbs/BbsManagerList';
import BbsManagerInfo from '../../components/admin/bbs/BbsManagerInfo';

const Bbs = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/admin/bbs"
        render={(props) => <BbsList {...props} />}
      />
      <AuthRoute
        path="/admin/bbs/input"
        render={(props) => <BbsInput {...props} />}
      />
      <AuthRoute
        path="/admin/bbs/view"
        render={(props) => <BbsView {...props} />}
      />
      <AuthRoute
        exact
        path="/admin/bbs/manager"
        render={(props) => <BbsManagerList {...props} />}
      />
      <AuthRoute
        path="/admin/bbs/manager/info"
        render={(props) => <BbsManagerInfo {...props} />}
      />
    </Switch>
  );
};

export default Bbs;