/*
  설명: 할인방법 선택 페이지
  - 2021.05.20. 안예린, import 수정
*/
import React from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import {
  BigLabel,
  Container,
  ParentWrap,
  SmallLabel,
} from '../../../css/common-component';

export default function DiscountType({ history }) {
  const dispatch = useDispatch();

  const _selectDiscountType = (discountType) => {
    dispatch(actions.selectDiscountType(discountType));
    history.push('/partnership/hp/selectCard');
  };
  return (
    <Container className="page">
      <ParentWrap
        padding="0px 0px auto"
        style={{ backgroundColor: 'transparent' }}
      >
        <BigLabel
          textAlign="center"
          margin="0 auto 9px auto"
          width="100%"
          bold="normal"
        >
          할인 방법 선택
        </BigLabel>
        <SmallLabel
          width="100%"
          height="16px"
          textAlign="center"
          margin={'0px auto 66px auto'}
          bold="normal"
        >
          통신사 할인 방법을 선택해 주세요!
        </SmallLabel>

        <div>
          <StyledButton
            onClick={() => _selectDiscountType('S')}
            bg="linear-gradient(to bottom, #c2cffb, #9488ff)"
          >
            <p>단말기 할인 (공시 지원)</p>
            <div>
              <li
                style={{
                  listStyle: 'none',
                  top: '6px',
                  right: '6px',
                  color: '#e2e2e2',
                }}
              >
                <span className="k-icon k-i-check"></span>
              </li>
              통신사에서 요금제에 따라 정해놓은 공시지원금을 개통할 때
              기기값에서 한번에 할인 받습니다.
            </div>
          </StyledButton>

          <StyledButton
            onClick={() => _selectDiscountType('C')}
            bg="linear-gradient(to bottom, #c2cffb, #88cbff)"
          >
            <p>요금 할인 (선택 약정)</p>
            <div>
              <li
                style={{
                  listStyle: 'none',
                  top: '6px',
                  right: '6px',
                  color: '#e2e2e2',
                }}
              >
                <span className="k-icon k-i-check"></span>
              </li>
              요금제 기본료의 25% 금액 만큼 매월 요금에서 할인 받습니다.
            </div>
          </StyledButton>
        </div>
      </ParentWrap>
    </Container>
  );
}

const StyledButton = styled.div`
  width: 343px;
  height: 115px;
  padding: 20px 31px 18px 36px;
  opacity: 0.8;
  border-radius: 10px;
  box-shadow: 0 10px 5px 0 rgba(0, 0, 0, 0.16);
  background-image: ${(props) => props.bg};
  margin-bottom: 19px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  p {
    font-size: 16px;
    margin-bottom: 12px;
    color: #000f34;
    line-height: 12px;
    margin-left: 24px;
  }
  div {
    line-height: 17px;
    font-size: 14px;
    color: #000f34;
    display: flex;
    .k-icon {
      margin-right: 9px;
      font-size: 16px;
      object-fit: contain;
      opacity: 0.8;
      color: red;
    }
  }
`;
