import styled from 'styled-components';
import { Link } from 'react-router-dom';
import theme from './theme';

export const Container = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
`;

export const Wrap = styled.div`
  height: auto;
  padding: 0 20px;
  object-fit: contain;
  background-color: ${theme.colors.white};

  @media (max-width: 768px) {
    width: 100%;
    padding-top: 100px;
    padding-bottom: 30px;
  }

  @media (min-width: 769px) {
    width: 335px;
    margin: 150px 0 310px;
    align-items: center;
    padding: 0 !important;
  }

  .loginAnchor {
    margin-right: 0;
  }

  .wideBtn_01,
  .wideBtn_02 {
    width: 100%;
    margin: 0;
  }
`;

export const LogoWrap = styled.div`
  width: 99px;
  height: 33px;
  margin: 0 auto 40px;
  cursor: pointer;
`;

export const FindIDPW = styled.div`
  display: flex;
  display: ${(props) => props.display};
  justify-content: flex-end;
  margin: 30px 0 40px;
`;

export const FindIDPWButton = styled(Link)`
  :first-child {
    margin-right: 4px;
  }
  font-family: NotoR;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.8;
  letter-spacing: normal;
  text-align: left;
  color: #0e2b64;
`;

//FindPW

export const CountdownWrap = styled.div`
  display: ${(props) => props.display};
  font-family: NotoM;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: right;
  color: #fe4747;
  margin-bottom: 23px;
`;

export const HpNoRoundInputBox = styled.div`
  position: relative;
  p {
    z-index: 1;
    position: absolute;
    top: 22%;
    left: 20px;
    ${theme.Body2};
    /* color: ${theme.colors.grey2}; */
    color: ${(props) =>
      props.className === 'is-active'
        ? 'rgb(254,71,71)'
        : '${theme.colors.grey2}'};
    border: none !important;
    cursor: default;
  }
  .findidinput {
    margin: 0 0 40px 0;
    border-bottom: none;
    ::placeholder {
      width: 181px;
      color: ${theme.colors.grey3};
      font-size: ${(props) => (props.type === 'password' ? '24px' : '14px')};
      line-height: 1.15;
      text-align: left;
    }

    .is-active {
      color: ${theme.colors.red};
      font-weight: 500;
      font-family: NotoM;
      border: 1px solid ${theme.colors.red};
    }
  }

  .error-red {
    position: absolute;
    bottom: 14px;
    right: 0;
    color: ${theme.colors.red};
    font-size: 12px;
    font-family: 'NotoR';
  }
`;
export const WarningIcon = styled.div`
  display: ${(props) => (props.display === 'none' ? 'none' : 'block')};
  position: absolute;
  right: 15px;
  top: 20px;
`;

export const LoginModal = styled.div`
  display: ${(props) => props.display};
  padding-bottom: 20px;
  h4 {
    ${theme.H4};
    color: ${theme.colors.navyBox};
    padding-bottom: 20px;
  }
  p {
    ${theme.Body1}
  }
`;

//FindID

export const FormWrap = styled.div`
  opacity: ${(props) => props.opacity}; ;
`;

//ChangePW

export const SmallText = styled.div`
  color: ${theme.colors.red};
  font-weight: 500;
  font-family: NotoM;
  font-size: 12px;
  text-align: right;
  margin-top: -30px;
  padding: 0;
`;
