import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import {
  Chart,
  ChartLegend,
  ChartSeries,
  ChartSeriesItem,
  ChartArea,
  ChartSeriesLabels,
} from '@progress/kendo-react-charts';
//개발자 라이브러리
import _axios from '../../../utils/axios';
//css
import theme from '../../../styles/theme';
//images
import more_horiz from '../../../images/mainAfterLogin/more_horiz.svg';


const PointStatus = ({ totalPoint, monthPoint, chartData, currentMonth }) => {
  return (
    <StyledContainer>
      <div className="titleWrap">
        <span>포인트</span>
        <span className="hamburger">
          <img src={more_horiz} alt="" />
          {/*
          <Link to='/mypage/home/point'>
            <img src={more_horiz} alt="" />
          </Link>
          */}
        </span>
      </div>

      <div className="totalPointWrap">
        <div>
          <p className="name">전체 누적 보너스</p>
          <p className="price">{totalPoint}원</p>
        </div>
        <div className="donutChart">
          <Chart>
            <ChartArea />
            <ChartSeries>
              <ChartSeriesItem
                type="donut"
                data={chartData}
                categoryField="assort"
                field="point"
                padding={0}
              >
                <ChartSeriesLabels
                  font="9px 'Noto Sans KR', sans-serif;"
                  color="#fff"
                  background="none"
                  content={(e) => e.category}
                />
              </ChartSeriesItem>
            </ChartSeries>
            <ChartLegend visible={false} />
          </Chart>
        </div>
      </div>

      <div className="currentPointWrap">
        <div>
          <p className="name">{currentMonth} 누적 보너스</p>
          <p className="price">{monthPoint}원</p>
        </div>
      </div>
    </StyledContainer>
  );
};

export default PointStatus;

const StyledContainer = styled.div`
  width: 100%;
  min-height: 422px;
  height: auto;
  padding: 22px;
  background: #f1f2f2;
  .titleWrap {
    ${theme.H3}
    color: ${theme.colors.navyBox};

    line-height: 36px;

    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;

    color: #0e2b64;
  }

  .hamburger {
    img {
      width: 100%;
      width: 28px;
      height: 24px;
    }
  }

  .totalPointWrap {
    margin: 22px 0;
    width: 100%;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 22px;
    p {
      margin: 0;
    }
  }
  .donutChart {
    max-width: 50%;
    .k-chart,
    .k-stockchart {
      height: 50%;
    }

    .k-chart-surface {
      svg {
        g {
          text {
            transform: translateX(18px);
          }
        }
      }
    }
  }
  .currentPointWrap {
    width: 100%;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 22px;
    p {
      margin: 0;
    }
  }

  .name {
    ${theme.H4};
    color: ${theme.colors.grey2};
  }

  .price {
    ${theme.H2};
    color: ${theme.colors.black2};
  }

  .line {
    border-top: 1px solid #a9a9a9;
    transform: rotate(90deg);
    width: 68px;
  }
`;
