/* **********************************************************************
 * 관리자페이지 > 환경설정 > 약관관리
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import LoginRoute from '../../components/common/AuthRoute';
import TermList from '../../components/admin/term/TermList';
import TermInfo from '../../components/admin/term/TermInfo';

const Term = () => {
  return (
    <Switch>
      <LoginRoute
        exact
        path="/admin/term"
        render={(props) => <TermList {...props} />}
      />
      <LoginRoute
        path="/admin/term/info"
        render={(props) => <TermInfo {...props} />}
      />
    </Switch>
  );
};

export default Term;