import React from 'react';
import styled from 'styled-components';

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 20px;
  border-bottom: 1px solid #dcdcdc;
  padding: 5px;
  font-size: 12px;
  color: #666666;
`;

// CMS 진행 로그
const CmsLog = (props) => {
  const { logData } = props;

  const logs = logData.map((log, index) => {
    return (
      <Row key={index}>
        <div style={{ width: '15%' }}>{log.adminId}</div>              
        <div style={{ width: '15%' }}>{log.adminName}</div>              
        <div style={{ width: '15%' }}>{log.logAssort}</div>              
        <div style={{ width: '35%' }}>{log.logMessage}</div>              
        <div style={{ width: '20%' }}>{log.wdate}</div>              
      </Row>
    )
  });

  return logs;
}

export default CmsLog;