import React from "react";
import { Switch } from "react-router-dom";
import AuthRoute from '../../components/common/AuthRoute';    // 로그인 필요 체크
import { 
  PointHome, // 포인트 Home
  Insu, // 자동차 다이렉트 보험 포인트
  Hp, // 휴대폰 실적 포인트
  Rental, // 렌탈 실적 포인트
  Support, // 후원 포인트 
  Withdraw // 출금 요청
} from "../../components/mypage/home/point";

export default function PointRoute() {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/mypage/home/point"
        render={(props) => <PointHome {...props} />}
      />
      <AuthRoute
        path="/mypage/home/point/insu"
        render={(props) => <Insu {...props} />}
      />
      <AuthRoute
        path="/mypage/home/point/hp"
        render={(props) => <Hp {...props} />}
      />
      <AuthRoute
        path="/mypage/home/point/rental"
        render={(props) => <Rental {...props} />}
      />
      <AuthRoute
        path="/mypage/home/point/support"
        render={(props) => <Support {...props} />}
      />
      <AuthRoute
        path="/mypage/home/point/withdraw"
        render={(props) => <Withdraw {...props} />}
      />
    </Switch>
  );
}