/*
  설명: 정보 입력 페이지
  - 2021.05.20. 안예린, import 수정
*/
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import '../../../css/common-kendo.css';
import {
  BigLabel,
  Container,
  Loading,
  MiddleLabel,
  NextButton,
  NumberInputLine,
  ParentWrap,
  SmallLabel,
  TextArea,
  UnderLineInput,
  UnderLineInputWrap,
} from '../../../css/common-component';

export default function InsertInfo(props) {
  const dispatch = useDispatch();

  const memId = useSelector((store) => store.auth.authStatus.userInfo.userId);
  const hp_params = useSelector((store) => store.hp);
  const loading = useSelector((store) => store.hp.loading);
  const response = useSelector((store) => store.hp.response);

  const benefitAssort = useSelector((store) => store.hp.benefitAssort);
  const benefitPrice = useSelector((store) => store.hp.benefitPrice);

  const [inputs, setInputs] = useState({
    memId: memId,
    custName: '',
    hpNo: '',
    hpNo1: '010',
    hpNo2: '',
    cardCode: '', // 제휴카드할인여부 (Y: 예, N: 아니오)
    comment: '',
  });

  const { custName, hpNo, hpNo1, hpNo2, comment } = inputs;
  const _handleInputChange = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  const _handleTextareaChange = (e) => {
    setInputs({
      ...inputs,
      comment: e.value,
    });
  };
  useEffect(() => {
    dispatch(actions.inserInfo(inputs));
  }, [inputs]);

  const _handleSubmit = (e) => {
    e.preventDefault();
    if (benefitAssort === 'C' && parseInt(benefitPrice) > 0) {
      props.history.push('/partnership/hp/account');
    } else {
      dispatch(actions.submitHpRequest(hp_params));
    }
  };

  useEffect(() => {
    let mounted = true;

    //dispatch(stackRequest('S', ''));
    setInputs({
      ...inputs,
      hpNo: hpNo1 + hpNo2,
    });
    dispatch(actions.inserInfo(inputs));

    if (response.result === '0' && mounted) {
      props.history.push('/partnership/hp/complete');
    }

    return () => (mounted = false);
  }, [dispatch, hpNo1, hpNo2, response.result]);
  const hpNoValidationMessage = '휴대폰번호를 입력해주세요';
  return (
    <Container className="page">
      <ParentWrap style={{ width: '100%' }} height="auto">
        <form
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
          onSubmit={_handleSubmit}
        >
          <BigLabel margin="1em 0 1em 0" textAlign="center">
            기본 정보 입력
          </BigLabel>

          <SmallLabel
            bold="normal"
            color="#404040"
            textAlign="center"
            style={{ fontSize: '14px', marginBottom: '1em' }}
          >
            개통하실 명의자의 기본 정보를 입력하세요.
          </SmallLabel>

          <UnderLineInputWrap style={{}}>
            <MiddleLabel
              width="35%"
              style={{
                display: 'flex',
                paddingLeft: '15px',
                alignItems: 'center',
                fontSize: '15px',
              }}
            >
              이름
            </MiddleLabel>
            <UnderLineInput
              type="text"
              name="custName"
              validityStyles={false}
              value={custName}
              maxLength={20}
              autoComplete="off"
              onChange={_handleInputChange}
              required={true}
              style={{ width: '65%' }}
              placeholder="고객 이름을 입력하세요"
            ></UnderLineInput>
          </UnderLineInputWrap>

          <UnderLineInputWrap
            width="auto"
            height="30px"
            margin="30px 1.8px 10px 0"
            id="insertInfoPage"
          >
            <MiddleLabel
              style={{
                display: 'flex',
                paddingLeft: '15px',
                alignItems: 'center',
                fontSize: '14px',
                width: '35%',
              }}
            >
              휴대폰번호
            </MiddleLabel>
            <div
              style={{
                width: '65%',
                display: 'flex',
                paddingLeft: '14px',
              }}
            >
              <NumberInputLine
                width="18%"
                id="hpNo1"
                type="number"
                name="hpNo1"
                value={hpNo1}
                validityStyles={true}
                mask="000"
                pattern={'[0-9]{3}'}
                autoComplete="off"
                onChange={_handleInputChange}
                required={true}
                validationMessage={hpNoValidationMessage}
              ></NumberInputLine>
              <NumberInputLine
                id="hpNo2"
                type="number"
                name="hpNo2"
                value={hpNo2}
                validityStyles={true}
                mask="-0000-0000"
                pattern={'[0-9]{4}-[0-9]{4}'}
                autoComplete="off"
                validationMessage={hpNoValidationMessage}
                onChange={_handleInputChange}
                required={true}
                placeholder="-0000-0000"
              ></NumberInputLine>
            </div>
          </UnderLineInputWrap>

          <MiddleLabel
            style={{ width: '100%', paddingLeft: '15px', fontSize: '14px' }}
            margin="30px 1.8px 10px 0"
          >
            메모
          </MiddleLabel>
          <TextArea
            placeholder="상담사에게 남기실 말씀"
            autoSize={true}
            rows={5}
            name="comment"
            value={comment}
            onChange={_handleTextareaChange}
          ></TextArea>

          <NextButton
            type={'submit'}
            width="256px"
            height="48px"
            margin="1em auto 1em auto"
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            신청하기
          </NextButton>
        </form>
      </ParentWrap>
      {loading && <Loading></Loading>}
    </Container>
  );
}
