import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
// 개발자 Component
import { logoutRequest } from '../../../../redux/actions/auth';
// css
import '../../../../styles/font.css';
import theme from '../../../../styles/theme';
// icon

const WebHeaderButton = () => {
  const dispatch = useDispatch();
  const authStatus = useSelector((store) => store.auth.authStatus);
  const _handleLogout = () => {
    dispatch(logoutRequest());
  };
  const pathName =
    authStatus.userAuth === 'A' || authStatus.userAuth === 'D'
      ? '/admin/home'
      : '/mypage/home';

  return (
    <Container>
      {authStatus.isLogIn === false ? (
        <Wrap>
          <Link to="/login" className="font-navy link">
            로그인
          </Link>
          <Link to="/account/checkRecomandID" className="font-white link">
            간편 추천 가입
          </Link>
        </Wrap>
      ) : (
        <Wrap>
          <Link to={pathName} className="font-navy link">
            마이비즈니스
          </Link>
          <button
            onClick={_handleLogout}
            className="font-white link"
            style={{
              lineHeight: '39px',
            }}
          >
            Logout
          </button>
        </Wrap>
      )}
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 100%;
`;

const Wrap = styled.div`
  display: flex;

  .link {
    width: 133px;
    height: 44px;
    line-height: 20px;
    margin-right: 1em;
    font-size: 14px;
    font-family: NotoR;
    text-align: center;
    // padding: 12px 24px;
    display: flex;
    align-items: center;
    justify-content: center;

    border: 1px solid ${theme.colors.navyBox};
    border-radius: 3px;

    &:last-child {
      margin-right: 0;
    }
  }

  .font-navy {
    color: ${theme.colors.navyBox};
  }

  .font-white {
    color: ${theme.colors.white};
    background: ${theme.colors.navyBox};
  }
`;

export default WebHeaderButton;
