import React from 'react';
import styled from 'styled-components';

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 20px;
  border-bottom: 1px solid #dcdcdc;
  padding: 5px;
  font-size: 12px;
  color: #666666;
`;

// 출금신청 로그
const CmsPayLog = (props) => {
  const { logData } = props;

  const logs = logData.map((log, index) => {
    return (
      <Row key={index}>
        <div style={{ width: '8%' }}>{log.payMonth}</div>  
        <div style={{ width: '8%' }}>{log.paymentKind}</div>  
        <div style={{ width: '20%' }}>{log.transactionId}</div>                    
        <div style={{ width: '5%' }}>{log.payAmount}</div>
        <div style={{ width: '5%' }}>{log.status}</div>    
        <div style={{ width: '30%' }}>{log.message}</div>              
        <div>{log.wdate}</div>              
      </Row>
    )
  });

  return logs;
}

export default CmsPayLog;