import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
// icon & image
import kakao_icon from '../../images/mainAfterLogin/smile_text.svg';
import smile from '../../images/mainAfterLogin/smile.png';
import kakao_icon_phone from '../../images/mainAfterLogin/phone.svg';
import kakao_icon_home from '../../images/mainAfterLogin/home.svg';
import kakao_icon_spider from '../../images/mainAfterLogin/spider.svg';
import kakao_icon_close from '../../images/mainAfterLogin/close.svg';

const KakaoLinkList = () => {
  const [isScroll, setIsScroll] = useState(false);
  const LinkListRef = useRef();
  const kakaoIconRef = useRef();
  const LinkListBackRef = useRef();

  // 카카오톡 아이콘 눌렀을때 이벤트
  const openLinkList = () => {
    setTimeout(() => {
      LinkListRef.current.style.transition = 'all 0.3s';
      LinkListRef.current.style.transform = 'translate(-300px,0)';
      kakaoIconRef.current.style.display = 'none';
    }, 100);
    kakaoIconRef.current.style.transition = 'all 0.3s';
    LinkListBackRef.current.style.display = 'block';
    kakaoIconRef.current.style.transform = 'translate(100px,0)';
  };
  //  링크 리스트 닫을때 이벤트
  const closeLinkList = () => {
    setTimeout(() => {
      kakaoIconRef.current.style.display = 'flex';
    }, 100);
    setTimeout(() => {
      kakaoIconRef.current.style.transition = 'all 0.3s';
      kakaoIconRef.current.style.transform = 'translate(0px,0)';
      LinkListBackRef.current.style.display = 'none';
    }, 200);

    LinkListRef.current.style.transition = 'all 0.3s';
    LinkListRef.current.style.transform = 'translate(100px,0)';
  };

  const handleScroll = () => {
    setIsScroll(true);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div>
      <LinkListStyle>
        <section ref={LinkListRef} className="listSection">
          <div style={{ display: 'flex' }}>
            <div className="listName">스파이더 플랫폼 문의</div>
            <div className="listIcon">
              <a
                rel="noreferrer"
                target="_blank"
                href=" http://pf.kakao.com/_eGPTK/chat"
              >
                <img src={kakao_icon_spider}></img>
              </a>
            </div>
          </div>
          <div style={{ display: 'flex' }}>
            <div className="listName">가자렌탈 문의</div>
            <div className="listIcon">
              <a
                rel="noreferrer"
                target="_blank"
                href="http://pf.kakao.com/_ANlDxb/chat"
              >
                <img src={kakao_icon_home}></img>
              </a>
            </div>
          </div>
          <div style={{ display: 'flex' }}>
            <div className="listName">휴대폰 간편 신청 문의</div>
            <div className="listIcon">
              <a
                rel="noreferrer"
                target="_blank"
                href="http://pf.kakao.com/_QgyzK/chat"
              >
                <img src={kakao_icon_phone}></img>
              </a>
            </div>
          </div>
          <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
            <div className="listCloseIcon" onClick={closeLinkList}>
              <img src={kakao_icon_close}></img>
            </div>
          </div>
        </section>

        <div
          className="listOpenIcon"
          ref={kakaoIconRef}
          id={isScroll ? 'scrollImg' : undefined}
        >
          {isScroll ? (
            <img
              onClick={openLinkList}
              src={smile}
              alt=""
              style={{
                width: '50px',
                height: '50px',
              }}
            />
          ) : (
            <img
              onClick={openLinkList}
              src={kakao_icon}
              alt=""
              style={{
                width: '100%',
                height: '100%',
              }}
            />
          )}
        </div>
      </LinkListStyle>
      <ListBackGround ref={LinkListBackRef}></ListBackGround>
    </div>
  );
};

const LinkListStyle = styled.div`
  object-fit: contain;
  position: fixed;
  z-index: 1;
  bottom: 80px;
  width: 10px;

  right: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 5px;
  justify-content: center;

  .listSection {
    position: relative;
    left: 300px;
    color: white;
    font-weight: 0;
    font-size: 14px;
  }

  .listName {
    display: flex;
    align-items: center;
    margin-right: 10px;
    width: 145px;
    justify-content: flex-end;
  }

  .listIcon {
    width: 55px;
    height: 55px;
    background-color: #ffffff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 7px;
  }

  .listCloseIcon {
    width: 55px;
    height: 55px;
    background-color: #fbd932;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
  }

  .listOpenIcon {
    width: 145px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .listOpenIcon#scrollImg {
    justify-content: flex-end;
  }
`;

const ListBackGround = styled.div`
  display: none;
  position: fixed;
  bottom: 0;
  top: 0;
  width: 1000px;
  height: 1000px;
  background-color: rgba(0, 0, 0, 0.8);
`;

export default KakaoLinkList;
