/*설명 : 아이디 찾기 페이지 
- 2021.06.03 안예린 페이지 리뉴얼 */
import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import { Dialog } from '@progress/kendo-react-dialogs';
import Countdown from 'react-countdown';
//개발자 라이브러리
import _axios from '../../utils/axios';
import {
  Container,
  Wrap,
  LogoWrap,
  FindIDPW,
  FindIDPWButton,
  LoginModal,
  HpNoRoundInputBox,
  WarningIcon,
  CountdownWrap,
  FormWrap,
} from '../../styles/account';
//css
import '../../css/kendo.css';
import { HpNoRoundInput, RoundButton2 } from '../../styles/common-component';
//button
import CommonInput from '../common/CommonInput';
import CommonButton from '../common/Button';
//image
import LoginLogo from '../../images/account/logo_9930.svg';
import warning from '../../images/account/warning.svg';
const FindID = (props) => {
  const [redCertifyNo, setRedCertifyNo] = useState(false);
  const [red, setRed] = useState(false);
  const [error, setError] = useState('');
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);
  const [state, setState] = useState({
    userId: userInfo.userId,
    userPw: '',
    userName: '',
    hpNo: '',
    success: false,
    message: null,
    visibleDialog: false,
    isCertify: false,
    isGetData: false,
    certifyNo: '',
    inputCertifyNo: '',
    disabled: false,
    date: Date.now() + 180000,
    isTimeOut: false,
  });
  const [inputValue, setInputValue] = useState(state.hpNo);
  const countDownRef = useRef();
  // 아이디를 찾고나면 로그인페이지로
  const _getData = async () => {
    const url = '/api/account/get_find_id.php';
    const params = { userName: state.userName, hpNo: state.hpNo };
    const response = await _axios(url, params);
    if (response.result === '0') {
      setState({
        ...state,
        success: true,
        isGetData: true,
        message: '',
        visibleDialog: false,
      });
      setRed(false);
      setRedCertifyNo(false);
      setError('');
      setTimeout(() => {
        props.history.push({
          pathname: '/login',
          state: { isCertify: true },
        });
      }, 0);
    }
  };
  //인증번호 받기
  const _getCertifyNo = async () => {
    const url = '/api/account/get_certify_no.php';
    const params = {
      assort: 'I',
      userName: state.userName,
      hpNo: state.hpNo,
    };
    const response = await _axios(url, params);
    setState({
      ...state,
      message: response.message,
      success: true,
      visibleDialog: true,
    });
    setTimeout(() => {
      if (response.result === '0') {
        countDownRef.current.start();
        setState({
          ...state,
          success: false,
          isCertify: true,
          isTimeOut: false,
          certifyNo: response.certifyNo,
        });
      } else {
        setState({
          ...state,
          success: false,
        });
        setRed(true);
        setError('정보가 올바르지 않습니다.');
      }
    });
  };
  // 입력받을때
  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
      message: '',
    });
    setRedCertifyNo(false);
    setRed(false);
    setError('');
  };
  useEffect(() => {
    if (inputValue.length === 10) {
      setInputValue(state.hpNo.replace(/(\d{3})(\d{4})(\d{3})/, '$1-$2-$3'));
    }
    if (inputValue.length === 13) {
      setInputValue(
        inputValue
          .replace(/-/g, '')
          .replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3'),
      );
    }
  }, [inputValue]);
  // 인증번호 받기
  const _handleSubmit = (e) => {
    e.preventDefault();
    _getCertifyNo();
  };

  //인증번호 확인
  const _onVaildCertifyNo = () => {
    if (parseInt(state.certifyNo) === parseInt(state.inputCertifyNo)) {
      countDownRef.current.pause();
      _getData();
    } else {
      setState({
        ...state,
        success: true,
        message: '인증번호가 올바르지 않습니다.',
        visibleDialog: false,
      });
      setRedCertifyNo(true);
      setRed(false);
    }
  };
  const renderer = ({ minutes, seconds, completed }) => {
    return (
      <span style={{ color: 'rgb(254,71,71)' }}>
        {minutes}:{seconds}
      </span>
    );
  };
  const _onTimeComplete = () => {
    setState({ ...state, isTimeOut: true, date: Date.now() + 180000 });
  };
  const hpNoMessage = '휴대폰번호를 입력해 주세요';
  const userNameMessage = '이름을 입력해 주세요';
  const certifyMessage = '인증번호를 입력해 주세요';
  return (
    <Container>
      <Wrap>
        <LogoWrap
          onClick={() =>
            props.history.push({
              pathname: '/',
            })
          }
        >
          <img src={LoginLogo} alt="logo"></img>
        </LogoWrap>
        <form onSubmit={_handleSubmit}>
          {/* <LoginModal display={state.isGetData ? 'block' : 'none'}>
              <h4>아이디가 전송되었습니다.</h4>
              <p>
                스파이더플랫폼 공식 카카오톡으로 아이디를 안내해
                <br />
                드렸습니다.
              </p>
            </LoginModal> */}
          <FormWrap>
            <CommonInput
              inputtype="text"
              width="100%"
              height="60px"
              value={state.userName}
              maxLength={6}
              autoComplete="off"
              name="userName"
              titlename="이름"
              placeholder="이름을 입력해주세요."
              onChange={_handleInputChange}
              required={true}
              validationMessage={userNameMessage}
              validityStyles={false}
              disabled={state.isCertify}
            />
            <HpNoRoundInputBox className={red ? 'is-active ' : ''}>
              <p className={red ? 'is-active hpinput' : 'hpinput'}>
                핸드폰 번호
              </p>
              <HpNoRoundInput
                color={red ? 'red' : ''}
                className={red ? 'is-active findidinput' : 'findidinput'}
                type="text"
                value={state.hpNo}
                autoComplete="off"
                name="hpNo"
                placeholder="010-0000-0000"
                onChange={_handleInputChange}
                mask="000-0000-0000"
                required={true}
                width="100%"
                height="60px"
                margin="0 0 20px 0"
                validationMessage={hpNoMessage}
                validityStyles={false}
                borderdisable={state.isCertify}
                disabled={state.isCertify}
              ></HpNoRoundInput>
              <WarningIcon display={red ? 'block' : 'none'}>
                <a id="warning" className="warning">
                  <img src={warning} />
                </a>
              </WarningIcon>
              <h1 className="error-red">{error}</h1>
            </HpNoRoundInputBox>
            {state.isTimeOut === true && (
              <RoundButton2
                display="none"
                className="replaybtn"
                width={'100%'}
                height={'44px'}
                color="#000f34"
                margin="0 0 0 0"
                border="solid 2px #e2e2e2"
                onClick={() => _getCertifyNo()}
              >
                인증번호 재전송
              </RoundButton2>
            )}
            {state.isCertify === false && (
              <CommonButton type="submit" kind="wideBtn_01">
                인증번호 받기
              </CommonButton>
            )}
            {state.isCertify && state.isTimeOut === false && (
              <CommonInput
                inputtype="text"
                value={state.inputCertifyNo}
                autoComplete="off"
                titlename="인증번호"
                kind={redCertifyNo ? 'is-active' : ''}
                name="inputCertifyNo"
                placeholder="인증번호를 입력하세요."
                onChange={_handleInputChange}
                required={true}
                width="100%"
                height="60px"
                validationMessage={certifyMessage}
                validityStyles={false}
                error2={state.message}
              />
            )}
            <CountdownWrap display={state.isCertify ? 'block' : 'none'}>
              <Countdown
                ref={countDownRef}
                date={state.date}
                renderer={renderer}
                autoStart={false}
                onComplete={_onTimeComplete}
              />
            </CountdownWrap>
            {state.isCertify && (
              <CommonButton
                type="button"
                onClickHandler={() => _onVaildCertifyNo()}
                kind="btn_01"
                width="100%"
                height="44px"
              >
                완료
              </CommonButton>
            )}
          </FormWrap>
        </form>
        <FindIDPW display={state.isCertify ? 'none' : 'flex'}>
          <FindIDPWButton to="/login">로그인 | </FindIDPWButton>
          <FindIDPWButton to="/account/findPW"> 비밀번호찾기</FindIDPWButton>
        </FindIDPW>
        {state.isCertify && (
          <FindIDPW display={state.isCertify ? 'flex' : 'none'}>
            <FindIDPWButton to="/account/findPW"> 비밀번호찾기</FindIDPWButton>
          </FindIDPW>
        )}
      </Wrap>
    </Container>
  );
};

export default FindID;
