/* **********************************************************************
  관리자페이지 > 환경설정 > 포인트관리
  - 2021.05.19. 박태수
  - 2021.06.01. 양민석 : 관리자페이지 코드 띄어쓰기 가독성해결
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const CashRequestList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '현금인출관리',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    isExcelDown: false,
    loadData: [],
    excelData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      memId: '',
      status: '',
      minDate: '',
      maxDate: '',
      statusOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/admin/point/get_cash_request_list.php';
    const params = {
      searchValue: state.searchAble.searchValue,
      memId: state.searchAble.memId,
      status: state.searchAble.status,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        statusOptions: response.statusOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          {type: 'text',width: '84%',name: 'searchValue',value: searchAble.searchValue,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색'},
        ],
      }
    : {
        title: '검색어',
        items: [{type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},],
      };
  const menuStatus = {
    title: '회원ID',
    items: [{type: 'text',width: '100%',name: 'memId',value: searchAble.memId,options: searchAble.statusOptions,},],
  };
  const status = {
    title: '요청상태',
    items: [{type: 'comboBox',width: '100%',name: 'status',value: searchAble.status,options: searchAble.statusOptions,},],
  };
  const wdate = {
    title: '등록일자',
    items: [
      {type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate,},
      {type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate,},
    ],
  };

  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, menuStatus, status, wdate];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',align: 'center',width: '10%',},
        {type: 'text',title: '포인트',name: 'point',width: '14%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',align: 'center',width: '15%',},
        {type: 'text',title: '회원ID',name: 'memId',align: 'center',width: '19%',},
        {type: 'text',title: '요청상태',name: 'statusName',align: 'center',width: '17%',},
        {type: 'text',title: '등록일자',name: 'wdate',align: 'center',width: '25%',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '5%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '5%',align: 'center',},
        {type: 'text',title: '회원ID',name: 'memId',width: '10%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '8%',align: 'center',},
        {type: 'text',title: '포인트',name: 'point',width: '8%',align: 'center',},
        {type: 'text',title: '지급율',name: 'cashRate',width: '7%',align: 'center',},
        {type: 'text',title: '지급액',name: 'cash',width: '10%',align: 'center',},
        {type: 'text',title: '소득세',name: 'taxAmount',width: '7%',align: 'center',},
        {type: 'text',title: '실지급액',name: 'accountAmount',width: '10%',align: 'center',},
        {type: 'text',title: '입금일자',name: 'paymentDate',width: '10%',align: 'center',},
        {type: 'text',title: '요청상태',name: 'statusName',width: '10%',align: 'center',},
        {type: 'text',title: '등록일자',name: 'wdate',width: '10%',align: 'center',},
      ];

  // Excel download 항목
  const listExcel = [
    { field: 'idx', title: 'IDX', lock: true, width: 50 },
    { field: 'memId', title: '회원ID', lock: true, width: 100 },
    { field: 'memName', title: '회원명', lock: true, width: 100 },
    { field: 'point', title: '사용포인트', lock: false, width: 80 },
    { field: 'cashRate', title: '지급율', lock: false, width: 50 },
    { field: 'cash', title: '지급액', lock: false, width: 80 },

    { field: 'taxAmount', title: '소득세', lock: false, width: 80 },
    { field: 'accountAmount', title: '실지급액', lock: false, width: 80 },
    { field: 'registNo', title: '주민번호', lock: false, width: 120 },
    { field: 'bankName', title: '입금은행', lock: false, width: 100 },
    { field: 'accountNo', title: '계좌번호', lock: false, width: 150 },
    { field: 'accountName', title: '예금주명', lock: false, width: 100 },

    { field: 'paymentDate', title: '입금일자', lock: false, width: 100 },
    { field: 'statusName', title: '요청상태', lock: false, width: 80 },
    { field: 'wdate', title: '등록일자', lock: false, width: 80 },
  ]

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>포인트관리 〉 인출요청목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={true}
            isDeleteButton={true}
            isExcelDownButton={true}                
            isExcelUpButton={true}            
            infoPath="/admin/point/request/info"
            excelUpPath="/admin/point/request/excelUp"            
            deleteUrl="/api/admin/point/set_cash_request_delete.php"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            excelHeader={listExcel}
            infoPath="/admin/point/request/info"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default CashRequestList;
