/*
  설명: 로그인전 home 페이지
  - 2021.05.25. 안예린
  - 2021.05.25. 박태수
*/
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
// 개발자 라이브러리
import { setHeader } from '../../redux/actions/header';
import _axios from '../../utils/axios';
import _isMobile from '../../utils/isMobile';
// css
import 'react-image-gallery/styles/css/image-gallery.css';
import theme from '../../styles/theme';
import { Container, Wrap, Button, Input, TextArea } from '../../styles/common';
import {
  StyledContents,
  StyledImgSlider,
  StyledService,
} from '../../styles/front/home';
import '../../styles/font.css';
// icon & images
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import arrowLeft from '../../images/common/icon/grey-home-left-arrow.svg';
import arrowRight from '../../images/common/icon/grey-home-right-arrow.svg';
import gajaimg from '../../images/newImage/mainBeforeLogin/gaja.jpg';
import phoneimg from '../../images/newImage/mainBeforeLogin/phone.jpg';
import serviceimg from '../../images/newImage/mainBeforeLogin/service.jpg';
import pcimg1 from '../../images/newImage/mainBeforeLogin/phonepc.jpg';
import Vector from '../../images/newImage/mainBeforeLogin/Vector.svg';
import spider from '../../images/newImage/mainBeforeLogin/spider.jpg';

const scrollToRef = (ref) => {
  window.scrollTo({ behavior: 'smooth', top: ref.current.offsetTop });
};

const Home = ({ props }) => {
  const dispatch = useDispatch();
  const authStatus = useSelector((store) => store.auth.authStatus);
  const isMobile = _isMobile();

  // Header setting
  const headerInfo = {
    isBackground: false,
    isBugger: authStatus.isLogIn ? true : false,
    isLogo: true,
    isHomeButton: false,
    isBackButton: false,
    titleName: '',
  };

  const [state, setState] = useState({
    name: '',
    phoneNum: '',
    eamil: '',
    contents: '',
    loadData: [],
  });
  // 이미지슬라이더 상태
  const [slideNum, setSlideNum] = useState(0);
  const [nextImgState, setNextImgState] = useState(false);
  const [beforeImgState, setBeforeImgState] = useState(false);
  const [clickImgCheck, setClickImgCheck] = useState(false);
  const [Xstate, setXstate] = useState(0);

  // 이미지 슬라이더 ref
  const Refimg1 = useRef();
  const Refimg2 = useRef();
  const Refimg3 = useRef();
  const Refimg4 = useRef();

  const _goGajaRental = () => {
    if (!authStatus.isLogIn) {
      props.history.push('/login');
    } else {
      window.open(
        `https://open.gajarental.com?token=${authStatus.userInfo?.data?.token}`,
      );
    }
  };
  // 모바일 터치 슬라이드 함수
  const touchStart = (e) => {
    setXstate(e.changedTouches[0].pageX);
  };
  const touchEnd = (e) => {
    if (Xstate > e.changedTouches[0].pageX) {
      setClickImgCheck(true);
      setSlideNum(-100);
      setNextImgState(true);
    } else {
      setClickImgCheck(true);
      setSlideNum(0);
      setBeforeImgState(true);
    }
  };

  // 이미지 슬라이드 3초반복
  useEffect(() => {
    const repeatSlide = () => {
      if (slideNum === 0 && clickImgCheck === false) {
        setTimeout(() => {
          setSlideNum(-100);
          setNextImgState(true);
        }, 3000);
      } else if (slideNum === -100 && clickImgCheck === false) {
        setTimeout(() => {
          setSlideNum(0);
          setBeforeImgState(true);
        }, 3000);
      }
    };
    repeatSlide();
    if (nextImgState) {
      Refimg1.current.style.transform = `translate(${slideNum}%,0)`;
      Refimg2.current.style.transform = `translate(${slideNum}%,0)`;
      if (!isMobile) {
        Refimg3.current.style.transform = `translate(${slideNum}%,0)`;
        Refimg4.current.style.transform = `translate(${slideNum}%,0)`;
      }
      setNextImgState(false);
    } else if (beforeImgState) {
      Refimg1.current.style.transform = `translate(${slideNum}%,0)`;
      Refimg2.current.style.transform = `translate(${slideNum}%,0)`;
      if (!isMobile) {
        Refimg3.current.style.transform = `translate(${slideNum}%,0)`;
        Refimg4.current.style.transform = `translate(${slideNum}%,0)`;
      }
      setBeforeImgState(true);
    }

    return () => {};
  }, [nextImgState, beforeImgState, slideNum, isMobile]);

  // 서비스 문의 상태값 변경
  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  // 서비스 문의 submit 이벤트
  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
  };

  // load data
  const _getData = async () => {
    const url = '/api/bbs/get_bbs_list.php';
    const params = {
      bbsCode: 'N_01',
      page: 1,
      rows: 5,
    };

    const { data } = await _axios(url, params);
    setState({
      ...state,
      loadData: data,
    });
  };

  // 서비스문의 내용 저장
  const _setData = async () => {
    const url = '/api/bbs/set_bbs_update.php';
    const params = {
      mode: 'insert',
      bbsCode: { code: 'S-01', name: '서비스문의' },
      memName: state.name,
      subject: '서비스문의',
      content: state.contents,
      hpNo: state.phoneNum,
      email: state.email,
    };

    const data = await _axios(url, params);
    alert('접수되었습니다');
  };

  useEffect(() => {
    // header 설정
    dispatch(setHeader(headerInfo));
    _getData();
  }, []);

  return (
    <Container backgroundColor={theme.colors.white}>
      <Wrap>
        <StyledContents>
          <section className="section1">
            <div>
              <h3>스파이더플랫폼</h3>
              {isMobile ? (
                <p style={{ textAlign: 'center' }}>
                  스파이더플랫폼은 휴대폰, 렌탈 제품을
                  <br />
                  통해 차별화된 수익을 제공합니다.
                </p>
              ) : (
                <p>
                  스파이더플랫폼은 휴대폰, 렌탈 제품을 통해 차별화된 수익을
                  제공합니다.
                </p>
              )}
            </div>
          </section>
          {isMobile && (
            <section className="mobileBoard">
              <div style={{ marginBottom: '10px' }} className="div1">
                <h4>공지사항</h4>
              </div>
              <div className="div2">
                {state.loadData.map((data) => (
                  <div key={data.idx}>
                    <span>{data.subject}</span>
                    <span style={{ marginBottom: '12px' }}>{data.wdate}</span>
                  </div>
                ))}
              </div>
            </section>
          )}
          <StyledImgSlider>
            <div className="mobile">
              <div className="mobile-image">
                <div
                  onClick={() => {
                    setClickImgCheck(true);
                    if (slideNum === 0) {
                      setSlideNum(-100);
                    } else {
                      setSlideNum((n) => n + 100);
                    }
                    setBeforeImgState(true);
                  }}
                  className="mobile-image-leftbtn"
                >
                  <img src={arrowLeft} alt="" />
                </div>

                <div
                  onClick={() => {
                    setClickImgCheck(true);
                    if (slideNum === -100) {
                      setSlideNum(0);
                    } else {
                      setSlideNum((n) => n - 100);
                    }

                    setNextImgState(true);
                  }}
                  className="mobile-image-rightbtn"
                >
                  <img src={arrowRight} alt="" />
                </div>
                <div className="mobile-image-div">
                  <div className="mobile-image-box">
                    <div className="mobile-image-box-div" ref={Refimg1}>
                      <img
                        onTouchStart={touchStart}
                        onTouchEnd={touchEnd}
                        src={gajaimg}
                      ></img>
                      <div className="mobile-intro">
                        <h4>국내 최대 규모의 렌탈 제품 보유</h4>
                        <h5>
                          350개 대기업 브랜드, 15,000여 가지의 상품
                          <br />
                          소득이 되는 렌탈 서비스.
                          <br />
                          지금 바로 시작하세요!
                        </h5>
                        <div className="mobile-button">
                          <Button justifyContent="flex-start">
                            <button onClick={_goGajaRental}>
                              최저가 렌탈 신청하기
                            </button>
                          </Button>
                        </div>
                      </div>
                    </div>
                    <div className="mobile-image-box-div" ref={Refimg2}>
                      <img
                        onTouchStart={touchStart}
                        onTouchEnd={touchEnd}
                        src={phoneimg}
                      ></img>
                      <div className="mobile-intro">
                        <h4>최신 휴대폰 VIP 전용관</h4>
                        <h5>
                          아이폰/갤럭시 최신 모델을
                          <br />
                          최대 95% 할인된 가격으로 구입하실 수 있습니다.
                        </h5>
                        <div className="mobile-button">
                          <Button justifyContent="flex-start">
                            <Link to="/partnership/hp/currentTelecom">
                              <button>휴대폰 간편 신청</button>
                            </Link>
                          </Button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {!isMobile && (
              <>
                <div className="pc2">
                  <h1>매우 쉽습니다.</h1>
                  <p>링크를 전달 하는 것만으로도 수익이 발생됩니다.</p>
                  <p>
                    링크는 누구에게나 전달이 가능하고 링크를 통해서 추가적인
                    수익을 발생시킬 수 있습니다.
                  </p>
                </div>

                <div
                  style={{ border: 'none', marginBottom: '0' }}
                  className="mobile"
                >
                  <div className="mobile-image">
                    <ChevronLeftIcon
                      onClick={() => {
                        setClickImgCheck(true);
                        if (slideNum === 0) {
                          setSlideNum(-100);
                        } else {
                          setSlideNum((n) => n + 100);
                        }
                        setBeforeImgState(true);
                      }}
                      className="mobile-image-leftbtn"
                    />
                    <ChevronRightIcon
                      onClick={() => {
                        setClickImgCheck(true);
                        if (slideNum === -100) {
                          setSlideNum(0);
                        } else {
                          setSlideNum((n) => n - 100);
                        }

                        setNextImgState(true);
                      }}
                      className="mobile-image-rightbtn"
                    />
                    <div className="mobile-image-div">
                      <div className="mobile-image-box">
                        <div className="mobile-image-box-div" ref={Refimg3}>
                          <img style={{ width: '100%' }} src={pcimg1}></img>
                          <div className="mobile-intro">
                            <div className="pc-vector">
                              <img
                                style={{ width: '27.43px', height: '54.86px' }}
                                src={Vector}
                              ></img>
                              <img
                                style={{ width: '27.43px', height: '54.86px' }}
                                src={Vector}
                              ></img>
                            </div>
                            <h3
                              style={{
                                width: '100%',
                                color: `${theme.colors.grey1}`,
                                fontFamily: 'NotoM',
                                fontSize: `${theme.fontSizes.H3}`,
                                marginBottom: '40px',
                              }}
                            >
                              스파이더플랫폼 정말 좋네요!
                              <br />
                              링크만 공유했는데 나도 모르게 수입이 발생되었어요.
                            </h3>
                            <div className="name">구독 홍**님</div>
                          </div>
                        </div>
                        <div className="mobile-image-box-div" ref={Refimg4}>
                          <img style={{ width: '100%' }} src={spider}></img>
                          <div className="mobile-intro">
                            <div className="pc-vector">
                              <img
                                style={{ width: '27.43px', height: '54.86px' }}
                                src={Vector}
                              ></img>
                              <img
                                style={{ width: '27.43px', height: '54.86px' }}
                                src={Vector}
                              ></img>
                            </div>
                            <h3
                              style={{
                                width: '100%',
                                color: `${theme.colors.grey1}`,
                                fontFamily: 'NotoM',
                                fontSize: `${theme.fontSizes.H3}`,
                                marginBottom: '40px',
                              }}
                            >
                              제가 이용만 해도 수익이 되는데
                              <br />
                              자랑만 해도 또 수익이 되더라구요.
                              <br />
                              스파이더플랫폼 모르면 손해에요.
                            </h3>
                            <div className="name">구독 홍**님</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </>
            )}
          </StyledImgSlider>

          <StyledService isMobile={isMobile} backgroundImage={serviceimg}>
            {isMobile && <img width="100%" src={serviceimg}></img>}
            <section className="section">
              <div className="intro">
                <h4>구독경제, 새로운 비즈니스</h4>
                <div style={{ textAlign: 'center' }}>
                  내가 구매해도, 누군가에게 추천을 해도 모두 이익입니다.
                  <br />
                  공급자의 수익을 소비자에게 모두 돌려드립니다.
                </div>
              </div>
              <div className="serviceform">
                <span>서비스 문의하기</span>
                <form onSubmit={_handleSubmit}>
                  <div className="input1">
                    <Input
                      name="name"
                      onChange={_handleInputChange}
                      width="48%"
                      placeholder="이름을 입력해주세요"
                    ></Input>
                    <Input
                      name="phoneNum"
                      onChange={_handleInputChange}
                      width="48%"
                      placeholder="휴대폰번호"
                    ></Input>
                  </div>
                  <div className="input2">
                    <Input
                      name="email"
                      onChange={_handleInputChange}
                      marginBottom="0.8em"
                      placeholder="이메일주소"
                    ></Input>
                    <TextArea
                      isMobile={isMobile}
                      height="229px"
                      name="contents"
                      onChange={_handleInputChange}
                      placeholder="문의내용을 입력하세요."
                    ></TextArea>
                  </div>
                  <Button>
                    <button>문의하기</button>
                  </Button>
                </form>
              </div>
            </section>
          </StyledService>
        </StyledContents>
      </Wrap>
    </Container>
  );
};

export default Home;
