import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../styles/board';

const CommiList = (props) => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '포인트 적립',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const authStatus = useSelector((store) => store.auth.authStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';
  const [state, setState] = useState({
    isLoading: true,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    pageCount: 1,
    rowTotal: 0,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      searchOptions: [],
      assort: '',
      assortOptions: [],
      status: props.location.status === undefined ? '' : props.location.status,
      statusOptions: [],
      minDate: '',
      maxDate: '',
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/member/get_commission_list.php';
    const params = {
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
      sponsId: authStatus.userId,
      searchValue: state.searchAble.searchValue,
      assort: state.searchAble.assort,
      status: state.searchAble.status,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        searchOptions: response.searchOptions,
        assortOptions: response.assortOptions,
        statusOptions: response.statusOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          { type: 'comboBox',width: '42%',name: 'status',value: searchAble.status,options: searchAble.statusOptions },
          { type: 'text',width: '42%',name: 'searchValue',value: searchAble.searchValue },
          { type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [{type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},],
      };

  const assort = {
    title: '수수료구분',
    items: [{type: 'comboBox',width: '100%',name: 'assort',value: searchAble.assort,options: searchAble.assortOptions,},],
  };
  const status = {
    title: '정산상태',
    items: [{type: 'comboBox',width: '100%',name: 'status',value: searchAble.status,options: searchAble.statusOptions,      },],
  };
  const wdate = {
    title: '등록일자',
    items: [
      {type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate,},
      {type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate,},
    ],
  };

  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, assort, status, wdate];

  // List 항목
  const listHeader = isMobile
    ? [
        { type: 'text', title: 'NO', name: 'no', width: '7%', align: 'center' },
        { type: 'text', title: '구분', name: 'assort', width: '20%', align: 'center' },
        { type: 'text', title: '회원ID', name: 'memId', width: '23%', align: 'center' },
        { type: 'text', title: '회원명', name: 'memName', width: '15%', align: 'center'},
        { type: 'text', title: '수수료', name: 'price', width: '10%', align: 'flex-end' },
        { type: 'text', title: '등록일자', name: 'wdate', width: '25%', align: 'center' },
      ]
    : [
        { type: 'checkbox', title: null, name: 'idx', width: '5%', align: 'center' },
        { type: 'text', title: 'NO', name: 'no', width: '5%', align: 'center' },
        { type: 'text', title: '구분', name: 'assort', width: '15%', align: 'center' },
        { type: 'text', title: '회원ID', name: 'memId', width: '15%', align: 'center' },
        { type: 'text', title: '회원명', name: 'memName', width: '15%', align: 'center' },
        { type: 'text', title: '포인트', name: 'price', width: '10%', align: 'center' },
        { type: 'text', title: '정산상태', name: 'status', width: '10%', align: 'center' },
        { type: 'text', title: '등록일자', name: 'wdate', width: '20%', align: 'center' },
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>포인트관리 〉 적립목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={false}
            isDeleteButton={false}
          />

          <ListView
            state={state}
            user={true}
            setState={setState}
            headers={listHeader}
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default CommiList;
