import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const BbsList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '게시판관리',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const auth = useSelector((store) => store.auth.login.userInfo);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    memId: auth.userId,
    memName: auth.userName,

    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    authWrite: false,
    authDelete: false,

    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchKey: '',
      searchValue: '',
      bbsCode: '',
      minDate: '',
      maxDate: '',
      searchOptions: [],
      bbsOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/bbs/get_bbs_list.php';
    const params = {
      memId: state.memId,
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
      bbsCode: state.searchAble.bbsCode,
      searchKey: state.searchAble.searchKey,
      searchValue: state.searchAble.searchValue,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      authWrite: response.authWrite,
      authDelete: response.authDelete,
      searchAble: {
        ...state.searchAble,
        bbsOptions: response.bbsOptions,
        searchOptions: response.searchOptions,
        rowTotal: response.rowTotal,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          {type: 'comboBox',width: '42%',name: 'searchKey',value: searchAble.searchKey,options: searchAble.searchOptions,},
          {type: 'text',width: '42%',name: 'searchValue',value: searchAble.searchValue,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [
          {type: 'comboBox',width: '50%',name: 'searchKey',value: searchAble.searchKey,options: searchAble.searchOptions,},
          {type: 'text',width: '50%',name: 'searchValue',value: searchAble.searchValue,},
        ],
      };
  const bbsCode = {
    title: '게시판구분',
    items: [
      {type: 'comboBox',width: '100%',name: 'bbsCode',value: searchAble.bbsCode,options: searchAble.bbsOptions,},
    ],
  };

  const writeDate = {
    title: '등록일자',
    items: [
      {type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate,},
      {type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate,},
    ],
  };

  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, bbsCode, writeDate];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',width: '10%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '20%',align: 'center',},
        {type: 'text',title: '글 제목',name: 'subject',width: '50%',align: 'left',},
        {type: 'text',title: '등록일자',name: 'wdate',width: '20%',align: 'center',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '3%',align: 'center',},
        { type: 'text', title: 'NO', name: 'no', width: '8%', align: 'center' },
        {type: 'text',title: '회원ID',name: 'memId',width: '10%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '10%',align: 'center',},
        {type: 'text',title: '글 제목',name: 'subject',width: '35%',align: 'left',},
        {type: 'text',title: '조회수',name: 'viewCount',width: '10%',align: 'center',},
        {type: 'text',title: '댓글수',name: 'replyCount',width: '10%',align: 'center',},
        {type: 'text',title: '등록일자',name: 'wdate',width: '10%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>게시글관리 〉 게시글목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={true}
            isDeleteButton={true}
            infoPath="/admin/bbs/input"
            deleteUrl="/api/bbs/set_bbs_delete.php"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            infoPath="/admin/bbs/view"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default BbsList;
