/*
  설명: 스파이더플랫폼 소개
  - 2021.05.28. 안예린 spider 리팩토링
*/
import React from 'react';
import styled from 'styled-components';
// css
import '../css/front.css';
// icon & images
import siteinfo_1 from '../images/siteinfo_1.jpg';

const SiteInfo = () => {
  return (
    <Container>
      <Wrap>
        <div className="cardWrap">
          <ImageSrc src={siteinfo_1} />
          <div className="textWrap">
            <div className="text11">소개</div>
            <div className="text08">
              가전, 인테리어, 교육, 예술품, 자동차 까지 실생활에 필요한 모든
              제품이 여기에 있습니다.
              <br />
              <br />
              이제는 소유의 시대에서 공유의 시대로 흐름이 바뀌고 있습니다.{' '}
              <br />
              고가의 제품을 구입하는 대신 빌려서 사용하는 것은 이제 합리적인
              소비를 지향하는 사람들에게는 흔한 일이 되었습니다.
              <br />
              스파이더플랫폼은 여기에서 한발 더 앞서서 수익까지 공유할 수 있는
              플랫폼을 제공합니다.
              <br />
              <br />
              스파이더플랫폼을 통해서 더 좋은 조건의 렌탈 서비스를 이용하시거나
              2차 수입으로 발전시킬 수 있도록 도움을 드립니다.
            </div>
          </div>
        </div>

        <div className="sectionWrap">
          <div className="sectionTitleRed">온라인구독플랫폼</div>
          <div className="sectionBar"></div>
        </div>
        <div className="cardWrap">
          <div className="text90" style={{ marginLeft: '2em' }}>
            월 신제품 구독료를 납부하고 렌탈 사업을 시작할 수 있습니다.
            <br />
            ​최소한의 비용으로 렌탈 사업을 지금 시작할 수 있기 때문에 ​사업을
            처음 시작하는 분들께 적합합니다.
            <br />
            모든 고객관리는 대기업에서 진행하므로 쉬운 사업이 가능합니다.
            <br />
            내가 필요할 때 렌탈은 무료, ​월 150~200만원의 추가 수익
          </div>
        </div>

        <div className="sectionWrap">
          <div className="sectionTitleRed">플랫폼MD</div>
          <div className="sectionBar"></div>
        </div>
        <div className="cardWrap">
          <div className="text90" style={{ marginLeft: '2em' }}>
            렌탈수수료, 월구독수수료, 유치수수료를 매월 받는 플랫폼MD 신청은
            별도 문의바랍니다.
            <br />
            플랫폼MD는 더 많은 사업의 기회를 제공받게 됩니다.
            <br />
            ​렌탈 사업으로 인세적 수익의 무한 확장이 가능한 플랫폼MD에 도전해
            보세요.
          </div>
        </div>
      </Wrap>
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  background: #fff;
  margin: 50px auto;

  * {
    div {
      line-height: 2;
    }
    .sectionTitleRed {
      height: 40px;
    }
  }

  @media (max-width: 768px) {
    margin-top: 50px;
  }

  @media (min-width: 769px) {
    /* margin-top: 60px; */
  }
`;

const Wrap = styled.div`
  display: flex;
  flex-direction: column;

  @media (max-width: 768px) {
    width: 95%;
  }

  @media (min-width: 769px) {
    width: 860px;
  }
`;

const ImageSrc = styled.img`
  width: 100%;
`;

export default SiteInfo;
