import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import TreeItem from '@material-ui/lab/TreeItem';
import TreeView from '@material-ui/lab/TreeView';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
// 개발자 라이브러리
//import { setBoardInit } from '../../redux/actions/board';
import _axios from '../../utils/axios';
// css 스타일 가이드
import theme from '../../styles/theme';
// icon
import Add from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import HomeIcon from '@material-ui/icons/Home';
import MemberIcon from '@material-ui/icons/MenuOutlined';
import MobileIcon from '@material-ui/icons/Smartphone';
import PointIcon from '@material-ui/icons/Timeline';
import SaleIcon from '@material-ui/icons/Payment';
import SettingsIcon from '@material-ui/icons/Settings';
import AdinsuIcon from '@material-ui/icons/Toys';
import BoardIcon from '@material-ui/icons/EventNote';
import CmsIcon from '@material-ui/icons/OndemandVideo';
import CommissionIcon from '@material-ui/icons/MonetizationOn';
import DirectionsCarIcon from '@material-ui/icons/DirectionsCar';
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';

const icons = {
  HomeIcon: HomeIcon,
  SettingsIcon: SettingsIcon,
  MemberIcon: MemberIcon,
  CmsIcon: CmsIcon,
  CommissionIcon: CommissionIcon,
  BoardIcon: BoardIcon,
  MobileIcon: MobileIcon,
  PointIcon: PointIcon,
  SaleIcon: SaleIcon,
  AdinsuIcon: AdinsuIcon,
  DirectionsCarIcon: DirectionsCarIcon,
  PeopleAltIcon: PeopleAltIcon,
};

const useTreeItemStyles = makeStyles((theme) => ({
  root: {
    color: theme.palette.text.secondary,
    '&:hover > $content': {
      backgroundColor: '#fff',
    },
    '&:focus > $content, &$selected > $content': {},
    '&:focus > $content $label, &:hover > $content $label, &$selected > $content $label':
      {
        backgroundColor: 'transparent',
      },
  },
  content: {
    color: theme.palette.text.secondary,
    flexDirection: 'row-reverse',
    background:
      'linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), #F8F8F8',
    paddingRight: theme.spacing(1),
    fontWeight: theme.typography.fontWeightMedium,
    '$expanded > &': {
      fontWeight: theme.typography.fontWeightRegular,
    },
  },
  group: {
    marginLeft: 0,
    '& $content': {
      paddingLeft: theme.spacing(2),
    },
  },
  expanded: {},
  selected: {},
  label: {
    fontWeight: 'inherit',
    color: 'inherit',
  },
  labelRoot: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0.5, 0),
  },
  labelRootSmall: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0.5, 0),
  },
  labelIcon: {
    marginRight: theme.spacing(1),
    position: 'relative',
  },
  labelText: {
    fontWeight: 'inherit',
    fontSize: '1em',
    flexGrow: 1,
  },
}));

function StyledTreeItem(props) {
  const classes = useTreeItemStyles();
  const { labelText, labelIcon: LabelIcon, color, bgColor, ...other } = props;

  return (
    <TreeItem
      label={
        <div
          className={
            props.menudivision === 1
              ? classes.labelRoot
              : classes.labelRootSmall
          }
          style={{
            height: '60px',
            color: `${theme.colors.navyBox}`,
          }}
        >
          <span style={{ marginRight: '1em' }}>{props.smallmenuicon}</span>
          {LabelIcon && (
            <LabelIcon
              style={{ fontSize: '1em' }}
              className={classes.labelIcon}
            />
          )}
          {props.menudivision === 1 ? (
            <Typography
              style={{
                position: 'relative',
                fontSize: '14px',
                color: '#404040',
                display: 'flex',
                alignItems: 'center',
              }}
              variant="body2"
              className={classes.labelText}
            >
              {labelText}
            </Typography>
          ) : (
            <Typography
              style={{
                position: 'relative',
                fontSize: '14px',
                fontFamily: 'NotoR',
              }}
              variant="body2"
              className={classes.labelText}
            >
              {labelText}
            </Typography>
          )}
        </div>
      }
      style={{
        '--tree-view-color': color,
        '--tree-view-bg-color': 'bgColor',
        width: '100%',
        borderBottom: '1px solid #e2e2e2',
      }}
      classes={{
        root: classes.root,
        content: classes.content,
        expanded: classes.expanded,
        selected: classes.selected,
        group: classes.group,
        label: classes.label,
      }}
      {...other}
    />
  );
}

StyledTreeItem.propTypes = {
  bgColor: PropTypes.string,
  color: PropTypes.string,
  labelInfo: PropTypes.string,
  labelText: PropTypes.string.isRequired,
};

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
  },
});

function TreePaint({ no, menuName, iconName, linkTo, children, onCloseMenu }) {
  const history = useHistory();

  return (
    <section
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <StyledTreeItem
        style={{
          width: '100%',
          borderBottom: '1px solid #e2e2e2',
        }}
        nodeId={no}
        labelText={menuName}
        labelIcon={icons[iconName]}
        onMouseDown={() => {}}
        onMouseUp={() => {
          //setBoardInit();
          linkTo !== '' && onCloseMenu && onCloseMenu({ menuOpen: false });
          linkTo !== '' &&
            history.push({
              pathname: linkTo,
            });
        }}
      >
        {children.map((row) => (
          <StyledTreeItem
            style={{
              borderBottom: 'none',
              display: 'flex',
              alignItems: 'center',
            }}
            key={row.no}
            smallmenuicon={''}
            nodeId={row.no}
            labelText={row.menuName}
            labelIcon={icons[row.iconName]}
            color="#e3742f"
            bgColor="#fff"
            onMouseDown={() => {}}
            onMouseUp={() => {
              //setBoardInit();
              onCloseMenu && onCloseMenu({ menuOpen: false });
              history.push({
                pathname: row.linkTo,
              });
            }}
          />
        ))}
      </StyledTreeItem>
    </section>
  );
}

// Tree menu 시작
function TreeMenu({ onCloseMenu, mobile }) {
  const classes = useStyles();
  const authStatus = useSelector((store) => store.auth.authStatus);

  const [state, setState] = useState({
    userId: authStatus.userInfo.userId,
    isLoading: true,
    loadData: [],
  });

  const _getData = async () => {
    const url = '/api/account/get_menu.php';
    const params = {
      userId: state.userId,
    };

    await _axios(url, params).then((response) => {
      setState({
        ...state,
        loadData: response.data,
        isLoading: false,
      });
    });
  };

  useEffect(() => {
    if (state.isLoading) {
      _getData();
    }
  }, [state.isLoading]);

  return (
    <TreeView
      style={
        mobile
          ? { paddingTop: '105px' }
          : { overflowY: 'scroll', height: '500px' }
      }
      className={classes.root}
      defaultExpanded={['0']}
      defaultCollapseIcon={
        <RemoveIcon style={{ color: theme.colors.navyBox }} />
      }
      defaultExpandIcon={<Add style={{ color: theme.colors.navyBox }} />}
    >
      {state.loadData.map((row) => (
        <TreePaint
          key={row.no}
          no={row.no}
          menuName={row.menuName}
          iconName={row.iconName}
          linkTo={row.linkTo}
          children={row.children}
          onCloseMenu={onCloseMenu}
        />
      ))}
    </TreeView>
  );
}

export default TreeMenu;
