import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const SalesList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '매출관리',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      searchOptions: [],
      assort: '',
      assortOptions: [],
      status: '',
      statusOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/admin/sales/get_sales_list.php';
    const params = {
      searchValue: state.searchAble.searchValue,
      assort: state.searchAble.assort,
      status: state.searchAble.status,
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        searchOptions: response.searchOptions,
        assortOptions: response.assortOptions,
        statusOptions: response.statusOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          {type: 'text',width: '84%',name: 'searchValue',value: searchAble.searchValue,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [{type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},],
      };
  const assort = {
    title: '매출구분',
    items: [{type: 'comboBox',width: '100%',name: 'assort',value: searchAble.assort,options: searchAble.assortOptions,},],
  };
  const status = {
    title: '정산상태',
    items: [
      {type: 'comboBox',width: '100%',name: 'status',value: searchAble.status,options: searchAble.statusOptions,},
    ],
  };

  const searchFields = isMobile ? [searchValue] : [searchValue, assort, status];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',align: 'center',width: '10%',},
        {type: 'text',title: '구분',name: 'assort',align: 'center',width: '30%',},
        {type: 'text',title: '회원명',name: 'memName',align: 'center',width: '15%',},
        {type: 'text',title: '회원ID',name: 'memId',align: 'center',width: '20%',},
        {type: 'text',title: '등록일자',name: 'wdate',align: 'center',width: '25%',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '5%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '5%',align: 'center',},
        {type: 'text',title: '구분',name: 'assort',width: '10%',align: 'center',},
        {type: 'text',title: '스폰서ID',name: 'sponsId',width: '10%',align: 'center',},
        {type: 'text',title: '스폰서명',name: 'sponsName',width: '15%',align: 'center',},
        {type: 'text',title: '회원ID',name: 'memId',width: '10%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '15%',align: 'center',},
        {type: 'text',title: '매출금액',name: 'price',width: '10%',align: 'center',},
        {type: 'text',title: '정산대기',name: 'status',width: '10%',align: 'center',},
        {type: 'text',title: '등록일자',name: 'wdate',width: '10%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>매출정산 〉 매출원장</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={true}
            isDeleteButton={false}
            infoPath="/admin/sales/info"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            infoPath="/admin/sales/info"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default SalesList;
