/*
  설명: 요금 정보
  - 2021.05.20. 안예린, import 수정
*/
import React, { useCallback, useEffect, useState } from 'react';
import SwiperCore, { Controller } from 'swiper';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import Tabs from 'react-bootstrap/Tabs';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
import _axios from '../../../utils/axios';
// css
import {
  BigLabel,
  Container,
  MiddleLabel,
  NextButton,
  ParentWrap,
  SwiperMiddleLabel,
} from '../../../css/common-component';
// icon & images
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { SwiperSlide } from 'swiper/react';
import { faAngleDoubleDown } from '@fortawesome/free-solid-svg-icons';

SwiperCore.use([Controller]);
export default function GuideTable(props) {
  const dispatch = useDispatch();

  const {
    changeTelecom,
    modelCode,
    cardCode,
    capacityCode,
    chargeCode,
    benefitAssort,
    marginPrice,
    assort,
    installment,
    discountType,
  } = useSelector((store) => store.hp);
  const [data, setData] = useState();

  const [isQuestion, setIsQuestion] = useState({ show: false, data: [] });

  const _goInsterInfo = () => {
    props.history.push('/partnership/hp/insertInfo');
  };

  const _getData = useCallback(async () => {
    const url = '/api/hp/get_charge_info.php';

    const params = {
      changeTelecom: changeTelecom,
      assort: assort,
      modelCode: modelCode,
      capacityCode: capacityCode,
      chargeCode: chargeCode,
      cardCode: cardCode,
      installment: installment,
      discountType: discountType,
      benefitAssort: benefitAssort,
      marginPrice: marginPrice,
    };

    const { data } = await _axios(url, params);
    // setData(data);
    return data;
  }, [
    assort,
    benefitAssort,
    capacityCode,
    cardCode,
    changeTelecom,
    chargeCode,
    discountType,
    installment,
    marginPrice,
    modelCode,
  ]);

  useEffect(() => {
    let mounted = true;

    _getData().then((res) => {
      if (res && mounted) {
        setData(res);
        dispatch(actions.setBankInfo(res.BankInfo));
      }
    });

    return () => (mounted = false);
  }, [_getData, dispatch]);

  const _onDescripsionShow = (item) => {
    setIsQuestion({ show: true, data: item });

    // setIsQuestion({ show: true, commission: commission });
  };

  const _onChargeInfoDescripsion = (item) => {
    let add = '최초 개통 요금, 의무 사용 기한 4개월 \n\n';
    setIsQuestion({ show: true, data: add + item });

    // setIsQuestion({ show: true, commission: commission });
  };

  const _toggleQuestionDialog = () => {
    isQuestion.show
      ? setIsQuestion({ show: false })
      : setIsQuestion({ show: true });
  };

  const _numberFormat = (value) => {
    const temp = value.replace(/,/gi, '');

    return parseInt(temp);
  };

  const _monthChargeFee = (chargePrice, discountPrice) => {
    let a = _numberFormat(chargePrice);
    let b = _numberFormat(discountPrice);

    return new Intl.NumberFormat().format(a + b);
  };

  const refs = data?.chargeInfo?.reduce((acc, value, index) => {
    acc[index] = React.createRef();
    return acc;
  }, {});

  return (
    <Container
      style={{ paddingTop: '82px', display: 'block', paddingBottom: '82px' }}
      className="page"
    >
      <BigLabel
        margin=" auto auto 27px 20px"
        width="auto"
        style={{ fontWeight: 'normal', fontSize: '20px', textAlign: 'left' }}
        bold="normal"
      >
        <span style={{ borderBottom: '1px solid' }}>
          {data?.modelName} 요금 정보
        </span>
      </BigLabel>
      <ParentWrap
        height="auto"
        padding="0"
        width="auto"
        style={{
          margin: '0 auto',
          backgroundColor: 'transparent',
          border: 'none',
        }}
      >
        {/* <Swiper
          spaceBetween={10}
          slidesPerView={1}
          centeredSlides={true}
          onSwiper={setControlledSwiper}
          onSlideChange={(swiper) => {
            setKey(swiper.activeIndex);
          }}
        > */}
        {data?.chargeInfo?.map((item, index) => {
          return (
            <React.Fragment key={index}>
              <StyledChargeName>
                <span className="title">{item.chargeName} 사용시</span>
              </StyledChargeName>
              {/* <StyledTabs
                id="controlled-tab-example"
                activeKey={key}
                onSelect={(k) => handleClick(k)}
              >
                <Tab eventKey="0" title="개통요금제"></Tab>
                <Tab eventKey="1" title="선택요금제"></Tab>
              </StyledTabs> */}

              <SwiperSlide
                ref={refs[index]}
                style={{
                  marginBottom: '7px',
                  height: 'auto',
                  backgroundColor: '#fafafa',
                  borderTop: 'solid 1px #f2f2f2',
                  borderRadius: '5px',
                }}
                key={index}
              >
                <div style={{ width: '90%', margin: '35px auto 0 auto' }}>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '7px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{
                        fontSize: '16px',
                        fontWeight: 'bold',
                        color: '#000',
                      }}
                    >
                      휴대폰 구매 정보
                    </StyledMiddleLabel>
                  </div>

                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '7px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px' }}
                    >
                      출고가
                    </StyledMiddleLabel>
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px' }}
                    >
                      {data?.mobileInfo?.factoryPrice}
                    </StyledMiddleLabel>
                  </div>

                  {data?.mobileInfo?.discount.map((item) => (
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        marginBottom: '7px',
                      }}
                      key={item?.discountName}
                    >
                      <StyledMiddleLabel
                        width="auto"
                        height="23px"
                        style={{ fontSize: '16px' }}
                      >
                        {item?.discountName}
                      </StyledMiddleLabel>
                      <StyledMiddleLabel
                        width="auto"
                        height="23px"
                        style={{ fontSize: '16px' }}
                      >
                        {item?.discountPrice}
                      </StyledMiddleLabel>
                    </div>
                  ))}

                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '7px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px' }}
                    >
                      할부원금(a)
                    </StyledMiddleLabel>
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px' }}
                    >
                      {data?.mobileInfo?.installmentPrice}
                    </StyledMiddleLabel>
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '7px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px' }}
                    >
                      할부 수수료(b)
                    </StyledMiddleLabel>
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px' }}
                    >
                      {data?.mobileInfo?.monthInstFee}
                    </StyledMiddleLabel>
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '18.5px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{
                        fontSize: '16px',
                        fontWeight: 'bold',
                        color: '#000f34',
                      }}
                    >
                      A.월 단말기 할부금(a+b/{installment}개월)
                    </StyledMiddleLabel>
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontSize: '16px', fontWeight: 'bold' }}
                    >
                      {data?.mobileInfo?.monthInstPrice}
                    </StyledMiddleLabel>
                  </div>
                  <div
                    style={{
                      width: 'auto',
                      height: '0',
                      borderTop: 'solid 1px rgba(0,0,0,.1)',
                      marginBottom: '18px',
                    }}
                  ></div>
                </div>

                <div style={{ width: '90%', margin: '0 auto' }}>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '7px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{
                        fontSize: '16px',
                        fontWeight: 'bold',
                        color: '#000',
                      }}
                    >
                      요금 정보
                    </StyledMiddleLabel>
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '9px',
                    }}
                  >
                    <SwiperMiddleLabel
                      width="auto"
                      height="23px"
                      onClick={() =>
                        _onChargeInfoDescripsion(item.chargeExplain)
                      }
                    >
                      <span style={{ borderBottom: '1px solid #979797' }}>
                        <span
                          style={{
                            display: 'flex',
                            width: 'fit-content',
                          }}
                        >
                          <span
                            style={{
                              borderBottom: '1px solid #979797',
                              marginRight: '5px',
                              fontWeight: 'bold',
                              color: '#404040',
                            }}
                          >
                            {item?.chargeName}
                          </span>

                          <li
                            style={{
                              listStyle: 'none',
                              color: '#e2e2e2',
                            }}
                          >
                            <span className="k-icon k-i-question"></span>
                          </li>
                        </span>
                      </span>
                    </SwiperMiddleLabel>
                    <SwiperMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontWeight: 'bold', color: '#404040' }}
                    >
                      {item?.chargePrice}
                    </SwiperMiddleLabel>
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '9px',
                    }}
                  >
                    <SwiperMiddleLabel
                      width="auto"
                      height="23px"
                      style={{ fontWeight: 'bold', color: '#404040' }}
                    >
                      월 요금 할인(선택약정)
                    </SwiperMiddleLabel>
                    <SwiperMiddleLabel width="auto" height="23px">
                      {item?.discountPrice}
                    </SwiperMiddleLabel>
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '18.5px',
                    }}
                  >
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{
                        fontSize: '16px',
                        fontWeight: 'bold',
                        color: '#000f34',
                      }}
                    >
                      B.월 납부 요금
                    </StyledMiddleLabel>
                    <StyledMiddleLabel
                      width="auto"
                      height="23px"
                      style={{
                        fontSize: '16px',
                        fontWeight: 'bold',
                        color: '#000f34',
                      }}
                    >
                      {_monthChargeFee(item?.chargePrice, item?.discountPrice)}
                    </StyledMiddleLabel>
                  </div>
                  <div
                    style={{
                      width: 'auto',
                      height: '0',
                      borderTop: 'solid 1px rgba(0,0,0,.1)',
                      marginBottom: '18px',
                    }}
                  ></div>

                  {item?.discount.map((i) => (
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        marginBottom: '9px',
                      }}
                      key={i?.discountName}
                    >
                      <SwiperMiddleLabel width="auto" height="23px">
                        {i?.discountName}
                      </SwiperMiddleLabel>
                      <SwiperMiddleLabel width="auto" height="23px">
                        {i?.discountPrice}
                      </SwiperMiddleLabel>
                    </div>
                  ))}

                  {item?.addService?.length > 0 && (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                        }}
                      >
                        <SwiperMiddleLabel
                          width="auto"
                          height="23px"
                          style={{ fontWeight: 'bold', color: '#000' }}
                        >
                          부가서비스 정보
                        </SwiperMiddleLabel>
                      </div>

                      <div style={{ marginBottom: '20px', marginTop: '8px' }}>
                        {item?.addService?.map((i) => (
                          <div
                            style={{
                              display: 'flex',
                              justifyContent: 'space-between',
                              marginBottom: '9px',
                              color: '#404040',
                            }}
                            key={i?.serviceName}
                          >
                            <SwiperMiddleLabel
                              width="185px"
                              height="auto"
                              margin={'0px 0px 0px 0px'}
                              onClick={() => _onDescripsionShow(i.descript)}
                            >
                              <span
                                style={{
                                  display: 'flex',
                                  width: 'fit-content',
                                }}
                              >
                                <span
                                  style={{
                                    borderBottom: '1px solid #979797',
                                    marginRight: '5px',
                                    color: '#404040',
                                    fontWeight: 'bold',
                                  }}
                                >
                                  {i?.serviceName}
                                </span>

                                <li
                                  style={{
                                    listStyle: 'none',
                                    color: '#e2e2e2',
                                  }}
                                >
                                  <span className="k-icon k-i-question"></span>
                                </li>
                              </span>
                            </SwiperMiddleLabel>
                            <SwiperMiddleLabel
                              width="auto"
                              height="auto"
                              style={{ fontWeight: 'bold', color: '#404040' }}
                            >
                              {i?.servicePrice}
                            </SwiperMiddleLabel>
                          </div>
                        ))}
                        <div
                          style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            marginBottom: '18.5px',
                          }}
                        >
                          <StyledMiddleLabel
                            width="auto"
                            height="23px"
                            style={{
                              fontSize: '16px',
                              fontWeight: 'bold',
                              color: '#000f34',
                            }}
                          >
                            C.합계
                          </StyledMiddleLabel>
                          <StyledMiddleLabel
                            width="auto"
                            height="23px"
                            style={{
                              fontSize: '16px',
                              fontWeight: 'bold',
                              color: '#000f34',
                            }}
                          >
                            {item?.sumAddService}
                          </StyledMiddleLabel>
                        </div>

                        <div
                          style={{
                            width: 'auto',
                            height: '0',
                            borderTop: 'solid 1px rgba(0,0,0,.1)',
                            marginBottom: '18px',
                          }}
                        ></div>
                      </div>
                    </>
                  )}

                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-around',
                      marginBottom: '4px',
                      backgroundColor: '#000f34',
                      borderRadius: '3px',
                    }}
                  >
                    <SwiperMiddleLabel
                      width="auto"
                      height="43px"
                      style={{
                        color: '#fff',
                        fontSize: '18px',
                        fontWeight: 'bold',
                        alignItems: 'center',
                        display: 'flex',
                      }}
                    >
                      {item?.addService
                        ? '최종 월청구 금액(A+B+C)'
                        : '최종 월청구 금액(A+B)'}
                    </SwiperMiddleLabel>
                    <SwiperMiddleLabel
                      width="auto"
                      height="43px"
                      style={{
                        fontWeight: 'bold',
                        color: '#fff',
                        fontSize: '18px',
                        alignItems: 'center',
                        display: 'flex',
                      }}
                    >
                      {item.monthCharge}
                    </SwiperMiddleLabel>
                  </div>
                  {/* <SwiperMiddleLabel
                width="auto"
                height="auto"
                margin="0 0 5px 0"
                style={{ fontSize: '14px' }}
              >
                기본데이터: {item?.chargeExplain}
              </SwiperMiddleLabel> */}

                  {index === 0 ? (
                    <div>
                      <SwiperMiddleLabel
                        width="auto"
                        height="auto"
                        margin="0 0 18.5px 0"
                        style={{ fontSize: '14px' }}
                      >
                        초기 개통 요금제, 의무 사용 기한 : 4개월
                      </SwiperMiddleLabel>

                      <StyledDownArrow>
                        <FontAwesomeIcon
                          icon={faAngleDoubleDown}
                          size="2x"
                          style={{ color: '#000', margin: '0px 11.7px' }}
                        />
                        <span>
                          {discountType === 'S'
                            ? '185일 이후 변경 가능'
                            : '125일 이후 변경 가능'}
                        </span>
                      </StyledDownArrow>
                    </div>
                  ) : (
                    <div>
                      <SwiperMiddleLabel
                        width="auto"
                        height="auto"
                        margin="0 0 18.5px 0"
                        style={{ fontSize: '14px' }}
                      >
                        개통 4개월 이후 요금제 변경 및 부가서비스 해지시 월
                        청구되는 금액입니다. 요금제 변경 및 부가서비스 해지는
                        가입자 본인이 직접 하셔야 됩니다.
                      </SwiperMiddleLabel>
                    </div>
                  )}
                  {/* <div
                    style={{
                      width: 'auto',
                      height: '0',
                      borderTop: 'solid 2px rgba(0,0,0,.1)',
                      marginBottom: '18px',
                    }}
                  ></div> */}
                </div>
              </SwiperSlide>
            </React.Fragment>
          );
        })}
        {/* </Swiper> */}

        <NextButton
          width="118px"
          height="48px"
          margin="25px 23px 43px 0px"
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            float: 'right',
          }}
          onClick={() => _goInsterInfo()}
        >
          다음
        </NextButton>

        {isQuestion.show && (
          <Dialog
            title={isQuestion.data.serviceName}
            id="hpModelOption"
            width={'256px'}
            height={'318px'}
          >
            <SwiperMiddleLabel
              style={{ color: '#000f34', fontSize: '15px', lineHeight: '1.27' }}
            >
              {isQuestion?.data?.split('\n').map((line, index) => {
                return (
                  <span key={index}>
                    {line}
                    <br />
                  </span>
                );
              })}

              {/* {isQuestion.data.descript} */}
            </SwiperMiddleLabel>

            <DialogActionsBar id="hpModelOptionButton">
              <button className="k-button" onClick={_toggleQuestionDialog}>
                확인
              </button>
            </DialogActionsBar>
          </Dialog>
        )}
      </ParentWrap>
    </Container>
  );
}

const StyledTabs = styled(Tabs)`
  padding-left: 20px;

  border-bottom: solid 1px #f2f2f2;
  .nav-link:focus,
  .nav-tabs .nav-link:hover {
    border-color: transparent;
  }
  .nav-link {
    color: #979797;
  }
  .nav-link.active {
    background-color: transparent;
    color: #000f34;
    font-weight: bold;
    border: 0;
    border-bottom: 7px solid #000f34;
  }
`;

const StyledChargeName = styled.div`
  width: 90%;
  margin: 0px auto 5px;
  .title {
    font-size: 18px;
    color: #000f34;
    border-bottom: 5px solid #000f34;
    width: fit-content;
  }
`;

const StyledMiddleLabel = styled(MiddleLabel)`
  font-weight: bold;
`;

const StyledDownArrow = styled.div`
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  margin-bottom: 27px;
  span {
    font-size: 18px;
    color: #000f34;
    align-self: center;
    font-weight: bold;
  }
`;
