/*
  설명: 로그인전 home 페이지 styled-component
  - 2021.05.20. 양민석,  디자인 리뉴얼
*/
// 라이브러리
import styled from 'styled-components';
// css 가이드
import theme from '../theme';
//이미지
import image1mobile from '../../images/newImage/mainBeforeLogin/image1mobile.jpg';
import image1pc from '../../images/newImage/mainBeforeLogin/image1pc.jpg';

export const StyledContents = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: ${theme.colors.lightGrey};

  @media (min-width: 769px) {
    background-color: ${theme.colors.white};
    margin-top: 100px;
  }

  .section1 {
    width: 100%;
    height: 27em;
    margin-bottom: 30px;
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(${image1mobile});

    @media (min-width: 769px) {
      background-image: url(${image1pc});
      height: 630px;
      margin-bottom: 100px;
    }

    div {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 75%;
      height: 65%;
      background-color: rgba(255, 255, 255, 0.5);
      margin-top: 3.2em;

      @media (min-width: 769px) {
        margin-top: 0;

        h3 {
          margin-bottom: 20px;
          ${theme.H1};
          color: ${theme.colors.navy};
        }
        p {
          ${theme.H3};
          color: ${theme.colors.black1};
        }
      }

      @media (max-width: 767px) {
        h3 {
          font-size: ${theme.fontSizes.H3};
          font-family: NotoM;
          color: ${theme.colors.navy};
          margin-bottom: 1.5em;
        }
        p {
          font-size: ${theme.fontSizes.Title};
          font-family: NotoM;
          color: ${theme.colors.black1};
        }
      }
    }
  }
  // 모바일 공지사항 섹션
  .mobileBoard {
    width: 90%;
    height: 246px;
    padding: 30px 10px 10px 10px;
    display: flex;
    border-radius: 3px;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    margin-bottom: 5em;
    background-color: ${theme.colors.white};
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);

    // 공지사항, 전체보기 섹션
    .div1 {
      width: 90%;
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.5em;
      h4 {
        ${theme.H4};
      }
      
      .div1-button {
        font-size: ${theme.fontSizes.Button};
        font-family: NotoM;
        color: ${theme.colors.royalblue};
        display: flex;
        align-items: center;
        button {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 1em;
          height: 1em;
          border: none;
          border-radius: 50%;
          background-color: ${theme.colors.royalblue};
          color: ${theme.colors.white};
          margin-left: 0.3em;
        }
      }
      border-bottom: 0.5px solid ${theme.colors.grey3};
    }
  }

  // 공지사항 게시물 섹선
  .div2 {
    width: 90%;
    overflow: hidden;

    div {
      display: flex;
      justify-content: space-between;
      ${theme.Body1};
      padding-right: 10px;
    }
  }
`;

// 렌탈 섹션
export const StyledImgSlider = styled.section`
  /* @media (min-width: 769px) {
    border: 10px solid ${theme.colors.boxGray};
  } */
  width: 90%;
  @media (min-width: 769px) {
    width: 100%;
  }
  .mobile {
    // 소개이미지 슬라이더
    @media (min-width: 769px) {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 675px;
      border: 10px solid ${theme.colors.boxGray};
      margin-bottom: 100px;
    }
    .mobile-image {
      width: 100%;
      height: 100%;
      color: ${theme.colors.boxGray};
      overflow: hidden;
      position: relative;
      .mobile-image-leftbtn {
        position: absolute;
        left: 0;
        top: 15%;
        font-size: 3em;
        z-index: 99;
        cursor: pointer;
        @media (min-width: 769px) {
          top: 45%;
        }
      }

      .mobile-image-rightbtn {
        position: absolute;
        right: 0;
        top: 15%;
        font-size: 3em;
        z-index: 99;
        cursor: pointer;
        @media (min-width: 769px) {
          top: 45%;
        }
      }
      .mobile-image-div {
        width: 200%;
        height: 100%;
        display: flex;

        align-items: center;
        .mobile-image-box {
          display: flex;
          height: 100%;
          width: 100%;
          div {
            width: 100%;
            @media (min-width: 769px) {
              display: flex;
              align-items: center;
            }
            transition: all 1s;
            img {
              object-fit: contain;
              width: 100%;
              @media (min-width: 769px) {
                height: 57%;
                margin-right: 9.14px;
              }
              height: 17em;
            }
          }
        }
      }
    }
    .name {
      font-size: ${theme.fontSizes.Body1};
      font-family: NotoM;
      color: ${theme.colors.grey2};
    }
    // 소개 글
    .mobile-intro {

      @media (min-width: 769px) {
        display: flex;
        flex-direction: column;
      }

      h4 {
        width: 100%;
        font-size: ${theme.fontSizes.H4};
        font-family: NotoM;
        color: ${theme.colors.black1};
        display: flex;
        margin-top: 5em;
        margin-bottom: 1.5em;
        border-bottom: 0.5px solid ${theme.colors.grey3};

        @media (min-width: 769px) {
          border-bottom: none;
          margin-top: 0;
          margin-bottom: 1em;
          font-size: ${theme.fontSizes.H1};
          font-family: NotoB;
          color: ${theme.colors.grey1};

         
        }
        padding-bottom: 0.2em;
      }

      h5 {
        width: 100%;
        height: 60px;
        ${theme.Title};
        color: ${theme.colors.grey2} ;

        @media (min-width: 769px) {
          margin-bottom: 3em; 
          ${theme.H5};
          height: auto;
        }
      }
    }

    // 서비스 신청 버튼
    .mobile-button {

      div {
        display: flex;
        justify-content: center;
        align-items: center; 
        
        @media (min-width: 769px) {
          justify-content: flex-start;
          height: auto;
        }

        button {
          /* width: 12em;
          height: 3em; */
          font-size: ${theme.fontSizes.Title};
          font-family: NotoM;
          color: ${theme.colors.white};
          background-color: ${theme.colors.navyBox};
          border: none;
        }
      }
    }
  }

  .pc {
    width: 100%;
    height: 30em;
    border: 10px solid ${theme.colors.boxGray};
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: ${theme.colors.boxGray};
    margin-bottom: 6em;
    .pc-div {
      width: 85%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 50%;
        height: 80%;
        margin-right: 2em;
      }

      .pc-intro {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 50%;
        height: 80%;
        color: ${theme.colors.black1};

        .name {
          font-size: ${theme.fontSizes.Body1};
          font-family: NotoM;
          color: ${theme.colors.black1};
        }
        h3 {
          font-size: ${theme.fontSizes.H3};
          font-family: NotoM;
          color: ${theme.colors.black1};
          margin-bottom: 1em;
        }
        .pc-vector {
          display: flex;
          img {
            width: 10%;
          }
        }
        h1 {
          font-size: ${theme.fontSizes.H1};
          font-family: NotoB;
          color: ${theme.colors.black1};
        }

        h5 {
          font-size: ${theme.fontSizes.H5};
          font-family: NotoM;
          color: ${theme.colors.grey3};
        }
      }
    }
    .mobile-image-leftbtn {
      position: absolute;
      left: 0;
      font-size: 3em;
    }

    .mobile-image-rightbtn {
      position: absolute;
      right: 0;
      font-size: 3em;
    }
  }

  .pc2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
      font-size: ${theme.fontSizes.H1};
      font-family: NotoB;
      color: ${theme.colors.black1};
      margin-bottom: 1em;
    }

    p {
      ${theme.H5};
    }
  }
`;
// 서비스 문의 섹션
export const StyledService = styled.section`
  background-image: ${(props) =>
    props.isMobile ? '' : 'url(' + props.backgroundImage + ')'};
  background-position: center;
  background-size: cover;
  width: 90%;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 879px;
  @media (min-width: 769px) {
    width: 100%;
  }
  .section {
    width: 100%;
    
    @media (min-width: 769px) {
      width: 627px;
    }
  }
  .intro {
    margin-top: 2em;
    margin-bottom: 2em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h4 {
      font-size: ${theme.fontSizes.H4};
      font-family: NotoM;
      color: ${theme.colors.navy};
      margin-bottom: 1.5em;
    }
    div {
      font-size: ${theme.fontSizes.Body1};
      font-family: NotoR;
      color: ${theme.colors.black1};
    }
    @media (min-width: 769px) {
      h4 {
        font-size: ${theme.fontSizes.H1};
        font-family: NotoB;
        color: ${theme.colors.navy};
        margin-bottom: 1.5em;
      }
      div {
        font-size: ${theme.fontSizes.H5};
        font-family: NotoM;
        color: ${theme.colors.black1};
      }
    }
  }

  .serviceform {
    width: 100%;

    span {
      display: block;
      ${theme.H5};
      color: ${theme.colors.black1};
      margin-bottom: 1em;
    }

    .input1 {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.7em;
    }

    .input2 {
      display: flex;
      flex-direction: column;
      margin-bottom: 0.7em;
    }
  }
`;
