import * as types from "../actions/BoardTypes";

const initialState = {
  boardStatus: {
    searchAble: {},
  }
};

export default function board(state = initialState, action) {
  switch (action.type) {
    case types.SET_BOARD:
      console.log(action.searchAble);
      return {
        ...state,
        boardStatus: {
          isLoading: false,
          searchAble: {
            ...action.searchAble,
          }
        },
      };

    default:
      return state;
  }
}
