import React, { useEffect, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Input, RadioGroup, TextArea } from '@progress/kendo-react-inputs';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import AllianceCardImgUpload from './AllianceCardImgUpload';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const AllianceCardInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,

    cardCode: '',
    cardName: '',
    telecom: '',
    usePrice: '',
    discountPrice: '',
    discountPrice: '',
    thumbnail: '',
    cardExplain: '',
    useYn: '',

    telecomOptions: [],
    useOptions: [],

    success: false,
    message: null,
    visibleDialog: false,

    files: [],
    events: [],
    filePreviews: {},
  });

  const _getData = async () => {
    const url = '/api/hp/allianceCard/get_card_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);
    setState({
      ...state,
      isLoading: false,
      cardCode: data.cardCode,
      cardName: data.cardName,
      telecom: { code: data.telecom, name: data.telecomName },
      usePrice: data.usePrice,
      discountPrice: data.discountPrice,
      thumbnail: data.thumbnail,
      cardExplain: data.cardExplain,
      useYn: data.useYn,
      telecomOptions: data.telecomOptions,
      useOptions: data.useOptions,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/hp/allianceCard/set_card_update.php';
    const params = {
      mode: state.mode,
      idx: state.idx,
      cardCode: state.cardCode,
      cardName: state.cardName,
      telecom: state.telecom,
      usePrice: state.usePrice,
      discountPrice: state.discountPrice,
      thumbnail: state.thumbnail,
      cardExplain: state.cardExplain,
      useYn: state.useYn,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleTextarea = (e) => {
    setState({ ...state, [e.target.element.current.name]: e.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, useYn: e.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const cardCodeMessage = "'카드코드'를 입력해주세요!";
  const cardNameMessage = "'카드명'을 입력해주세요!";
  const usePriceMessage = "'사용실적'을 입력해주세요!";
  const discountPriceMessage = "'할인금액'을 입력해주세요!";
  const telecomMessage = "'통신사'를 선택해주세요!";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 제휴카드할인 〉 목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>카드코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="cardCode"
                  value={state.cardCode}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={cardCodeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>카드명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="cardName"
                  value={state.cardName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={cardNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용실적</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="usePrice"
                  value={state.usePrice}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={usePriceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인금액</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="discountPrice"
                  value={state.discountPrice}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={discountPriceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="telecom"
                  value={state.telecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={telecomMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용여부</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    marginBottom: '1px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="useYn"
                    defaultValue={state.useYn}
                    data={state.useOptions}
                    value={state.useYn}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>카드설명</Label>
                <TextArea
                  name="cardExplain"
                  value={state.cardExplain}
                  style={{ width: '74%' }}
                  autoSize={true}
                  onChange={_handleTextarea}
                />
              </Td>
              <Td width={tdWidth} height={'100px'} valign={'flex-start'}>
                <Label width={labelWidth} height={'100px'}>
                  썸네일
                </Label>
                <AllianceCardImgUpload state={state} setState={setState} />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default AllianceCardInfo;
