import React, { useEffect } from 'react';
import DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';
import '@ckeditor/ckeditor5-build-classic/build/translations/ko';

function Editor({ state, content, setContent }) {
  const full = [
    'fontFamily',
    'fontSize',
    'bold',
    'italic',
    'underline',
    'fontColor',
    'fontBackgroundColor',
    'link',
    'bulletedList',
    'numberedList',
    '|',
    'indent',
    'outdent',
    '|',
    'codeBlock',
    'blockQuote',
    'insertTable',
    'imageUpload',
    'mediaEmbed',
    'undo',
    'redo',
  ];

  const small = [
    'bold',
    'italic',
    'underline',
    'fontColor',
    'fontBackgroundColor',
    'imageUpload',
  ];

  const toolbar = state.type === 'full' ? full : small;

  const _init = async (initValue) => {
    try {
      await DecoupledEditor.create(document.querySelector('#editor'), {
        language: 'ko',
        initialData: initValue,
        // 폰트 크기
        fontSize: { options: [10, 12, 14, 16, 18, 20, 24] },
        // 폰트 타입
        fontFamily: {
          options: ['default', 'Arial', '궁서체', '바탕', '돋움'],
          supportAllValues: true,
        },
        // 제목 타입
        heading: {},
        // 이미지 툴바
        image: {
          resizeUnit: 'px',
          toolbar: [
            'imageStyle:alignLeft',
            'imageStyle:full',
            'imageStyle:alignRight',
            '|',
            'imageTextAlternative',
          ],
          styles: ['full', 'alignLeft', 'alignRight'],
          type: ['JPEG', 'JPG', 'GIF', 'PNG'],
        },
        toolbar: toolbar,
        // 이미지 업로드
        ckfinder: {
          uploadUrl: 'https://spiderplatform.co.kr/utils/editorImage.php',
        },
      }).then((editor) => {
        const toolbarContainer = document.querySelector('#toolbar-container');
        toolbarContainer.appendChild(editor.ui.view.toolbar.element);

        editor.model.document.on('change:data', (e) => {
          setContent({
            ...state,
            content: editor.getData(),
          });
        });
      });
    } catch (e) {
      //console.error(e);
    }
  };

  useEffect(() => {
    _init(content);
  });

  return (
    <>
      {state.position === 'top' ? (
        <div>
          <div id="toolbar-container"></div>
          <div id="editor" style={{ border: '1px solid #dcdcdc' }}></div>
        </div>
      ) : (
        <div>
          <div id="editor" style={{ border: '1px solid #dcdcdc' }}></div>
          <div id="toolbar-container"></div>
        </div>
      )}
    </>
  );
}

export default Editor;
