/* 설명: naviBottom
- 2021.05.18 안예린 setIsLogin useEffect 부분 삭제
- 2021.05.27 안예린 로그인 전, 비즈니스 아이콘 눌러도 로그인이 안 떠서 25줄 pathName 부분 수정함 
*/
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
// 개발자 라이브러리
import { logoutRequest } from '../../redux/actions/auth';
// css
import theme from '../../styles/theme';
// 아이콘 및 이미지
import briefcase from '../../images/navBottom/font-awsome-briefcase.svg';
import home from '../../images/navBottom/font-awsome-home.svg';
import signOut from '../../images/navBottom/font-awsome-sign-out-alt.svg';
import signIn from '../../images/navBottom/font-awsome-sign-in-alt.svg';

function NaviBottom() {
  const dispatch = useDispatch();
  const authStatus = useSelector((store) => store.auth.authStatus);
  const userAuth = authStatus.userAuth;
  const isLogIn = authStatus.isLogIn;
  const pathName =
    userAuth === 'A' || userAuth === 'D' ? '/admin/home' : '/mypage/home';
  const _handleLogout = () => {
    dispatch(logoutRequest());
  };
  if (
    window.location.pathname === '/login' ||
    window.location.pathname === '/account/findID' ||
    window.location.pathname === '/account/findPW' ||
    window.location.pathname === '/account/changePW'
  )
    return null;
  return (
    <NavBottomContain>
      <IconWrap1 to="/" className="icon">
        <div className="iconImg-wrap">
          <Icon src={home}></Icon>
        </div>
        <IconLabel>홈</IconLabel>
      </IconWrap1>
      <IconWrap2 to={pathName} className="icon">
        <div className="iconImg-wrap">
          <Icon src={briefcase}></Icon>
        </div>
        <IconLabel>비즈니스</IconLabel>
      </IconWrap2>

      {isLogIn ? (
        <LogOut onClick={_handleLogout} className="icon">
          <div className="iconImg-wrap">
            <Icon src={signOut}></Icon>
          </div>
          <IconLabel>로그아웃</IconLabel>
        </LogOut>
      ) : (
        <IconWrap3 to="/login" className="icon">
          <div className="iconImg-wrap">
            <Icon src={signIn}></Icon>
          </div>
          <IconLabel>로그인</IconLabel>
        </IconWrap3>
      )}
    </NavBottomContain>
  );
}

export default NaviBottom;

const NavBottomContain = styled.div`
  position: fixed;
  display: flex;
  justify-content: space-around;
  align-items: center;
  bottom: 0;
  width: 100%;
  height: 56px;
  box-shadow: 0 0 10px 0 rgba(35, 35, 35, 0.3);
  border: solid 1px rgba(255, 255, 255, 0.16);
  background-color: #ffffff;
  z-index: 2;

  .icon {
    text-align: center;

    :hover {
      color: #0056b3;
      text-decoration: none;
    }

    .iconImg-wrap {
      display: inline-flex;
      width: 24px;
      height: 24px;

      img {
        width: 100%;
      }
    }
  }
`;

const IconLabel = styled.div`
  ${theme.Body2};
  color: ${theme.colors.navyBox};
`;

const IconWrap1 = styled(Link)``;

const IconWrap2 = styled(Link)``;

const IconWrap3 = styled(Link)``;

const LogOut = styled.div``;

const Icon = styled.img``;
