import React, { useEffect, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Input, RadioGroup } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
// 개발자 Component
import _axios from '../../../utils/axios';
//css
import '../../../css/kendo.css';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const DiscountInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    isDelete: false,
    loadData: [],
    skip: 0,
    take: 10,
    rowTotal: 0,
    idx: props.location.idx,
    mode: props.location.mode,
    discountCode: '',
    discountName: '',
    discountPrice: '',

    discountType: '',
    typeOptions: [],
    allYn: '',
    allOptions: [],
    useYn: '',
    useOptions: [],

    models: [],
    charges: [],
    ynOptions: [],

    success: false,
    message: null,
    visibleDialog: false,
  });

  // 요금제 정보
  const _getData = async () => {
    const url = '/api/hp/discount/get_discount_info.php';
    const params = {
      idx: state.idx,
    };

    await _axios(url, params).then((response) => {
      setState((prevState) => ({
        ...state,
        isLoading: false,
        loadData: response.planData,
        discountCode: response.data.discountCode,
        discountName: response.data.discountName,
        discountPrice: response.data.discountPrice,
        discountType: response.data.discountType,
        typeOptions: response.data.typeOptions,
        allYn: response.data.allYn,
        allOptions: response.data.allOptions,
        useYn: response.data.useYn,
        useOptions: response.data.useOptions,

        models: response.models,
        modelOptions: response.modelOptions,
        charges: response.charges,
        chargeOptions: response.chargeOptions,
        ynOptions: response.ynOptions,
      }));
    });
  };

  // 요금제 정보 저장
  const _setData = async () => {
    const params = {
      mode: state.mode,
      idx: state.idx,
      discountCode: state.discountCode,
      discountName: state.discountName,
      discountPrice: state.discountPrice,
      discountType: state.discountType,
      allYn: state.allYn,
      useYn: state.useYn,

      models: state.models,
      charges: state.charges,
    };

    const url = '/api/hp/discount/set_discount_update.php';
    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  // 적용 전체/개별 체크
  const _handleAllChange = (e) => {
    setState({ ...state, allYn: e.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, useYn: e.value });
  };

  // 할인구분 체크
  const _handleTypeChange = (e) => {
    setState({ ...state, discountType: e.value });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const discountCodeMessage = "'할인코드'를 입력해주세요!";
  const discountNameMessage = "'할인명'을 입력해주세요!";
  const discountPriceMessage = "'할인요금'을 입력해주세요!";

  const targetName = state.discountType === 'M' ? '단말기' : '요금제';
  const mobileMargin = "marginBottom : '2px'";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 할인제관리 〉 목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="discountCode"
                  value={state.discountCode}
                  style={{ width: '74%' }}
                  maxLength={20}
                  autoComplete="off"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={discountCodeMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="discountName"
                  value={state.discountName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  autoComplete="off"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={discountNameMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인구분</Label>
                <RadioGroup
                  name="assort"
                  defaultValue={state.discountType}
                  data={state.typeOptions}
                  value={state.discountType}
                  style={{
                    width: '74%',
                    display: 'flex',
                    alignItems: 'center',
                    paddingLeft: '5px',
                    background: '#fff',
                    marginBottom: '2px',
                  }}
                  onChange={_handleTypeChange}
                  layout="horizontal"
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인요금</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="discountPrice"
                  value={state.discountPrice}
                  style={{ width: '74%' }}
                  maxLength={20}
                  autoComplete="off"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={discountPriceMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>적용{targetName}</Label>
                <RadioGroup
                  name="allYn"
                  defaultValue={state.allYn}
                  data={state.allOptions}
                  value={state.allYn}
                  style={{
                    width: '74%',
                    paddingLeft: '5px',
                    display: 'flex',
                    alignItems: 'center',
                    background: '#fff',
                    marginBottom: '2px',
                  }}
                  onChange={_handleAllChange}
                  layout="horizontal"
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용여부</Label>
                <div
                  style={{
                    width: '74%',

                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="useYn"
                    defaultValue={state.useYn}
                    data={state.useOptions}
                    value={state.useYn}
                    style={{ marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                style={{ marginRight: '10px' }}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default DiscountInfo;
