import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import AdminList from '../../components/admin/admin/AdminList';
import AdminInfo from '../../components/admin/admin/AdminInfo';

const Admin = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/admin/admin"
        render={(props) => <AdminList {...props} />}
      />
      <AuthRoute
        path="/admin/admin/info"
        render={(props) => <AdminInfo {...props} />}
      />
    </Switch>
  );
};

export default Admin;