import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Card from '@material-ui/core/Card';
import {
  Chart,
  ChartSeries,
  ChartSeriesItem,
  ChartCategoryAxis,
  ChartCategoryAxisItem,
  ChartLegend,
} from '@progress/kendo-react-charts';
// 개발자 라이브러리
import { setHeader } from '../../redux/actions/header';
import _axios from '../../utils/axios';
// css
import {
  Container,
  Wrap,
  CardWrap,
  Header,
  Body,
  Row,
  CellHeader,
  CellWrap,
  Cell,
} from '../../styles/myHome';

const AdminHome = () => {
  const dispatch = useDispatch();
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);

  const headerInfo = {
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '비지니스',
  };

  const [state, setState] = useState({
    isLoading: true,
    userId: userInfo.userId,
    userAuth: '',
    memStatus: [],
    commiStatus: [],
    salesStatus: [],
    categoryMemMonth: [],
    seriesMemMonth: [],
    categoryCommiMonth: [],
    seriesCommiMonth: [],
    categorySalesMonth: [],
    seriesSalesMonth: [],
  });

  const _getData = async () => {
    const url = '/api/admin/get_business_home.php';
    const params = {
      userId: state.userId,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      userAuth: data.userAuth,
      memStatus: data.memStatus,
      commiStatus: data.commiStatus,
      salesStatus: data.salesStatus,
      categoryMemMonth: data.categoryMemMonth,
      seriesMemMonth: data.seriesMemMonth,
      categoryCommiMonth: data.categoryCommiMonth,
      seriesCommiMonth: data.seriesCommiMonth,
      categorySalesMonth: data.categorySalesMonth,
      seriesSalesMonth: data.seriesSalesMonth,
    });
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    _getData();
  }, []);

  const { memStatus, commiStatus, salesStatus } = state;

  return (
    <Container>
      <Wrap>
        <CardWrap>
          <Card style={{ width: '95%', margin: '0.5em' }}>
            <Header>회원현황</Header>
            <Body>
              <Row>
                <CellWrap>
                  <CellHeader style={{ width: '25%', textAlign: 'center' }}>
                    구분
                  </CellHeader>
                  <CellHeader style={{ width: '25%', textAlign: 'right' }}>
                    플랫폼MD
                  </CellHeader>
                  <CellHeader style={{ width: '25%', textAlign: 'right' }}>
                    구독플랫폼
                  </CellHeader>
                  <CellHeader style={{ width: '25%', textAlign: 'right' }}>
                    합계
                  </CellHeader>
                </CellWrap>
                {memStatus.map((row, index) => (
                  <CellWrap key={index}>
                    <Cell style={{ width: '25%', textAlign: 'center' }}>
                      {row.title}
                    </Cell>
                    <Cell style={{ width: '25%', textAlign: 'right' }}>
                      {row.mdCount}
                    </Cell>
                    <Cell style={{ width: '25%', textAlign: 'right' }}>
                      {row.osCount}
                    </Cell>
                    <Cell style={{ width: '25%', textAlign: 'right' }}>
                      {row.sumCount}
                    </Cell>
                  </CellWrap>
                ))}
              </Row>
              <Row>
                <Chart style={{ height: 180 }}>
                  <ChartLegend position="top" orientation="horizontal" />
                  <ChartCategoryAxis>
                    <ChartCategoryAxisItem
                      categories={state.categoryMemMonth}
                      startAngle={45}
                    />
                  </ChartCategoryAxis>
                  <ChartSeries>
                    {state.seriesMemMonth.map((item, idx) => (
                      <ChartSeriesItem
                        key={idx}
                        type="column"
                        tooltip={{ visible: true }}
                        data={item.data}
                        name={item.name}
                      />
                    ))}
                  </ChartSeries>
                </Chart>
              </Row>
            </Body>
          </Card>
        </CardWrap>

        <CardWrap>
          <Card style={{ width: '95%', margin: '0.5em' }}>
            <Header>수수료현황</Header>
            <Body>
              <Row>
                <CellWrap>
                  <CellHeader style={{ width: '20%', textAlign: 'center' }}>
                    구분
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    개설
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    구독(대)
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    구독(판)
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    렌탈
                  </CellHeader>
                </CellWrap>
                {commiStatus.map((row, index) => (
                  <CellWrap key={index}>
                    <Cell style={{ width: '20%', textAlign: 'center' }}>
                      {row.title}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.csPrice}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.maPrice}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.msPrice}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.crPrice}
                    </Cell>
                  </CellWrap>
                ))}
              </Row>
              <Row>
                <Chart style={{ height: 180 }}>
                  <ChartLegend position="top" orientation="horizontal" />
                  <ChartCategoryAxis>
                    <ChartCategoryAxisItem
                      categories={state.categoryCommiMonth}
                      startAngle={45}
                    />
                  </ChartCategoryAxis>
                  <ChartSeries>
                    {state.seriesCommiMonth.map((item, idx) => (
                      <ChartSeriesItem
                        key={idx}
                        type="column"
                        tooltip={{ visible: true }}
                        data={item.data}
                        name={item.name}
                      />
                    ))}
                  </ChartSeries>
                </Chart>
              </Row>
            </Body>
          </Card>
        </CardWrap>

        <CardWrap>
          <Card style={{ width: '95%', margin: '0.5em' }}>
            <Header>매출현황</Header>
            <Body>
              <Row>
                <CellWrap>
                  <CellHeader style={{ width: '20%', textAlign: 'center' }}>
                    구분
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    개설(대)
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    개설(판)
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    구독(대)
                  </CellHeader>
                  <CellHeader style={{ width: '20%', textAlign: 'right' }}>
                    구독(판)
                  </CellHeader>
                </CellWrap>
                {salesStatus.map((row, index) => (
                  <CellWrap key={index}>
                    <Cell style={{ width: '20%', textAlign: 'center' }}>
                      {row.title}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.saPrice}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.ssPrice}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.paPrice}
                    </Cell>
                    <Cell style={{ width: '20%', textAlign: 'right' }}>
                      {row.psPrice}
                    </Cell>
                  </CellWrap>
                ))}
              </Row>
              <Row>
                <Chart style={{ height: 180 }}>
                  <ChartLegend position="top" orientation="horizontal" />
                  <ChartCategoryAxis>
                    <ChartCategoryAxisItem
                      categories={state.categorySalesMonth}
                      startAngle={45}
                    />
                  </ChartCategoryAxis>
                  <ChartSeries>
                    {state.seriesSalesMonth.map((item, idx) => (
                      <ChartSeriesItem
                        key={idx}
                        type="column"
                        tooltip={{ visible: true }}
                        data={item.data}
                        name={item.name}
                      />
                    ))}
                  </ChartSeries>
                </Chart>
              </Row>
            </Body>
          </Card>
        </CardWrap>

        <CardWrap>
          <Card style={{ width: '95%', margin: '0.5em' }}>
            <Header>공지사항</Header>
            <Body>
              <Row>
                <CellWrap>
                  <Cell style={{ width: '100%' }}>공지사항이 없습니다.</Cell>
                </CellWrap>
              </Row>
            </Body>
          </Card>
        </CardWrap>
      </Wrap>
    </Container>
  );
};

export default AdminHome;
