import React from 'react';
import styled, { css } from 'styled-components';
import theme from '../../styles/theme';

const CommonButton = ({
  width,
  height,
  type,
  kind,
  children,
  onClickHandler,
  disabled,
}) => {
  return (
    <StyledButton
      width={width}
      height={height}
      onClick={onClickHandler}
      type={type}
      className={kind}
      disabled={disabled}
    >
      {children}
    </StyledButton>
  );
};

const StyledButton = styled.button`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  margin: ${(props) => props.margin};
  padding: 0 1.5em;
  font-size: ${theme.fontSizes.Body1};
  cursor: pointer;
  border-radius: 3px;
  border: none;
  z-index: 1;
  &:focus {
    box-shadow: none;
  }
  img {
    margin-right: 5px;
  }

  /* kind */
  &.btn_01 {
    background-color: ${({ theme }) => theme.colors.navyBox};
    color: ${({ theme }) => theme.colors.white};
    /* transition: background-color 200ms ease-in-out; */
  }
  &.btn_02 {
    background-color: transparent;
    color: ${({ theme }) => theme.colors.navyBox};
    border: 1px solid ${({ theme }) => theme.colors.navyBox};
    /* transition: background-color 200ms ease-in-out; */
  }
  &.wideBtn_01 {
    background-color: ${({ theme }) => theme.colors.navyBox};
    color: ${({ theme }) => theme.colors.white};
    width: 335px;
    height: 44px;
    /* transition: background-color 200ms ease-in-out; */
  }
  &.wideBtn_02 {
    background-color: transparent;
    color: ${({ theme }) => theme.colors.navyBox};
    border: 1px solid ${({ theme }) => theme.colors.navyBox};
    width: 335px;
    height: 44px;
    /* transition: background-color 200ms ease-in-out; */
  }

  /* status */
  ${(kind) =>
    kind &&
    css`
      &.btn_01:hover:enabled,
      &.btn_02:hover:enabled,
      &.wideBtn_01:hover:enabled {
        background-color: ${theme.colors.lapis};
      }
      &.wideBtn_02:hover:enabled {
        background-color: ${theme.colors.lapis};
        border: ${theme.colors.royalblue};
        color: ${theme.colors.white};
      }

      &.btn_01:active:enabled,
      &.btn_02:active:enabled,
      &.wideBtn_01:active:enabled {
        background-color: ${theme.colors.royalblue};
      }
      &.wideBtn_02:active:enabled {
        background-color: ${theme.colors.royalblue};
        border: ${theme.colors.royalblue};
        color: ${theme.colors.white};
      }

      &.btn_01:disabled,
      &.btn_02:disabled,
      &.wideBtn_01:disabled,
      &.wideBtn_02:disabled {
        opacity: 0.5;
      }
    `};

  @media ${({ theme }) => theme.device.mobile} {
  }
`;

export default React.memo(CommonButton);
