/*
  설명: BugerMenu
  - 2021.05.20. Footer Logo, Copyright 정렬 수정
  - 2021.05.25 Footer spider1 코드로 변경
  - 2021.06.01 login 조건문 추가
  - 2021.06.01 양민석. 약관 에러 해결
*/
import React, { useEffect, useState } from 'react';
import { useMedia } from 'react-media';
import { Window } from '@progress/kendo-react-dialogs';
import styled from 'styled-components';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
//css
import theme from '../../../styles/theme';
// icon & images
import footer_logo from '../../../images/newImage/footer/footer_logo.svg';
import footer_m_logo from '../../../images/newImage/footer/footer_m_logo.png';

const Footer = () => {
  const isMobile = useMedia({ query: '(max-width: 767px)' });
  const [state, setState] = useState({
    isTerm01: false,
    isTerm02: false,
    isTerm03: false,
    term_01: '',
    term_02: '',
    term_03: '',
  });

  const _toggleTerm01 = () => {
    setState({ ...state, isTerm01: !state.isTerm01 });
  };

  const _toggleTerm02 = () => {
    setState({ ...state, isTerm02: !state.isTerm02 });
  };

  const _toggleTerm03 = () => {
    setState({ ...state, isTerm03: !state.isTerm03 });
  };

  const _getData = async () => {
    const url = '/api/account/get_term_info.php';
    const params = {};

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      term01: data.term_01,
      term02: data.term_02,
      term03: data.term_03,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const innerHeight = window.innerHeight + 10;
  const innerWidth = window.innerWidth;

  if (
    (isMobile &&
      (window.location.pathname === '/login' ||
        window.location.pathname === '/account/findID' ||
        window.location.pathname === '/account/findPW')) ||
    window.location.pathname === '/account/changePW'
  )
    return <LoginFooter>&copy; 스파이더플랫폼</LoginFooter>;

  return (
    <Container>
      {isMobile ? (
        <Wrap>
          <LogoWrap>
            <div className="img-wrap">
              <img src={footer_logo} alt="" />
            </div>
          </LogoWrap>
          <TermWrap>
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
              }}
            >
              <TermItem onClick={_toggleTerm01}>이용약관</TermItem>
              <TermItem>|</TermItem>
              <TermItem onClick={_toggleTerm02}>개인정보취급방침</TermItem>
              <TermItem>|</TermItem>
              <TermItem onClick={_toggleTerm03}>회원약관</TermItem>
            </div>
          </TermWrap>

          <CompanyWrap>
            <CompanyName>
              <span>서울특별시 성동구 성수이로7길 7, 307호</span>
              <span>통신판매업신고:제2020-서울성동-01567</span>
              <span>국민은행 205701-04-361105 (주)스파이더플랫폼</span>
              <span>개인정보관리책임자 : 박영환</span>

              <span>
                Copyright 스파이더플랫폼 Co., Ltd. All rights reserved.
              </span>
            </CompanyName>
          </CompanyWrap>
        </Wrap>
      ) : (
        <Wrap>
          <Inner>
            <CompanyWrap>
              <div className="companyWrap-img">
                <img src={footer_logo} alt="" />
              </div>
              <CompanyName>
                <span>서울특별시 성동구 성수이로7길 7, 307호</span>
                <span>통신판매업신고:제2020-서울성동-01567</span>
                <span style={{ marginBottom: '16px' }}>
                  국민은행 205701-04-361105 (주)스파이더플랫폼
                </span>
                <span>개인정보관리책임자 : 박영환</span>
                <span>
                  Copyright 스파이더플랫폼 Co., Ltd. All rights reserved.
                </span>
              </CompanyName>
            </CompanyWrap>
            <TermWrap>
              <div style={{ display: 'flex' }}>
                <TermItem onClick={_toggleTerm01}>이용약관</TermItem>
                <TermItem>|</TermItem>
                <TermItem onClick={_toggleTerm02}>개인정보취급방침</TermItem>
                <TermItem>|</TermItem>
                <TermItem onClick={_toggleTerm03}>회원약관</TermItem>
              </div>
            </TermWrap>
          </Inner>
        </Wrap>
      )}

      {state.isTerm01 && (
        <div
          style={
            isMobile
              ? { position: 'fixed', left: '0', top: '50px' }
              : {
                  position: 'fixed',
                  left: '0',
                  top: '100px',
                  zIndex: '99',
                  width: '100%',
                }
          }
        >
          <Window
            title={'이용약관'}
            onClose={_toggleTerm01}
            initialHeight={innerHeight}
            initialWidth={innerWidth}
          >
            {state.term01.split('\n').map((line, index) => {
              return (
                <span key={index}>
                  {line}
                  <br />
                </span>
              );
            })}
          </Window>
        </div>
      )}

      {state.isTerm02 && (
        <div
          style={
            isMobile
              ? { position: 'fixed', left: '0', top: '50px' }
              : {
                  position: 'fixed',
                  left: '0',
                  top: '100px',
                  zIndex: '99',
                  width: '100%',
                }
          }
        >
          <Window
            title={'개인정보취급방침'}
            onClose={_toggleTerm02}
            initialHeight={innerHeight}
            initialWidth={innerWidth}
          >
            {state.term02.split('\n').map((line, index) => {
              return (
                <span key={index}>
                  {line}
                  <br />
                </span>
              );
            })}
          </Window>
        </div>
      )}

      {state.isTerm03 && (
        <div
          style={
            isMobile
              ? { position: 'fixed', left: '0', top: '50px' }
              : {
                  position: 'fixed',
                  left: '0',
                  top: '100px',
                  zIndex: '99',
                  width: '100%',
                }
          }
        >
          <Window
            title={'회원약관'}
            onClose={_toggleTerm03}
            initialHeight={innerHeight}
            initialWidth={innerWidth}
          >
            {state.term03.split('\n').map((line, index) => {
              return (
                <span key={index}>
                  {line}
                  <br />
                </span>
              );
            })}
          </Window>
        </div>
      )}
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 328px;
  background-color: ${theme.colors.boxGray};

  @media (min-width: 769px) {
    padding: 2em;
    height: auto;
    border-top: 1px solid rgba(169, 169, 169, 0.5);
  }
`;

const LogoWrap = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 70px;
  border-bottom: 1px solid ${theme.colors.white};

  .img-wrap {
    width: 89px;
    height: 70px;
    line-height: 82px;

    img {
      width: 100%;
    }
  }
`;

const Wrap = styled.div``;

const Inner = styled.div`
  display: flex;
  max-width: 1620px;
  margin: 0 auto;
`;

const CompanyWrap = styled.div`
  margin-top: 20px;
  text-align: center;

  .companyWrap-img {
    flex-shrink: 0;
    width: 123px;
    margin-right: 5em;

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  @media (min-width: 769px) {
    display: flex;
    width: 100%;
    text-align: left;
    margin-top: 0;
  }
`;

const CompanyName = styled.div`
  display: flex;
  flex-direction: column;
  flex: 65%;
  flex-shrink: 0;
  ${theme.Body2};
  color: ${theme.colors.black1};

  span {
    margin-bottom: 5px;
  }
`;

const TermWrap = styled.div`
  width: 260px;
  height: 14px;
  margin: 17px auto 0;
  ${theme.Body1};
  text-align: center;
  color: ${theme.colors.black1};
  object-fit: contain;

  @media (min-width: 769px) {
    display: flex;
    justify-content: flex-end;
    flex: 35%;
    flex-shrink: 0;
    margin: 0;
  }
`;

const TermItem = styled.div`
  margin-right: 5px;
  cursor: pointer;

  &:nth-child(5) {
    margin-right: 0;
  }
`;

const LoginFooter = styled.div`
  position: absolute;
  bottom: 30px;
  width: 100%;
  font-size: 14px;
  text-align: center;
  color: ${theme.colors.grey3};
  font-family: NotoR;
  @media (min-width: 769px) {
    display: none;
  }
`;
export default Footer;
