/* 설명 : 비밀번호 바꾸는 페이지
-2021.06.07 안예린 페이지 리뉴얼
*/
import React, { useEffect, useState } from 'react';
import { Dialog } from '@progress/kendo-react-dialogs';
// 개발자 라이브러리
import _axios from '../../utils/axios';
import CommonInput from '../common/CommonInput';
import CommonButton from '../common/Button';
import {
  Container,
  Wrap,
  LogoWrap,
  LoginModal,
  SmallText,
  FormWrap,
} from '../../styles/account';
// css
import '../../css/kendo.css';
// images & icons
import LoginLogo from '../../images/account/logo_9930.svg';
import warning from '../../images/account/warning.svg';
import check from '../../images/account/Correct_Icon.svg';

const ChangePW = (props) => {
  const userId = props.location.userId;
  const [red, setRed] = useState(false);
  const [error, setError] = useState('');
  const [opacity, setOpacity] = useState(false);
  const [check1, setCheck1] = useState(false);
  const [check2, setCheck2] = useState(false);
  const [state, setState] = useState({
    userId: userId,
    userPw: '',
    newPw: '',
    success: false,
    message: null,
    visibleDialog: false,
    isCertify: false,
    isCmsCheck: false,
    isEyesOn: false,
    userInfo: [],
    isCertifyPW: false,
  });

  const _setChagePW = async () => {
    const url = '/api/account/set_pw_change.php';
    const params = {
      assort: 'P',
      userId: state.userId,
      userPw: state.userPw,
    };
    const response = await _axios(url, params);
    if (response.result === '0') {
      setState({
        ...state,
        isCertify: true,
      });
      setOpacity(true);

      setTimeout(() => {
        props.history.push({
          pathname: '/login',
          state: { isCertifyPW: true },
        });
      }, 0);
    }
  };

  const _handleInputChange = (e) => {
    setCheck1(false);
    setCheck2(false);
    setRed(false);
    setError('');
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });

    if (state.newPw !== state.userPw) {
      setError(true);
    }
  };

  useEffect(() => {
    const isOk = /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/.test(
      state.newPw,
    );
    if (isOk) {
      setCheck1(true);
    }

    setError(true);

    if (isOk && state.newPw === state.userPw) {
      setCheck2(true);
    }
  }, [state.newPw, state.userPw]);

  const _handleSubmit = (e) => {
    e.preventDefault();
    setError('');
    const isOk = /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/.test(
      state.userPw,
    );

    if (isOk && state.newPw === state.userPw) {
      _setChagePW();
    } else if (state.newPw !== state.userPw) {
      setError('비밀번호가 일치하지 않습니다.');
      setRed(true);
      setState({
        ...state,
      });
    } else {
      setError('비밀번호 조합을 확인해주세요.');
      setState({
        ...state,
      });
    }
  };

  const newPwMessage = '새로운 비밀번호를 입력해주세요!';

  return (
    <Container>
      <Wrap height="auto">
        <LogoWrap
          onClick={() =>
            props.history.push({
              pathname: '/',
            })
          }
        >
          <img src={LoginLogo} alt="logo"></img>
        </LogoWrap>
        <form onSubmit={_handleSubmit}>
          <FormWrap opacity={opacity ? '0.3' : '1'}>
            <CommonInput
              inputtype="password"
              width="100%"
              height="60px"
              value={state.newPw}
              autoComplete="off"
              name="newPw"
              maxLength={20}
              titlename="새 비밀번호"
              placeholder="새 비밀번호를 입력해주세요."
              onChange={_handleInputChange}
              required={true}
              validationMessage={newPwMessage}
              validityStyles={false}
              src={check1 ? check : ''}
            />
            <SmallText>영문+숫자+특수기호 조합 8자리 이상</SmallText>
            <CommonInput
              inputtype="password"
              kind={red ? 'is-active' : ''}
              width="100%"
              height="60px"
              value={state.userPw}
              autoComplete="off"
              name="userPw"
              maxLength={20}
              titlename="새 비밀번호 확인"
              placeholder="비밀번호를 다시 입력해주세요."
              onChange={_handleInputChange}
              required={true}
              validityStyles={false}
              error={error}
              src={!red ? (check2 ? check : '') : warning}
            />
            <CommonButton type="submit" kind="wideBtn_01">
              확인
            </CommonButton>
          </FormWrap>
        </form>
      </Wrap>
    </Container>
  );
};

export default ChangePW;
