/* **********************************************************************
 * 관리자 > 수수료관리 > 정산서목록 > 정산 세부 목록
 *********************************************************************** */
import React, { useCallback, useEffect, useState } from 'react';
import Card from '@material-ui/core/Card';
import { ListView } from '@progress/kendo-react-listview';
import { Pager } from '@progress/kendo-react-data-tools';
import { isMobile } from 'react-device-detect';
// 개발자 Conponent
import _axios from '../../../utils/axios';
// css
// css
import {
  CardWrap,
  Container,
  Table,
  Td,
  Th,
  Tr,
  Wrap,
} from '../../../css/list';

const AccurateDetail = (props) => {
  const pageSize = isMobile ? 5 : 10;
  const [state, setState] = useState({
    isLoading: true,
    loadData: [],
    // 페이징
    skip: 0,
    rowTotal: 0,
    page: 0,
    pageCount: 1,
    pageable: {
      buttonCount: 10,
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      pageSize: pageSize,
    },
  });

  const _getData = useCallback(async () => {
    const url = '/api/admin/commission/get_accurate_detail.php';
    const params = {
      idx: props.idx,
      rows: state.searchAble.pageSize,
      page: state.page,
    };

    const response = await _axios(url, params);

    setState((prevState) => ({
      ...state,
      isLoading: false,
      loadData: response.data,
      page: state.page + 1,
      rowTotal: response.rowTotal,
      pageCount: response.pageCount,
    }));
  }, [props.idx, state]);

  // 페이지 클릭
  const _handlePageChange = (e) => {
    const { searchAble } = state;
    const { skip, take } = e;

    setState({
      ...state,
      isLoading: true,
      loadData: [],
      skip: skip,
      page: skip / take + 1,
      searchAble: {
        ...searchAble,
        pageSize: take,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading) {
      _getData();
    }
  }, [_getData, state.isLoading]);

  // 모바일용 리스트
  const _renderCards = (props) => {
    const item = props.dataItem;

    return (
      <Card style={{ width: '96%', margin: '0.5em' }}>
        <Tr width={'100%'} height={'30px'} align={'flex-start'}>
          <Td>
            {item.no}．{item.assort} / {item.memName} [ {item.memId} ]
          </Td>
        </Tr>
        <Tr width={'100%'} direction={'row'}>
          <Td width={'50%'} style={{ paddingLeft: '25px' }}>
            수수료: {item.price}
          </Td>
          <Td width={'50%'}>등록일자: {item.commiDate}</Td>
        </Tr>
      </Card>
    );
  };

  // 웹용 리스트
  const _renderGrid = (props) => {
    const item = props.dataItem;
    const background = item.no % 2 === 0 ? '#fff' : '#f5f5f5';

    return (
      <Tr width={'100%'} height={'30px'} background={background}>
        <Td width={'5%'} align={'center'}>
          {item.no}
        </Td>
        <Td width={'15%'} align={'center'}>
          {item.memId}
        </Td>
        <Td width={'15%'} align={'center'}>
          {item.memName}
        </Td>
        <Td width={'15%'} align={'center'}>
          {item.assort}
        </Td>
        <Td width={'10%'} align={'flex-end'}>
          {item.price}
        </Td>
        <Td width={'15%'} align={'center'}>
          {item.commiDate}
        </Td>
      </Tr>
    );
  };
  const { isLoading, loadData, searchAble } = state;
  const conWidth = isMobile ? '100%' : '600px';

  return (
    <Container width={conWidth}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap width={'100%'}>
          {isMobile ? (
            <CardWrap width={'100%'}>
              <ListView
                data={loadData}
                item={_renderCards}
                style={{ width: '100%', background: '#dcdcdc' }}
              />
              <Pager
                skip={state.skip}
                take={searchAble.pageSize}
                total={state.rowTotal}
                buttonCount={state.buttonCount}
                info={state.pageable.info}
                type={state.pageable.type}
                pageSizes={
                  searchAble.pageSizes ? searchAble.pageSizes : undefined
                }
                previousNext={state.pageable.previousNext}
                onPageChange={_handlePageChange}
              />
            </CardWrap>
          ) : (
            <Table width={'100%'}>
              <Tr width={'100%'} height={'35px'} background={'#dcdcdc'}>
                <Th width={'5%'} align={'center'}>
                  No
                </Th>
                <Th width={'15%'} align={'center'}>
                  회원ID
                </Th>
                <Th width={'15%'} align={'center'}>
                  회원명
                </Th>
                <Th width={'15%'} align={'center'}>
                  구분
                </Th>
                <Th width={'10%'} align={'center'}>
                  수수료
                </Th>
                <Th width={'15%'} align={'center'}>
                  등록일자
                </Th>
              </Tr>

              <ListView
                data={loadData}
                item={_renderGrid}
                style={{ width: '100%', background: '#fff' }}
              />

              <Pager
                skip={state.skip}
                take={searchAble.pageSize}
                total={state.rowTotal}
                buttonCount={state.buttonCount}
                info={state.pageable.info}
                type={state.pageable.type}
                pageSizes={
                  searchAble.pageSizes ? searchAble.pageSizes : undefined
                }
                previousNext={state.pageable.previousNext}
                onPageChange={_handlePageChange}
              />
            </Table>
          )}
        </Wrap>
      )}
    </Container>
  );
};

export default AccurateDetail;
