import React, { useEffect, useState, useRef } from 'react';
import { isMobile } from 'react-device-detect';
import { Input, MaskedTextBox } from '@progress/kendo-react-inputs';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Button } from '@progress/kendo-react-buttons';
import {
  ExcelExport,
  ExcelExportColumn,
} from '@progress/kendo-react-excel-export';
// 개발자 라이브러리
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import _axios from '../../../utils/axios';

const CmsPayLogExcel = (props) => {
  const [state, setState] = useState({
    isExport: false,
    isLoading: true,
    loadData: [],
    searchAble: {
      searchValue: '',
      searchOptions: [],
      status: '',
      statusOptions: [],
      payMonth: '',
    },
  });
  const _handleInputChange = (e) => {
    const { searchAble } = state;

    setState({
      ...state,
      searchAble: {
        ...searchAble,
        [e.target.name]: e.target.value,
      },
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _getData('down');
  };

  const _getData = async (mode) => {
    const isMode = mode === 'down' ? true : false;
    const url = '/api/admin/cms/get_pay_log_excel.php';
    const params = {
      searchValue: state.searchAble.searchValue,
      status: state.searchAble.status,
      payMonth: state.searchAble.payMonth,
    };

    await _axios(url, params).then((response) => {
      setState((prevState) => ({
        ...state,
        isExport: isMode,
        isLoading: false,
        loadData: response.data,
        searchAble: {
          ...prevState.searchAble,
          searchOptions: response.searchOptions,
          statusOptions: response.statusOptions,
        },
      }));
    });
  };

  // 엑셀 다운로드
  const _exporter = useRef();
  // _exporter;
  const _export = () => {
    _exporter.current.save();
    setState({
      ...state,
      isExport: false,
    });
  };

  useEffect(() => {
    if (!state.isLoading && state.isExport) {
      _export();
    } else {
      _getData('');
    }
  }, [!state.isLoading && state.isExport]);
  const { searchAble, isExport, loadData } = state;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '650px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form>
        <Wrap width={'100%'}>
          <Title>CMS관리 〉 출금목록 〉출금신청로그 〉 다운로드</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>검색어</Label>
                <ComboBox
                  style={{
                    width: '120px',
                    color: '#dcdcdc',
                    borderRight: '2px solid #dcdcdc',
                    marginBottom: '2px',
                  }}
                  name="searchKey"
                  value={searchAble.searchKey}
                  data={searchAble.searchOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
                <Input
                  style={{ width: '120px' }}
                  validityStyles={false}
                  name="searchValue"
                  value={searchAble.searchValue}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청결과</Label>
                <ComboBox
                  style={{ width: '74%', color: '#dcdcdc' }}
                  name="status"
                  value={searchAble.status}
                  data={searchAble.statusOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>해당년월</Label>
                <MaskedTextBox
                  width="74%"
                  name="payMonth"
                  value={searchAble.payMonth}
                  mask="0000-00"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            <Button onClick={_handleSubmit} primary={true}>
              다운로드
            </Button>
            <Button onClick={() => props.history.push('/admin/payLog')}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {isExport && (
        <ExcelExport data={loadData} fileName="paylog.xlsx" ref={_exporter}>
          <ExcelExportColumn field="no" title="NO" locked={true} width={50} />
          <ExcelExportColumn
            field="memId"
            title="회원ID"
            locked={true}
            width={100}
          />
          <ExcelExportColumn
            field="memName"
            title="회원명"
            locked={true}
            width={100}
          />
          <ExcelExportColumn field="payMonth" title="해당년월" width={85} />
          <ExcelExportColumn field="payAmount" title="신청금액" width={85} />
          <ExcelExportColumn field="status" title="결과" width={60} />
          <ExcelExportColumn field="message" title="결과메세지" width={100} />
          <ExcelExportColumn field="adminName" title="관리자명" width={80} />
          <ExcelExportColumn field="wdate" title="신청일자" width={100} />
        </ExcelExport>
      )}
    </Container>
  );
};

export default CmsPayLogExcel;
