import React, { useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input, TextArea } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const TermInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,
    code: '',
    subject: '',
    content: '',
    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = async () => {
    const url = '/api/admin/setting/get_term_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      code: data.code,
      subject: data.subject,
      content: data.content,
    });
  };

  const _setData = async () => {
    const url = '/api/admin/setting/set_term_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleTextarea = (e) => {
    setState({ ...state, [e.target.element.current.name]: e.value });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({ ...state, visibleDialog: !state.visibleDialog });
  };

  useEffect(() => {
    _getData();
  }, []);

  const codeMessage = "'코드'를 입력해주세요!";
  const subjectMessage = "'제목'을입력해주세요!";

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>게시판관리 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="code"
                  value={state.code}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={codeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>제목</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="subject"
                  value={state.subject}
                  style={{ width: '74%' }}
                  maxLength={50}
                  required={true}
                  validationMessage={subjectMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td style={{ width: '100%' }}>
                <Label style={{ width: '12.8%' }}>약관내용</Label>
                <TextArea
                  name="content"
                  value={state.content}
                  style={{ width: '90%' }}
                  rows={15}
                  onChange={_handleTextarea}
                />
              </Td>
            </Tr>
          </Table>
          <div
            style={{
              marginTop: '5px',
              borderTop: '2px solid rgb(220, 220, 220)',
              borderLeft: '2px solid rgb(220, 220, 220)',
              borderRight: '2px solid rgb(220, 220, 220)',
            }}
          ></div>
          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default TermInfo;
