import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../styles/board';

const CmsStatus = (props) => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '회원구독료현황',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const authStatus = useSelector((store) => store.auth.authStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '500px';
  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      memId: '',
      payStatus: props.location.payStatus,
      payOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/member/get_cms_pay_list.php';
    const params = {
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
      memId: authStatus.userId,
      searchValue: state.searchAble.searchValue,
      payStatus: state.searchAble.payStatus,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        payOptions: response.payOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          {type: 'comboBox',width: '42%',name: 'payStatus',value: searchAble.payStatus, options: searchAble.payOptions,},
          {type: 'text',width: '42%',name: 'searchValue',value: searchAble.searchValue,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [
          {type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},
        ],
      };

  const payStatus = {
    title: '납부상태',
    items: [
      {type: 'comboBox',width: '100%',name: 'payStatus',value: searchAble.payStatus, options: searchAble.payOptions,},
    ],
  };

  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, payStatus];

  // List 항목
  const listHeader = [
      {type: 'text',title: '회원ID',name: 'memId',width: '20%',align: 'center',},
      {type: 'text',title: '회원명',name: 'memName',width: '22%',align: 'center',},
      {type: 'text',title: '년월',name: 'payMonth',width: '18%',align: 'center',},
      {type: 'text',title: '구독료',name: 'payAmount',width: '18%',align: 'center',},
      {type: 'text',title: '상태',name: 'payStatus',width: '18%',align: 'center',},
    ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
      {!isMobile && <Title>회원관리 〉 구독료현황</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={false}
            isDeleteButton={false}
          />

          <ListView
            state={state}
            user={true}
            setState={setState}
            headers={listHeader}
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default CmsStatus;
