/*
설명 : 추천 가입
- 2021.05.28 안예린 spider걸로 갱신, import정리
*/
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import styled from 'styled-components';
//개발자 라이브러리
import _axios from '../../utils/axios';
import { setHeader } from '../../redux/actions/header';

//css
import {
  AgreeWrap,
  CheckBoxInfo,
  Container,
  Loading,
  NumberInputLine,
  ParentWrap,
  SwiperMiddleLabel,
} from '../../css/common-component';
//icon & images
import angledown from '../../images/angledown.jpeg';

export default function RecommandJoin({ history }) {
  const headerInfo = {
    isBackground: false, // 백그라운드
    isBugger: false, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '회원가입',
  };

  const dispatch = useDispatch();
  const { userId } = useSelector((store) => store.auth.login);
  const [bankOptions, setBankOptions] = useState();
  const [data, setData] = useState();
  const [isVaild, setIsVaild] = useState(true);
  const [radioValue, setRadioValue] = useState('CMS');
  const radios = [
    { name: '자동이체', value: 'CMS' },
    { name: '신용카드', value: 'CARD' },
  ];
  const [key, setKey] = useState('MD');

  const [inputs, setInputs] = useState({
    recommandId: userId,
    memAssort: 'M',
    memName: '',
    hpNo: '',
    paymentKind: 'CMS',
    paymentCompany: '',
    paymentNumber: '',
    payerName: '',
    payerNumber: '',
    valid: '',
    cardPasswd: '',
  });

  const [isQuestion, setIsQuestion] = useState({ show: false, content: '' });
  const [isComplete, setIsComplete] = useState({ show: false, content: '' });
  const [isAccountCheckAlert, setIsAccountCheckAlert] = useState({
    show: false,
    content: '',
  });

  const [isLoading, setIsLoading] = useState(false);
  const handleClick = (key) => {
    setKey(key);
  };

  const _handleInputChange = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  const _handleMemNameInputChange = (e) => {
    setInputs({
      ...inputs,
      memName: e.target.value,
      payerName: e.target.value,
    });
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    const url = '/api/account/set_join.php';
    const data = await _axios(url, inputs);

    setIsComplete({ show: true, result: data.result, content: data.message });
    setIsLoading(false);
  };

  const _toggleCompleteDialog = () => {
    isComplete.show
      ? setIsComplete({ show: false })
      : setIsComplete({ show: true });
  };

  const _onYes = (result) => {
    result === '1' ? _toggleCompleteDialog() : history.push('/home');
  };

  const _getInformation = useCallback(async () => {
    const url = '/api/account/get_join_term.php';
    const { data } = await _axios(url);

    setData(data);
    setBankOptions(data.bank_info);
  }, []);

  useEffect(() => {
    _getInformation();
  }, [_getInformation]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const _onQuestionClick = (e, content) => {
    e.stopPropagation();
    setIsQuestion({ show: true, content: content });
  };

  const _toggleQuestionDialog = () => {
    isQuestion.show
      ? setIsQuestion({ show: false })
      : setIsQuestion({ show: true });
  };

  const _accountInputVaild = () => {
    if (
      inputs.paymentCompany === '' ||
      inputs.paymentNumber === '' ||
      inputs.payerNumber === ''
    ) {
      if (inputs.paymentNumber === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '계좌번호를 입력해주세요',
        });
      }
      if (inputs.paymentCompany === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '은행을 선택 해주세요',
        });
      }
      if (inputs.payerNumber === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '생년월일을 입력해주세요',
        });
      }
      return false;
    } else {
      return true;
    }
  };

  const _onCheckAccount = async () => {
    if (_accountInputVaild()) {
      const url = '/api/account/get_real_check.php';
      const params = {
        paymentCompany: inputs.paymentCompany,
        paymentNumber: inputs.paymentNumber,
        payerNumber: inputs.payerNumber,
      };
      console.log(params);

      const data = await _axios(url, params);
      console.log(data);
      if (data.result === '1') {
        setIsAccountCheckAlert({
          show: true,
          result: data.result,
          content: data.message,
        });
        setIsVaild(true);
      } else {
        setIsAccountCheckAlert({
          show: true,
          result: data.result,
          content: data.message,
        });
        setIsVaild(false);
      }
    }
  };

  const agreeCheckMessage = '이용약관을 동의해주세요';

  return (
    <Container
      style={{
        paddingTop: '82px',
        display: 'block',
        paddingBottom: '82px',
        paddingLeft: '20px',
        paddingRight: '20px',
      }}
      className="page"
    >
      <Inner>
        <ParentWrap
          height="auto"
          padding="0"
          width="auto"
          style={{
            margin: '0 auto',
            backgroundColor: 'transparent',
            border: 'none',
          }}
        >
          <StyledTabs
            id="controlled-tab-example"
            activeKey={key}
            onSelect={(k) => {
              handleClick(k);
              setInputs({
                ...inputs,
                memAssort: k === 'MD' ? 'M' : 'S',
              });
            }}
          >
            <Tab
              eventKey="MD"
              title={
                <span style={{ display: 'flex' }}>
                  MD 가입
                  <StyledQuestionIcon
                    onClick={(e) =>
                      _onQuestionClick(e, data?.assortExplain.explain_m)
                    }
                  >
                    <span className="k-icon k-i-question"></span>
                  </StyledQuestionIcon>
                </span>
              }
            >
              <form onSubmit={onSubmit}>
                <StyledMdWrap>
                  <div
                    className="title"
                    style={{ marginTop: '37.5px', marginBottom: '16px' }}
                  >
                    기본정보
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '25px',
                    }}
                  >
                    <input
                      type="text"
                      name="memName"
                      placeholder="이름"
                      className="input_name"
                      onChange={_handleMemNameInputChange}
                      required
                    ></input>
                    <NumberInputLine
                      type="text"
                      name="hpNo"
                      placeholder="휴대폰 번호"
                      className="input_phone"
                      validityStyles={false}
                      mask="000-0000-0000"
                      maxLength={20}
                      autoComplete="off"
                      onChange={_handleInputChange}
                      required={true}
                      style={{ transform: 'translate(20px, -4px)' }}
                    ></NumberInputLine>
                  </div>
                  <hr></hr>
                  <div
                    className="title"
                    style={{ marginBottom: '16px', display: 'flex' }}
                  >
                    CMS 정보 입력
                    <StyledQuestionIcon
                      onClick={(e) =>
                        _onQuestionClick(e, data.term_info.cms_term)
                      }
                    >
                      <span className="k-icon k-i-question"></span>
                    </StyledQuestionIcon>
                  </div>
                  {radios.map((radio, index) => (
                    <label key={index}>
                      <input
                        key={index}
                        type="radio"
                        value={radio.value}
                        checked={radioValue === radio.value}
                        onChange={(e) => {
                          setRadioValue(e.currentTarget.value);
                          setInputs({
                            ...inputs,
                            paymentKind: e.currentTarget.value,
                          });
                        }}
                      />
                      {radio.name}
                    </label>
                  ))}
                  {radioValue === 'CMS' ? (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          marginBottom: '16px',
                        }}
                      >
                        <select
                          name="bank"
                          className="bankSelect"
                          onChange={(e) => {
                            setInputs({
                              ...inputs,
                              paymentCompany: e.target.value,
                            });
                          }}
                          required
                        >
                          <option value="" className="default">
                            은행선택
                          </option>
                          {bankOptions?.map((bank, index) => (
                            <option value={bank.code} key={index}>
                              {bank.name}
                            </option>
                          ))}
                        </select>
                        <input
                          type="text"
                          name="payerNumber"
                          placeholder="예금주 생년월일(6자리)"
                          className="input_birth"
                          onChange={_handleInputChange}
                          maxLength="6"
                          required
                        ></input>
                      </div>
                      <div style={{ marginBottom: '16px' }}>
                        <input
                          type="text"
                          name="paymentNumber"
                          placeholder="계좌번호를 입력하세요.( - 제외)"
                          className="input_account"
                          onChange={_handleInputChange}
                          required
                        ></input>
                      </div>
                      <div className="accountConfirm" onClick={_onCheckAccount}>
                        계좌정보 확인
                      </div>
                    </>
                  ) : (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          marginBottom: '16px',
                        }}
                      >
                        <input
                          type="text"
                          name="payerName"
                          placeholder="소유자명"
                          className="input_payerName"
                          onChange={_handleInputChange}
                          required
                        ></input>
                        <input
                          type="text"
                          name="payerNumber"
                          placeholder="생년월일(6자리)"
                          className="input_birth2"
                          onChange={_handleInputChange}
                          maxLength="6"
                          required
                        ></input>
                      </div>
                      <div style={{ marginBottom: '16px' }}>
                        <input
                          type="text"
                          name="paymentNumber"
                          placeholder="카드번호를 입력하세요.( - 제외)"
                          className="input_account"
                          onChange={_handleInputChange}
                          required
                        ></input>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          marginBottom: '16px',
                        }}
                      >
                        <NumberInputLine
                          type="text"
                          name="valid"
                          placeholder="유효기간(MM/YY)"
                          className="input_vaild"
                          validityStyles={false}
                          mask="00/00"
                          maxLength={20}
                          autoComplete="off"
                          onChange={_handleInputChange}
                          required={true}
                          // style={{ transform: 'translate(20px, -4px)' }}
                        ></NumberInputLine>
                        <input
                          type="text"
                          name="cardPasswd"
                          placeholder="비밀번호 앞 2자리"
                          className="input_birth2"
                          onChange={_handleInputChange}
                          maxLength="2"
                          max="2"
                          required
                        ></input>
                      </div>
                      {/* <div className="accountConfirm">카드정보 확인</div> */}
                    </>
                  )}
                  <AgreeWrap
                    width="100%"
                    height="16px"
                    margin="49px auto 17px auto"
                  >
                    <CheckBoxInfo
                      name="agreeCheck"
                      required={true}
                      validationMessage={agreeCheckMessage}
                      type="checkbox"
                      defaultChecked={false}
                    ></CheckBoxInfo>
                    <label style={{ fontSize: '15px', color: '#000f34' }}>
                      <span
                        onClick={(e) =>
                          _onQuestionClick(e, data.term_info.person_term)
                        }
                      >
                        이용약관 및 개인정보 이용 동의
                      </span>
                    </label>
                  </AgreeWrap>
                  <AgreeWrap
                    width="100%"
                    height="16px"
                    margin="0px auto 0px auto"
                  >
                    <CheckBoxInfo
                      name="agreeCheck"
                      required={true}
                      validationMessage={agreeCheckMessage}
                      type="checkbox"
                      defaultChecked={false}
                    ></CheckBoxInfo>
                    <label style={{ fontSize: '15px', color: '#000f34' }}>
                      <span
                        onClick={(e) =>
                          _onQuestionClick(e, data.term_info.cms_term)
                        }
                      >
                        매월 {data?.cms_info.cms_m}원 자동 이체됨에 동의합니다.
                      </span>
                    </label>
                  </AgreeWrap>
                </StyledMdWrap>
                {radioValue === 'CMS' ? (
                  <StyledSubmitButton type="submit" disabled={isVaild}>
                    확인
                  </StyledSubmitButton>
                ) : (
                  <StyledSubmitButton type="submit">확인</StyledSubmitButton>
                )}
              </form>
            </Tab>
            <Tab
              eventKey="구독"
              title={
                <span style={{ display: 'flex' }}>
                  구독 가입
                  <StyledQuestionIcon
                    onClick={(e) =>
                      _onQuestionClick(e, data?.assortExplain.explain_s)
                    }
                  >
                    <span className="k-icon k-i-question"></span>
                  </StyledQuestionIcon>
                </span>
              }
            >
              <form onSubmit={onSubmit}>
                <StyledMdWrap>
                  <div
                    className="title"
                    style={{ marginTop: '37.5px', marginBottom: '16px' }}
                  >
                    기본정보
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      marginBottom: '25px',
                    }}
                  >
                    <input
                      type="text"
                      name="memName"
                      placeholder="이름"
                      className="input_name"
                      onChange={_handleMemNameInputChange}
                      required
                    ></input>
                    <NumberInputLine
                      type="text"
                      name="hpNo"
                      placeholder="휴대폰 번호"
                      className="input_phone"
                      validityStyles={false}
                      mask="000-0000-0000"
                      maxLength={20}
                      autoComplete="off"
                      onChange={_handleInputChange}
                      required={true}
                      style={{ transform: 'translate(20px, -4px)' }}
                    ></NumberInputLine>
                  </div>
                  <hr></hr>
                  <div
                    className="title"
                    style={{ marginBottom: '16px', display: 'flex' }}
                  >
                    CMS 정보 입력
                    <StyledQuestionIcon>
                      <span className="k-icon k-i-question"></span>
                    </StyledQuestionIcon>
                  </div>
                  {radios.map((radio, index) => (
                    <label key={index}>
                      <input
                        key={index}
                        type="radio"
                        value={radio.value}
                        checked={radioValue === radio.value}
                        onChange={(e) => {
                          setRadioValue(e.currentTarget.value);
                          setInputs({
                            ...inputs,
                            paymentKind: e.currentTarget.value,
                          });
                        }}
                      />
                      {radio.name}
                    </label>
                  ))}
                  {radioValue === 'CMS' ? (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          marginBottom: '16px',
                        }}
                      >
                        <select
                          name="bank"
                          className="bankSelect"
                          onChange={(e) => {
                            setInputs({
                              ...inputs,
                              paymentCompany: e.target.value,
                            });
                          }}
                          required
                        >
                          <option value="" className="default">
                            은행선택
                          </option>
                          {bankOptions?.map((bank, index) => (
                            <option value={bank.code} key={index}>
                              {bank.name}
                            </option>
                          ))}
                        </select>
                        <input
                          type="number"
                          name="payerNumber"
                          placeholder="예금주 생년월일(6자리)"
                          className="input_birth"
                          onChange={_handleInputChange}
                          maxLength="6"
                          required
                        ></input>
                      </div>
                      <div style={{ marginBottom: '16px' }}>
                        <input
                          type="text"
                          name="paymentNumber"
                          placeholder="계좌번호를 입력하세요.( - 제외)"
                          className="input_account"
                          onChange={_handleInputChange}
                          required
                        ></input>
                      </div>
                      <div className="accountConfirm" onClick={_onCheckAccount}>
                        계좌정보 확인
                      </div>
                    </>
                  ) : (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          marginBottom: '16px',
                        }}
                      >
                        <input
                          type="text"
                          name="payerName"
                          placeholder="소유자명"
                          className="input_payerName"
                          onChange={_handleInputChange}
                          required
                        ></input>
                        <input
                          type="text"
                          name="payerNumber"
                          placeholder="생년월일(6자리)"
                          className="input_birth2"
                          onChange={_handleInputChange}
                          maxLength="6"
                          required
                        ></input>
                      </div>
                      <div style={{ marginBottom: '16px' }}>
                        <input
                          type="text"
                          name="paymentNumber"
                          placeholder="카드번호를 입력하세요.( - 제외)"
                          className="input_account"
                          onChange={_handleInputChange}
                          required
                        ></input>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          marginBottom: '16px',
                        }}
                      >
                        <NumberInputLine
                          type="text"
                          name="valid"
                          placeholder="유효기간(MM/YY)"
                          className="input_vaild"
                          validityStyles={false}
                          mask="00/00"
                          maxLength={20}
                          autoComplete="off"
                          onChange={_handleInputChange}
                          required={true}
                          // style={{ transform: 'translate(20px, -4px)' }}
                        ></NumberInputLine>
                        <input
                          type="text"
                          name="cardPasswd"
                          placeholder="비밀번호 앞 2자리"
                          className="input_birth2"
                          onChange={_handleInputChange}
                          maxLength="2"
                          max="2"
                          required
                        ></input>
                      </div>
                      {/* <div className="accountConfirm">카드정보 확인</div> */}
                    </>
                  )}
                  <AgreeWrap
                    width="100%"
                    height="16px"
                    margin="49px auto 17px auto"
                  >
                    <CheckBoxInfo
                      name="agreeCheck"
                      required={true}
                      validationMessage={agreeCheckMessage}
                      type="checkbox"
                      defaultChecked={false}
                    ></CheckBoxInfo>
                    <label style={{ fontSize: '15px', color: '#000f34' }}>
                      <span
                        onClick={(e) =>
                          _onQuestionClick(e, data.term_info.person_term)
                        }
                      >
                        이용약관 및 개인정보 이용 동의
                      </span>
                    </label>
                  </AgreeWrap>
                  <AgreeWrap
                    width="100%"
                    height="16px"
                    margin="0px auto 0px auto"
                  >
                    <CheckBoxInfo
                      name="agreeCheck"
                      required={true}
                      validationMessage={agreeCheckMessage}
                      type="checkbox"
                      defaultChecked={false}
                    ></CheckBoxInfo>
                    <label style={{ fontSize: '15px', color: '#000f34' }}>
                      <span
                        // style={{ textDecoration: 'underline' }}
                        onClick={(e) =>
                          _onQuestionClick(e, data.term_info.cms_term)
                        }
                      >
                        매월 {data?.cms_info.cms_s}원 자동 이체됨에 동의합니다.
                      </span>
                    </label>
                  </AgreeWrap>
                </StyledMdWrap>
                {radioValue === 'CMS' ? (
                  <StyledSubmitButton type="submit" disabled={isVaild}>
                    확인
                  </StyledSubmitButton>
                ) : (
                  <StyledSubmitButton type="submit">확인</StyledSubmitButton>
                )}
              </form>
            </Tab>
          </StyledTabs>
          {isQuestion.show && (
            <Dialog
              id="bonusModal"
              width={'256px'}
              height={'auto'}
              title={' '}
              onClose={_toggleQuestionDialog}
            >
              <SwiperMiddleLabel
                width="216px"
                height="auto"
                margin="0px auto 0px auto"
                style={{
                  color: '#000f34',
                  fontSize: '15px',
                  textAlign: 'center',
                }}
              >
                {isQuestion.content.split('\n').map((line, index) => {
                  return (
                    <span key={index} style={{ textAlign: 'left' }}>
                      {line}
                      <br />
                    </span>
                  );
                })}
                {/* {isQuestion.content} */}
              </SwiperMiddleLabel>
            </Dialog>
          )}
          {isComplete.show && (
            <StyledDialog
              id="bonusModal"
              width={'256px'}
              height={'auto'}
              title={' '}
              onClose={_toggleCompleteDialog}
            >
              <SwiperMiddleLabel
                width="216px"
                height="auto"
                margin="22.7px auto 0px auto"
                style={{
                  color: '#000f34',
                  fontSize: '15px',
                  textAlign: 'center',
                }}
              >
                {isComplete.content}
              </SwiperMiddleLabel>
              <DialogActionsBar>
                <button
                  className="k-button"
                  onClick={() => _onYes(isComplete.result)}
                >
                  확인
                </button>
              </DialogActionsBar>
            </StyledDialog>
          )}
          {isAccountCheckAlert.show && (
            <StyledDialog
              // id="bonusModal"
              width={'256px'}
              height={'auto'}
              title={' '}
              onClose={() => {
                isAccountCheckAlert.show
                  ? setIsAccountCheckAlert({ show: false })
                  : setIsAccountCheckAlert({ show: true });
              }}
            >
              <SwiperMiddleLabel
                width="216px"
                height="auto"
                margin="0px auto 0px auto"
                style={{
                  color: '#000f34',
                  fontSize: '15px',
                  textAlign: 'center',
                }}
              >
                {isAccountCheckAlert.content}
              </SwiperMiddleLabel>
              <DialogActionsBar>
                <button
                  className="k-button"
                  onClick={() => {
                    isAccountCheckAlert.show
                      ? setIsAccountCheckAlert({ show: false })
                      : setIsAccountCheckAlert({ show: true });
                  }}
                >
                  확인
                </button>
              </DialogActionsBar>
            </StyledDialog>
          )}
        </ParentWrap>
      </Inner>
      {isLoading && <Loading style={{ left: '35%' }}></Loading>}
    </Container>
  );
}

const StyledTabs = styled(Tabs)`
  border-bottom: solid 1px #f2f2f2;
  .nav-link:focus,
  .nav-tabs .nav-link:hover {
    border-color: transparent;
  }
  .nav-link {
    color: #979797;
  }
  .nav-link.active {
    background-color: transparent;
    color: #000f34;
    font-weight: bold;
    border: 0;
    border-bottom: 7px solid #000f34;
  }
`;

const StyledMdWrap = styled.div`
  height: auto;
  .title {
    color: #979797;
    font-size: 18px;
  }
  .input_name {
    width: 30%;
    height: 34px;
    padding: 9px 0px 8px 11px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }

  .input_phone {
    width: 65%;
    height: 34px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }

  hr {
    width: 100%;
    height: 0px;
    border-top: solid 1px #979797;
    margin-bottom: 28px;
  }

  label {
    font-size: 18px;
    margin-right: 26px;
    margin-bottom: 23px;
  }
  input[type='radio'] {
    margin-right: 8px;
    width: 15px;
    height: 15px;
    border: solid 1px #707070;
  }
  input[type='radio']:checked {
    appearance: none;
    width: 15px;
    height: 15px;
    border-radius: 100%;
    border: solid 1px #000f34;
    background-color: #000f34;
  }

  .bankSelect {
    appearance: none;
    background: url(${angledown}) no-repeat 95% 50%;

    width: 40%;
    height: 34px;
    padding: 9px 7.5px 8px 12px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #ffffff;
    background-size: 23px;
    font-size: 15px;
    option.default {
      appearance: none;
      color: red;
    }
  }

  .input_birth {
    width: 55%;
    height: 34px;
    padding: 9px 0px 8px 12px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }

  .input_payerName {
    width: 45%;
    height: 34px;
    padding: 9px 0px 8px 12px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }

  .input_vaild {
    width: 45%;
    height: 34px;
    padding: 0px 0px 0px 12px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }

  .input_birth2 {
    width: 45%;
    height: 34px;
    padding: 9px 0px 8px 12px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
    font-size: 15px;
    ::placeholder {
      color: #c7c7c7;
    }
  }
  .input_account {
    width: 100%;
    height: 34px;
    padding: 9px 119px 8px 12px;
    border-radius: 3px;
    border: solid 1px #707070;
    background-color: #fff;
  }

  .accountConfirm {
    width: 145px;
    height: 31px;
    margin-bottom: 49px;
    border-radius: 8px;
    border: solid 1px #000;
    background-color: #000f34;
    color: #fff;
    text-align: center;
    float: right;
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .k-textbox {
    transform: translateY(0px);
    height: 100%;
  }
  .k-textbox::placeholder {
    font-size: 15px;
    color: #c7c7c7 !important;
  }
`;

const Inner = styled.section`
  @media (min-width: 769px) {
    padding: 5em 38em 5em;
  }
`;

const StyledSubmitButton = styled.button`
  width: 118px;
  height: 48px;
  object-fit: contain;
  border-radius: 24px;

  margin-top: 43px;
  float: right;
  border: 1px;
  font-size: 20px;
  line-height: 2.5;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
  background-color: #000f34;
  color: #fff;
  &:disabled {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
    background-color: #979797;
    color: #fff;
  }
`;

const StyledQuestionIcon = styled.li`
  list-style: none;
  margin-left: 5px;
  color: #979797;
  .k-i-question {
    font-size: 20px;
  }
`;

const StyledDialog = styled(Dialog)`
  .k-dialog-button-layout-stretched {
    background-color: #000f34;
    color: #fff;
  }

  .k-button {
    background-color: #000f34;
    color: #fff;
  }
`;
