/* **********************************************************************
  관리자페이지 > 포인트관리 > 포인트목록
  - 2021.05.19. 박태수
  - 2021.05.28. 양민석 : 스파이더2 리뉴얼작업
  - 2021.06.01. 양민석 : 관리자페이지 코드 띄어쓰기 가독성해결
 *********************************************************************** */
/* 주의사항 
  코드 자동정렬 하시면 안됩니다 코드 길어진다고 하지말라하셨어요
  파일 > 기본설정 > 설정 > formatonsave 검색후 > formatonsave 체크박스 해제하시면 저장시 자동정렬안됩니다.
*/
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const PointList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '포인트관리',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      memId: '',
      assort: '',
      minDate: '',
      maxDate: '',
      assortOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/admin/point/get_point_list.php';
    const params = {
      searchValue: state.searchAble.searchValue,
      memId: state.searchAble.memId,
      assort: state.searchAble.assort,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        assortOptions: response.assortOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
      {type: 'comboBox',width: '84%',name: 'authAssort',value: searchAble.authAssort,options: searchAble.authOptions,},
      {type: 'submit', width: '16%', name: 'submit', value: '검색' },],}
    : {
        title: '검색어',
        items: [{type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},],
      };
  const menuStatus = {
    title: '회원ID',
    items: [
      {type: 'text',width: '100%',name: 'memId',value: searchAble.memId,},
    ],
  };

  const assort = {
    title: '포인트구분',
    items: [{type: 'comboBox',width: '100%',name: 'assort',value: searchAble.assort,options: searchAble.assortOptions,},],
  };

  const minDate = {
    title: '등록일자',
    items: [
      {type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate,},
      {type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate,},
    ],
  };

  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, menuStatus, assort, minDate];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',align: 'center',width: '10%',},
        {type: 'text',title: '구분',name: 'assortName',align: 'center',width: '18%',},
        {type: 'text',title: '회원명',name: 'memName',align: 'center',width: '12%',},
        {type: 'text',title: '회원ID',name: 'memId',align: 'center',width: '20%',},
        {type: 'text',title: '포인트',name: 'point',align: 'center',width: '15%',},
        {type: 'text',title: '등록일자',name: 'wdate',align: 'center',width: '25%',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '5%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '5%',align: 'center',},
        {type: 'text',title: '회원ID',name: 'memId',width: '15%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '10%',align: 'center',},
        {type: 'text',title: '구분',name: 'assortName',width: '10%',align: 'center',},
        {type: 'text',title: '적요',name: 'descript',width: '25%',align: 'center',},
        {type: 'text',title: '포인트',name: 'point',width: '15%',align: 'center',},
        {type: 'text',title: '등록일자',name: 'wdate',width: '10%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>포인트관리 〉 포인트목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={true}
            isDeleteButton={true}
            infoPath="/admin/point/info"
            deleteUrl="/api/admin/point/set_point_delete.php"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            infoPath="/admin/point/info"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default PointList;
