import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import DetailList from './DetailList';
import { Button } from '@progress/kendo-react-buttons';
import { Input } from '@progress/kendo-react-inputs';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
// css
import '../../../css/kendo.css';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../styles/list';

const AccurateInfo = (props) => {
  const dispatch = useDispatch();
  const isMobile = _isMobile();
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,
    memId: '',
    memName: '',
    dueDate: '',
    commission: 0,
    otherDescript: '',
    otherAmount: 0,
    totalAmount: 0,
    accurateDate: '',
    accurateStatus: '',
    statusName: '',
  });

  const _getData = async () => {
    const url = '/api/member/get_accurate_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      memId: data.memId,
      memName: data.memName,
      dueDate: data.dueDate,
      commission: data.commission,
      otherDescript: data.otherDescript,
      otherAmount: data.otherAmount,
      totalAmount: data.totalAmount,
      taxAmount: data.taxAmount,
      accurateAmount: data.accurateAmount,
      accurateDate: data.accurateDate,
      accurateStatus: data.accurateStatus,
      statusName: data.statusName,
      taxOptions: data.taxOptions,
      taxAssort: { code: data.taxAssort, name: data.taxName },
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    // _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  useEffect(() => {
    _getData();
  }, []);

  // const { isLoading, isSearch, loadData, searchAble } = state;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '900px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';
  console.log(isMobile)
  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap>
          <Title>수수료정산 〉 정산서목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산기간</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="dueDate"
                  value={state.dueDate}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>수수료</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commission"
                  value={state.commission}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가감내용</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="otherDescript"
                  value={state.otherDescript}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>가감금액</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="otherAmount"
                  value={state.otherAmount}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>합계금액</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="totalAmount"
                  value={state.totalAmount}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산상태</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="statusName"
                  value={state.statusName}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="accurateDate"
                  value={state.accurateDate}
                  style={{ width: '74%' }}
                  maxLength={20}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      <Wrap>
        <DetailList idx={state.idx} />
      </Wrap>
    </Container>
  );
};

export default AccurateInfo;
