/* **********************************************************************
 * 모바일홈 > 포인트 자세히 보기
 * 작성자: 이해영
 * 2021. 06. 07. 양민석. 포인트 월표시 개선
 *********************************************************************** */
import React, { useState, useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';
import PointHomeChart from './PointHomeChart';
// 개발자 라이브러리
import { setHeader } from '../../../../redux/actions/header';
import _axios from '../../../../utils/axios';
// css
import '../../../../styles/font.css';
import theme from '../../../../styles/theme';
import CommonButton from '../../../common/Button';
// images
import circleBtn from '../../../../images/front/commi-circleBtn.svg';
import price from '../../../../images/front/price.svg';
import blackArrow from '../../../../images/front/black-arrow.svg';
import won from '../../../../images/front/won.svg';

const PointHomeMobile = () => {
  const headerInfo = {
    isBackground: false,
    isBugger: true,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '포인트정보',
  };

  const dispatch = useDispatch();
  const history = useHistory();
  const authStatus = useSelector((state) => state.auth.authStatus);
  const date = new Date();
  const todayMonth = date.getMonth();
  const [monthIndex, setMonthIndex] = useState(todayMonth);
  const [month, setMonth] = useState(true);

  const [state, setState] = useState({
    isLoading: true,
    userId: authStatus.userInfo.userId,

    totalPoint: 0,
    pointBalance: 0,
    pointData: [],
    performance: [],
    support: [],
    chartData: [],
    monthData: [],
    year: 2021,
    yearOptions: [],
  });

  const _getData = useCallback(
    async (year) => {
      const url = '/api/member/get_point_home.php';
      const params = {
        userId: state.userId,
        year: year,
      };

      const response = await _axios(url, params);
      console.log(response);
      setState({
        isLoading: false,
        totalPoint: response.totalPoint,
        pointBalance: response.pointBalance,
        pointData: response.pointData,
        performance: response.performance,
        support: response.support,
        chartData: response.chartData,
        monthData: response.monthData,
        year: response.year,
        yearOptions: response.yearOptions,
      });
    },
    [state.userId],
  );

  useEffect(() => {
    dispatch(setHeader(headerInfo));
    // _getData();
  }, [dispatch, headerInfo]);

  useEffect(() => {
    // dispatch(setHeader(headerInfo));
    _getData(state.year);
  }, [_getData, state.year]);

  return (
    <Container>
      {state.isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          <BonusTitle className="bonus-first">
            <div className="bonus-retain">
              <h1>보유 포인트</h1>

              <div>
                <div className="img">
                  <img src={price} alt="보유 포인트" />
                </div>
                <strong>{state.pointBalance}원</strong>
              </div>
            </div>

            <div className="bonus-price first">
              <div className="withdraw price">
                <div 
                  onClick={() => {
                    history.push({
                      pathname: '/mypage/point',
                      assort: {
                        code: 'OC',
                        name: '현금인출',
                      },
                    });
                  }}
                >
                  <p>출금완료</p>
                  <div className="img">
                    <img src={blackArrow} alt="출금 완료" />
                  </div>
                </div>
                <strong>{state.pointData[3].point}</strong>
              </div>
              <div className="withdraw price">
                <div
                  onClick={() => {
                    history.push({
                      pathname: '/mypage/point/request',
                    });
                  }}                
                >
                  <p>출금진행중</p>
                  <div className="img">
                    <img src={blackArrow} alt="출금 진행중" />
                  </div>
                </div>
                <strong>{state.pointData[4].point}</strong>
              </div>
              <div className="waiting price">
                <div>
                  <p>적립대기</p>
                </div>
                <strong>{state.pointData[0].point}</strong>
              </div>
            </div>

            <div className="button">
              <CommonButton
                kind="btn_01"
                width="120px"
                height="30px"
                type="button"
              >
                <img src={won} alt="출금하기" />
                출금하기
              </CommonButton>
            </div>

          </BonusTitle>

          <BonusTitle>
            <div className="bonus-retain">
              <h1>누적 포인트</h1>
              <div>
                <div className="img">
                  <img src={price} alt="보유 포인트" />
                </div>
                <strong>{state.totalPoint}원</strong>
              </div>
            </div>

            <div className="bonus-price">
              <div className="withdraw price">
                <div
                  onClick={() => {
                    history.push({
                      pathname: '/mypage/point/commi',
                      status: {
                        code: '0',
                        name: '정산대기',
                      },
                    });
                  }}   
                >
                  <p>적립대기</p>
                  <div className="img">
                    <img src={blackArrow} alt="적립 대기" />
                  </div>
                </div>
                <strong>{state.pointData[0].point}</strong>
              </div>
              <div className="withdraw price">
                <div
                  onClick={() => {
                    history.push({
                      pathname: '/mypage/point',
                      assort: {
                        code: 'OA',
                        name: '정산적립'
                      }
                    });
                  }}                
                >
                  <p>적립완료</p>
                  <div className="img">
                    <img src={blackArrow} alt="적립 완료" />
                  </div>
                </div>
                <strong>{state.pointData[1].point}</strong>
              </div>
              <div className="withdraw price none">
                <div
                  onClick={() => {
                    history.push({
                      pathname: '/mypage/point',
                    });
                  }}                
                >
                  <p>기타적립</p>
                  <div className="img">
                    <img src={blackArrow} alt="기타 적립" />
                  </div>
                </div>
                <strong>{state.pointData[2].point}</strong>
              </div>
            </div>

          </BonusTitle>

          <BonusPoint className="point">
            <div className="point-sum">
              <h1>연간 포인트</h1>
              <select
                defaultValue={2021}
                onChange={(e) =>
                  setState({ ...state, year: parseInt(e.target.value) })
                }
              >
                {state.yearOptions.map((item, index) => {
                  return (
                    <option key={index} value={item}>
                      {item}
                    </option>
                  );
                })}
              </select>
            </div>
            <div>
              <ul>
                <li className="list-title">
                  <p>실적 포인트</p>
                  <p>{state.performance.sumPoint}원</p>
                </li>
                <li className="list-sub">
                  <p id="royalblue">
                    휴대폰
                    <i>
                      <img
                        onClick={() => {
                          history.push({
                            pathname: '/mypage/home/point/hp',
                            year: state.year,
                          });
                        }}
                        src={circleBtn}
                        alt=""
                      />
                    </i>
                  </p>
                  <p>{state.performance?.hp?.hp}원</p>
                </li>
                <li className="list-sub list-sub-item">
                  <p id="royalblue">
                    렌탈
                    <i>
                      <img
                        onClick={() => {
                          history.push({
                            pathname: '/mypage/home/point/rental',
                            year: state.year,
                          });
                        }}
                        src={circleBtn}
                        alt=""
                      />
                    </i>
                  </p>
                  <p>{state?.performance?.rental.rental}원</p>
                  <ul className="list-sub-item-depth">
                    <li className="list-desc first">
                      <p className="list-desc-pr">직접 판매</p>
                      <p>{state?.performance?.rental?.detail?.direct}원</p>
                    </li>
                    <li className="list-desc">
                      <p className="list-desc-pr">뎁스 판매</p>
                      <p>{state?.performance?.rental?.detail?.depth}원</p>
                    </li>
                  </ul>
                </li>
                <li className="list-sub" style={{ paddingTop: '1.5rem' }}>
                  <p id="royalblue">
                    자동차 다이렉트 보험
                    <i>
                      <img
                        onClick={() => {
                          history.push({
                            pathname: '/mypage/home/point/insu',
                            year: state.year,
                          });
                        }}
                        src={circleBtn}
                        alt=""
                      />
                    </i>
                  </p>
                  <p>{state?.performance?.insurance?.insurance}원</p>
                </li>
              </ul>
            </div>
            <div>
              <ul>
                <li className="list-title">
                  <p id="royalblue">
                    후원 포인트
                    <i>
                      <img
                        onClick={() => {
                          history.push({
                            pathname: '/mypage/home/point/support',
                            year: state.year,
                          });
                        }}
                        src={circleBtn}
                        alt=""
                      />
                    </i>
                  </p>
                  <p>{state.support.sumPoint}원</p>
                </li>
                <li className="list-sub">
                  <p>MD 유치</p>
                  <p>{state?.support?.invite}원</p>
                </li>
                <li className="list-sub mb">
                  <p>월 구독료</p>
                  <p>{state?.support?.subscribe}원</p>
                </li>
              </ul>
            </div>
            <div>
              <ul>
                <li className="list-title">
                  <p>
                    기타 포인트
                    <i>
                      <img
                        onClick={() => {
                          history.push({
                            pathname: '/',
                            year: state.year,
                          });
                        }}
                        src={circleBtn}
                        alt=""
                      />
                    </i>
                  </p>
                  <p>{state.support.sumPoint}원</p>
                </li>
                <li className="list-sub">
                  <p>가입비</p>
                  <p>{state?.support?.invite}원</p>
                </li>
                <li className="list-sub">
                  <p>기타적립</p>
                  <p>{state?.support?.invite}원</p>
                </li>
                <li className="list-sub">
                  <p>기타차감</p>
                  <p>{state?.support?.subscribe}원</p>
                </li>
              </ul>
            </div>
          </BonusPoint>

          <MonthPoint>
            <div className="month-point">
              <p className="month-title">월간 포인트</p>
              <select
                onChange={(event) => {
                  setMonthIndex(parseInt(event.target.value));
                  // console.log(event.target.value);
                }}
              >
                {state.chartData.map((item, index) => {
                  return (
                    <option key={index} value={index}>
                      {item.month}
                    </option>
                  );
                })}
              </select>
            </div>
            <div className="phone-month">
              <div>
                <ul
                  style={{ height: '80px', marginTop: '2.5em' }}
                  className="phone-month-list"
                >
                  {state.chartData.length !== 0 &&
                    state.chartData.map((n, index) => (
                      <PointHomeChart
                        key={index}
                        monthIndex={monthIndex}
                        setMonthIndex={setMonthIndex}
                        percent={n.percent}
                        index={index}
                      />
                    ))}
                </ul>
              </div>

              <div className="phone-num">
                <ol>
                  {state.chartData.length !== 0 &&
                    state.chartData.map((n, index) => (
                      <li className={month && 'active'} key={index}>
                        {n.month}
                      </li>
                    ))}
                </ol>
              </div>
              <div>
                <ul className="detail-list">
                  <li className="detail-list-title">
                    <p>2021년 {monthIndex + 1}월</p>
                    {state.chartData.length !== 0 && (
                      <p>{state.chartData[monthIndex].point}</p>
                    )}
                  </li>
                  <li
                    style={{ display: 'flex', flexDirection: 'column' }}
                    className="detail-list-sub"
                  >
                    {state.monthData.length !== 0 &&
                      state.monthData.map((n, index) => (
                        <div key={index}>
                          {parseInt(n.month) === monthIndex + 1 && (
                            <div
                              style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                marginBottom: '12px',
                              }}
                            >
                              <p>{n.assort}</p>
                              <p>{n.point}</p>
                            </div>
                          )}
                        </div>
                      ))}
                  </li>
                </ul>
              </div>
            </div>
          </MonthPoint>

          <PointBtn>
            <h1>출금 가능 포인트</h1>
            <div className="bonus-point">
              <strong>
                {parseInt(state.pointBalance) <= 0 ? 0 : state.pointBalance}
              </strong>
              <div className="bonus-button">
                <button
                  onClick={() => {
                    history.push({
                      pathname: '/mypage/home/point/withdraw',
                      year: state.year,
                    });
                  }}
                  type="button"
                >
                  출금요청
                </button>
              </div>
            </div>
          </PointBtn>
        </Wrap>
      )}
    </Container>
  );
};

export default PointHomeMobile;

export const Container = styled.section`
  background-color: #f8f8f8;
  padding: 2rem 1.5em 4rem;
  width: 100%;
  h1 {
    margin: 0;
  }
  ul {
    padding: 0;
  }
  li {
    list-style: none;
    display: flex;
    justify-content: space-between;
  }
  p {
    margin: 0;
    font-family: NotoM;
  }
  strong {
    font-weight: NotoM;
  }
`;

export const Wrap = styled.section`
  width: 100%;
  height: 100%;
`;

export const BonusTitle = styled.section`
  margin-bottom: 0.4em;
  padding: 2.6rem 2rem;
  background: ${theme.colors.white};
  border-radius: 3px;

  &.bonus-first {
    margin-bottom: 2em;
  }

  .bonus-retain {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0.3em;
    ${theme.H5};
    border-bottom: 1px solid ${theme.colors.boxGray};

    & > div {
      display: flex;

      .img {
        display: inline-flex;
        margin-right: 10px;
      }
    }
  }

  .bonus-price {
    display: flex;
    justify-content: space-between;
    margin-top: 2.3em;
    text-align: center;

    &.first {
      margin-bottom: 1em;
    }
    
    .withdraw {
      position: relative;
      color: ${theme.colors.grey1};

      & > div {
        display: flex;

        .img {
          display: inline-flex;
          margin-left: 10px;
        } 
      }

      &:before {
        position: absolute;
        display: block;
        content:'';
        right: -23px;
        width: 1px;
        height: 42px;
        background-color: ${theme.colors.boxGray};
      }
    }

    .waiting {
      color: ${theme.colors.grey3};
    }

    .price {
      ${theme.Body1};
    }

    .none:before {
      width: 0;
    }
  }

  .button {
    width: 100%;
    text-align: -webkit-right;
  }
`;

export const BonusPoint = styled.section`
  margin-top: 2em;
  background-color: ${theme.colors.white};
  padding: 2em 1.5em;
  width: 100%;
  .point {
    padding: 2em 1.5em;
    background-color: ${theme.colors.white};
    border-radius: 3px;
    &-sum {
      font-weight: 500;
      display: flex;
      justify-content: space-between;
      align-items: center;
      ${theme.H5};
    }
  }

  select {
    width: 93px;
    height: 32px;
    padding: 0 8px;
    border: 1px solid ${theme.colors.boxGray};
    background-color: ${theme.colors.white};
    box-sizing: content-box;
    font-size: 14px;
    font-family: NotoR;
    option {
      font-family: NotoR;
    }
  }
  .list-title {
    margin-top: 4rem;
    color: ${theme.colors.black1};
    border-bottom: 1px solid ${theme.colors.grey2};
    ${theme.H5};
    p#royalblue {
      color: ${theme.colors.royalblue};
    }
  }
  .list-sub {
    align-items: center;
    margin: 1em 0;

    ${theme.Title};
    p {
      color: ${theme.colors.grey2};
      display: flex;
    }
    p#royalblue {
      color: ${theme.colors.royalblue};
    }
  }
  .list-sub-item {
    position: relative;
    margin-bottom: 4rem;
    &-depth {
      position: absolute;
      top: 100%;
      width: 100%;
      .first {
        margin-top: 0.5em;
      }
      .list-desc {
        padding-left: 2em;
        p {
          ${theme.SubTitle1};
        }
      }
    }
  }
  i {
    margin-left: 0.3em;
    display: inline-flex;
    a {
      display: inline-flex;
    }
  }
`;

export const MonthPoint = styled.section`
  padding: 2em 1.5em;
  margin-top: 2em;
  background-color: ${theme.colors.white};
  border-radius: 3px;

  .month-point {
    display: flex;
    justify-content: space-between;
    align-items: center;
    ${theme.H5};
  }

  .month-title {
    font-size: ${theme.fontSizes.H5};
    padding-bottom: 0.2em;
  }

  .phone-month {
    &-list {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
      li {
        position: relative;
        width: 15.3px;
        height: 70px;
        background: ${theme.colors.royalblue};
        text-align: center;
      }
    }
  }

  select {
    width: 93px;
    height: 32px;
    padding: 0 8px;
    border: 1px solid ${theme.colors.boxGray};
    background-color: ${theme.colors.white};
    box-sizing: content-box;
    font-size: 14px;
    font-family: NotoR;
    option {
      font-family: NotoR;
    }
  }

  .phone-num {
    ol {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
    }
    li {
      justify-content: center;
      position: relative;
      width: 15.3px;
      margin-top: 5px;
      color: ${theme.colors.grey1};
      ${theme.Button2};
      /* 삼각형 */
      /* &:before {
        position: absolute;
        display: block;
        content: '';
        top: 49px;
        left: 2px;
        width: 0;
        height: 0;
        border-top: 1.5em solid transparent;
        border-right: 1em solid transparent;
        border-bottom: 1.5em solid transparent;
        border-left: 1em solid transparent;
      } */
      &.active {
        &:before {
          border-bottom: 1.5em solid ${theme.colors.boxGray};
        }
      }
    }
  }
  .detail-list {
    position: relative;
    margin-top: 3em;
    padding: 2em;
    border-radius: 3px;
    background-color: ${theme.colors.boxGray};
    &-title {
      p {
        font-size: ${theme.fontSizes.H5};
        color: ${theme.colors.black1};
      }
    }
    &-sub {
      margin-top: 1em;
      p {
        font-size: ${theme.fontSizes.SubTitle1};
        font-family: NotoR;
        color: ${theme.colors.grey2};
      }
    }
  }
`;

export const PointBtn = styled.div`
  margin-top: 2rem;
  padding: 1em;
  background-color: ${theme.colors.white};
  ${theme.H5};
  h1 {
    height: 36px;
    display: flex;
    align-items: baseline;
    border-bottom: 1px solid ${theme.colors.grey2};
  }
  .bonus-point {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.5em;
  }
  button {
    width: 121px;
    height: 32px;
    color: ${theme.colors.white};
    font-size: ${theme.fontSizes.Button1};
    font-family: NotoR;
    background-color: ${theme.colors.royalblue};
    border-style: none;
    border-radius: 3px;
  }
`;
