import React, { useEffect, useState, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { Button } from '@progress/kendo-react-buttons';
import { Input } from '@progress/kendo-react-inputs';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import Tree from './Tree';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
// css
import '../../../styles/losmap-tree.css';

const renderTree = (nodes) => (
  <Tree key={nodes.idx} id={nodes.memId} content={nodes.memName}>
    {Array.isArray(nodes.children)
      ? nodes.children.map((node) => renderTree(node))
      : null
    }
  </Tree>
);

const Losmap = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: 'Losmap',
  };

  const dispatch = useDispatch();
  const isMobile = _isMobile();

  const [state, setState] = useState({
    isLoading: true,
    loadData: [],
    sponsId: '',
  });

  const _getData = useCallback(async () => {
    const url = '/api/admin/member/get_losmap.php';
    const params = {
      sponsId: state.sponsId,
    };

    await _axios(url, params).then((response) => {
      console.log(response);

      setState((prevState) => ({
        ...state,
        isLoading: false,
        loadData: response.data,
      }));
    });
  }, [state]);

  const _handleSubmit = async (e) => {
    e.preventDefault();

    const url = '/api/admin/member/get_losmap.php';
    const params = {
      sponsId: state.sponsId,
    };

    // console.log(params);

    await _axios(url, params).then((response) => {
      // console.log(response);

      setState((prevState) => ({
        ...state,
        isLoading: false,
        loadData: response.data,
      }));
    });
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  useEffect(() => {
    if (state.isLoading) {
      _getData();
    }
  }, [_getData, state.isLoading]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const { isLoading, loadData } = state;

  return (
    <Container>
      <SearchWrap>
        {!isMobile && <Title>회원관리 〉 Losmap</Title>}
        <Input
          validityStyles={false}
          type="text"
          name="sponsId"
          value={state.sponsId}
          style={{ width: '100px' }}
          maxLength={20}
          required={false}
          placeholder="회원ID"
          onChange={_handleInputChange}
        />
        <Button style={{ marginLeft: '10px' }} onClick={_handleSubmit}>
          검색
        </Button>
      </SearchWrap>

      {isLoading ? (
        <div style={{ padding: '20px' }}>loading...</div>
      ) : (
        <TreeWrap>{renderTree(loadData)}</TreeWrap>
      )}
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
`;

// 검색
const SearchWrap = styled.div`
  padding: 10px;
`;

const TreeWrap = styled.div`
  @media (max-width: 768px) {
    height: 80%;
  }

  @media (min-width: 769px) {
    height: 65vh;
  }

  padding: 10px;
  background: #fff;
  overflow: auto;
  font-size: 13px;
  line-height: 21px;
`;

const Title = styled.div`
  font-size: 0.9em;
  font-weight: 600;
  color: #666666;
  margin-bottom: 15px;
  margin-top: 10px;
`;

export default Losmap;
