import _axios from '../../utils/axios';

export const initialState = {
  loading: false,
  result: '',
  message: '',
  memId: '', // 회원ID
  custName: '', // 고객명
  hpNo: '', // 고객명 연락처
  carNoType: '차량번호', //차량/차대번호 구분
  carNo: '', // 차량/차대번호
  expiredDate: '만기월 선택', //만기월
  custRegion: '거주지역 선택', //거주지역
  insuCheck: false,
  marketingFlag: 'Y', //insu 마케팅 동의 여부
  marketingAgree: 'Y', //스파이더 플랫폼 마케팅 동의 여부
};

export const INSU_CHECK = 'insu/INSU_CHECK';
export const INSERT_MEMBER_ID = 'insu/INSERT_MEMBER_ID';
export const INSERT_INFO = 'insu/INSERT_INFO';
export const SUCCESS = 'insu/SUCCESS';
export const SUBMIT = 'insu/SUBMIT';
export const INIT = 'insu/INIT';
export const MARKETTING_CHECK = 'insu/MARKETTING_CHECK';
export const LOADING = 'insu/LOADING';
export const INPUT_CHANGE = 'insu/INPUT_CHANGE';
export const insuCheck = (check) => async (dispatch) => {
  dispatch({
    type: INSU_CHECK,
    insuCheck: check,
  });
};

export const inserMemberId = (memid) => async (dispatch) => {
  dispatch({
    type: INSERT_MEMBER_ID,
    memId: memid,
  });
};

export const inserInfo = (inputs) => async (
  dispatch,
  getState,
  { history },
) => {
  dispatch({
    type: INSERT_INFO,
    custName: inputs.custName,
    hpNo: inputs.hpNo,
    carNoType: inputs.carNoType,
    carNo: inputs.carNo,
    expiredDate: inputs.expiredDate,
    custRegion: inputs.custRegion,
  });

  history.push('/insu/apply3');
};

export const marketingCheck = (marketingAgree, marketingFlag) => async (
  dispatch,
  getState,
  { history },
) => {
  dispatch({
    type: MARKETTING_CHECK,
    marketingFlag: marketingFlag,
    marketingAgree: marketingAgree,
  });
  history.goBack();
};

export const apply = (inputs) => async (dispatch, getState) => {
  dispatch({
    type: LOADING,
    loading: true,
  });

  dispatch({
    type: INSERT_INFO,
    custName: inputs.custName,
    hpNo: inputs.hpNo,
    carNoType: inputs.carNoType,
    carNo: inputs.carNo,
    expiredDate: inputs.expiredDate,
    custRegion: inputs.custRegion,
  });

  const data = getState().insu;

  const {
    memId, // 회원ID
    custName, // 고객명
    hpNo, // 고객명 연락처
    carNoType, //차량/차대번호 구분
    carNo, // 차량/차대번호
    expiredDate, //만기월
    custRegion, //거주지역
    marketingFlag,
    marketingAgree,
  } = data;

  const url = '/api/partner/chabot/set_request_regist.php';
  //const url = '/api/partner/insu/set_request_update.php';
  let temp;
  if (carNoType === '차량번호') {
    temp = '1';
  } else {
    temp = '2';
  }
  const params = {
    memId, // 회원ID
    custName, // 고객명
    hpNo, // 고객명 연락처
    carNoType: temp, //차량/차대번호 구분
    carNo, // 차량/차대번호
    expiredDate, //만기월
    custRegion, //거주지역
    marketingFlag,
    marketingAgree,
  };
  console.log(params);

  const response = await _axios(url, params);
  console.log(response);

  return dispatch({
    type: SUCCESS,
    result: response.result,
    message: response.message,
  });
};

export const loadingFalse = (check) => async (dispatch) => {
  dispatch({
    type: LOADING,
    loading: false,
  });
};

export const insuInit = (check) => async (dispatch) => {
  dispatch({
    type: INIT,
  });
};

export const replaceApply1Page = (memId) => async (
  dispatch,
  getState,
  { history },
) => {
  console.log('실행');
  history.push(`/insu/applyComplete/?memId=${memId}`);
};

export default function insu(state = initialState, action) {
  switch (action.type) {
    case INSU_CHECK:
      return {
        ...state,
        insuCheck: action.insuCheck,
      };

    case INSERT_MEMBER_ID:
      return {
        ...state,
        memId: action.memId,
      };

    case INSERT_INFO:
      return {
        ...state,
        custName: action.custName,
        hpNo: action.hpNo,
        carNoType: action.carNoType,
        carNo: action.carNo,
        expiredDate: action.expiredDate,
        custRegion: action.custRegion,
      };

    case MARKETTING_CHECK:
      return {
        ...state,
        marketingFlag: action.marketingFlag,
        marketingAgree: action.marketingAgree,
      };

    case LOADING:
      return {
        ...state,
        loading: action.loading,
      };

    case SUCCESS:
      return {
        ...state,
        result: action.result,
        message: action.message,
      };

    case INIT:
      return {
        ...state,
        loading: false,
        result: '',
        message: '',
        custName: '', // 고객명
        hpNo: '', // 고객명 연락처
        carNoType: '차량번호', //차량/차대번호 구분
        carNo: '', // 차량/차대번호
        expiredDate: '만기월 선택', //만기월
        custRegion: '거주지역 선택', //거주지역
        insuCheck: false,
        marketingFlag: 'Y', //insu 마케팅 동의 여부
        marketingAgree: 'Y',
      };

    default:
      return state;
  }
}
