import React from 'react';
import { Switch } from 'react-router-dom';
// 개발자 Components
import _isMobile from '../utils/isMobile';
import AuthRoute from '../components/common/AuthRoute'; // 로그인 Component 체크
import AdminHome from '../components/admin/AdminHome'; // 관리자페이지 > 홈

import Member from './admin/Member'; // 회원관리
import Cms from './admin/Cms'; // CMS관리
import Point from './admin/Point'; // 포인트관리
import Sales from './admin/Sales'; // 매출관리
import Hp from './admin/Hp'; // 휴대폰신청관리
import InsuRequest from './admin/InsuRequest'; // 다이렉트보험
import Bbs from './admin/Bbs'; // 게시판관리
// 환경설정
import Menu from './admin/Menu'; // 메뉴관리
import Admin from './admin/Admin'; // 관리자관리
import SmsMessage from './admin/SmsMessage'; // SMS메세지관리
import Term from './admin/Term'; // 약관관리

import InitSetting from '../components/admin/initSetting/InitSetting'; // 기초정보설정
import MyInfo from '../components/admin/myInfo/MyInfo'; // 내정보

export default function AdminRoute() {
  const isMobile = _isMobile();

  return (
    <Switch>
      <AuthRoute
        path="/admin/home"
        render={(props) => <AdminHome {...props} />}
      />
      <AuthRoute
        path="/admin/member"
        render={(props) => <Member {...props} />}
      />
      <AuthRoute 
        path="/admin/cms" 
        render={(props) => <Cms {...props} />} 
      />
      <AuthRoute 
        path="/admin/sales" 
        render={(props) => <Sales {...props} />} 
      />
      <AuthRoute 
        path="/admin/point" 
        render={(props) => <Point {...props} />} 
      />
      <AuthRoute
        path="/admin/hp"
        render={(props) => <Hp {...props} />}
      />
      <AuthRoute
        path="/admin/insuRequest"
        render={(props) => <InsuRequest {...props} />}
      />
      <AuthRoute 
        path="/admin/bbs" 
        render={(props) => <Bbs {...props} />} 
      />
      {/* 환경설정 */}
      <AuthRoute path="/admin/menu" render={(props) => <Menu {...props} />} />
      <AuthRoute path="/admin/admin" render={(props) => <Admin {...props} />} />
      <AuthRoute
        path="/admin/initSetting"
        render={(props) => <InitSetting {...props} />}
      />
      <AuthRoute
        path="/admin/smsMessage"
        render={(props) => <SmsMessage {...props} />}
      />
      <AuthRoute path="/admin/term" render={(props) => <Term {...props} />} />
      <AuthRoute
        path="/admin/adminInfo"
        render={(props) => <MyInfo {...props} />}
      />
    </Switch>
  );
}
