/* **********************************************************************
  관리자페이지 > 수수료관리 〉수수료목록
  - 2021.05.19. 박태수
  - 2021.05.26. 양민석 : 스파이더2 리뉴얼
  - 2021.06.01. 양민석 : 관리자페이지 코드 띄어쓰기 가독성해결
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const AccurateList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: true, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '정산서관리',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';

  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      rowTotal: 0,
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      idx: '',
      accurateDate: '',
      accurateStatus: '',
      statusOptions: [],
      changeStatus: {
        code: '',
        name: '',
      },
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/admin/commission/get_accurate_list.php';
    const params = {
      searchValue: state.searchAble.searchValue,
      idx: state.searchAble.idx,
      accurateDate: state.searchAble.accurateDate,
      accurateStatus: state.searchAble.accurateStatus,
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        statusOptions: response.statusOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '',
        items: [
          {type: 'text',width: '84%',name: 'searchValue',value: searchAble.searchValue,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '검색어',
        items: [{type: 'text',width: '100%',name: 'searchValue',value: searchAble.searchValue,},],
      };
  const menuStatus = {
    title: '정산번호',
    items: [{type: 'text',width: '100%',name: 'idx',value: searchAble.idx,options: searchAble.statusOptions,},],
  };

  const wdate = {
    title: '정산일자',
    items: [
      {type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate,},
      {type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate,},
    ],
  };

  const accurateStatus = {
    title: '정산상태',
    items: [
      {type: 'comboBox',width: '100%',name: 'accurateStatus',value: searchAble.accurateStatus,options: searchAble.statusOptions,},
    ],
  };

  const searchFields = isMobile
    ? [searchValue]
    : [searchValue, menuStatus, wdate, accurateStatus];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',align: 'center',width: '10%',},
        {type: 'text',title: '번호',name: 'idx',align: 'center',width: '10%',},
        {type: 'text',title: '회원명',name: 'memName',align: 'center',width: '15%',},
        {type: 'text',title: '회원ID',name: 'memId',align: 'center',width: '20%',},
        {type: 'text',title: '납부수단',name: 'accurateStatus',align: 'center',width: '20%',},
        {type: 'text',title: '가입일자',name: 'wdate',align: 'center',width: '25%',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '5%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '5%',align: 'center',},
        {type: 'text',title: '번호',name: 'idx',width: '7%',align: 'center',},
        {type: 'text',title: '회원ID',name: 'memId',width: '10%',align: 'center',},
        {type: 'text',title: '회원명',name: 'memName',width: '10%',align: 'center',},
        {type: 'text',title: '정산기간',name: 'accurateDate',width: '15%',align: 'center',},
        {type: 'text',title: '수수료',name: 'commission',width: '10%',align: 'center',},
        {type: 'text',title: '가감금액',name: 'otherAmount',width: '8%',align: 'center',},
        {type: 'text',title: '정산금액',name: 'totalAmount',width: '10%',align: 'center',},
        {type: 'text',title: '정산상태',name: 'accurateStatus',width: '8%',align: 'center',},
        {type: 'text',title: '정산일자',name: 'wdate',width: '12%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>포인트관리 〉 정산목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={true}
            isDeleteButton={false}
            infoPath="/admin/point/accurate/info"
            deleteUrl="/api/admin/setting/set_menu_delete.php"
          />

          <ListView
            state={state}
            setState={setState}
            headers={listHeader}
            infoPath="/admin/point/accurate/info"
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default AccurateList;
