import React, { useCallback, useEffect, useState } from 'react';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import { Input, TextArea } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { useDispatch } from 'react-redux';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const SmsMessageInfo = (props) => {
  const dispatch = useDispatch();
  const [state, setState] = useState({
    isLoading: true,
    isDelete: false,
    idx: props.location.idx,
    mode: props.location.mode,
    assort: '',
    assortOptions: [],
    code: '',
    subject: '',
    content: '',
    success: false,
    message: null,
    visibleDialog: false,
    confirmDialog: false,
  });

  const _getData = useCallback(async () => {
    const url = '/api/admin/setting/get_sms_message_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      assort: { code: data.assort, name: data.assortName },
      assortOptions: data.assortOptions,
      code: data.code,
      subject: data.subject,
      content: data.content,
    });
  }, [state]);

  const _setData = async () => {
    const url = '/api/admin/setting/set_sms_message_update.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _handleTextarea = (e) => {
    setState({
      ...state,
      [e.target.element.current.name]: e.value,
    });
  };

  // 삭제
  const _deleteData = useCallback(async () => {
    const url = '/api/admin/setting/set_sms_message_delete.php';
    const { message } = await _axios(url, state);
    setState({
      ...state,
      isDelete: false,
      success: true,
      message: message,
      visibleDialog: true,
    });
  }, [state]);

  // 저장
  const _handleSubmit = (e) => {
    e.preventDefault();
    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, [dispatch]);

  useEffect(() => {
    if (state.isDelete) {
      _deleteData();
    }
  }, [state.isDelete, _deleteData]);

  const assortMessage = "'구분'을 선택해주세요!";
  const codeMessage = "'코드'를 입력해주세요!";
  const subjectMessage = "'제목'을입력해주세요!";

  const direction = isMobile ? 'column' : 'column';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '100%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form
        style={{ display: 'flex', justifyContent: 'center' }}
        onSubmit={_handleSubmit}
      >
        <Wrap width={'98%'}>
          <Title>SMS메세지관리 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>구분</Label>
                <ComboBox
                  style={{ width: '74%', color: '#dcdcdc' }}
                  name="assort"
                  value={state.assort}
                  data={state.assortOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={assortMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="code"
                  value={state.code}
                  style={{ width: '74%', marginTop: '1px' }}
                  maxLength={20}
                  required={true}
                  validationMessage={codeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>제목</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="subject"
                  value={state.subject}
                  style={{ width: '74%', fontWeight: 'bold' }}
                  maxLength={50}
                  required={true}
                  validationMessage={subjectMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>내용</Label>
                <TextArea
                  name="content"
                  value={state.content}
                  style={{ width: '74%', marginTop: '1px' }}
                  rows={15}
                  onChange={_handleTextarea}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() =>
                  setState({
                    ...state,
                    isSave: true,
                  })
                }
              >
                저장하기
              </Button>
            )}

            <Button
              style={{ marginLeft: '10px' }}
              type={'button'}
              onClick={() => props.history.goBack()}
            >
              이전화면
            </Button>
            <Button
              style={{ marginLeft: '10px' }}
              type={'button'}
              onClick={() => {
                setState({
                  ...state,
                  confirmDialog: true,
                });
              }}
            >
              삭제하기
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}

      {state.confirmDialog && (
        <Dialog title={'확인'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>
            삭제하시겠습니까?
          </p>
          <DialogActionsBar>
            <button className="k-button" onClick={_toggleDialog}>
              아니오
            </button>
            <button
              className="k-button"
              onClick={() => {
                setState({
                  ...state,
                  confirmDialog: false,
                  isDelete: true,
                });
                setTimeout(() => {
                  setState({ ...state, success: false });
                  props.history.goBack();
                }, 1000);
              }}
            >
              예
            </button>
          </DialogActionsBar>
        </Dialog>
      )}
    </Container>
  );
};

export default SmsMessageInfo;
