/*
  설명: 기본 정보 수정 페이지
  - 2021.05.18. 안예린, import 수정
*/
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import MyProfile from './MyProfile';
import MyInfoGuide from './MyInfoGuide';
// css
import { DropDownListRound } from '../../../css/common-component';
import { SwiperMiddleLabel } from '../../../css/common-component';
import '../../../css/kendo.css';
import theme from '../../../styles/theme';

// icon & images
import FileCopyIcon from '@material-ui/icons/FileCopy';

const MyInfoUpdate = () => {
  const headerInfo = {
    isBackground: false,
    isBugger: true,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '기본정보',
  };
  const dispatch = useDispatch();
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);
  const [state, setState] = useState({
    isLoading: true,
    userId: userInfo.userId,
    userPw: '',
    userName: '',
    hpNo: '',
    email: '',
    gajaId: '',
    registNo: '',
    registInput1: '',
    registInput2: '',
    accountName: '',
    accountNo: '',
    accountBank: '',
    accountBankName: [{}],
    cmsStatus: '',
    contractStatus: '',
    memAssort: '',
    memStatus: '',
    confirmPw: '',
    photo: '',
    taxAssort: '',
    success: false,
    message: null,
    visibleDialog: false,
    bankOptions: [],
    //테스트 이미지상태
    sendImg: false,
  });
  // 모바일버전 상태
  const [originalState, setOriginalState] = useState({
    accountNo: '',
    payerNumber: '',
    accountName: '',
  });
  const [newCustomerState, setNewCustomerState] = useState(
    localStorage.getItem('myinfoGuide') ? false : true,
  );
  const [buttonCheckState, setButtonCheckState] = useState(false);
  const [isAccountCheckAlert, setIsAccountCheckAlert] = useState({
    show: false,
    content: '',
  });
  const [photo, setPhoto] = useState([]);
  const _getData = async () => {
    const url = '/api/member/get_my_info.php';
    const params = {
      userId: state.userId,
    };
    const { data } = await _axios(url, params);
    // console.log(data);
    // 은행리스트 api
    const url2 = '/api/account/get_user_info.php';
    const params2 = {
      userId: state.userId,
    };
    const bankList = await _axios(url2, params2);
    setState({
      ...state,
      isLoading: false,
      userPw: data.userPw,
      userName: data.userName,
      hpNo: data.hpNo,
      email: data.email,
      gajaId: data.gajaId,
      registNo: data.registNo,
      registNo2: data.registNo2,
      registInput1: data.registNo.substring(0, 6),
      registInput2: data.registNo.substring(7, 14),
      accountName: data.accountName,
      accountNo: data.accountNo,
      accountBank: data.accountBank,
      cmsStatus: data.cmsStatus,
      contractStatus: data.contractStatus,
      memAssort: data.memAssort,
      memStatus: data.memStatus,
      taxAssort: data.taxAssort,
      businessAssort: data.businessAssort,
      photo: data.photo,
      bankOptions: bankList.data.bankOptions,
      accountBankName: bankList.data.bankOptions.filter(
        (v) => v.code === data.accountBank,
      ),
    });
    setOriginalState({
      accountNo: data.accountNo,
      payerNumber: data.registNo,
      accountName: data.accountName,
    });
  };

  // 실명계좌 확인
  const _OnCheckAccount = async (e) => {
    e.preventDefault();
    const url = '/api/account/get_real_check.php';
    const params = {
      paymentCompany: state.accountBank,
      paymentNumber: state.accountNo,
      payerNumber: state.registInput1,
    };
    const { result, message } = await _axios(url, params);
    if (result === '1') {
      setIsAccountCheckAlert({
        show: true,
        result: result,
        content: message,
      });
    } else {
      _accountSetData();
    }
  };
  const RefCopy = useRef();
  const RefCopyIcon = useRef();
  const RefregistNo1 = useRef();
  const RefregistNo2 = useRef();
  // 정산계좌정보의 내용이 바뀔때만 수정버튼 활성화
  const _buttonCheck = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
    if (e.target.value !== originalState[e.target.name]) {
      setButtonCheckState(true);
    } else if (e.target.value === originalState[e.target.name]) {
      setButtonCheckState(false);
    }
  };
  // 정산 계좌 정보 저장 함수
  const _accountSetData = async () => {
    const url = '/api/member/set_my_bank_update.php';
    const params = {
      userId: state.userId,
      registNo: state.registInput1 + '-' + state.registInput2,
      accountName: state.accountName,
      accountNo: state.accountNo,
      accountBank: state.accountBank,
    };
    const { result, message } = await _axios(url, params);
    setIsAccountCheckAlert({
      show: true,
      result: result,
      content: message,
    });
    setTimeout(() => {
      setIsAccountCheckAlert({
        show: false,
        result: result,
        content: message,
      });
    }, 1000);
  };
  const _handleSelectBank = (e) => {
    setState({
      ...state,
      accountBank: e.value.code,
    });
  };
  // 가자렌탈 아이디 복사 함수
  const f_copyText = () => {
    RefCopyIcon.current.style.color = '#000f34';
    RefCopy.current.select();
    RefCopy.current.setSelectionRange(0, 99999);
    document.execCommand('copy');
    setTimeout(() => {
      RefCopyIcon.current.style.color = '#979797';
    }, 500);
  };

  useEffect(() => {
    if (state.sendImg) {
      _photoData();
    } else if (state.isLoading) {
      _getData();
    }
  }, [state.sendImg, state.isLoading]);
  const _photoData = async () => {
    const url = '/api/member/set_photo_update.php';
    const params = {
      userId: state.userId,
      photo:
        'http://spiderplatform.co.kr/upload/thumbnail/' + photo[0].file.name,
    };
    const { result, message } = await _axios(url, params);
    setState({
      ...state,
      sendImg: false,
      isLoading: true,
    });
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  return (
    <Container>
      <Wrap>
        <Inner>
          <MyInfoGuide
            newCustomerState={newCustomerState}
            setNewCustomerState={setNewCustomerState}
          />
          <MyProfile
            state={state}
            setState={setState}
            photo={photo}
            setPhoto={setPhoto}
          />

          <StyledMyInfo style={{ marginBottom: '48px' }}>
            <StyledTitleCss>기본정보</StyledTitleCss>
            <StyledListBox>
              <div style={{ width: '30%' }}>휴대폰</div>
              <div style={{ width: '70%' }}>{state.hpNo}</div>
            </StyledListBox>
            <StyledListBox>
              <div style={{ width: '30%' }}>가자렌탈ID</div>
              <div style={{ width: '70%', justifyContent: 'space-between' }}>
                <input
                  style={{
                    border: 'none',
                    backgroundColor: 'transparent',
                    outline: 'none',
                  }}
                  ref={RefCopy}
                  value={state.gajaId || ''}
                  readOnly
                ></input>
                <FileCopyIcon
                  ref={RefCopyIcon}
                  onClick={f_copyText}
                  style={{
                    color: '#979797',
                    fontSize: '1.5em',
                    marginRight: '0.2em',
                  }}
                />
              </div>
            </StyledListBox>
          </StyledMyInfo>
          <StyledCalculate newCustomerState={newCustomerState}>
            <StyledTitleCss>
              <div>정산 계좌 정보</div>

              <StyledUpdataButton>
                {buttonCheckState ? (
                  <button
                    style={{ backgroundColor: '#000f34' }}
                    onClick={_OnCheckAccount}
                  >
                    수정하기
                  </button>
                ) : (
                  <button
                    // ref={RefButton}
                    type="button"
                  >
                    수정하기
                  </button>
                )}
              </StyledUpdataButton>
            </StyledTitleCss>
            <StyledListBox>
              <div style={{ width: '30%' }}>은행명</div>
              <DropDownListRound
                style={{
                  borderRadius: '0',
                  border: 'none',
                  padding: 'none',
                  fontSize: '16px',
                  backgroundColor: 'transparent',
                }}
                type="text"
                name="accountBank"
                data={state.bankOptions}
                dataItemKey="code"
                textField="name"
                onChange={_handleSelectBank}
                required={false}
                width={'55%'}
                height={'100%'}
                defaultItem={{
                  name:
                    state.accountBankName.length === 0
                      ? ''
                      : state.accountBankName[0].name,
                  code: state.accountBank,
                }}
              ></DropDownListRound>
            </StyledListBox>
            <StyledListBox>
              <div style={{ width: '30%' }}>예금주</div>
              <input
                onChange={_buttonCheck}
                style={{ width: '55%' }}
                name="accountName"
                value={state.accountName}
                required={false}
              ></input>
            </StyledListBox>
            <StyledListBox>
              <div style={{ width: '30%' }}>계좌번호</div>
              <input
                onChange={_buttonCheck}
                style={{ width: '55%' }}
                name="accountNo"
                value={state.accountNo}
                required={false}
              ></input>
            </StyledListBox>
            <StyledListBox>
              <div style={{ width: '30%' }}>생년월일</div>
              <section style={{ width: '55%', display: 'flex' }}>
                <input
                  ref={RefregistNo1}
                  onChange={(e) => {
                    // if (e.target.value.length === 6) {
                    //   RefregistNo2.current.focus();
                    // }
                    _buttonCheck(e);
                  }}
                  name="registInput1"
                  value={state.registInput1}
                  style={{ width: '45%' }}
                  maxLength="6"
                  type="text"
                />
                {/* <div
                  style={{
                    width: '10%',
                    paddingRight: '0.5em',
                    display: 'flex',
                    justifyContent: 'center',
                  }}
                >
                  -
                </div>
                <input
                  name="registInput2"
                  value={state.registInput2}
                  onChange={(e) => {
                    if (e.target.value.length === 0) {
                      RefregistNo1.current.focus();
                    }
                    _buttonCheck(e);
                  }}
                  ref={RefregistNo2}
                  style={{ width: '45%' }}
                  maxLength="7"
                  type="password"
                /> */}
              </section>
            </StyledListBox>
          </StyledCalculate>

          <StyledMyInfo style={{ marginTop: '48px' }}>
            <StyledTitleCss>세무정보</StyledTitleCss>
            <StyledListBox>
              <div style={{ width: '30%' }}>구분</div>
              <div style={{ width: '70%' }}>{state.businessAssort}</div>
            </StyledListBox>
            <StyledListBox>
              <div style={{ width: '30%' }}>세금구분</div>
              <div style={{ width: '70%' }}>{state.taxAssort}</div>
            </StyledListBox>
            <StyledListBox>
              <div style={{ width: '30%' }}>주민번호</div>
              <div style={{ width: '70%' }}>{state.registNo2}</div>
            </StyledListBox>
          </StyledMyInfo>

          {isAccountCheckAlert.show && (
            <StyledDialog
              // id="bonusModal"
              width={'256px'}
              height={'auto'}
              title={' '}
              onClose={() => {
                isAccountCheckAlert.show
                  ? setIsAccountCheckAlert({ show: false })
                  : setIsAccountCheckAlert({ show: true });
              }}
            >
              <SwiperMiddleLabel
                width="216px"
                height="auto"
                margin="0px auto 0px auto"
                style={{
                  color: '#000f34',
                  fontSize: '15px',
                  textAlign: 'center',
                }}
              >
                {isAccountCheckAlert.content}
              </SwiperMiddleLabel>
              <DialogActionsBar>
                <button
                  className="k-button"
                  onClick={() => {
                    isAccountCheckAlert.show
                      ? setIsAccountCheckAlert({ show: false })
                      : setIsAccountCheckAlert({ show: true });
                  }}
                >
                  확인
                </button>
              </DialogActionsBar>
            </StyledDialog>
          )}
        </Inner>
      </Wrap>
    </Container>
  );
};

// 회원수정 스타일 컴포넌트
const Container = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
`;

const Wrap = styled.div`
  display: flex;
  flex-direction: column;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 450px;
    margin-top: 50px;
  }
`;

const Inner = styled.div`
  background-color: ${theme.colors.lightGrey};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
`;
const StyledTitleCss = styled.div`
  display: flex;
  justify-content: space-between;
  color: #8f92a1;
  align-items: center;
  ${theme.Caption}
`;
const StyledListBox = styled.div`
  height: 2.5em;
  color: #000000;
  display: flex;
  align-items: center;
  // border-bottom: 1px solid #d6d6d6;
  ${theme.Body1}
  div {
    display: flex;
    align-items: center;
  }
  input {
    border: none;
    background-color: transparent;
    padding: 0;
  }
  .k-dropdown .k-input {
    border: none;
    justify-content: flex-start;
    height: 100%;
    color: black;
    ${theme.Body1}
    padding: 0px;
  }
  .k-maskedtextbox .k-textbox {
    font-size: 16px;
    border: none;
    background-color: transparent;
    color: black;
    // padding-left: 0.5em;
  }
  .k-input {
    height: 100%;
  }
`;

const StyledMyInfo = styled.section`
  width: 90%;
  margin-bottom: 2em;
  div {
    // padding-left: 0.5em;
  }

  @media (min-width: 769px) {
    padding-left: 2em;
  }
`;
const StyledCalculate = styled.section`
  width: 90%;
  position: relative;

  div {
    // padding-left: 0.5em;
  }

  @media (min-width: 769px) {
    padding-left: 2em;
  }
`;
const StyledUpdataButton = styled.section`
  width: auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;

  button {
    outline: none;
    border: none;
    border-radius: 3px;
    width: 88px;
    height: 2.5em;
    background-color: ${theme.colors.navyBox};
    color: #ffffff;
    ${theme.Button1};
  }

  @media (min-width: 769px) {
    padding-left: 2em;
  }
`;
const StyledDialog = styled(Dialog)`
  .k-dialog-button-layout-stretched {
    background-color: #000f34;
    color: #fff;
  }
  .k-button {
    background-color: #000f34;
    color: #fff;
  }
`;
export default MyInfoUpdate;
