import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
import { SquareButton } from '../../../css/common-component';
import _isMobile from '../../../utils/isMobile';
import { setHeader } from '../../../redux/actions/header';
// css
import {
  Container,
  Grid,
  SmallLabel,
  Td,
  Th,
  XsmallLabel,
} from '../../../css/commiBest';
// 아이콘 , 이미지
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowAltCircleRight } from '@fortawesome/free-solid-svg-icons';

const CommiBest = (props) => {
  const dispatch = useDispatch();
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '수수료목록',
  };
  const [mounted, setMounted] = useState(true);
  const [state, setState] = useState({
    SKT: [],
    KT: [],
    'LGU+': [],
    알뜰폰: [],
    select: 'SKT',
  });

  const _onGoAllCommi = () => {
    props.history.push('/mypage/hpRequest/commiTelecom');
  };

  const _getData = useCallback(async () => {
    const url = '/api/member/hp/get_commiBest.php';
    const { data } = await _axios(url);
    return data;
  });

  useEffect(() => {
    dispatch(setHeader(headerInfo));
    _getData().then((response) => {
      if (response && mounted) {
        setState({
          SKT:
            response[0].telecomName === 'SKT'
              ? response[0].telecomInfo
              : alert('정보가 맞지않습니다'),
          KT:
            response[1].telecomName === 'KT'
              ? response[1].telecomInfo
              : alert('정보가 맞지않습니다'),
          'LGU+':
            response[2].telecomName === 'LGU+'
              ? response[2].telecomInfo
              : alert('정보가 맞지않습니다'),
          알뜰폰:
            response[3].telecomName === '알뜰폰'
              ? response[3].telecomInfo
              : alert('정보가 맞지않습니다'),
          select: 'SKT',
        });
      }
    });
    return () => {
      setMounted(false);
    };
  }, []);

  const { SKT, KT } = state;
  const LGU = state['LGU+'];
  let selectTelocom;
  let assort;
  let items = [];
  const handleSktItem = (index) => {
    let item;
    item = (
      <>
        <div className="col-2 font">{selectTelocom[index]?.priceSkt}</div>
        <div className="col-3 font">{selectTelocom[index]?.priceKt}</div>
        <div className="col-2 font">{selectTelocom[index]?.priceLgu}</div>
      </>
    );
    return item;
  };

  const handleKtItem = (index) => {
    let item;
    item = (
      <>
        <div className="col-2 font">{selectTelocom[index]?.priceKt}</div>
        <div className="col-3 font">{selectTelocom[index]?.priceSkt}</div>
        <div className="col-2 font">{selectTelocom[index]?.priceLgu}</div>
      </>
    );
    return item;
  };

  const handleLgItem = (index) => {
    let item;
    item = (
      <>
        <div className="col-2 font">{selectTelocom[index]?.priceLgu}</div>
        <div className="col-3 font">{selectTelocom[index]?.priceSkt}</div>
        <div className="col-2 font">{selectTelocom[index]?.priceKt}</div>
      </>
    );
    return item;
  };

  if (state.select === 'SKT') {
    items = [];
    selectTelocom = SKT;
    assort = (
      <>
        <Th className="col-2"> SKT </Th>
        <Th className="col-2"> KT </Th>
        <Th className="col-2"> LGT </Th>
      </>
    );
    selectTelocom.map((element, index) => {
      items.push(handleSktItem(index));
    });
    // console.log(items);
  } else if (state.select === 'KT') {
    items = [];
    selectTelocom = KT;
    assort = (
      <>
        <Th className="col-2"> KT </Th>
        <Th className="col-2"> SKT </Th>
        <Th className="col-2"> LGT </Th>
      </>
    );

    selectTelocom.map((element, index) => {
      items.push(handleKtItem(index));
    });
  } else {
    items = [];
    selectTelocom = LGU;
    assort = (
      <>
        <Th className="col-2"> LGT </Th>
        <Th className="col-2"> SKT </Th>
        <Th className="col-2"> KT </Th>
      </>
    );
    selectTelocom.map((element, index) => {
      items.push(handleLgItem(index));
    });
  }
  return (
    <>
      <Container>
        <SmallLabel>
          <FontAwesomeIcon
            icon={faArrowAltCircleRight}
            size="2x"
            style={{ color: '#000f34', marginRight: '5px', fontSize: '13px' }}
          />{' '}
          {state.select} 수수료 TOP 3
        </SmallLabel>

        <Grid className="contaner">
          <div
            className="row justify-content-between"
            style={{
              borderBottom: 'solid 1px #707070',
              paddingBottom: '12px',
            }}
          >
            <Th className="col-2 p-0 text-left">구분</Th>
            <Th className="col-4 p-0 text-left">모델명</Th>
            {assort}
          </div>

          <div
            className="row text-left"
            style={{ borderBottom: 'solid 0.3px #707070' }}
          >
            <Th
              className="col-2 align-self-center text-left"
              style={{ padding: '0' }}
            >
              신규
            </Th>

            <div className="col-10 p-0">
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[0]?.modelName}
                </div>

                {items[0]}
              </Td>
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[1]?.modelName}
                </div>

                {items[1]}
              </Td>
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[2]?.modelName}
                </div>

                {items[2]}
              </Td>
            </div>
          </div>

          <div
            className="row justify-content-between"
            style={{ borderBottom: 'solid 0.3px #707070' }}
          >
            <Th className="col-1 align-self-center" style={{ padding: '0' }}>
              번호 이동
            </Th>
            <div className="col-10 p-0">
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[3]?.modelName}
                </div>

                {items[3]}
              </Td>
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[4]?.modelName}
                </div>

                {items[4]}
              </Td>
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[5]?.modelName}
                </div>

                {items[5]}
              </Td>
            </div>
          </div>

          <div
            className="row justify-content-between"
            style={{ borderBottom: 'solid 0.3px #707070' }}
          >
            <Th className="col-1 align-self-center" style={{ padding: '0' }}>
              기기 변경
            </Th>
            <div className="col-10 p-0">
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[6]?.modelName}
                </div>

                {items[6]}
              </Td>
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[7]?.modelName}
                </div>

                {items[7]}
              </Td>
              <Td className="row p-0 ">
                <div className="col-5 p-0 text-left font name">
                  {selectTelocom[8]?.modelName}
                </div>

                {items[8]}
              </Td>
            </div>
          </div>
        </Grid>

        <div
          style={{
            display: 'flex',
            marginTop: '40px',
            justifyContent: 'flex-end',
          }}
        >
          <SquareButton
            width="70px"
            height="23px"
            borderRadius="20px"
            color={state.select === 'SKT' ? '#fff' : ' #000f34'}
            backgroundColor={state.select === 'SKT' ? '#000f34' : ' #e2e2e2'}
            onClick={() => setState({ ...state, select: 'SKT' })}
            style={{ fontSize: '9px', marginRight: '2px' }}
          >
            SKT
          </SquareButton>
          <SquareButton
            style={{ fontSize: '9px', marginRight: '2px' }}
            width="70px"
            height="23px"
            borderRadius="20px"
            color={state.select === 'KT' ? '#fff' : ' #000f34'}
            backgroundColor={state.select === 'KT' ? '#000f34' : ' #e2e2e2'}
            onClick={() => setState({ ...state, select: 'KT' })}
          >
            KT
          </SquareButton>
          <SquareButton
            style={{ fontSize: '9px' }}
            width="70px"
            height="23px"
            borderRadius="20px"
            color={state.select === 'LGT' ? '#fff' : ' #000f34'}
            backgroundColor={state.select === 'LGT' ? '#000f34' : ' #e2e2e2'}
            onClick={() => setState({ ...state, select: 'LGT' })}
          >
            LGT
          </SquareButton>
        </div>

        <XsmallLabel style={{ textAlign: 'right' }}>
          확인하실 통신사를 선택하세요.
        </XsmallLabel>

        <SquareButton
          width="auto"
          height="54px"
          borderRadius="5px"
          color="#fff"
          border="solid 1px #e2e2e2"
          backgroundColor="#000f34"
          margin="20px auto"
          onClick={() => _onGoAllCommi()}
        >
          전체 수수료 정보 보기
        </SquareButton>
      </Container>
    </>
  );
};

export default CommiBest;
