import React, { useCallback, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
//chart
import 'hammerjs';
import {
  Chart,
  ChartSeries,
  ChartSeriesItem,
} from '@progress/kendo-react-charts';
//개발자 라이브러리
import _axios from '../../../../utils/axios';
// css
import { Table, Td, Th, Tr } from '../../../../css/list';
import styled from 'styled-components';
import { PhoneButton, PhoneTable } from './Hp';
import theme from '../../../../styles/theme';
import { setHeader } from '../../../../redux/actions/header';

const Support = () => {
  const { userId } = useSelector((store) => store.auth.login.userInfo);
  const dispatch = useDispatch();
  const history = useHistory();

  const [data, setData] = useState({
    listData: [],
    chartData: [],
  });

  const headerInfo = {
    isBackground: false,
    isBugger: false,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '후원 포인트',
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const _getData = useCallback(async () => {
    const date = new Date();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();

    const url = '/api/member/get_point_support.php';

    const params = {
      userId: userId,
      year: String(year),
      month: String(0) + String(month - 1),
    };

    const data = await _axios(url, params);
    console.log(data);
    setData({
      ...data,
      chartData: data.chartData,
    });
  }, [userId]);

  useEffect(() => {
    _getData();
  }, [_getData]);

  const SERIES = [
    {
      category: data.chartData.납부,
      value: data.chartData.payPercent / 100,
      color: '#FEB447',
    },
    {
      category: data.chartData.미납,
      value: data.chartData.unpaidPercent / 100,
      color: '#3C64B1',
    },
  ];

  // Chart
  const labelContent = (props) => {
    let formatedNumber = Number(props.dataItem.value).toLocaleString(
      undefined,
      {
        style: 'percent',
        minimumFractionDigits: 0,
      },
    );
    return `${formatedNumber}
            ${props.dataItem.category}`;
  };

  return (
    <CommiSupportInner>
      <PhoneTable>
        <h3>2021년 MD 가입 포인트</h3>

        <Tr width={'100%'} height={'35px'}>
          <Th width={'39%'}>개통일자</Th>
          <Th width={'29%'}>고객명</Th>
          <Th width={'32%'}>포인트</Th>
        </Tr>

        <Table width={'100%'} className="table">
          {data.listData?.map((item, index) => {
            return (
              <Tr width={'100%'} height={'30px'} key={index}>
                <Td width={'40%'}>{item.wdate}</Td>
                <Td width={'30%'}>{item.memName}</Td>
                <Td width={'30%'}>500,000</Td>
                {/* <Td width={'30%'}>{item.point}</Td> */}
              </Tr>
            );
          })}
        </Table>
      </PhoneTable>

      <ChartInner>
        <PointChart>
          <div className="chart">
            <div className="chart-title">
              <p>구독료 포인트</p>
              <PhoneButton
                onClick={() => {
                  history.push({
                    pathname: '/mypage/member/cmsStatus',
                    payStatus: {
                      code: '10',
                      name: '미납',
                    },
                  });
                }}
              >
                <button type="button">미납자 정보</button>
              </PhoneButton>
            </div>
            <div className="chart-sales">
              <p className="pay">납부</p>
              <p className="unPaid">미납</p>
            </div>
          </div>
        </PointChart>

        <Chart>
          <ChartSeries>
            <ChartSeriesItem
              type="pie"
              data={SERIES}
              colorField="color"
              categoryField="0"
              labels={{
                visible: true,
                content: labelContent,
                position: 'relative',
                color: '#000000',
                font: 'bold 12px Noto Sans KR, sans-serif',
              }}
            />
          </ChartSeries>
        </Chart>
      </ChartInner>

      <PhoneButton>
        <Link to="/mypage/member">
          <button type="button">회원 전체 목록</button>
        </Link>
      </PhoneButton>
    </CommiSupportInner>
  );
};

export default Support;

export const CommiSupportInner = styled.section`
  width: 100%;
  padding: 2em 1.5em;
  background-color: #f8f8f8;

  ul {
    padding: 0;
  }
  li {
    position: relative;
    list-style: none;
  }
  p {
    margin: 0;
  }

  ${PhoneTable} {
    margin-top: 0;
  }

  .k-chart {
    svg {
      g:nth-child(2) g {
        g:nth-child(3) {
          path {
            fill: transparent;
          }
          text {
            transform: translate(-45px, 12px);
          }
        }
        g:nth-child(4) {
          path {
            fill: transparent;
          }
          text {
            transform: translate(44px, 34px);
          }
        }
      }
    }
  }
`;

export const ChartInner = styled.div`
  margin-top: 3em;
  border: 1px solid ${theme.colors.boxGray};
  border-radius: 3px;
`;

export const PointChart = styled.section`
  padding: 1.5em;
  background-color: ${theme.colors.white};

  .chart {
    &-title {
      display: flex;
      justify-content: space-between;

      p {
        font-size: ${theme.fontSizes.H3};
        font-family: NotoM;
      }

      ${PhoneButton} {
        width: auto;
        margin: 0;

        button {
          position: relative;
          width: 100px;
          height: 32px;
          padding: 0;
          box-shadow: 0 20px 50px 0 rgb(0 0 0 / 30%);
          z-index: 1;
        }
      }
    }

    &-sales {
      display: flex;
      padding-top: 1em;
      padding-left: 1.5em;

      p {
        position: relative;
        font-size: ${theme.fontSizes.Button2};

        &:before {
          content: '';
          position: absolute;
          display: block;
          left: -19px;
          width: 14px;
          height: 14px;
          margin-bottom: 0.5em;
          border-radius: 28px;
        }

        &.pay {
          margin-right: 3em;

          &:before {
            background: ${theme.colors.murstard};
          }
        }

        &.unPaid {
          &:before {
            background: ${theme.colors.royalblue};
          }
        }
      }
    }
  }
`;
