/*
  설명: 상담 정보 입력
  - 2021.05.18. 안예린, import수정
*/
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import { DropDownList } from '@progress/kendo-react-dropdowns';
import { MaskedTextBox } from '@progress/kendo-react-inputs';
// 개발자 라이브러리
import {
  insuCheck,
  insuInit,
  apply,
  inserInfo,
  loadingFalse,
  replaceApply1Page,
} from '../../redux/reducers/insu';
// icon & images
import insu_header from '../../images/adinsu_header.png';

export default function Apply2(props) {
  const dispatch = useDispatch();

  const insuStore = useSelector((state) => state.insu);

  useEffect(() => {
    if (!insuStore.memId) {
      props.history.push('/insu/apply1');
    }
  }, [props.history, insuStore.memId]);
  const [inputs, setInputs] = useState({
    memId: insuStore.memId, // 회원ID
    custName: insuStore.custName, // 고객명
    hpNo: insuStore.hpNo, // 고객명 연락처
    carNoType: insuStore.carNoType, //차량/차대번호 구분
    carNo: insuStore.carNo, // 차량/차대번호
    expiredDate: insuStore.expiredDate, //만기월
    custRegion: insuStore.custRegion, //거주지역
    check: insuStore.marketingFlag,
  });
  const { loading, result, message } = insuStore;

  const source = ['차량번호', '차대번호'];
  const expiredDateSource = [
    '신차',
    '1월',
    '2월',
    '3월',
    '4월',
    '5월',
    '6월',
    '7월',
    '8월',
    '9월',
    '10월',
    '11월',
    '12월',
    '모름',
  ];
  const custRegionSource = [
    '서울',
    '강원',
    '경기',
    '경남',
    '경북',
    '광주',
    '대구',
    '대전',
    '부산',
    '울산',
    '인천',
    '전남',
    '전북',
    '제주',
    '충남',
    '충북',
    '세종',
  ];
  const _handleInputChange = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };
  useEffect(() => {
    dispatch(insuCheck(true));
    return () => {
      // dispatch(insuCheck(false));
    };
  }, [dispatch]);

  const onApply = (event) => {
    event.preventDefault();
    if (!inputs.custName || !inputs.hpNo) return;
    dispatch(apply(inputs));
    // props.history.push("/insu/apply3");
  };

  const goApply2 = () => {
    dispatch(inserInfo(inputs));
  };

  const _toggleDialog = () => {
    dispatch(loadingFalse());
  };

  const _toggleDialog2 = () => {
    return Promise.all([
      dispatch(loadingFalse()),
      dispatch(replaceApply1Page(insuStore.memId)),
      dispatch(insuInit()),
      dispatch(insuCheck(true)),
    ]);
  };
  const { check, custName, hpNo, carNo, carNoType, expiredDate, custRegion } =
    inputs;
  return (
    <>
      <StyledBackGroundWrap>
        <div className="title">다이렉트 자동차 보험</div>
        <div className="content">한번에 비교하고, 크게 할인 받으세요!</div>
      </StyledBackGroundWrap>

      <StyledContent onSubmit={onApply}>
        <div className="title">
          본 신청은 가입이 아니며 상담사를 통한 상담 후<br /> 본인의 가입 의사에
          따라 진행됩니다.
        </div>
        <div
          className="label"
          style={{ marginTop: '30px', marginBottom: '10px' }}
        >
          이름
        </div>
        <input
          style={{
            width: '100%',
            height: '37px',
            margin: '10px 7px 0px 0px',
            // borderRadius: "5px",
            border: 'solid 1px #707070',
            backgroundColor: '#ffffff',
            color: '#707070',
            display: 'flex',
            alignItems: 'center',
          }}
          value={custName}
          onChange={_handleInputChange}
          name="custName"
          placeholder={'이름을 입력해 주세요.'}
          required={true}
        />

        <div
          className="label"
          style={{ marginTop: '20px', marginBottom: '10px' }}
        >
          연락처
        </div>
        <StyledInput
          required={true}
          validationMessage={'연락처를 입력 해주세요.'}
          placeholder="000-0000-0000"
          value={hpNo}
          name="hpNo"
          type="text"
          mask="000-0000-0000"
          onChange={_handleInputChange}
        ></StyledInput>

        <div
          className="label"
          style={{ marginTop: '20px', marginBottom: '10px' }}
        >
          차량번호 또는 차대번호
        </div>
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <StyledSelectBox
            name="carNoType"
            data={source}
            onChange={_handleInputChange}
            value={carNoType}
            size={'30%'}
          />
          <input
            style={{
              width: '65%',
              height: '37px',
              border: 'solid 1px #707070',
              backgroundColor: '#ffffff',
              color: '#707070',
              display: 'flex',
              alignItems: 'center',
            }}
            value={carNo}
            maxLength={carNoType === '차량번호' ? 9 : 17}
            onChange={_handleInputChange}
            name="carNo"
            placeholder={'번호를 입력하세요.'}
            required={true}
          />
        </div>

        <div className="example_text">
          차량번호 최대 9자리 / 서울01가0000, 000가1000
          <br />
          차대번호 17자리 / KMHFN14C9FA000001
        </div>

        <div
          className="label"
          style={{ marginTop: '20px', marginBottom: '10px' }}
        >
          자동차보험 만기월
        </div>
        <StyledSelectBox
          name="expiredDate"
          data={expiredDateSource}
          onChange={_handleInputChange}
          value={expiredDate}
          size={'40%'}
          required={true}
          valid={expiredDate !== '만기월 선택'}
          validationMessage={'선택해주세요'}
          validityStyles={false}
        />

        <div
          className="label"
          style={{ marginTop: '20px', marginBottom: '10px' }}
        >
          거주지역
        </div>
        <StyledSelectBox
          name="custRegion"
          data={custRegionSource}
          onChange={_handleInputChange}
          value={custRegion}
          size={'40%'}
          required={true}
          valid={custRegion !== '거주지역 선택'}
          validationMessage={'선택해주세요'}
          validityStyles={false}
        />

        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            marginTop: '20px',
          }}
        >
          <div style={{ borderBottom: '1px solid' }} onClick={goApply2}>
            개인정보 수집 및 활용 동의
          </div>
          <input
            name="check"
            type="checkbox"
            checked={check}
            required={true}
            onChange={() => {
              setInputs({
                ...inputs,
                check: !check,
              });
            }}
            className="checkBox"
          />
          <span>동의합니다.</span>
        </div>

        <div style={{ textAlign: 'right' }}>
          <button type="submit">상담신청</button>
        </div>
      </StyledContent>

      {result === '1' && loading && (
        <Dialog>
          <p style={{ margin: '25px', textAlign: 'left' }}>
            {message}
            <br />
            <br />
            서버가 원활하지 않습니다.
            <br />
            잠시후 다시 시도해주세요.
            <br />
          </p>
          <DialogActionsBar>
            <button className="k-button" onClick={_toggleDialog}>
              확인
            </button>
          </DialogActionsBar>
        </Dialog>
      )}

      {result === '0' && loading && (
        <Dialog>
          <p style={{ margin: '25px', textAlign: 'left' }}>
            접수가 완료되었습니다. 상담사가 곧 연락드려서 최적의 상품을 추천드릴
            예정이오니 잠시만 기다려주시기 바랍니다.
            <br />
            감사합니다.
          </p>
          <DialogActionsBar>
            <button className="k-button" onClick={_toggleDialog2}>
              확인
            </button>
          </DialogActionsBar>
        </Dialog>
      )}

      {loading && <Loading></Loading>}
    </>
  );
}

const StyledBackGroundWrap = styled.div`
  background-image: url(${insu_header});

  /* Full height */
  height: 15vh;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  .title {
    font-weight: bold;
    padding: 30px 0 15px 0;
    font-size: 20px;
    text-align: center;
  }

  .content {
    font-size: 15px;
    text-align: center;
  }
`;

const StyledContent = styled.form`
  height: 100%;
  background-color: #f8f8f8;
  padding: 35px 20px 0 20px;
  font-family: NanumGothic;
  .title {
    font-size: 15px;
  }

  .label {
    font-size: 15px;
  }

  .example_text {
    font-size: 14px;
    color: #afafaf;
  }

  .checkBox {
    width: 24px;
    height: 24px;
    margin: 0 12px 0 51px;
    object-fit: contain;
    border: solid 1px #707070;
    background-color: #ffffff;
  }

  button {
    /* position: absolute;
    bottom: 46px;
    right: 21px; */
    width: 91px;
    height: 33px;
    border-width: 0;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #000f34;
    color: #ffffff;
    margin-top: 33px;
    margin-bottom: 33px;
  }

  input::placeholder {
    color: #afafaf;
  }
`;

const StyledInput = styled(MaskedTextBox)`
  width: 100%;
  height: 37px;
  margin: 10px 7px 0px 0px;
  border: 1px solid #707070;
  background-color: #ffffff;
  color: #707070;
  display: flex;
  align-items: center;
  .k-maskedtextbox {
  }
  .k-textbox::placeholder {
    color: #afafaf !important;
    font-size: 15px;
  }
`;

const StyledSelectBox = styled(DropDownList)`
  width: ${(props) => props.size};
  height: 37px;

  border: 1px solid #707070;
  background-color: #ffffff;
  color: #707070;
  display: flex;
  align-items: center;
  .k-dropdown-wrap {
    border-radius: 0px;
  }
  .k-input {
    justify-content: start;
    color: #afafaf;
  }
`;

const Loading = styled.div`
  position: fixed;
  left: 35%;
  top: 50%;
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`;
