import React, { useEffect, useState } from 'react';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import { useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
import { Button } from '@progress/kendo-react-buttons';
import { Input } from '@progress/kendo-react-inputs';
// 개발자 Component
import _axios from '../../../utils/axios';
import LogList from './CmsPayLog';
// css
import '../../../css/kendo.css';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';

const CmsPayInfo = (props) => {
  const authStatus = useSelector((store) => store.auth.authStatus);
  const [state, setState] = useState({
    isLoading: true,
    isStatus: false,
    isDelete: false,

    idx: props.location.idx,
    mode: props.location.mode,
    adminId: authStatus.userId,

    memId: '',
    memName: '',
    paymentKind: '',
    memAssort: '',
    transactionId: '',
    requestStatus: '',
    payMonth: '',
    payAmount: '',
    payStatus: '',
    payMessage: '',
    wdate: '',
    logData: [],

    success: false,
    message: null,
    visibleDialog: false,
    confirmMessage: null,
    confirmDialog: false,
  });
  const _getData = async () => {
    const url = '/api/admin/cms/get_cms_pay_info.php';
    const params = {
      idx: state.idx,
    };

    const { data, logData } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isStatus: false,
      isDelete: false,

      memId: data.memId,
      memName: data.memName,
      memAssort: data.memAssort,
      paymentKind: data.paymentKind,
      transactionId: data.transactionId,
      requestStatus: data.requestStatus,
      payMonth: data.payMonth,
      payAmount: data.payAmount,
      payStatus: data.payStatus,
      payMessage: data.payMessage,
      wdate: data.wdate,
      logData: logData,
    });
  };

  // 출금신청상태조회
  const _setCmsPayStatus = async () => {
    const url = '/api/admin/cms/set_cms_pay_status.php';
    const params = {
      adminId: state.adminId,
      transactionId: state.transactionId,
    };
    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false, isStatus: false });
    }, 1000);
  };

  // 출금신청취소
  const _setCmsPayDelete = async () => {
    const url = '/api/admin/cms/set_cms_pay_delete.php';
    const params = {
      adminId: state.adminId,
      transactionId: state.transactionId,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false, isDelete: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    if (state.isStatus) {
      _setCmsPayStatus();
    } else if (state.isDelete) {
      _setCmsPayDelete();
    } else {
      _getData();
    }
  }, [state.isStatus, state.isDelete]);

  // const { isLoading, isSearch, loadData, searchAble } = state;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '650px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form>
        <Wrap width={'100%'}>
          <Title>CMS관리 〉 출금목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memId"
                  value={state.memId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memName"
                  value={state.memName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원구분</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memAssort"
                  value={state.memAssort}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>납부수단</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="paymentKind"
                  value={state.paymentKind}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>

              <Td width={tdWidth}>
                <Label width={labelWidth}>해당년월</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="payMonth"
                  value={state.payMonth}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>출금금액</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="payAmount"
                  value={state.payAmount}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>거래번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="transactionId"
                  value={state.transactionId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="wdate"
                  value={state.wdate}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청상태</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="requestStatus"
                  value={state.requestStatus}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>출금상태</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="payStatus"
                  value={state.payStatus}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청메세지</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="payMessage"
                  value={state.payMessage}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>

            <Button
              style={{ marginLeft: '10px' }}
              type={'button'}
              onClick={() => setState({ ...state, isStatus: true })}
            >
              승인조회
            </Button>
            <Button
              type={'button'}
              onClick={() => {
                setState({
                  ...state,
                  confirmDialog: true,
                  confirmMessage: '취소하시겠습니까?',
                });
              }}
            >
              출금취소
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}

      {state.confirmDialog && (
        <Dialog title={'확인'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>
            {state.confirmMessage}
          </p>
          <DialogActionsBar>
            <button className="k-button" onClick={_toggleDialog}>
              아니오
            </button>
            <button
              className="k-button"
              onClick={() => {
                setState({ ...state, confirmDialog: false, isDelete: true });
              }}
            >
              예
            </button>
          </DialogActionsBar>
        </Dialog>
      )}

      {!isMobile && (
        <Wrap width={'100%'}>
          <Title style={{ marginTop: '20px' }}>출금신청 로그</Title>
          <LogList logData={state.logData} />
        </Wrap>
      )}
    </Container>
  );
};

export default CmsPayInfo;
