import Modal from './index';
import React from 'react';
import styled from 'styled-components';

const CommonModal = ({
  children,
  width,
  height,
  padding,
  zIndex,
  onCancel,
  transformY,
}) => {
  return (
    <Modal
      width={width}
      height={height}
      padding={padding}
      zIndex={zIndex}
      onCancel={onCancel}
      transformY={transformY}
    >
      <StyledCommonModal>{children}</StyledCommonModal>
    </Modal>
  );
};

const StyledCommonModal = styled.div`
  .close-header {
    height: 7.6rem;
    padding: 1.6rem 1.6rem 2.4rem 1.6rem;
    display: flex;
    justify-content: flex-end;
  }

  img {
    height: 3rem;
    width: 3rem;
  }
`;

export default React.memo(CommonModal);
