/*
  설명: home화면 휴대폰 간편신청
  - 2021.05.31. 이해영 : css 작업
*/
import { useSelector } from 'react-redux';
import { useHistory, Link } from 'react-router-dom';
//개발자 라이브러리
import _axios from '../../../utils/axios';
//css
import styled from 'styled-components';
import theme from '../../../styles/theme';
//images
import more_horiz from '../../../images/mainAfterLogin/more_horiz.svg';

const HpRequestStatus = ({ hpStatus_0, hpStatus_1, hpStatus_9 }) => {
  const history = useHistory();

  return (
    <StyledContainer>
      <div className="titleWrap">
        <span>휴대폰 간편신청 현황</span>
        <span className="hamburger">
          <Link to="/mypage/hpRequest">
            <img src={more_horiz} alt="" />
          </Link>
        </span>
      </div>

      <div className="progressWrap">
        <div
          className="ing one"
          onClick={() => {
            history.push({
              pathname: '/mypage/hpRequest',
              requestStatus: {
                code: '0',
                name: '접수중',
              },
            });
          }}
        >
          <p>접수중</p>
          <p className="number">{hpStatus_0}</p>
        </div>

        <div className="line"></div>

        <div
          className="ing two"
          onClick={() => {
            history.push({
              pathname: '/mypage/hpRequest',
              requestStatus: {
                code: '1',
                name: '처리중',
              },
            });
          }}
        >
          <p>처리중</p>
          <p className="number">{hpStatus_1}</p>
        </div>

        <div className="line"></div>

        <div
          className="ing three"
          onClick={() => {
            history.push({
              pathname: '/mypage/hpRequest',
              requestStatus: {
                code: '9',
                name: '개통완료',
              },
            });
          }}
        >
          <p>개통완료</p>
          <p className="number">{hpStatus_9}</p>
        </div>
      </div>
    </StyledContainer>
  );
};

export default HpRequestStatus;

const StyledContainer = styled.div`
  width: 100%;
  height: auto;
  padding: 22px;
  background: #f1f2f2;
  .titleWrap {
    ${theme.H3};

    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    color: #0e2b64;
  }

  .hamburger {
    img {
      width: 100%;
      width: 28px;
      height: 24px;
    }
  }

  .progressWrap {
    margin-top: 22px;
    width: 100%;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 22px 40px;
  }

  .ing {
    display: flex;
    flex-direction: column;
    align-items: center;
    ${theme.Title};
    text-align: center;

    color: ${theme.colors.grey2};
    cursor: pointer;
    .number {
      ${theme.H3};
      display: flex;
      align-items: center;
      text-align: center;
      color: ${theme.colors.royalblue};
    }
  }

  .one,
  .three {
    width: 26%;
  }

  .two {
    width: 44%;
  }

  .line {
    border-top: 1px solid #a9a9a9;
    transform: rotate(90deg);
    width: 68px;
  }
`;
