import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute'; // 로그인 필요 체크
import {
  MyInfo,
  MyInfoUpdate,
  MyInfoCMS,
  MyInfoUnpaid,
  AccountEdit,
} from '../../components/mypage/myinfo';

export default function MyInfoRoute() {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/mypage/myinfo"
        render={(props) => <MyInfo {...props} />}
      />
      <AuthRoute
        path="/mypage/myinfo/update"
        render={(props) => <MyInfoUpdate {...props} />}
      />
      <AuthRoute
        path="/mypage/myinfo/accountEdit"
        render={(props) => <AccountEdit {...props} />}
      />
      <AuthRoute
        path="/mypage/myinfo/cms"
        render={(props) => <MyInfoCMS {...props} />}
      />
      <AuthRoute
        path="/mypage/myinfo/unpaid"
        render={(props) => <MyInfoUnpaid {...props} />}
      />
    </Switch>
  );
}
