/* eslint-disable jsx-a11y/alt-text */
import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import theme from '../../styles/theme';

const CommonInput = ({
  width,
  height,
  inputtype,
  kind,
  children,
  onClickHandler,
  disabled,
  placeholder,
  titlename,
  value,
  src,
  error,
  idName,
  maxLength,
  autoComplete,
  onChange,
  validityStyles,
  name,
  required,
  mask,
  error2,
}) => {
  return (
    <StyledInpuWrap>
      <p className={kind}>{titlename}</p>
      <StyledInput
        id={idName}
        className={kind}
        type={inputtype}
        name={name}
        value={value}
        width={width}
        height={height}
        onChange={onChange}
        disabled={disabled}
        required={required}
        placeholder={placeholder}
        maxLength={maxLength}
        autoComplete={autoComplete}
        validityStyles={validityStyles}
        mask={mask}
      >
        {children}
      </StyledInput>
      <a id="eyesOn" className="inputIcon" onClick={onClickHandler}>
        <img src={src}></img>
      </a>
      <h1>{error}</h1>
      <h2>{error2}</h2>
    </StyledInpuWrap>
  );
};

const StyledInpuWrap = styled.div`
  position: relative;

  p {
    position: absolute;
    top: 35%;
    left: 20px;
    ${theme.Body2};
    color: ${theme.colors.grey2};
    border: none !important;
    cursor: default;
  }
  .inputIcon {
    display: ${(props) => (props.type === 'text' ? 'none' : 'block')};
    position: absolute;
    top: ${(props) => (props.src === '{warning}' ? '10px' : '25px')};
    right: 15px;
    border: none;
    background: none;
    cursor: pointer;
  }

  .is-active {
    color: ${theme.colors.red};
    font-weight: 500;
    font-family: NotoM;
    border: 1px solid ${theme.colors.red};
  }

  h1 {
    position: absolute;
    bottom: -30px;
    right: 0;
    color: ${theme.colors.red};
    font-size: 12px;
    font-family: 'NotoR';
  }

  h2 {
    position: absolute;
    /* bottom: -30px; */
    right: 66px;
    color: ${theme.colors.red};
    font-size: 12px;
    font-family: 'NotoR';
  }
`;

const StyledInput = styled.input`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  margin: ${(props) => (props.type === 'password' ? '20px 0 40px' : '20px 0')};
  padding: 20px 10% 20px 35%;
  color: ${theme.colors.black1};
  cursor: default;
  font-size: ${(props) => (props.type === 'password' ? '14px' : '14px')};
  letter-spacing: ${(props) => (props.type === 'password' ? '3px' : 'normal')};
  font-family: NotoR;
  border: 1px solid #f1f2f2;
  z-index: 1;

  &:focus {
    box-shadow: none;
    outline: none;
    /* border: 1px solid #f1f2f2; */
  }

  ::placeholder {
    color: ${theme.colors.grey3};
    font-size: ${(props) => (props.type === 'password' ? '14px' : '14px')};
    font-family: NotoR;
    /* transform: ${(props) =>
      props.type === 'password' ? 'translateY(-12px)' : ''}; */
    letter-spacing: ${(props) =>
      props.type === 'password' ? 'normal' : 'normal'};
  }

  img {
    margin-right: 5px;
  }

  :disabled {
    color: #9b9b9b;
    border: none;
  }

  /* status */
  ${(kind) =>
    kind &&
    css`
      &.btn_01:hover:enabled,
      &.btn_02:hover:enabled,
      &.wideBtn_01:hover:enabled {
        background-color: ${theme.colors.lapis};
      }
      &.wideBtn_02:hover:enabled {
        background-color: ${theme.colors.lapis};
        border: ${theme.colors.royalblue};
        color: ${theme.colors.white};
      }

      &.btn_01:active:enabled,
      &.btn_02:active:enabled,
      &.wideBtn_01:active:enabled {
        background-color: ${theme.colors.royalblue};
      }
      &.wideBtn_02:active:enabled {
        background-color: ${theme.colors.royalblue};
        border: ${theme.colors.royalblue};
        color: ${theme.colors.white};
      }

      &.btn_01:disabled,
      &.btn_02:disabled,
      &.wideBtn_01:disabled,
      &.wideBtn_02:disabled {
        opacity: 0.5;
      }
    `};

  @media ${({ theme }) => theme.device.mobile} {
  }
`;

export default CommonInput;
