/*
  설명: myinfo - guide 페이지
  - 2021.05.18. 안예린, import 수정
*/
import React from 'react';
import styled from 'styled-components';
// icon & images
import PersonIcon from '@material-ui/icons/Person';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import CloseIcon from '@material-ui/icons/Close';

const MyInfoGuide = ({ newCustomerState, setNewCustomerState }) => {
  return (
    <StyledInfoUpdateGuide newCustomerState={newCustomerState}>
      <section
        style={{
          width: '100%',
          height: '13%',
          backgroundColor: '#000f34',
          borderBottomLeftRadius: '1em',
          borderBottomRightRadius: '1em',
          marginBottom: '5%',
          display: 'flex',
          alignItems: 'center',
          position: 'relative',
        }}
      >
        <div
          style={{
            position: 'fixed',
            zIndex: '2',
            backgroundColor: 'rgba(0,0,0,0.5)',
            width: '100%',
            height: '300vh',
          }}
        ></div>
        <div
          style={{
            position: 'relative',
            zIndex: '3',
            color: 'black',
            width: '4em',
            height: '4em',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: 'white',
            borderRadius: '50%',
            marginLeft: '10%',
            marginRight: '5%',
          }}
        >
          <PersonIcon style={{ fontSize: '2em' }} />
          <div
            style={{
              position: 'absolute',
              color: 'white',
              zIndex: '3',
              left: '0.5em',
              width: '100vw',
              top: '3em',
              fontSize: '18px',
            }}
          >
            <ArrowUpwardIcon style={{ fontSize: '2em' }} />
            <br />
            프로필 사진을 등록해 보세요.
            <br />
            각 서비스 링크에서 프로필 사진이
            <br />
            노출 됩니다.
          </div>
        </div>
        <div style={{ color: 'white' }}>
          <div>홍길동</div>
          <div>a123456789</div>
        </div>
      </section>
      <StyledMyInfo>
        <StyledTitleCss>기본정보</StyledTitleCss>
        <StyledListBox>
          <div style={{ width: '30%' }}>휴대폰</div>
          <div style={{ width: '70%' }}>010-1111-1111</div>
        </StyledListBox>
        <StyledListBox>
          <div style={{ width: '30%' }}>가자렌탈ID</div>
          <div style={{ width: '70%' }}>a123456789</div>
        </StyledListBox>
      </StyledMyInfo>
      <StyledCalculate newCustomerState={newCustomerState}>
        <StyledTitleCss>정산 계좌 정보</StyledTitleCss>
        <StyledListBox>
          <div style={{ width: '30%' }}>은행명</div>
          <input
            value="우리은행"
            readOnly
            style={{ width: '55%', paddingLeft: '0.5em' }}
          ></input>
        </StyledListBox>
        <StyledListBox>
          <div style={{ width: '30%' }}>예금주</div>
          <input
            readOnly
            value="홍길동"
            style={{ width: '55%', paddingLeft: '0.5em' }}
          ></input>
        </StyledListBox>
        <StyledListBox>
          <div style={{ width: '30%' }}>계좌번호</div>
          <input
            readOnly
            value="100********"
            style={{ width: '55%', paddingLeft: '0.5em' }}
          ></input>
        </StyledListBox>
        <StyledListBox>
          <div style={{ width: '30%' }}>주민번호</div>
          <section style={{ width: '55%', display: 'flex' }}>
            <input
              readOnly
              value="111111"
              style={{ width: '45%', paddingLeft: '0.5em' }}
            ></input>
            <div style={{ width: '10%' }}>-</div>
            <input
              readOnly
              value="********"
              style={{ width: '45%', paddingLeft: '0.5em' }}
            ></input>
          </section>
        </StyledListBox>
        <div
          style={{
            position: 'absolute',
            color: 'white',
            zIndex: '3',
            // top: '27vh',
            fontSize: '18px',
          }}
        >
          <ArrowUpwardIcon style={{ fontSize: '2em' }} />
          <br />
          내 정보에서는 정산 계좌 정보를
          <br />
          수정할 수 있습니다.
        </div>
      </StyledCalculate>
      <StyledUpdataButton>
        <button style={{ backgroundColor: '#000f34' }}>수정</button>
      </StyledUpdataButton>
      <StyledMyInfo>
        <StyledTitleCss>세무정보</StyledTitleCss>
        <StyledListBox>
          <div style={{ width: '30%' }}>구분</div>
          <div style={{ width: '70%' }}>개인</div>
        </StyledListBox>
        <StyledListBox>
          <div style={{ width: '30%' }}>세금구분</div>
          <div style={{ width: '70%' }}>사업소득세</div>
        </StyledListBox>
        <StyledListBox>
          <div style={{ width: '30%' }}>주민번호</div>
          <div style={{ width: '70%' }}>111111-********</div>
        </StyledListBox>
      </StyledMyInfo>
      <div
        style={{
          position: 'fixed',
          right: '1%',
          zIndex: '3',
          color: 'white',
        }}
      >
        <CloseIcon
          onClick={() => {
            setNewCustomerState(false);
          }}
          style={{ fontSize: '5em' }}
        />
      </div>
      <div
        style={{
          position: 'fixed',
          display: 'flex',
          zIndex: '3',
          bottom: '5%',
          right: '5%',
          color: 'white',
          fontSize: '18px',
        }}
        onClick={() => {
          localStorage.setItem('myinfoGuide', '다시보지않기누른유저');
          setNewCustomerState(false);
        }}
      >
        <input
          style={{ width: '1.7em', height: '1.7em', marginRight: '1em' }}
          type="checkbox"
        />
        <div
          style={{
            textDecoration: 'underline',
            display: 'flex',
            alignItems: 'center',
          }}
        >
          다시 보지 않기
        </div>
      </div>
    </StyledInfoUpdateGuide>
  );
};

const StyledInfoUpdateGuide = styled.div`
  display: ${(props) => (props.newCustomerState ? 'flex' : 'none')};
  position: fixed;
  top: 55px;
  z-index: 99;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  background-color: #f2f2f2;
`;

const StyledTitleCss = styled.div`
  font-size: 12px;
  color: #8f92a1;
`;

const StyledListBox = styled.div`
  font-size: 16px;
  height: 6vh;
  color: #171717;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #d6d6d6;
  div {
    display: flex;
    align-items: center;
  }
  input {
    border: 2px solid #d6d6d6;
    height: 2.5rem;
    /* padding-left: 0.5em; */
  }
  .k-dropdown .k-input {
    justify-content: flex-start;
    color: black;
  }
  .k-maskedtextbox .k-textbox {
    font-size: 16px;
    border: 2px solid #d6d6d6;
    color: black;
    padding-left: 0.5em;
  }
`;

const StyledMyInfo = styled.section`
  width: 90%;
  margin-bottom: 5%;
  div {
    padding-left: 0.5em;
  }
`;

const StyledCalculate = styled.section`
  background-color: white;
  width: 90%;
  position: relative;
  z-index: 3;
  div {
    padding-left: 0.5em;
  }
`;

const StyledUpdataButton = styled.section`
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 1em 0 0 0;
  button {
    outline: none;
    border: none;
    border-radius: 6px;
    width: 4.5em;
    height: 2.5em;
    background-color: #979797;
    color: #ffffff;
  }
`;

export default MyInfoGuide;
