import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import _isMobile from '../../../utils/isMobile';
import ListView from '../../common/board/ListView';
import SearchForm from '../../common/board/SearchForm';
import ConfirmDialog from '../../common/board/ConfirmDialog';
// css
import { Container, Wrap, Title } from '../../../css/board';

const CashRequestList = () => {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '포인트 출금',
  };

  const dispatch = useDispatch();
  const board = useSelector((store) => store.board.boardStatus);
  const authStatus = useSelector((store) => store.auth.authStatus);
  const isMobile = _isMobile();
  const pageSize = isMobile ? 10 : 10;
  const containerWidth = isMobile ? '100%' : '900px';
  const [state, setState] = useState({
    isLoading: false,
    isRestore: false,
    isDelete: false,
    loadData: [],
    success: false,
    message: null,
    confirmMessage: null,
    visibleDialog: false,
    confirmDialog: false,
    // 페이징
    skip: 0,
    rowTotal: 0,
    pageCount: 1,
    pageable: {
      info: true,
      type: 'input',
      pageSizes: false,
      previousNext: true,
    },
    // 검색
    searchAble: {
      page: 1,
      pageSize: pageSize,
      searchValue: '',
      memId: '',
      status: '',
      minDate: '',
      maxDate: '',
      statusOptions: [],
    },
  });

  // load data
  const _getData = async () => {
    const url = '/api/member/get_cash_request_list.php';
    const params = {
      page: state.searchAble.page,
      rows: state.searchAble.pageSize,
      memId: authStatus.userId,
      status: state.searchAble.status,
      minDate: state.searchAble.minDate,
      maxDate: state.searchAble.maxDate,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isRestore: false,
      loadData: response.data,
      rowTotal: response.rowTotal,
      searchAble: {
        ...state.searchAble,
        statusOptions: response.statusOptions,
      },
    });
  };

  useEffect(() => {
    if (state.isLoading || state.isRestore) {
      _getData();
    }
  }, [state.isLoading, state.isRestore]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (board.isLoading !== undefined) {
      const page = board.searchAble.page;
      const skip = (page - 1) * board.searchAble.pageSize;

      setState({
        ...state,
        isLoading: false,
        isRestore: true,
        skip: skip,
        searchAble: board.searchAble,
      });
    } else{
      setState({
        ...state,
        isLoading: true,
      });
    };
  }, []);

  const { isLoading, searchAble } = state;

  // Search 항목
  const searchValue = isMobile
    ? {
        title: '요청상태',
        items: [
          {type: 'comboBox',width: '84%',name: 'status',value: searchAble.status,options: searchAble.statusOptions,},
          {type: 'submit', width: '16%', name: 'submit', value: '검색' },
        ],
      }
    : {
        title: '요청상태',
        items: [{type: 'comboBox',width: '100%',name: 'status',value: searchAble.status,options: searchAble.statusOptions,},],
      };

  const writeDate = {
    title: '요청일자',
    items: [
      {type: 'date',width: '50%',name: 'minDate',value: searchAble.minDate},
      {type: 'date',width: '50%',name: 'maxDate',value: searchAble.maxDate,},
    ],
  };

  const searchFields = isMobile ? [searchValue] : [searchValue, writeDate];

  // List 항목
  const listHeader = isMobile
    ? [
        {type: 'text',title: 'NO',name: 'no',width: '10%',align: 'center',},
        {type: 'text',title: '포인트',name: 'point',width: '20%',align: 'center',},
        {type: 'text',title: '지급액',name: 'cash',width: '20%',align: 'center',},
        {type: 'text',title: '실지급액',name: 'accountAmount',width: '20%',align: 'center',},
        {type: 'text',title: '요청일자',name: 'wdate',width: '30%',align: 'center',},
      ]
    : [
        {type: 'checkbox',title: null,name: 'idx',width: '5%',align: 'center',},
        {type: 'text',title: 'NO',name: 'no',width: '5%',align: 'center',},
        {type: 'text',title: '포인트',name: 'point',width: '10%',align: 'center',},
        {type: 'text',title: '지급율',name: 'cashRate',width: '10%',align: 'center',},
        {type: 'text',title: '지급액',name: 'cash',width: '10%',align: 'center',},
        {type: 'text',title: '소득세',name: 'taxAmount',width: '10%',align: 'center',},
        {type: 'text',title: '실지급액',name: 'accountAmount',width: '10%',align: 'center',},
        {type: 'text',title: '입금일자',name: 'paymentDate',width: '10%',align: 'center',},
        {type: 'text',title: '요청상태',name: 'statusName',width: '10%',align: 'center',},
        {type: 'text',title: '요청일자',name: 'wdate',width: '20%',align: 'center',},
      ];

  return (
    <Container style={{ width: containerWidth }}>
      {isLoading ? (
        <div>Loading...</div>
      ) : (
        <Wrap>
          {!isMobile && <Title>포인트관리 〉 출금요청목록</Title>}

          <SearchForm
            state={state}
            setState={setState}
            searchFields={searchFields}
            isInsertButton={false}
            isDeleteButton={false}
          />

          <ListView
            state={state}
            user={true}
            setState={setState}
            headers={listHeader}
          />

          <ConfirmDialog state={state} setState={setState} />
        </Wrap>
      )}
    </Container>
  );
};

export default CashRequestList;
