import React from 'react';
import { Route, useLocation } from "react-router-dom";

import FrontLayout from "../layouts/FrontLayout";  // 프론트 레이아웃
import InsuLayout from "../layouts/InsuLayout";    // 다이렉트보험 레이아웃

const MainRoutes = () => {
  const location = useLocation();
  const pathname = location.pathname;

  return (
    <React.Fragment>
      {pathname.includes('/insu/')
        ?
        <Route path="/insu" component={InsuLayout} />
        :
        <Route path="/" component={FrontLayout} />
      }
    </React.Fragment>
  );
}

export default MainRoutes;