import { BigLabel, Container, MiddleLabel, MiddleLabelBold, NextButton, ParentWrap, SmallLabel } from "../../css/common-component";

import React from "react";
import _axios from "../../utils/axios";
import { connect } from "react-redux";
import processPoint from "../../images/process-point3.svg";

class JoinComplete extends React.Component {
  constructor(props) {
    super(props);
    // console.log(this.props);

    this.state = {
      isLoading: true,
      userId: props.location.userId,
      content: "",
      memAssort: props.location.memAssort ? props.location.memAssort : "",
    };
  }

  _getData = async () => {
    const url = "/api/account/get_join_complete.php";
    const params = {
      userId: this.state.userId,
    };

    const { data } = await _axios(url, params);
    console.log(data);

    this.setState({
      isLoading: false,
      content: data.content,
    });
  };

  componentDidMount() {
    this._getData();
  }

  _goLogin() {
    this.props.history.push("/login");
  }
  render() {
    // const { content } = this.state;
    let messageComponent = "";
    if (this.state.memAssort === "플랫폼MD(대리점)") {
      messageComponent = (
        <ParentWrap width="375px" padding="35px 21px 42px 20px">
          <BigLabel textAlign="center" margin="0 82px 16px 97px" width="141px">
            가입신청완료
          </BigLabel>
          <img src={processPoint} className="process-step" alt="" />
          <SmallLabel width="238px" height="16px" textAlign="center" margin="55px auto 35px auto">
            이제, 모든 서비스 이용이 가능합니다.
          </SmallLabel>

          <MiddleLabel width="321px" height="46px" margin="35px 0 33px">
            MD 가입자는 아래 계좌로 초기가맹금 을 입금하세요.
          </MiddleLabel>

          <MiddleLabelBold width="186px" height="69px" margin="33px 136px 8px 0">
            KB국민은행
            <br />
            205701-04-361105
            <br />
            (주)스파이더플랫폼
          </MiddleLabelBold>

          <MiddleLabelBold width="193px" height="23px" margin=" 8px 168px 33px 0">
            금액 : 700,000포인트
          </MiddleLabelBold>
          <MiddleLabel width="312px" height="46px" margin="33px 9px 55px 0">
            초기가맹금은 2일 이내에 입금하셔야 로그인이 유지됩니다.
          </MiddleLabel>

          {/* <JoinButtonWrap width={"256px"} height={"48px"} margin="55px 24px 0 40px">
            <JoinButton to="/login">로그인</JoinButton>
          </JoinButtonWrap> */}

          <NextButton width="256px" height="48px" margin="55px 24px 0 40px" style={{ display: "flex", justifyContent: "center", alignItems: "center" }} onClick={() => this._goLogin()}>
            로그인
          </NextButton>
        </ParentWrap>
      );
    } else {
      messageComponent = (
        <ParentWrap width="375px" padding="0px 30px 0px 29px">
          <BigLabel textAlign="center" margin="45px auto 16px auto" width="141px">
            가입신청완료
          </BigLabel>
          <img src={processPoint} className="process-step" alt="" />
          <MiddleLabel width="317px" height="23px" textAlign="center" margin="197px 0px">
            이제, 모든 서비스 이용이 가능합니다.
          </MiddleLabel>
          <NextButton width="256px" height="48px" margin="30px auto 0 auto" style={{ display: "flex", justifyContent: "center", alignItems: "center" }} onClick={() => this._goLogin()}>
            로그인
          </NextButton>
        </ParentWrap>
      );
    }

    return <Container>{messageComponent}</Container>;
  }
}

const mapStateToProps = (state) => {};

const mapDispatchToProps = (dispatch) => {};

export default connect(mapStateToProps, mapDispatchToProps)(JoinComplete);
