/*
  설명: 모델 선택 상세
  - 2021.05.20. 안예린, import 수정
*/
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { DropDownList } from '@progress/kendo-react-dropdowns';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
import _axios from '../../../utils/axios';
// css
import {
  Container,
  BigLabel,
  MenuButtonWrap,
  MenuImg,
  MiddleLabel,
  NameLabel,
  SquareButton,
  SwiperMiddleLabel,
} from '../../../css/common-component';
import '../../../css/common-kendo.css';
import { CurrentWrap } from './CurrentTelecom';

export default function ModelSelectDetail(props) {
  const dispatch = useDispatch();

  const {
    changeTelecom,
    useTelecom,
    models,
    parentName,
    cardCode,
    marginPrice,
  } = useSelector((store) => store.hp);

  const [color, setColor] = useState();
  const [capacity, setCapacity] = useState();
  const [modelOption, setModelOption] = useState({
    capacitys: [],
    colors: [],
  });
  const { capacitys, colors } = modelOption;
  const [isModal, setIsModal] = useState(false);
  const [isQuestion, setIsQuestion] = useState({ show: false, commission: '' });
  const _selectModel = (data) => {
    dispatch(actions.selectModel(data));
    _getModelOption(data.modelCode);
    setIsModal(true);
  };

  const _getModelOption = async (modelCode) => {
    let assort;

    if (useTelecom === '') {
      assort = 'N';
    } else {
      if (changeTelecom === useTelecom) {
        assort = 'C';
      } else if (changeTelecom !== useTelecom) {
        assort = 'M';
      } else {
        assort = '';
      }
    }

    const url = '/api/hp/get_model_info.php';
    const params = {
      changeTelecom: changeTelecom,
      assort: assort,
      modelCode: modelCode,
      marginPrice: marginPrice,
    };
    const { data } = await _axios(url, params);

    setColor(data.colors[0]);
    setCapacity(data.capacitys[0]);

    dispatch(actions.selectModelColor(data.colors[0].colorCode));
    dispatch(actions.selectModelCapacity(data.capacitys[0]?.code));
    setModelOption({
      ...modelOption,
      capacitys: data.capacitys,
      colors: data.colors,
    });
  };

  const _handleSelectColor = (e) => {
    dispatch(actions.selectModelColor(e.target.value.colorCode));
    setColor(e.target.value);
  };

  const _handleSelectCapacity = (e) => {
    dispatch(actions.selectModelCapacity(e.target.value.code));
    setCapacity(e.target.value);
  };
  const _toggleDialog = () => {
    isModal ? setIsModal(false) : setIsModal(true);
  };

  const _toggleQuestionDialog = () => {
    isQuestion.show
      ? setIsQuestion({ show: false })
      : setIsQuestion({ show: true });
  };
  const _handleSubmit = (e) => {
    e.preventDefault();

    if (color && capacity) {
      props.history.push('/partnership/hp/feeSelect');
    } else {
      _toggleDialog();
    }
  };

  const _onQuestionClick = (e, commission) => {
    e.stopPropagation();
    setIsQuestion({ show: true, commission: commission });
  };

  useEffect(() => {
    //dispatch(stackRequest('S', ''));
    dispatch(actions.assort(useTelecom, changeTelecom));
  }, [changeTelecom, dispatch, useTelecom]);

  const element = models?.map((item) => {
    if (item.commission === 0) {
      return (
        <div
          style={{
            width: '100%',
            padding: '0',
            marginBottom: '35px',
            maxWidth: '157px',
            maxHeight: '186px',
          }}
          className="col-lg-6 col-6 col-6"
          key={item.modelCode}
        >
          <SquareButton
            width="157px"
            height="157px"
            border="none"
            boxShadow=" 0 0 10px 0 rgba(0, 0, 0, 0.1);"
            borderRadius="0px"
            style={{}}
          >
            <div
              style={{
                position: 'absolute',
                opacity: '0.49',
                backgroundColor: '#000000',
                width: '157px',
                height: '157px',
                colof: '#fff',
                fontSize: '20px',
                color: '#fff',
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <span style={{ margin: '0 auto' }}>SOLD OUT</span>
            </div>
            <MenuImg src={item.thumbnail} alt={item.modelName} width="60%" />
            <li
              style={{
                listStyle: 'none',
                position: 'absolute',
                top: '6px',
                right: '6px',
                color: '#e2e2e2',
              }}
            >
              <span className="k-icon k-i-question"></span>
            </li>
          </SquareButton>
          <NameLabel
            width="auto"
            height="auto"
            style={{
              marginTop: '9px',
              fontWeight: 'normal',
              textAlign: 'left',
            }}
          >
            {item.modelName}
          </NameLabel>
        </div>
      );
    } else {
      return (
        <div
          style={{
            width: '100%',
            padding: '0',
            marginBottom: '35px',
            maxWidth: '157px',
            maxHeight: '186px',
          }}
          className="col-lg-6 col-6 col-6"
          key={item.modelCode}
        >
          <SquareButton
            width="157px"
            height="157px"
            border="none"
            // backgroundColor={isHover ? "#e2e2e2" : "#ffffff"}
            backgroundColor="#ffffff"
            boxShadow=" 0 0 10px 0 rgba(0, 0, 0, 0.1);"
            borderRadius="0px"
            onClick={() => {
              _selectModel({
                modelCode: `${item.modelCode}`,
                modelName: `${item.modelName}`,
              });
            }}
          >
            <MenuImg src={item.thumbnail} alt={item.modelName} width="60%" />
            <li
              style={{
                listStyle: 'none',
                position: 'absolute',
                top: '6px',
                right: '6px',
                color: '#e2e2e2',
              }}
              onClick={(e) => _onQuestionClick(e, item.commission)}
            >
              <span className="k-icon k-i-question"></span>
            </li>
          </SquareButton>
          <NameLabel
            width="auto"
            height="auto"
            style={{
              marginTop: '9px',
              fontWeight: 'normal',
              textAlign: 'left',
            }}
          >
            {item.modelName}
          </NameLabel>
        </div>
      );
    }
  });

  return (
    <Container className="page" style={{ padding: '6em 1.5em'}}>
      <CurrentWrap>
        <h1>{parentName}</h1>
        <p className="carrier"> 신청하시려는 휴대폰을 선택하세요.</p>

        <MenuButtonWrap
          className="row justify-content-between"
          margin="1em 0 0"
        >
          {element?.length === 0 ? (
            <div style={{ width: '100%', textAlign: 'center' }}>
              선택하진 제조사 모델이 없습니다. <br /> 다른 제조사를 선택해주세요
            </div>
          ) : (
            element
          )}
        </MenuButtonWrap>

        {isModal && (
          <Dialog
            title={'선택'}
            id="hpModelOption"
            width={'256px'}
            height={'318px'}
          >
            <div style={{ margin: '0 0 0 0', textAlign: 'center' }}>
              <MiddleLabel
                width="82px"
                height="23px"
                style={{ transform: 'translateY(-16px)', fontSize: '17px' }}
              >
                용량
              </MiddleLabel>
              <DropDownList
                type="text"
                name="capacity"
                defaultValue={capacitys[0]}
                value={capacity}
                data={capacitys}
                dataItemKey="code"
                textField="name"
                onChange={_handleSelectCapacity}
                required={false}
                style={{ transform: 'translate(0px, -4px)' }}
              ></DropDownList>
            </div>
            <div style={{ margin: '25px 0 0 0', textAlign: 'center' }}>
              <MiddleLabel
                width="82px"
                height="23px"
                style={{ transform: 'translateY(-16px)', fontSize: '17px' }}
              >
                색상
              </MiddleLabel>
              <DropDownList
                type="text"
                name="color"
                defaultValue={colors[0]}
                value={color}
                data={colors}
                dataItemKey="colorCode"
                textField="colorName"
                onChange={_handleSelectColor}
                required={false}
                style={{ transform: 'translate(0px, -4px)' }}
              ></DropDownList>
            </div>

            <DialogActionsBar id="hpModelOptionButton">
              <button className="k-button" onClick={_handleSubmit}>
                확인
              </button>
              <button className="k-button" onClick={_toggleDialog}>
                취소
              </button>
            </DialogActionsBar>
          </Dialog>
        )}

        {isQuestion.show && (
          <Dialog
            id="bonusModal"
            width={'256px'}
            height={'auto'}
            title={' '}
            onClose={_toggleQuestionDialog}
          >
            <BigLabel
              width="66px"
              height="62px"
              margin="90px auto 0px auto"
              style={{
                lineHeight: 'normal',
                textAlign: 'center',
                fontSize: '54px',
              }}
            >
              {isQuestion.commission}
            </BigLabel>
            <SwiperMiddleLabel
              width="216px"
              height="74px"
              margin="22.7px auto 0px auto"
              style={{
                color: '#000f34',
                fontSize: '15px',
                textAlign: 'center',
              }}
            >
              *명이 상담중입니다.
              <br />
              <br /> 순차적으로 상담이 진행중이오니
              <br /> 잠시만 기다려주세요.
            </SwiperMiddleLabel>
          </Dialog>
        )}
      </CurrentWrap>
    </Container>
  );
}
