/*
  설명: 휴대폰 간편신청 링크
  - 2021.05.20. 안예린, import 수정
*/
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { CopyToClipboard } from 'react-copy-to-clipboard';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import SharedLinkFee from './SharedLinkFee';
import _axios from '../../../utils/axiosUrl';
// css

export default function SharedLink(props) {
  const headerInfo = {
    // Header
    isBackground: false, // 백그라운드
    isBugger: false, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: '휴대폰신청',
  };

  const dispatch = useDispatch();
  const memId = useSelector((store) => store.auth.login.userId);
  const [isCopy, setIsCopy] = useState(false);
  const [link, setLink] = useState('');
  // const _goHpRequestList = () => {
  //   props.history.push("/mypage/request");
  // };

  const creactShortLink = useCallback(async () => {
    const bitly_url = 'https://api-ssl.bitly.com/v4/shorten';
    const long_url = `http://www.yourphone.co.kr?memId=${memId}&fee=0`;
    const token = 'e58e936d6e0871655d674c279d1d47802ac43faf';
    const response = await _axios(
      bitly_url,
      { long_url: long_url },
      {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    );
    const { link } = response;
    setLink(link);
  }, [memId]);

  useEffect(() => {
    let mounted = true;
    creactShortLink();
    return () => {
      mounted = false;
    };
  }, [creactShortLink]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  return (
    <Container>
      <div
        style={{
          fontSize: '18px',
          color: '#000f34',
          marginLeft: '20px',
          marginBottom: '44px',
          width: 'auto',
          fontWeight: 'normal',
        }}
      >
        휴대폰 간편신청 링크
      </div>

      <StyledLinkWrap>
        <div 
          className="title"
          style={{ marginBottom: '10px' }}
        >
          <span>기본 링크</span>
          <span>마진 FULL</span>
        </div>
        <div className="subTitle">
          각 단말기 수수료 전액이 나의 마진으로 설정된 링크입니다.
        </div>
        <div className="d-flex align-items-start">
          <input
            style={{
              width: '235px',
              height: '33px',
              margin: '10px 7px 0px 0px',
              border: 'solid 1px #707070',
              backgroundColor: '#ffffff',
              color: '#707070',
              display: 'flex',
              alignItems: 'center',
            }}
            value={link}
            disabled={true}
          />
          <CopyToClipboard text={link} onCopy={() => setIsCopy(true)}>
            <NextButton
              width="91px"
              height="33px"
              margin="10px 0 0 0"
              style={{
                display: 'flex',
                fontSize: '14px',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: '0',
              }}
            >
              링크 복사
            </NextButton>
          </CopyToClipboard>
        </div>
        {isCopy ? (
          <div style={{ color: '#374057', textAlign: 'center' }}>
            복사되었습니다.
          </div>
        ) : null}
      </StyledLinkWrap>

      <div
        style={{
          fontSize: '15px',
          color: '#000f34',
          marginLeft: '20px',
          width: 'auto',
        }}
      >
        수수료 설정 링크
      </div>
      <pre
        style={{
          color: '#4b4b4b',
          fontSize: '12px',
          marginLeft: '20px',
          lineHeight: '14px',
          height: '40px'
        }}
      >
        {`아래 설정된 수수료가 실제 내가 받는 수수료입니다.
          나머지 수수료는 신청자에게 할인 또는 차액 지급됩니다.`}
      </pre>

      <SharedLinkFee memId={memId} fee={'50,000'}></SharedLinkFee>
      <SharedLinkFee memId={memId} fee={'70,000'}></SharedLinkFee>
      <SharedLinkFee memId={memId} fee={'100,000'}></SharedLinkFee>
      <SharedLinkFee memId={memId} fee={'150,000'}></SharedLinkFee>
    </Container>
  );
}

const Container = styled.div`
  width: 100%;
  height: 100%;
`;

const NextButton = styled.button`
  width: ${(props) => (props.width ? props.width : '100%')};
  height: ${(props) => (props.height ? props.height : '48px')};
  margin: ${(props) => (props.margin ? props.margin : '')};
  object-fit: contain;
  border-radius: 24px;

  border: none;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(113deg, #000f34 31%, #374057 79%);

  text-align: center;

  font-family: NanumGothic;
  font-size: 20px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.15;
  letter-spacing: normal;
  color: #fff;
  :focus {
    outline: none;
  }
`;

const StyledLinkWrap = styled.div`
  width: 100%;
  height: 98px;
  padding: 10px 21px;
  background-color: #f2f2f2;
  margin-bottom: 44px;

  .title {
    display: flex;
    justify-content: space-between;
    color: #000f34;
  }
  .subTitle {
    color: #4b4b4b;
    font-size: 12px;
  }
`;
