/*
  설명: 할부개월 선택
  - 2021.05.20. 안예린, import 수정
*/
import { useDispatch, useSelector } from 'react-redux';
import React from 'react';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import {
  BigLabel,
  Container,
  ParentWrap,
  SmallLabel,
  SquareButton,
} from '../../../css/common-component';

export default function Installment({ history }) {
  const dispatch = useDispatch();

  const { benefitPrice } = useSelector((store) => store.hp);

  const _selectInstallment = (installment) => {
    dispatch(actions.selectInstallment(installment));
    history.push('/partnership/hp/guideTable');
  };
  return (
    <Container className="page">
      <ParentWrap
        padding="0px 39px 242px"
        style={{ backgroundColor: 'transparent' }}
      >
        <BigLabel
          textalign="left"
          margin="0 auto 0px auto"
          width="100%"
          bold="normal"
        >
          할부 개월 선택
        </BigLabel>
        <SmallLabel
          width="100%"
          height="auto"
          textalign="left"
          margin={'10px auto 87px auto'}
          bold="normal"
          fontsize="12px"
        >
          구입하시려는 휴대폰의 할부 개월을 선택하세요.
        </SmallLabel>

        <div
          style={{
            display: 'flex',
            marginTop: '55px',
            marginBottom: '55px',
            justifyContent: 'space-between',
            flexWrap: 'wrap',
          }}
        >
          <SquareButton
            width="118px"
            height="118px"
            margin="0 0 20px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor="#ffffff"
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectInstallment(24)}
          >
            <span
              style={{
                color: '#000f34',
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
              }}
            >{`24개월`}</span>
          </SquareButton>
          <SquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor="#ffffff"
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectInstallment(30)}
          >
            <span
              style={{
                color: '#000f34',
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
              }}
            >{`30개월`}</span>
          </SquareButton>

          <SquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor="#ffffff"
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectInstallment(36)}
          >
            <span
              style={{
                color: '#000f34',
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
              }}
            >{`36개월`}</span>
          </SquareButton>

          {/* <SquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor="#ffffff"
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectInstallment('C')}
          >
            <span
              style={{
                color: '#000f34',
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
              }}
            >{`48개월`}</span>
          </SquareButton> */}
        </div>
      </ParentWrap>
    </Container>
  );
}
