import * as types from '../actions/HpTypes';

const initialState = {
  memId: '',
  makerCode: '',
  modelCode: '',
  // modelName: "",
  useTelecom: '',
  changeTelecom: '',
  chargeCode: '',
  // planName: "",
  custName: '',
  hpNo: '',
  comment: '',
  colorCode: '', //색상
  capacityCode: '', //용량
  assort: '', //신규(N), 번호이동(M), 기기변경(C)
  cardCode: '',
  marginPrice: 0,
  installment: 0,
  BankInfo: [],
  bankCode: '',
  accountName: '',
  accountNo: '',
  benefitAssort: '',
  loading: false,
  response: { result: '', memId: '', message: '' },
};

export default function hp(state = initialState, action) {
  switch (action.type) {
    case types.SELECT_MANUFACTURER:
      return {
        ...state,
        makerCode: action.makerCode,
      };
    case types.SELECT_MODEL:
      return {
        ...state,
        modelCode: action.modelCode,
      };
    case types.SELECT_CURRENT_TELECOM:
      return {
        ...state,
        useTelecom: action.useTelecom,
      };
    case types.CHANGE_TELECOM:
      return {
        ...state,
        changeTelecom: action.changeTelecom,
      };
    case types.SELECT_FEE:
      return {
        ...state,
        chargeCode: action.chargeCode,
      };
    case types.INSERT_INFO:
      return {
        ...state,
        custName: action.custName,
        hpNo: action.hpNo,
        comment: action.comment,
        memId: action.memId,
      };
    case types.INSERT_ACCOUNT_INFO:
      return {
        ...state,
        bankCode: action.bankCode,
        accountName: action.accountName,
        accountNo: action.accountNo,
      };
    case types.SELECT_COLOR:
      return {
        ...state,
        colorCode: action.colorCode,
      };
    case types.SELECT_CAPACITY:
      return {
        ...state,
        capacityCode: action.capacityCode,
      };
    case types.SUBMIT_HPREQUEST:
      console.log(action);

      return {
        ...state,
        response: {
          ...state.response,
          result: action.response.result,
          memId: action.response.memId,
          message: action.response.message,
        },
        loading: false,
      };

    case types.SET_ASSORT:
      return {
        ...state,
        assort: action.assort,
      };

    case types.SELECT_MARGIN_PRICE:
      return {
        ...state,
        marginPrice: action.marginPrice,
      };

    case types.CARD_DISCOUNT:
      return {
        ...state,
        cardCode: action.cardCode,
      };

    case types.INIT:
      return {
        ...state,
        memId: '',
        makerCode: '',
        modelCode: '',
        useTelecom: '',
        changeTelecom: '',
        chargeCode: '',
        cardCode: '',
        custName: '',
        hpNo: '',
        comment: '',
        colorCode: '', //색상
        capacityCode: '', //용량
        response: { result: '', memId: '', message: '' },
      };

    case types.SET_MODELS:
      return {
        ...state,
        models: action.models,
        parentName: action.parentName,
      };

    case types.SELECT_BANEFIT_ASSORT:
      return {
        ...state,
        benefitAssort: action.benefitAssort,
      };

    case types.SELECT_DISCOUNT_TYPE:
      return {
        ...state,
        discountType: action.discountType,
      };

    case types.SET_BENEFIT_PRICE:
      return {
        ...state,
        benefitPrice: action.benefitPrice,
      };

    case types.SET_BANK_INFO:
      return {
        ...state,
        BankInfo: action.BankInfo,
      };
    case types.SELECT_INSTALLMENT:
      return {
        ...state,
        installment: action.installment,
      };

    case types.LOADING:
      return {
        ...state,
        loading: action.loading,
      };

    default:
      return state;
  }
}
