/*
  설명: cms정기결제 페이지
  - 2021.05.20. 안예린, import 수정
*/
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
// css
import {
  NumberInputLine,
  SwiperMiddleLabel,
} from '../../../css/common-component';
import { DropDownListRound } from '../../../css/common-component';
import '../../../css/kendo.css';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
// icon & images
import PersonIcon from '@material-ui/icons/Person';

const MyInfoCMS = (props) => {
  const headerInfo = {
    isBackground: true, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: 'CMS정보'
  };

  const dispatch = useDispatch();
  const { userId } = useSelector((store) => store.auth.login);
  const [cmsButtonCheck, setCmsButtonCheck] = useState(false);
  const [cardButtonCheck, setCardButtonCheck] = useState(false);
  const [isAccountCheckAlert, setIsAccountCheckAlert] = useState({
    show: false,
    content: '',
  });
  const [isAccountCheckAlertButton, setIsAccountCheckAlertButton] =
    useState(false);
  // radio Toggle
  const [radioValue, setRadioValue] = useState('CMS');
  const radios = [
    { name: '자동이체', value: 'CMS' },
    { name: '신용카드', value: 'CARD' },
  ];
  const [state, setState] = useState({
    // user정보
    userId: userId,
    userName: '',
    photo: '',
    // 은행 리스트
    bankOptions: [],
    // cms 실명계좌조회,input
    cmsaccountBank: '',
    cmsAccountNo: '',
    cmsregistInput1: '',
    // 신용카드 input
    cardAccountNo: '',
    cardregistInput1: '',
    cardholderName: '',
    valid: '',
    cardPasswd: '',
    // cms 정보조회
    cmsStatus: '',
    paymentCompany: '',
    paymentKind: '',
    paymentNumber: '',
  });

  // 기존 은행 정보
  const [originalState, setOriginalState] = useState({
    cmsAccountNo: '',
    payerNumber: '',
    accountName: '',
  });

  // 은행 리스트 선택
  const _handleSelectBank = (e) => {
    setState({
      ...state,
      cmsaccountBank: e.value.code,
    });
  };

  // 정산계좌정보의 내용이 바뀔때만 수정버튼 활성화
  const _buttonCheck = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
    if (e.target.value !== originalState[e.target.name]) {
      setCmsButtonCheck(true);
      setCardButtonCheck(true);
    } else if (e.target.value === originalState[e.target.name]) {
      setCmsButtonCheck(false);
      setCardButtonCheck(false);
    }
  };

  // 실명계좌 확인
  const _OnCheckCms = async (e) => {
    e.preventDefault();
    const url = '/api/account/get_real_check.php';
    const params = {
      paymentCompany: state.cmsaccountBank,
      paymentNumber: state.cmsAccountNo,
      payerNumber: state.cmsregistInput1,
    };
    const { result, message } = await _axios(url, params);

    if (result === '1') {
      setIsAccountCheckAlert({
        show: true,
        result: result,
        content: message,
      });
      setIsAccountCheckAlertButton(true);
    } else {
      _accountSetData();
    }
  };

  // 빈input 체크
  const _accountInputCheck = () => {
    if (
      state.cardAccountNo === '' ||
      state.cardregistInput1 === '' ||
      state.cardholderName === '' ||
      state.valid === '' ||
      state.cardPasswd === ''
    ) {
      if (state.cardPasswd === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '카드 비밀번호 앞 2자리를 입력해주세요',
        });
      }

      if (state.valid === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '카드 유효기간을 입력해주세요.',
        });
      }

      if (state.cardAccountNo === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '카드번호를 입력해주세요',
        });
      }
      if (state.cardregistInput1 === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '생년월일을 입력해주세요.',
        });
      }
      if (state.cardholderName === '') {
        setIsAccountCheckAlert({
          show: true,
          result: '1',
          content: '카드 소유자명을 입력해주세요',
        });
      }
      return false;
    } else {
      return true;
    }
  };

  // cms변경등록 함수
  const _accountSetData = async () => {
    if (radioValue === 'CARD' ? _accountInputCheck() : true) {
      const url = '/api/member/set_my_cms_update.php';

      const cmsTrue = radioValue === 'CMS';

      const params = {
        userId: state.userId,
        paymentKind: radioValue,
        paymentCompany: cmsTrue ? state.cmsaccountBank : '',
        paymentNumber: cmsTrue ? state.cmsAccountNo : state.cardAccountNo,
        payerName: cmsTrue ? '예금주' : state.cardholderName,
        payerNumber: cmsTrue ? state.cmsregistInput1 : state.cardregistInput1,
        valid: cmsTrue ? '' : state.valid,
        cardPasswd: cmsTrue ? '' : state.cardPasswd,
      };

      const { result, message } = await _axios(url, params);
      setIsAccountCheckAlert({
        show: true,
        result: result,
        content: message || '잘못된 정보입니다',
      });
      if (result === '1') {
        setIsAccountCheckAlertButton(true);
      }
    }
  };

  // slideModal
  const [isComplete, setIsComplete] = useState({
    show: false,
    content: '',
  });

  const _getData = async () => {
    // 사진, 유저이름,유저아이디
    const url = '/api/member/get_my_info.php';

    const params = {
      userId: state.userId,
    };
    const { data } = await _axios(url, params);

    // 은행리스트
    const url2 = '/api/account/get_user_info.php';
    const params2 = {
      userId: state.userId,
    };

    const bankList = await _axios(url2, params2);

    // cms 정보
    const url3 = '/api/member/get_my_cms_info.php';
    const params3 = {
      userId: state.userId,
    };

    const cmsData = await _axios(url3, params3);

    setState({
      ...state,
      // user정보 state
      userName: data.userName,
      photo: data.photo,
      // 은행리스트 state
      bankOptions: bankList.data.bankOptions,
      // cms state
      paymentKind: cmsData.data.paymentKind,
      paymentCompany: cmsData.data.paymentCompany,
      paymentNumber: cmsData.data.paymentNumber,
      cmsStatus: cmsData.data.cmsStatus,
    });
  };

  useEffect(() => {
    _getData();
  }, [
    isAccountCheckAlert,
    state.paymentKind,
    state.paymentCompany,
    state.cmsStatus,
  ]);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const testRef = useRef();

  return (
    <Container>
      <Wrap>
        <Inner>
          <StyledInfoUpdate>
            <CMSprofile>
              <div ref={testRef} className="cmsprofile-img">
                {!state.photo ? (
                  <PersonIcon style={{ fontSize: '2.5em' }} />
                ) : (
                  <img src={state.photo} />
                )}
              </div>
              <div className="cmsprofile-txt">
                <p className="cmsprofile-txt-name">{state.userName}</p>
                <p className="cmsprofile-txt-num">{state.userId}</p>
              </div>
            </CMSprofile>
            
            <CMSInner>
              {/* 등록된 CMS 정보 */}
              <StyledMyInfo>
                <StyledTitleCss>등록된 CMS 정보</StyledTitleCss>
                <StyledListBox>
                  <div style={{ width: '30%' }}>결제수단</div>
                  <div style={{ width: '70%' }}>{state.paymentKind}</div>
                </StyledListBox>
                <StyledListBox>
                  <div style={{ width: '30%' }}>결제번호</div>
                  <div style={{ width: '70%', justifyContent: 'space-between' }}>
                    {state.paymentNumber}({state.paymentCompany})
                  </div>
                </StyledListBox>
                <StyledListBox>
                  <div style={{ width: '30%' }}>납부상태</div>
                  <div style={{ width: '70%', justifyContent: 'space-between' }}>
                    {state.cmsStatus}
                  </div>
                </StyledListBox>
                {state.cmsStatus !== '정상' && (
                  <MyinfoButton
                    onClick={() => {
                      props.history.push('/mypage/myinfo/unpaid');
                    }}
                    className="myinfo-button"
                  >
                    미납료 납부
                  </MyinfoButton>
                )}
              </StyledMyInfo>
              {/* CMS 변경 정보 입력 */}
              <StyledCalculate>
                <StyledTitleCss>CMS 변경 정보 입력</StyledTitleCss>
                {radios.map((radio, index) => (
                  <label key={index}>
                    <input
                      key={index}
                      type="radio"
                      value={radio.value || ''}
                      checked={radioValue === radio.value}
                      onChange={(e) => {
                        setRadioValue(e.currentTarget.value);
                      }}
                    />
                    {radio.name}
                  </label>
                ))}
                {radioValue === 'CMS' ? (
                  <>
                    <div className="radioValue-cms">
                      <DropDownListRound
                        style={{
                          borderRadius: '0',
                          height: '100%',
                          borderRadius: '3px',
                          padding: 'none',
                          fontSize: '14px',
                        }}
                        border={'#c7c7c7'}
                        type="text"
                        name="cmsaccountBank"
                        data={state.bankOptions}
                        dataItemKey="code"
                        textField="name"
                        onChange={_handleSelectBank}
                        required={false}
                        width={'55%'}
                        height={'2.7rem'}
                        defaultItem={{
                          name: '은행명',
                          code: '',
                        }}
                      ></DropDownListRound>
                      <input
                        type="number"
                        name="cmsregistInput1"
                        value={state.cmsregistInput1 || ''}
                        placeholder="예금주 생년월일(6자리)"
                        className="radioValue-cms-payerNumber"
                        onChange={_buttonCheck}
                        maxLength="6"
                        required
                      ></input>
                    </div>
                    <div>
                      <input
                        type="text"
                        name="cmsAccountNo"
                        value={state.cmsAccountNo || ''}
                        placeholder="계좌번호를 입력하세요.( - 제외)"
                        className="radioValue-cms-account"
                        onChange={_buttonCheck}
                        required
                      ></input>
                    </div>
                    {cmsButtonCheck ? (
                      <CMSbutton onClick={_OnCheckCms}>납부정보수정</CMSbutton>
                    ) : (
                      <CMSbutton
                        type="button"
                        style={{ backgroundColor: '#979797' }}
                      >
                        납부정보수정
                      </CMSbutton>
                    )}
                    {/* <CMSbutton onClick={_onCheckID}>납부정보수정</CMSbutton> */}
                    {isComplete.show && (
                      <StyledDialog
                        width={'256px'}
                        height={'auto'}
                        title={' '}
                        onClose={() => {
                          isComplete.show
                            ? setIsComplete({ show: false })
                            : setIsComplete({ show: true });
                        }}
                      >
                        <SwiperMiddleLabel
                          width="216px"
                          height="auto"
                          margin="0px auto 0px auto"
                          style={{
                            color: '#000f34',
                            fontSize: '15px',
                            textAlign: 'center',
                          }}
                        >
                          {isComplete.result === '0'
                            ? '확인되었습니다.'
                            : `입력하신 정보가 없습니다.
                              다시 입력하세요.`}
                        </SwiperMiddleLabel>
                        <DialogActionsBar>
                          <button
                            className="k-button"
                            onClick={() => {
                              isComplete.show
                                ? setIsComplete({ show: false })
                                : setIsComplete({ show: true });
                            }}
                          >
                            닫기
                    </button>
                        </DialogActionsBar>
                      </StyledDialog>
                    )}
                  </>
                ) : (
                  <>
                    <div className="radioValue-cms">
                      <input
                        type="text"
                        name="cardholderName"
                        value={state.cardholderName}
                        placeholder="소유자명"
                        className="radioValue-card-name"
                        onChange={_buttonCheck}
                        required
                      ></input>
                      <input
                        type="text"
                        name="cardregistInput1"
                        placeholder="생년월일(6자리)"
                        value={state.cardregistInput1}
                        className="radioValue-card-birth"
                        onChange={_buttonCheck}
                        maxLength="6"
                        required
                      ></input>
                    </div>
                    <div style={{ marginBottom: '16px' }}>
                      <input
                        type="text"
                        name="cardAccountNo"
                        value={state.cardAccountNo}
                        placeholder="카드번호를 입력하세요.( - 제외)"
                        className="radioValue-card-number"
                        onChange={_buttonCheck}
                        required
                      ></input>
                    </div>
                    <div className="radio-card-validity">
                      <NumberInputLine
                        type="text"
                        name="valid"
                        placeholder="유효기간(MM/YY)"
                        value={state.valid}
                        // validityStyles={false}
                        mask="00/00"
                        maxLength={20}
                        autoComplete="off"
                        onChange={_buttonCheck}
                      // required={true}
                      ></NumberInputLine>
                      <input
                        type="password"
                        name="cardPasswd"
                        placeholder="비밀번호 앞 2자리"
                        value={state.cardPasswd}
                        className="radio-card-validity-input"
                        onChange={_buttonCheck}
                        maxLength="2"
                        max="2"
                        required
                      ></input>
                    </div>
                    {cardButtonCheck ? (
                      <CMSbutton onClick={_accountSetData}>납부정보수정</CMSbutton>
                    ) : (
                      <CMSbutton
                        type="button"
                        style={{ backgroundColor: '#979797' }}
                      >
                        납부정보수정
                      </CMSbutton>
                    )}
                  </>
                )}
              </StyledCalculate>
            </CMSInner>
            {isAccountCheckAlert.show && (
              <StyledDialog
                // id="bonusModal"
                width={'256px'}
                height={'auto'}
                title={' '}
                onClose={() => {
                  isAccountCheckAlert.show
                    ? setIsAccountCheckAlert({ show: false })
                    : setIsAccountCheckAlert({ show: true });
                }}
              >
                <SwiperMiddleLabel
                  width="216px"
                  height="auto"
                  margin="0px auto 0px auto"
                  style={{
                    color: '#000f34',
                    fontSize: '15px',
                    textAlign: 'center',
                  }}
                >
                  {isAccountCheckAlert.content}
                </SwiperMiddleLabel>
                <DialogActionsBar>
                  <button
                    className="k-button"
                    onClick={() => {
                      isAccountCheckAlert.show
                        ? setIsAccountCheckAlert({ show: false })
                        : setIsAccountCheckAlert({ show: true });
                      setIsAccountCheckAlertButton(false);
                      if (
                        isAccountCheckAlert.content === '생년월일/사업자번호불일치'
                      ) {
                        setState({
                          ...state,
                          cmsregistInput1: '',
                        });
                      } else if (isAccountCheckAlert.content === '계좌번호오류') {
                        setState({
                          ...state,
                          cmsAccountNo: '',
                        });
                      } else if (isAccountCheckAlert.content === '생년월일오류') {
                        setState({
                          ...state,
                          cardregistInput1: '',
                        });
                      } else if (isAccountCheckAlert.content === '카드번호 오류') {
                        setState({
                          ...state,
                          cardAccountNo: '',
                        });
                      } else if (isAccountCheckAlert.content === '유효기간 오류') {
                        setState({
                          ...state,
                          valid: '',
                        });
                      }
                    }}
                  >
                    {isAccountCheckAlertButton ? '다시 입력' : '확인'}
                  </button>
                </DialogActionsBar>
              </StyledDialog>
            )}
          </StyledInfoUpdate>
        </Inner>

      </Wrap>
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
`;

const Wrap = styled.div`
  display: flex;
  flex-direction: column;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 450px;
    margin-top: 50px;
  }
`;

const Inner = styled.div`
  width:100%;
  background: red;

`

const CMSprofile = styled.div`
  height: 8em;
  display: flex;
  align-items: center;
  margin-bottom: 3em;
  padding: 2em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  background-color: rgb(13, 34, 75);;

  .cmsprofile-img {
    width: 5em;
    height: 5em;
    margin-right: 1.5em;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: white;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .cmsprofile-txt {
    color: white;
    padding: 0.2em;

    p {
      padding-top: 0.4em;
      margin-bottom: 0;
    }

    &-name {
      font-weight: 700;
    }
  }
`;

const StyledInfoUpdate = styled.div`
  width: 100%;
  padding: 0 0 6em;
  background-color: #f2f2f2;

  .k-maskedtextbox .k-textbox {
    border: solid 1px #c7c7c7;
    transform: translateY(0px);
    height: 100%;

    ::placeholder {
      padding: 0.5em;
      font-size: 14px;
    }
  }
`;

const CMSInner = styled.div`
  padding: 0 2em;
`;

const StyledTitleCss = styled.div`
  font-size: 12px;
  color: #8f92a1;
`;

const StyledListBox = styled.div`
  display: flex;
  font-size: 16px;
  padding: 1.2em 0 0.6em;
  color: #171717;
  border-bottom: 1px solid #d6d6d6;
`;

const StyledMyInfo = styled.section`
  position: relative;
  width: 100%;
  margin-bottom: 5em;

  div {
    padding-left: 0.5em;
  }
`;

// modal
const StyledDialog = styled(Dialog)`
  .k-dialog {
    border-top-right-radius: 22px;
    border-top-left-radius: 22px;
    border-bottom-right-radius: 22px;
    border-bottom-left-radius: 22px;
  }

  .k-window-titlebar {
    padding: 0px;
  }

  .k-dialog-content {
    margin-bottom: 30px;
  }
  .k-dialog-titlebar {
    background-color: transparent;
    border: none;
  }

  .k-dialog-button-layout-stretched {
    background-color: #000f34;
    color: #fff;
    border-bottom-right-radius: 22px;
    border-bottom-left-radius: 22px;
  }

  .k-button {
    background-color: #000f34;
    color: #fff;
    height: 59px;
  }
`;

const MyinfoButton = styled.button`
  outline: none;
  border: none;
  padding: 0.5em 2em 0.6em;
  margin-top: 1em;
  background-color: #000f34;
  color: white;
  border-radius: 8px;
  display: flex;
  position: absolute;
  right: 0;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
`;

const CMSbutton = styled.button`
  font-size: 14px;
  outline: none;
  border: none;
  padding: 0.5em 2em 0.6em;
  margin-top: 1em;
  background-color: #000f34;
  color: white;
  border-radius: 8px;
  display: flex;
  position: absolute;
  right: 0;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
`;

// CMS 변경 정보 입력
const StyledCalculate = styled.section`
  position: relative;
  width: 100%;
  font-size: 16px;

  select {
    font-size: 14px;
    padding: 0.4em 4em 0.4em 0.5em;
    color: #c7c7c7;
    margin-right: 0.8em;
    height: 32px;
  }

  label {
    margin: 1em 1.5em 0 0;
    color: #000f34;
  }

  input {
    width: 100%;
    border: solid 1px #c7c7c7;
    border-radius: 3px;
    padding: 0.5em 0.7em;
    font-size: 14px;

    ::placeholder {
      color: #c7c7c7;
    }

    &[type='radio'] {
      width: 1em;
      height: 1em;
      margin-right: 0.4em;
      padding: 0;

      &:checked {
        appearance: none;
        width: 1em;
        height: 1em;
        border-radius: 100%;
        border: solid 1px #000f34;
        background-color: #000f34;
      }
    }
  }

  .radioValue-cms {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 1em 0;

    &-payerNumber {
      padding: 0.5em;
      margin-left: 1em;
    }

    &-account {
      font-size: 14px;
      padding: 0.5em 0.7em;
    }

    .radioValue-card {
      padding: 0.5em 0 0.5em 0.7em;

      &.name {
        margin-right: 0.8em;
      }
    }
  }

  /* 신용카드 */
  .radio-card-validity {
    display: flex;
    justify-content: space-between;

    ${NumberInputLine} {
      width: 48.4%;
    }

    &-input {
      width: 49%;
    }
  }

  .radioValue-card-name {
    width: 48.4%;
  }

  .radioValue-card-birth {
    width: 48.4%;
  }
`;

export default MyInfoCMS;
