/* **********************************************************************
 * 회원 > 마이페이지 > 포인트 Route
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import { 
  PointList, 
  CashRequestList,
  CommiList, 
  AccurateList,
  AccurateInfo
} from '../../components/mypage/point';

const Commi = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/mypage/point"
        render={(props) => <PointList {...props} />}
      />
      <AuthRoute
        exact
        path="/mypage/point/request"
        render={(props) => <CashRequestList {...props} />}
      />
      <AuthRoute
        exact
        path="/mypage/point/commi"
        render={(props) => <CommiList {...props} />}
      />
      <AuthRoute
        exact
        path="/mypage/point/accurate"
        render={(props) => <AccurateList {...props} />}
      />
      <AuthRoute
        path="/mypage/point/accurate/info"
        render={(props) => <AccurateInfo {...props} />}
      />
    </Switch>
  );
};

export default Commi;