/*  설명 : stackRequest제거 2021. 05. 28 양민석  */
/* **********************************************************************
 * 휴대폰신청 > 부가서비스 > 목록 > 상세페이지
 * 작성자: 박태수
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import { Input, RadioGroup, TextArea } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const ServiceInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,

    serviceCode: '',
    serviceName: '',
    servicePrice: '',
    periodDay: '',
    descript: '',
    telecom: '',
    useYn: '',

    telecomOptions: [],
    useOptions: [],

    success: false,
    message: null,
    visibleDialog: false,
  });

  // 부가서비스 정보
  const _getData = async () => {
    const url = '/api/hp/addService/get_service_info.php';
    const params = {
      idx: state.idx,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      ...state,
      isLoading: false,
      serviceCode: response.data.serviceCode,
      serviceName: response.data.serviceName,
      servicePrice: response.data.servicePrice,
      periodDay: response.data.periodDay,
      descript: response.data.descript,
      telecom: { code: response.data.telecom, name: response.data.telecomName },
      useYn: response.data.useYn,

      telecomOptions: response.telecomOptions,
      useOptions: response.useOptions,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/hp/addService/set_service_update.php';
    const params = {
      mode: state.mode,
      idx: state.idx,
      serviceCode: state.serviceCode,
      serviceName: state.serviceName,
      servicePrice: state.servicePrice,
      periodDay: state.periodDay,
      descript: state.descript,
      telecom: state.telecom,
      useYn: state.useYn,
    };

    const { message } = await _axios(url, params);

    setState({
      ...state,
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleTextarea = (e) => {
    setState({ ...state, [e.target.element.current.name]: e.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, useYn: e.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const serviceCodeMessage = "'서비스코드'를 입력해주세요!";
  const serviceNameMessage = "'서비스명'을 입력해주세요!";
  const servicePriceMessage = "'서비스요금'을 입력해주세요!";
  const periodDayMessage = "'유지기간'을 입력해주세요!";
  const telecomMessage = "'통신사'를 선택해주세요!";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 부가서비스관리 〉 목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>서비스코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="serviceCode"
                  value={state.serviceCode}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={serviceCodeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>서비스명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="serviceName"
                  value={state.serviceName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={serviceNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>서비스요금</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="servicePrice"
                  value={state.servicePrice}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={servicePriceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>유지기간</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="periodDay"
                  value={state.periodDay}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={periodDayMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="telecom"
                  value={state.telecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={telecomMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용여부</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="useYn"
                    defaultValue={state.useYn}
                    data={state.useOptions}
                    value={state.useYn}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>서비스설명</Label>
                <TextArea
                  name="descript"
                  value={state.descript}
                  style={{ width: '74%' }}
                  autoSize={true}
                  onChange={_handleTextarea}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default ServiceInfo;
