/* **********************************************************************
 * 관리자페이지 > 게시판관리 > 게시글목록 > 상세정보
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Input } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { Dialog } from '@progress/kendo-react-dialogs';
import { useMedia } from 'react-media';
import renderHTML from 'react-render-html';
// 개발자 Component
import _axios from '../../../utils/axios';
import BbsReply from './BbsReply';
// css
import styled from 'styled-components';
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
  WriterWrap,
} from '../../../css/list';

const BbsView = (props) => {
  const auth = useSelector((store) => store.auth.login.userInfo);
  const isMobile = useMedia({ query: '(max-width: 767px)' });

  const [state, setState] = useState({
    memId: auth.userId,
    memName: auth.userName,
    mode: props.location.mode,
    idx: props.location.idx,

    bbsCode: '',
    subject: '',
    content: '',
    wdate: '',
    memId: '',
    memName: '',

    bbsInfo: {},
  });

  // 게시글 정보
  const _getData = async () => {
    const url = '/api/bbs/get_bbs_info.php';
    const params = {
      idx: state.idx,
      memId: state.memId,
    };
    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      bbsCode: response.data.bbsCode,
      bbsName: response.data.bbsName,
      subject: response.data.subject,
      content: response.data.content,
      wdate: response.data.wdate,
      memId: response.data.memId,
      memName: response.data.memName,
      bbsInfo: response.bbsInfo,
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <Wrap width={'100%'}>
        <Title>게시글관리 〉 게시글목록 〉 상세정보</Title>
        <Table width={'100%'}>
          <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
            <Td width={tdWidth}>
              <Label width={labelWidth}>게시판구분</Label>
              <Input
                validityStyles={false}
                type="text"
                name="bbsName"
                value={state.bbsName}
                style={{ width: '74%' }}
                maxLength={20}
                required={false}
              />
            </Td>
            <Td width={tdWidth}>
              <Label width={labelWidth}>글 제목</Label>
              <Input
                validityStyles={false}
                type="text"
                name="subject"
                value={state.subject}
                style={{ width: '74%' }}
                maxLength={20}
                required={false}
              />
            </Td>
          </Tr>
          <Tr width={'100%'}>
            <Td
              width={'100%'}
              style={{
                padding: '5px',
                border: '1px solid #dcdcdc',
                display: 'block',
              }}
            >
              <StyledEditorDiv>{renderHTML(state.content)}</StyledEditorDiv>
            </Td>
          </Tr>
          <Tr width={'100%'}>
            <WriterWrap width={'100%'}>
              {state.wdate} by{' '}
              <span style={{ fontWeight: 'bold', paddingLeft: '5px' }}>
                {state.memName} [{state.memId}]
              </span>
            </WriterWrap>
          </Tr>
        </Table>

        <ButtonWrap>
          {state.bbsInfo.authEdit && (
            <Button
              type={'button'}
              onClick={() => {
                props.history.push({
                  pathname: '/admin/bbs/input',
                  idx: state.idx,
                  mode: 'update',
                });
              }}
            >
              수정하기
            </Button>
          )}

          <Button
            style={{ marginLeft: '2px' }}
            type={'button'}
            onClick={() => props.history.goBack()}
          >
            이전화면
          </Button>
        </ButtonWrap>

        {state.bbsInfo.useReply && (
          <BbsReply props={props} parentIdx={state.idx} />
        )}

        {state.success && state.visibleDialog && (
          <Dialog title={'알림'} onClose={_toggleDialog}>
            <p style={{ margin: '25px', textAlign: 'center' }}>
              {state.message}
            </p>
          </Dialog>
        )}
      </Wrap>
    </Container>
  );
};

const StyledEditorDiv = styled.div`
  img {
    width: 100%;
  }
`;

export default BbsView;
