import React from 'react';
//css
import styled from 'styled-components';
import Table from 'react-bootstrap/Table';
import theme from '../../../styles/theme';
//images

const Notice = (props) => {
  const noticeData = props.noticeData;

  return (
    <StyledContainer>
      <div className="titleWrap">
        <span>공지사항</span>
        {/* 개발 중 */}
        {/* <span className="hamburger">
          <img src={more_horiz} alt="" />
        </span> */}
      </div>

      <StyledTable>
        <thead className="B1_18 bold">
          <tr>
            <th className="title">No</th>
            <th className="title">제목</th>
            <th className="title">작성자</th>
            <th className="title">작성일자</th>
          </tr>
        </thead>
        <tbody>
          {noticeData.map((item, index) => (
            <tr key={index}>
              <td className="B2_15 book">{index}</td>
              <td className="B2_15 book">{item.subject}</td>
              <td className="B2_15 book">{item.memName}</td>
              <td className="B2_15 book title">{item.wdate}</td>
            </tr>
          ))}
        </tbody>
      </StyledTable>
    </StyledContainer>
  );
};

export default Notice;

const StyledContainer = styled.div`
  margin-top: 30px;
  width: 100%;
  min-height: 575px;
  height: auto;
  padding: 22px;
  background: #f1f2f2;
  .titleWrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: ${theme.colors.navyBox};
    ${theme.H3};
    text-align: center;
  }

  .hamburger {
    img {
      width: 100%;
      width: 28px;
      height: 24px;
    }
  }

  .noticeWrap {
  }

  .line {
    border-top: 1px solid #a9a9a9;
    transform: rotate(90deg);
    width: 68px;
  }
`;

const StyledTable = styled(Table)`
  margin: 22px 0;
  width: 100%;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  background-color: white;
  padding: 22px;

  thead {
    color: ${theme.colors.white};
    ${theme.fontSizes.Body1};
    text-align: center;
    background: ${theme.colors.lavender};
  }

  tbody {
    color: ${theme.colors.black1};
    ${theme.Body2};
    text-align: center;
  }
`;
