import React, { useCallback, useEffect, useState } from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import styled from 'styled-components';
// 개발자 라이브러리
import _axios from '../../../utils/axiosUrl';
// css
import { NextButton } from '../../../css/common-component';

export default function SharedLink({ memId, fee }) {
  const [isCopy, setIsCopy] = useState(false);
  const [link, setLink] = useState('');

  const creactShortLink = useCallback(async () => {
    const bitly_url = 'https://api-ssl.bitly.com/v4/shorten';
    const long_url = `http://www.yourphone.co.kr?memId=${memId}&fee=${fee}`;
    const token = 'e58e936d6e0871655d674c279d1d47802ac43faf';
    const response = await _axios(
      bitly_url,
      { long_url: long_url },
      {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    );
    const { link } = response;
    setLink(link);
  }, [memId, fee]);

  useEffect(() => {
    let mounted = true;
    creactShortLink();
    return () => {
      mounted = false;
    };
  }, [creactShortLink]);

  return (
    <>
      <StyledLinkWrap style={{ height: '80px' }}>
        <div className="title">
          <span>{fee}원 수수료 링크</span>
        </div>
        <div className="d-flex align-items-start">
          <input
            style={{
              width: '235px',
              height: '33px',
              margin: '10px 7px 0px 0px',
              border: 'solid 1px #707070',
              backgroundColor: '#ffffff',
              color: '#707070',
              display: 'flex',
              alignItems: 'center',
            }}
            value={link}
            disabled={true}
          />
          <CopyToClipboard text={link} onCopy={() => setIsCopy(true)}>
            <NextButton
              width="91px"
              height="33px"
              margin="10px 0 0 0"
              style={{
                display: 'flex',
                fontSize: '14px',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: '0',
              }}
            >
              링크 복사
            </NextButton>
          </CopyToClipboard>
        </div>
        {isCopy ? (
          <div style={{ color: '#374057', textAlign: 'center' }}>
            복사되었습니다.
          </div>
        ) : null}
      </StyledLinkWrap>
    </>
  );
}

const StyledLinkWrap = styled.div`
  width: 100%;
  height: 98px;
  padding: 10px 21px;
  background-color: #f2f2f2;
  margin-bottom: 10px;

  .title {
    display: flex;
    justify-content: space-between;
    color: #000f34;
  }
  .subTitle {
    color: #4b4b4b;
    font-size: 12px;
  }
`;
