import {
  AUTH_GET_STATUS,
  AUTH_GET_STATUS_FAILURE,
  AUTH_GET_STATUS_SUCCESS,
  AUTH_LOGIN,
  AUTH_LOGIN_FAILURE,
  AUTH_LOGIN_SUCCESS,
  AUTH_LOGOUT,
  ID_SAVE,
} from "./AuthTypes";

import _axios from "../../utils/axios";

/* *************************** 로그인 ************************************* */
export function idSave(userId) {
  // Storage 저장
  localStorage.setItem("user", userId);

  return {
    type: ID_SAVE,
    saveId: userId,
  };
}

export function init() {
  return {
    type: AUTH_LOGIN,
  };
}

export function loginRequest(userId, userPw) {
  return (dispatch) => {
    dispatch(login());

    const url = "/api/account/get_login.php";
    const params = {
      userId: userId,
      userPw: userPw,
    };

    // API REQUEST
    return _axios(url, params)
      .then((response) => {
        // SUCCEED
        dispatch(loginSuccess(response));
      })
      .catch((error) => {
        // FAILED
        dispatch(loginFailure());
      });
  };
}

export function login() {
  return {
    type: AUTH_LOGIN,
  };
}

export function loginSuccess(userInfo) {
  if (userInfo.result === "0") {
    let loginData = {
      isLogIn: true,
      userId: userInfo.userId,
      userAuth: userInfo.userAuth,
      userInfo: {
        userId: userInfo.userId,
        userName: encodeURIComponent(userInfo.userName),
        userAuth: userInfo.userAuth,
      }
    };

    // 세션에 저장
    sessionStorage.setItem("user", btoa(JSON.stringify(loginData)));

    return {
      type: AUTH_LOGIN_SUCCESS,
      userId: userInfo.userId,
      userAuth: userInfo.userAuth,
      userInfo: userInfo,
    };
  } else {
    return {
      type: AUTH_LOGIN_FAILURE,
      userInfo: userInfo,
    };
  }
}

export function loginFailure() {
  return {
    type: AUTH_LOGIN_FAILURE,
  };
}

/* *************************** GET STATUS ************************************* */
export function getStatus() {
  return {
    type: AUTH_GET_STATUS,
  };
}

export function getStatusRequest() {
  return (dispatch) => {
    dispatch(getStatus());
    
    let sessionData = sessionStorage.getItem("user");

    if (sessionData === null) {
      dispatch(getStatusFailure());
    } else {
      sessionData = JSON.parse(atob(sessionData));

      const userInfo = {
        userId: sessionData.userInfo.userId,
        userName: decodeURIComponent(sessionData.userInfo.userName),
        userAuth: sessionData.userInfo.userAuth,
      }

      dispatch(getStatusSuccess(sessionData.userId, sessionData.userAuth, userInfo));
    }
  };
}

export function getStatusSuccess(userId, userAuth, userInfo) {
  return {
    type: AUTH_GET_STATUS_SUCCESS,
    userId,
    userAuth,
    userInfo
  };
}

export function getStatusFailure() {
  return {
    type: AUTH_GET_STATUS_FAILURE,
  };
}

/* *************************** 로그아웃 ************************************* */
export function logoutRequest() {
  return (dispatch) => {
    return dispatch(logout());
  };
}

export const logout = () => async (dispatch) => {
  //dispatch({
  //  type: AUTH_LOGOUT,
  //});
  
  // session 삭제
  sessionStorage.removeItem("user");
  // 새로고침
  window.location.reload();
};
