/* **********************************************************************
 * 회원 > 마이페이지 > 회원정보 Route
 *********************************************************************** */
import React from 'react';
import { Switch } from 'react-router-dom';
import AuthRoute from '../../components/common/AuthRoute';
import { 
  MemberList,
  Losmap,
  CmsStatus
} from '../../components/mypage/member';

const Member = () => {
  return (
    <Switch>
      <AuthRoute
        exact
        path="/mypage/member"
        render={(props) => <MemberList {...props} />}
      />
      <AuthRoute
        path="/mypage/member/losmap"
        render={(props) => <Losmap {...props} />}
      />
      <AuthRoute
        path="/mypage/member/cmsStatus"
        render={(props) => <CmsStatus {...props} />}
      />    
    </Switch>
  );
};

export default Member;