import React, { useEffect } from 'react';
import { Route, Switch, useLocation } from 'react-router-dom';

import Home from '../components/front/Home'; // 프론트 페이지 > 홈
import SiteInfo from '../components/SiteInfo'; // 스파이더플랫폼 소개
import Login from '../components/account/Login'; // 로그인

import AccountRoute from './AccountRoute'; // 계정 Route
import HpRoute from './HpRoute'; // 휴대폰신청 Route
import AdminLayout from '../layouts/AdminLayout'; // 관리자페이지 레이아웃
import MypageLayout from '../layouts/MypageLayout'; // 마이페이지 레이아웃

//import { ShareLink } from '../components/insu'; // 다이렉트보험 > 링크복사
//import HpRequest from '../components/partnership/hpRequest'; // 휴대폰신청

const FrontRoutes = () => {
  const { pathname } = useLocation();

  // 페이지이동시 스크롤 상단
  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };
  useEffect(() => {
    scrollToTop();
  }, [pathname]);

  return (
    <React.Fragment>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/login" component={Login} />
        <Route path="/account" component={AccountRoute} />
        <Route path="/partnership/hp" component={HpRoute} />
        <Route path="/admin" component={AdminLayout} />
        <Route path="/mypage" component={MypageLayout} />
        {/*
        <Route path="/insu/shareLink" component={ShareLink} />
        <Route path="/partnership/hp" component={HpRequest} />
        */}
        <Route path="/siteInfo" component={SiteInfo} />
      </Switch>
    </React.Fragment>
  );
};

export default FrontRoutes;
