import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
// css
import {
  Container,
  MenuButton,
  MenuButtonWrap,
  MenuImg,
} from '../../../css/common-component';
import { CurrentWrap } from './CurrentTelecom';
// icon & images
import apple_logo from '../../../images/apple_logo.svg';
import etc_logo from '../../../images/etc_logo.svg';
import lg_logo from '../../../images/lg_logo.svg';
import samsung_logo from '../../../images/samsung_logo.svg';

export default function ManufacturerType(props) {
  const dispatch = useDispatch();
  const _selectManufacture = (makerCode) => {
    dispatch(actions.selectManufacture(makerCode));
    props.history.push('/partnership/hp/modelSelect');
  };

  useEffect(() => {
    //dispatch(stackRequest('S', ''));
  }, [dispatch]);

  return (
    <Container className="page" style={{ padding: '6em 2em'}}>
      <CurrentWrap padding=" 75px 34px 0">
        <h1>제조사 선택</h1>
        <p className="carrier">신청하시려는 휴대폰의 제조사를 선택하세요.</p>

        <MenuButtonWrap className="row">
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _selectManufacture('S');
            }}
          >
            <MenuImg src={samsung_logo} alt="삼성" />
          </MenuButton>
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _selectManufacture('L');
            }}
          >
            <MenuImg src={lg_logo} alt="엘지" />
          </MenuButton>
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _selectManufacture('A');
            }}
          >
            <MenuImg src={apple_logo} alt="애플" />
          </MenuButton>
          <MenuButton
            className="col-lg-6 col-6 col-6"
            onClick={() => {
              _selectManufacture('O');
            }}
          >
            <MenuImg src={etc_logo} alt="기타" />
          </MenuButton>
        </MenuButtonWrap>
      </CurrentWrap>
    </Container>
  );
}
