import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dialog, DialogActionsBar } from '@progress/kendo-react-dialogs';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import _axios from '../../../utils/axios';
import MyProfile from './MyProfile';
import MyInfoGuide from './MyInfoGuide';
// css
import { DropDownListRound } from '../../../css/common-component';
import { SwiperMiddleLabel } from '../../../css/common-component';
import '../../../css/kendo.css';
import theme from '../../../styles/theme';
import CommonInput from '../../common/CommonInput';
import CommonButton from '../../common/Button';

// icon & images
import FileCopyIcon from '@material-ui/icons/FileCopy';

const AccountEdit = () => {
  const headerInfo = {
    isBackground: false,
    isBugger: true,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '정산 계좌 정보 수정',
  };
  const dispatch = useDispatch();
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);
  const [state, setState] = useState({
    isLoading: true,
    userId: userInfo.userId,
    userPw: '',
    userName: '',
    hpNo: '',
    email: '',
    gajaId: '',
    registNo: '',
    registInput1: '',
    registInput2: '',
    accountName: '',
    accountNo: '',
    accountBank: '',
    accountBankName: [{}],
    cmsStatus: '',
    contractStatus: '',
    memAssort: '',
    memStatus: '',
    confirmPw: '',
    photo: '',
    taxAssort: '',
    success: false,
    message: null,
    visibleDialog: false,
    bankOptions: [],
    //테스트 이미지상태
    sendImg: false,
  });
  // 모바일버전 상태
  const [originalState, setOriginalState] = useState({
    accountNo: '',
    payerNumber: '',
    accountName: '',
  });
  const [newCustomerState, setNewCustomerState] = useState(
    localStorage.getItem('myinfoGuide') ? false : true,
  );
  const [buttonCheckState, setButtonCheckState] = useState(false);
  const [isAccountCheckAlert, setIsAccountCheckAlert] = useState({
    show: false,
    content: '',
  });
  const [photo, setPhoto] = useState([]);
  const _getData = async () => {
    const url = '/api/member/get_my_info.php';
    const params = {
      userId: state.userId,
    };
    const { data } = await _axios(url, params);
    // console.log(data);
    // 은행리스트 api
    const url2 = '/api/account/get_user_info.php';
    const params2 = {
      userId: state.userId,
    };
    const bankList = await _axios(url2, params2);
    setState({
      ...state,
      isLoading: false,
      userPw: data.userPw,
      userName: data.userName,
      hpNo: data.hpNo,
      email: data.email,
      gajaId: data.gajaId,
      registNo: data.registNo,
      registNo2: data.registNo2,
      registInput1: data.registNo.substring(0, 6),
      registInput2: data.registNo.substring(7, 14),
      accountName: data.accountName,
      accountNo: data.accountNo,
      accountBank: data.accountBank,
      cmsStatus: data.cmsStatus,
      contractStatus: data.contractStatus,
      memAssort: data.memAssort,
      memStatus: data.memStatus,
      taxAssort: data.taxAssort,
      businessAssort: data.businessAssort,
      photo: data.photo,
      bankOptions: bankList.data.bankOptions,
      accountBankName: bankList.data.bankOptions.filter(
        (v) => v.code === data.accountBank,
      ),
    });
    setOriginalState({
      accountNo: data.accountNo,
      payerNumber: data.registNo,
      accountName: data.accountName,
    });
  };

  // 실명계좌 확인
  const _OnCheckAccount = async (e) => {
    e.preventDefault();
    const url = '/api/account/get_real_check.php';
    const params = {
      paymentCompany: state.accountBank,
      paymentNumber: state.accountNo,
      payerNumber: state.registInput1,
    };
    const { result, message } = await _axios(url, params);
    if (result === '1') {
      setIsAccountCheckAlert({
        show: true,
        result: result,
        content: message,
      });
    } else {
      _accountSetData();
    }
  };
  const RefCopy = useRef();
  const RefCopyIcon = useRef();
  const RefregistNo1 = useRef();
  const RefregistNo2 = useRef();
  // 정산계좌정보의 내용이 바뀔때만 수정버튼 활성화
  const _buttonCheck = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
    if (e.target.value !== originalState[e.target.name]) {
      setButtonCheckState(true);
    } else if (e.target.value === originalState[e.target.name]) {
      setButtonCheckState(false);
    }
  };
  // 정산 계좌 정보 저장 함수
  const _accountSetData = async () => {
    const url = '/api/member/set_my_bank_update.php';
    const params = {
      userId: state.userId,
      registNo: state.registInput1 + '-' + state.registInput2,
      accountName: state.accountName,
      accountNo: state.accountNo,
      accountBank: state.accountBank,
    };
    const { result, message } = await _axios(url, params);
    setIsAccountCheckAlert({
      show: true,
      result: result,
      content: message,
    });
    setTimeout(() => {
      setIsAccountCheckAlert({
        show: false,
        result: result,
        content: message,
      });
    }, 1000);
  };
  const _handleSelectBank = (e) => {
    setState({
      ...state,
      accountBank: e.value.code,
    });
  };
  // 가자렌탈 아이디 복사 함수
  const f_copyText = () => {
    RefCopyIcon.current.style.color = '#000f34';
    RefCopy.current.select();
    RefCopy.current.setSelectionRange(0, 99999);
    document.execCommand('copy');
    setTimeout(() => {
      RefCopyIcon.current.style.color = '#979797';
    }, 500);
  };

  useEffect(() => {
    if (state.sendImg) {
      _photoData();
    } else if (state.isLoading) {
      _getData();
    }
  }, [state.sendImg, state.isLoading]);
  const _photoData = async () => {
    const url = '/api/member/set_photo_update.php';
    const params = {
      userId: state.userId,
      photo:
        'http://spiderplatform.co.kr/upload/thumbnail/' + photo[0].file.name,
    };
    const { result, message } = await _axios(url, params);
    setState({
      ...state,
      sendImg: false,
      isLoading: true,
    });
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  return (
    <Container>
      <Wrap>
        <Inner>
          <form onSubmit={() => {}}>
            <CommonInput
              className="input-id"
              inputtype="text"
              width="100%"
              height="60px"
              value={state.accountBankName}
              maxLength={20}
              autoComplete="off"
              name="accountBank"
              onChange={() => {
                console.log();
              }}
              required={true}
              validityStyles={false}
              titlename="은행명"
            />

            <CommonInput
              className="input-id"
              inputtype="text"
              width="100%"
              height="60px"
              value={state.accountName}
              maxLength={20}
              autoComplete="off"
              name="accountName"
              onChange={() => {
                console.log();
              }}
              required={true}
              placeholder="홍길동"
              validityStyles={false}
              titlename="예금주"
            />

            <CommonInput
              className="input-id"
              inputtype="text"
              width="100%"
              height="60px"
              value={state.accountNo}
              maxLength={20}
              autoComplete="off"
              name="accountNo"
              onChange={() => {
                console.log();
              }}
              required={true}
              validityStyles={false}
              titlename="계좌번호"
            />

            <CommonInput
              className="input-id"
              inputtype="text"
              width="100%"
              height="60px"
              value={state.registInput1}
              maxLength={20}
              autoComplete="off"
              name="registInput1"
              onChange={() => {
                console.log();
              }}
              required={true}
              validityStyles={false}
              titlename="생년월일   "
            />

            <CommonButton
              type={'submit'}
              className="loginAnchor"
              kind="wideBtn_01"
            >
              수정하기
            </CommonButton>
          </form>
        </Inner>
      </Wrap>
    </Container>
  );
};

// 회원수정 스타일 컴포넌트
const Container = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
`;

const Wrap = styled.div`
  display: flex;
  flex-direction: column;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (min-width: 769px) {
    width: 450px;
    margin-top: 50px;
  }
`;

const Inner = styled.div`
  background-color: ${theme.colors.lightGrey};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  form {
    margin-top: 75px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    div {
      width: 100%;
    }
    input {
      margin: 0;
    }
    button {
      margin-top: 40px;
      margin-bottom: 300px;
    }
  }
`;
export default AccountEdit;
