import React, { useCallback, useEffect, useState } from 'react';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Input } from '@progress/kendo-react-inputs';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
//import { scrollRequest, stackRequest } from '../../../redux/actions/auth';
import { useDispatch } from 'react-redux';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const CommitInfo = (props) => {
  const dispatch = useDispatch();
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,

    sponsId: '',
    sponsName: '',
    memId: '',
    memName: '',
    price: '',
    assort: '',
    assortOptions: [],
    clearAssort: '',
    clearStatus: '',
    clearOptions: [],
    status: '',
    statusOptions: [],
    remarks: '',
    wdate: '',

    success: false,
    message: null,
    visibleDialog: false,
  });

  const _getData = useCallback(async () => {
    const url = '/api/admin/commission/get_commi_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      sponsId: data.sponsId,
      sponsName: data.sponsName,
      memId: data.memId,
      memName: data.memName,
      price: data.price,
      assort: { code: data.assort, name: data.assortName },
      assortOptions: data.assortOptions,
      clearAssort: data.clearAssort,
      clearStatus: { code: data.clearStatus, name: data.clearStatusName },
      clearOptions: data.clearOptions,
      status: { code: data.status, name: data.statusName },
      statusOptions: data.statusOptions,
      remarks: data.remarks,
      wdate: data.wdate,
    });
  }, [state]);

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/admin/commission/set_commi_update.php';
    const { message } = await _axios(url, state);

    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    //dispatch(stackRequest('S', '수수료관리'));
    _getData();
  }, [dispatch]);

  // useEffect(() => {
  //   if (state.isDelete) {
  //     // _deleteData(); 미구현
  //   }
  // }, []);

  // const { isLoading, isSearch, loadData, searchAble } = state;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const assortMessage = "'수수료구분'을 선택해주세요!";
  const memIdMessage = "'회원ID'를 입력해주세요!";
  const memNameMessage = "'회원명'을 입력해주세요!";
  const priceMessage = "'수수료'를 입력해주세요!";
  const clearMessage = "'보류여부'를 선택해주세요!";
  const statusMessage = "'정산상태'를 선택해주세요!";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>포인트관리 〉 적립목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>스폰서 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="sponsId"
                  value={state.sponsId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>스폰서명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="sponsName"
                  value={state.sponsName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원 ID</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memId"
                  value={state.memId}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={memIdMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>회원명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="memName"
                  value={state.memName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={memNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>구분</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="assort"
                  value={state.assort}
                  data={state.assortOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={assortMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>수수료</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="price"
                  value={state.price}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={priceMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>보류여부</Label>
                <ComboBox
                  style={{ width: '34%', borderBottom: '1px solid #dcdcdc' }}
                  name="clearStatus"
                  value={state.clearStatus}
                  data={state.clearOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={clearMessage}
                />
                <Input
                  validityStyles={false}
                  type="text"
                  name="clearAssort"
                  value={state.clearAssort}
                  style={{ width: '40%' }}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>정산상태</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="status"
                  value={state.status}
                  data={state.statusOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={true}
                  validationMessage={statusMessage}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>등록일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="wdate"
                  value={state.wdate}
                  style={{ width: '74%' }}
                  maxLength={20}
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>기타사항</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="remarks"
                  value={state.remarks}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default CommitInfo;
