import React from 'react';
import { Switch } from 'react-router-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
// 개발자 Component
import AuthRoute from '../components/common/AuthRoute';
import Account from '../components/partnership/hpRequest/Account';
import BanefitType from '../components/partnership/hpRequest/BanefitType';
import ChangeTelecom from '../components/partnership/hpRequest/ChangeTelecom';
import Complete from '../components/partnership/hpRequest/Complete';
import CurrentTelecom from '../components/partnership/hpRequest/CurrentTelecom';
import DiscountType from '../components/partnership/hpRequest/DiscountType';
import FeeSelect from '../components/partnership/hpRequest/FeeSelect';
import FeeSetting from '../components/partnership/hpRequest/FeeSetting';
import GuideTable from '../components/partnership/hpRequest/GuideTable';
import InsertInfo from '../components/partnership/hpRequest/InsertInfo';
import Installment from '../components/partnership/hpRequest/Installment';
import ManufacturerType from '../components/partnership/hpRequest/ManufacturerType';
import ModelSelect from '../components/partnership/hpRequest/ModelSelect';
import ModelSelectDetail from '../components/partnership/hpRequest/ModelSelectDetail';
import OpeningType from '../components/partnership/hpRequest/OpeningType';
import SelectCard from '../components/partnership/hpRequest/SelectCard';
import SharedLink from '../components/partnership/hpRequest/SharedLink';

export default function HpRoute() {
  return (
    <TransitionGroup>
      <CSSTransition timeout={300} classNames="fade">
        <Switch>
          <AuthRoute
            exact
            path={`/partnership/hp/feeSetting`}
            render={(props) => <FeeSetting {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/currentTelecom`}
            render={(props) => <CurrentTelecom {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/openingType`}
            render={(props) => <OpeningType {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/sharedLink`}
            render={(props) => <SharedLink {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/changeTelecom`}
            render={(props) => <ChangeTelecom {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/manufacturerType`}
            render={(props) => <ManufacturerType {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/modelSelect`}
            render={(props) => <ModelSelect {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/modelSelectDetail`}
            render={(props) => <ModelSelectDetail {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/feeSelect`}
            render={(props) => <FeeSelect {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/selectCard`}
            render={(props) => <SelectCard {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/banefitType`}
            render={(props) => <BanefitType {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/discountType`}
            render={(props) => <DiscountType {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/installment`}
            render={(props) => <Installment {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/guideTable`}
            render={(props) => <GuideTable {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/insertInfo`}
            render={(props) => <InsertInfo {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/complete`}
            render={(props) => <Complete {...props} />}
          />
          <AuthRoute
            exact
            path={`/partnership/hp/account`}
            render={(props) => <Account {...props} />}
          />
          {/*
          <LoginRoute
            exact
            path={`/partnership/hpRequest/request`}
            render={(props) => <Request {...props} />}
          />
          */}
        </Switch>
      </CSSTransition>
    </TransitionGroup>        
  );
}
