import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
// 개발자 라이브러리
import { setHeader } from '../../../redux/actions/header';
import Tree from '../../admin/member/Tree';
import _axios from '../../../utils/axios';
import _isMobile from "../../../utils/isMobile";
// css
import '../../../styles/losmap-tree.css';

const renderTree = (nodes) => (
  <Tree key={nodes.idx} id={nodes.memId} content={nodes.memName}>
    {Array.isArray(nodes.children)
      ? nodes.children.map((node) => renderTree(node))
      : null}
  </Tree>
);

const Losmap = () => {
  const headerInfo = {
    isBackground: false, // 백그라운드
    isBugger: true, // 버거메뉴
    isLogo: false, // 로고
    isHomeButton: false, // Home 버튼
    isBackButton: true, // Back 버튼
    titleName: 'Losmap'
  };

  const dispatch = useDispatch();
  const userInfo = useSelector((store) => store.auth.authStatus.userInfo);
  const isMobile = _isMobile();

  const [state, setState] = useState({
    isLoading: true,
    loadData: [],
    sponsId: userInfo.userId,
  });

  const _getData = async () => {
    const url = '/api/member/get_losmap.php';
    const params = {
      sponsId: state.sponsId,
    };

    await _axios(url, params).then((response) => {
      setState((prevState) => ({
        ...state,
        isLoading: false,
        loadData: response.data,
      }));
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    setState({ ...state, isLoading: true });
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));

    if (state.isLoading) {
      _getData();
    }
  }, []);

  const { isLoading, loadData } = state;

  return (
    <Container>
      {!isMobile && (
        <SearchWrap>
          <Title>회원관리 〉 LOS MAP</Title>
        </SearchWrap>
      )}

      {isLoading ? (
        <div style={{ padding: '20px' }}>loading...</div>
      ) : (
        <TreeWrap>{renderTree(loadData)}</TreeWrap>
      )}
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
`;

// 검색
const SearchWrap = styled.div``;

const TreeWrap = styled.div`
  @media (max-width: 768px) {
    height: 80%;
  }

  @media (min-width: 769px) {
    height: 65vh;
  }

  padding: 10px;
  background: #fff;
  overflow: auto;
  font-size: 13px;
  line-height: 21px;
`;

const Title = styled.div`
  font-size: 0.9em;
  font-weight: 600;
  color: #666666;
  margin-bottom: 15px;
  margin-top: 10px;
  margin-left: 10px;
`;

export default Losmap;
