/* **********************************************************************
 * 모바일홈 > 보너스 자세히 보기> 휴대폰 실적 포인트
 * 작성자: 이해영
 * 2021. 06. 07. 양민석. 포인트 월표시 개선
 *********************************************************************** */
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
//개발자 라이브러리
import { setHeader } from '../../../../redux/actions/header';
// css
import styled from 'styled-components';
import { Table, Td, Th, Tr } from '../../../../css/list';
import theme from '../../../../styles/theme';
import _axios from '../../../../utils/axios';
import { MonthPoint } from './PointHomeMobile';
import PointHomeChart from './PointHomeChart';

const Hp = () => {
  const { userId } = useSelector((store) => store.auth.login.userInfo);
  const [data, setData] = useState([]);

  const headerInfo = {
    isBackground: false,
    isBugger: false,
    isLogo: false,
    isHomeButton: false,
    isBackButton: true,
    titleName: '휴대폰 실적 포인트',
  };

  const dispatch = useDispatch();

  const [month, setMonth] = useState(true);
  const date = new Date();
  const todayMonth = date.getMonth();
  const [monthIndex, setMonthIndex] = useState(todayMonth);

  useEffect(() => {
    // header setting
    dispatch(setHeader(headerInfo));
  }, []);

  const _getData = useCallback(async () => {
    const date = new Date();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();

    const url = '/api/member/get_point_hp.php';

    const params = {
      userId: userId,
      year: String(year),
      month: String(0) + String(month - 1),
    };

    const data = await _axios(url, params);
    setData(data);
  }, [userId]);
  useEffect(() => {
    _getData();
  }, [_getData]);
  return (
    <CommiPhoneInner>
      <MonthPoint style={{ marginTop: '0' }}>
        <p className="month-title">휴대폰 포인트</p>

        <div className="phone-month">
          <div>
            <ul
              style={{ height: '80px', marginTop: '2.5em' }}
              className="phone-month-list"
            >
              {data.length !== 0 &&
                data.chartData.map((n, index) => (
                  <PointHomeChart
                    key={index}
                    monthIndex={monthIndex}
                    setMonthIndex={setMonthIndex}
                    percent={n.percent}
                    index={index}
                  />
                ))}
            </ul>
          </div>

          <div className="phone-num">
            <ol>
              {data.length !== 0 &&
                data.chartData.map((n, index) => (
                  <li key={index} className={month && 'active'} key={index}>
                    {n.month}
                  </li>
                ))}
            </ol>
          </div>

          <div>
            <ul className="detail-list">
              <li className="detail-list-title">
                <p>2021년 {monthIndex + 1}월</p>
                {data.length !== 0 && <p>{data.chartData[monthIndex].point}</p>}
              </li>
            </ul>
          </div>
        </div>
      </MonthPoint>

      <PhoneTable>
        <h3>휴대폰 개통 실적</h3>

        <Tr width={'100%'} height={'35px'} background={'#ffffff'}>
          <Th width={'39%'}>개통일자</Th>
          <Th width={'29%'}>고객명</Th>
          <Th width={'32%'}>포인트</Th>
        </Tr>

        <Table width={'100%'} className="table">
          {data.length !== 0 &&
            data.listData.map((item, index) => (
              <div key={index}>
                {parseInt(item.month) === monthIndex + 1 && (
                  <Tr height={'30px'} key={index}>
                    <Td width={'40%'}>{item.openingDate}</Td>
                    <Td width={'30%'}>{item.custName}</Td>
                    <Td width={'30%'}>{item.point}</Td>
                  </Tr>
                )}
              </div>
            ))}
        </Table>
      </PhoneTable>

      <PhoneButton>
        <Link to="/mypage/hpRequest">
          <button type="button">휴대폰 간편신청 전체 목록</button>
        </Link>
      </PhoneButton>
    </CommiPhoneInner>
  );
};

export default Hp;

export const CommiPhoneInner = styled.section`
  width: 100%;
  padding: 2em 1.5em;
  background-color: #f8f8f8;

  ul {
    padding: 0;
  }
  li {
    list-style: none;
  }
  p {
    margin: 0;
  }

  ${MonthPoint} {
    .month-title {
      ${theme.H3};
    }

    .phone-num {
      li {
        display: flex;
        justify-content: center;
      }
    }

    .detail-list {
      padding: 1.5em;

      &-title {
        display: flex;
        justify-content: space-between;
        font-size: ${theme.fontSizes.H5};
        font-family: NotoM;
      }
    }
  }
`;

export const PhoneMonth = styled.section`
  padding: 1em;
  background-color: #ffffff;
  border-radius: 3px;

  .phone-month {
    &-title {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.5em;
      font-size: 14px;
      color: #263238;

      .year {
        font-size: 12px;
        transform: translateY(1px);
      }
    }

    &-list {
      display: flex;
      overflow-x: scroll;
      margin-bottom: 10px;

      li {
        position: relative;
        display: inline-block;
        margin: 0 5px 5px 0;
        padding: 1.5em;
        height: 70px;
        opacity: 0.32;
        background: #ff473e;
        text-align: center;

        span {
          position: absolute;
          width: 100%;
          left: 0;
          bottom: 2px;
          color: #000;
          font-weight: 500;
        }
      }
    }
  }
`;

export const PhoneSum = styled.section`
  display: flex;
  justify-content: space-between;
  padding: 0.8em 1em 0.7em 0;
  margin: 2em 0;
  border-bottom: 1px solid #737b7d;
  background-color: #f0f3f4;

  p {
    font-size: 18px;
  }

  strong {
    font-size: 24px;
  }
`;

export const PhoneTable = styled.section`
  margin-top: 3em;
  padding: 2em 1.5em;
  background: ${theme.colors.white};

  h3 {
    margin-bottom: 1.5em;
    ${theme.H3};
    border-bottom: 1px solid ${theme.colors.grey2};
  }

  ${Tr} {
    ${theme.Body1};
    color: ${theme.colors.black1};
  }

  ${Td} {
    ${theme.Body1};
    color: ${theme.colors.black1};
  }

  .table {
    height: 250px;
    overflow-y: scroll;
    border-radius: 3px;

    .table-ellipsis {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
`;

export const PhoneButton = styled.div`
  display: block;
  width: 100%;
  margin: 3em 0 2em;
  text-align: center;

  button {
    padding: 0.7em 3em;
    color: #ffffff;
    ${theme.Button1};
    background-color: ${theme.colors.royalblue};
    border-style: none;
    border-radius: 3px;
    box-shadow: 0 20px 50px 0 rgba(0, 0, 0, 0.3);
  }
`;
