/* **********************************************************************
  관리자페이지 > 환경설정 > 휴대폰신청
  - 2021.05.19. 박태수
  - 2021.05.27. 양민석 : 스파이더2 리뉴얼
 *********************************************************************** */
import React, { useEffect, useState } from 'react';
import {
  Input,
  RadioGroup,
  Checkbox,
  TextArea,
} from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
import { isMobile } from 'react-device-detect';
import GoodsImgUpload from './GoodsImgUpload';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';
// icon
import AddIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import RemoveIcon from '@material-ui/icons/RemoveCircleOutlineOutlined';

const GoodsInfo = (props) => {
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,

    goodsCode: '',
    goodsName: '',
    makerCode: '',
    telecoms: [],
    imtAssort: '',
    thumbnail: '',
    content: '',
    useYn: '',

    models: [],

    makerOptions: [],
    imtOptions: [],
    telecomOptions: [],
    capacityOptions: [],
    chargeOptions: [],
    useOptions: [],
    useOptions2: [],

    success: false,
    message: null,
    visibleDialog: false,

    files: [],
    events: [],
    filePreviews: {},

    sendImg: false,
  });

  // 상품 정보
  const _getData = async () => {
    const url = '/api/hp/goods/get_goods_info.php';
    const params = {
      idx: state.idx,
    };

    const response = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,

      goodsCode: response.data.goodsCode,
      goodsName: response.data.goodsName,
      makerCode: {
        code: response.data.makerCode,
        name: response.data.makerName,
      },
      telecoms: response.data.telecoms,
      imtAssort: response.data.imtAssort,
      installment: response.data.installment,
      thumbnail: response.data.thumbnail,
      content: response.data.content,
      useYn: response.data.useYn,

      models: response.models,

      makerOptions: response.makerOptions,
      imtOptions: response.imtOptions,
      telecomOptions: response.telecomOptions,
      capacityOptions: response.capacityOptions,
      chargeOptions: response.chargeOptions,
      useOptions: response.useOptions,
      useOptions2: response.useOptions2,
    });
  };

  // 수정된 내용 저장
  const _setData = async () => {
    const url = '/api/hp/goods/set_goods_update.php';
    const params = {
      mode: state.mode,
      idx: state.idx,
      goodsCode: state.goodsCode,
      goodsName: state.goodsName,
      makerCode: state.makerCode,
      telecoms: state.telecoms,
      imtAssort: state.imtAssort,
      thumbnail: state.thumbnail,
      content: state.content,
      useYn: state.useYn,
      models: state.models,
    };
    const { message } = await _axios(url, params);
    setState({
      ...state,
      success: true,
      message: message,
      visibleDialog: true,
    });

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    _setData();
    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _handleTextarea = (e) => {
    setState({ ...state, [e.target.element.current.name]: e.value });
  };

  // 통신사 체크
  const _handleTelecomChange = (e, index) => {
    state.telecoms[index].checked = e.value;
    setState({ ...state, telecoms: state.telecoms });
  };

  // 통신망 체크
  const _handleImtChange = (e) => {
    setState({ ...state, imtAssort: e.value });
  };

  // 사용여부 체크
  const _handleUseChange = (e) => {
    setState({ ...state, useYn: e.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };
  // ***************** 모델정보 시작 ****************************
  const _handleModelAdd = () => {
    const model = {
      idx: 0,
      modelCode: '',
      modelName: '',
      modelThumb: '',
      modelYn: {
        code: 'Y',
        name: '사용함',
      },
      capacitys: [],
      colors: [],
      charges: [],
      files: [],
      events: [],
      filePreviews: {},
    };

    setState({ ...state, models: [...state.models, model] });
  };

  const _handleModelRemove = (index) => {
    state.models.splice(index, 1);
    setState({ ...state, models: state.models });
  };

  const _handleModelInputChange = (e, index) => {
    state.models[index][e.target.name] = e.target.value;
    setState({ ...state, models: state.models });
  };

  // ***************** 용량 정보 시작 ****************************
  const _handleCapacityAdd = (e, modelIndex) => {
    const capacity = {
      idx: 0,
      capacityCode: '',
      factoryPrice: '',
      capacityYn: {
        code: 'Y',
        name: '사용함',
      },
    };

    const capacitys = [...state.models[modelIndex].capacitys, capacity];
    state.models[modelIndex].capacitys = capacitys;
    setState({ ...state, models: state.models });
  };

  const _handleCapacityRemove = (modelIndex, index) => {
    state.models[modelIndex].capacitys.splice(index, 1);
    setState({ ...state, models: state.models });
  };

  const _handleCapacityCodeChange = (e, modelIndex, index) => {
    state.models[modelIndex].capacitys[index].capacityCode = e.target.value;
    setState({ ...state, models: state.models });
  };

  const _handleCapacityPriceChange = (e, modelIndex, index) => {
    state.models[modelIndex].capacitys[index].factoryPrice = e.target.value;
    setState({ ...state, models: state.models });
  };

  const _handleCapacityYnChange = (e, modelIndex, index) => {
    state.models[modelIndex].capacitys[index].capacityYn = e.target.value;
    setState({ ...state, models: state.models });
  };

  // ***************** 색상 정보 시작 ****************************
  const _handleColorAdd = (e, modelIndex) => {
    const color = {
      idx: 0,
      colorName: '',
      colorTelecoms: [
        {
          code: 'S',
          name: 'SKT',
          checked: false,
        },
        {
          code: 'K',
          name: 'KT',
          checked: false,
        },
        {
          code: 'L',
          name: 'LGU+',
          checked: false,
        },
        {
          code: 'A',
          name: '알뜰폰+',
          checked: false,
        },
      ],
      colorYn: {
        code: 'Y',
        name: '사용함',
      },
    };

    const colors = [...state.models[modelIndex].colors, color];
    state.models[modelIndex].colors = colors;
    setState({ ...state, models: state.models });
  };

  const _handleColorRemove = (modelIndex, index) => {
    state.models[modelIndex].colors.splice(index, 1);
    setState({ ...state, models: state.models });
  };

  const _handleColorNameChange = (e, modelIndex, index) => {
    state.models[modelIndex].colors[index].colorName = e.target.value;
    setState({ ...state, models: state.models });
  };

  const _handleColorTelecomChange = (e, modelIndex, colorIndex, index) => {
    state.models[modelIndex].colors[colorIndex].colorTelecoms[index].checked =
      e.value;
    setState({ ...state, models: state.models });
  };

  const _handleColorYnChange = (e, modelIndex, index) => {
    state.models[modelIndex].colors[index].colorYn = e.target.value;
    setState({ ...state, models: state.models });
  };

  // ***************** 기본요금제 정보 시작 ****************************
  const _handleChargeAdd = (e, modelIndex) => {
    const charge = {
      idx: 0,
      chargeTelecom: {
        code: '',
        name: '',
      },
      chargeCode: {
        code: '',
        name: '',
      },
      chargeYn: {
        code: 'Y',
        name: '사용함',
      },
    };

    const charges = [...state.models[modelIndex].charges, charge];
    state.models[modelIndex].charges = charges;
    setState({ ...state, models: state.models });
  };

  const _handleChargeRemove = (modelIndex, index) => {
    state.models[modelIndex].charges.splice(index, 1);
    setState({ ...state, models: state.models });
  };

  const _handleChargeInputChange = (e, modelIndex, index) => {
    state.models[modelIndex].charges[index][e.target.name] = e.target.value;
    setState({ ...state, models: state.models });
  };

  const _handleChargeTelecomChange = (e, modelIndex, index) => {
    state.models[modelIndex].charges[index].chargeTelecom = e.target.value;
    setState({ ...state, models: state.models });
    state.models[index][e.target.name] = e.target.value;
    setState({ ...state, models: state.models });
  };

  const _handleChargeCodeChange = (e, modelIndex, index) => {
    state.models[modelIndex].charges[index].chargeCode = e.target.value;
    setState({ ...state, models: state.models });
  };

  const _handleChargeYnChange = (e, modelIndex, index) => {
    state.models[modelIndex].charges[index].chargeYn = e.target.value;
    setState({ ...state, models: state.models });
  };

  useEffect(() => {
    _getData();
  }, []);

  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '800px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  const goodsCodeCodeMessage = "'상품코드'를 입력해주세요!";
  const goodsNameMessage = "'상품명'을 입력해주세요!";
  const modelCodeMessage = "'모델코드'를 입력해주세요!";
  const modelNameMessage = "'모델명'을 입력해주세요!";

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 상품관리 〉 목록 〉 상세정보</Title>

          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>상품코드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="goodsCode"
                  value={state.goodsCode}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={goodsCodeCodeMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>상품명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="goodsName"
                  value={state.goodsName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={true}
                  validationMessage={goodsNameMessage}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>제조사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="makerCode"
                  value={state.makerCode}
                  data={state.makerOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신사</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    paddingTop: '5px',
                    marginBottom: '1px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  {state.telecoms.map((telecom, index) => (
                    <Checkbox
                      key={index}
                      value={telecom.code}
                      checked={telecom.checked}
                      onChange={(e) => _handleTelecomChange(e, index)}
                    >
                      <label
                        style={{
                          width: '40px',
                          fontSize: '0.9em',
                          marginLeft: '5px',
                        }}
                      >
                        {telecom.name}
                      </label>
                    </Checkbox>
                  ))}
                </div>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>통신망</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    marginBottom: '1px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="imtAssort"
                    defaultValue={state.imtAssort}
                    data={state.imtOptions}
                    value={state.imtAssort}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleImtChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>사용여부</Label>
                <div
                  style={{
                    width: '74%',
                    height: '30px',
                    marginBottom: '1px',
                    paddingLeft: '5px',
                    background: '#fff',
                  }}
                >
                  <RadioGroup
                    name="useYn"
                    defaultValue={state.useYn}
                    data={state.useOptions2}
                    value={state.useYn}
                    style={{ fontSize: '0.9em', marginTop: '5px' }}
                    onChange={_handleUseChange}
                    layout="horizontal"
                  />
                </div>
              </Td>
            </Tr>
          </Table>

          {state.models.map((model, index) => {
            const modelIndex = index;
            return (
              <Table key={index} width={'100%'}>
                <Tr align={'flex-end'}>
                  <Td width={'60px'}>Model. {index + 1}</Td>
                  <Td>
                    <RemoveIcon
                      style={{ cursor: 'pointer', marginTop: '5px' }}
                      onClick={() => _handleModelRemove(index)}
                    />
                  </Td>
                </Tr>
                <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>모델코드</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="modelCode"
                      value={model.modelCode}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={true}
                      validationMessage={modelCodeMessage}
                      onChange={(e) => _handleModelInputChange(e, modelIndex)}
                    />
                  </Td>
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>모델명</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="modelName"
                      value={model.modelName}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={true}
                      validationMessage={modelNameMessage}
                      onChange={(e) => _handleModelInputChange(e, modelIndex)}
                    />
                  </Td>
                  <Td width={tdWidth} height={'60px'} valign={'flex-start'}>
                    <Label width={labelWidth}>사용여부</Label>
                    <ComboBox
                      style={{
                        width: '74%',
                        borderBottom: '1px solid #dcdcdc',
                      }}
                      name="modelYn"
                      value={model.modelYn}
                      data={state.useOptions}
                      dataItemKey="code"
                      textField="name"
                      onChange={(e) => _handleModelInputChange(e, modelIndex)}
                      required={false}
                    />
                  </Td>
                  <Td width={tdWidth} height={'60px'} valign={'flex-start'}>
                    <Label width={labelWidth} height={'60px'}>
                      썸네일
                    </Label>
                    <GoodsImgUpload
                      state={state}
                      setState={setState}
                      modelIndex={modelIndex}
                    />

                    {/* {model.thumbnail !== '' && (
                      <img src={model.modelThumb} width={'60px'} alt="" />
                    )} */}
                  </Td>
                </Tr>

                {/* 모델의 용량옵션 */}
                <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
                  <Td width={'100%'}>
                    <Table style={{ width: '100%' }}>
                      <Tr style={{ width: '100%', background: '#f5f5f5' }}>
                        <Td
                          style={{
                            width: '25%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          용량
                        </Td>
                        <Td
                          style={{
                            width: '35%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          출고가
                        </Td>
                        <Td
                          style={{
                            width: '20%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          사용여부
                        </Td>
                        <Td
                          style={{
                            width: '5%',
                            height: '20px',
                            marginLeft: '2px',
                          }}
                        >
                          <AddIcon
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => _handleCapacityAdd(e, modelIndex)}
                          />
                        </Td>
                      </Tr>
                      {model.capacitys.map((capacity, index) => {
                        return (
                          <Tr key={index} style={{ width: '100%' }}>
                            <Td style={{ width: '25%' }}>
                              <ComboBox
                                style={{
                                  width: '90%',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name="capacityCode"
                                value={capacity.capacityCode}
                                data={state.capacityOptions}
                                dataItemKey="code"
                                textField="name"
                                onChange={(e) =>
                                  _handleCapacityCodeChange(
                                    e,
                                    modelIndex,
                                    index,
                                  )
                                }
                                required={false}
                              />
                            </Td>
                            <Td style={{ width: '35%' }}>
                              <Input
                                validityStyles={false}
                                type="text"
                                name="factoryPrice"
                                value={capacity.factoryPrice}
                                style={{ width: '90%' }}
                                maxLength={20}
                                autoComplete="off"
                                onChange={(e) =>
                                  _handleCapacityPriceChange(
                                    e,
                                    modelIndex,
                                    index,
                                  )
                                }
                              />
                            </Td>
                            <Td style={{ width: '20%' }}>
                              <ComboBox
                                style={{
                                  width: '90%',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name="capacityYn"
                                value={capacity.capacityYn}
                                data={state.useOptions}
                                dataItemKey="code"
                                textField="name"
                                onChange={(e) =>
                                  _handleCapacityYnChange(e, modelIndex, index)
                                }
                                required={false}
                              />
                            </Td>
                            <Td style={{ width: '5%' }}>
                              <RemoveIcon
                                style={{ cursor: 'pointer', marginTop: '5px' }}
                                onClick={() =>
                                  _handleCapacityRemove(modelIndex, index)
                                }
                              />
                            </Td>
                          </Tr>
                        );
                      })}
                    </Table>
                  </Td>
                </Tr>

                {/* 모델의 색상옵션 */}
                <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
                  <Td width={'100%'}>
                    <Table style={{ width: '100%' }}>
                      <Tr style={{ width: '100%', background: '#f5f5f5' }}>
                        <Td
                          style={{
                            width: '25%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          색상
                        </Td>
                        <Td
                          style={{
                            width: '35%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          통신사
                        </Td>
                        <Td
                          style={{
                            width: '20%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          사용여부
                        </Td>
                        <Td
                          style={{
                            width: '5%',
                            height: '20px',
                            marginLeft: '2px',
                          }}
                        >
                          <AddIcon
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => _handleColorAdd(e, modelIndex)}
                          />
                        </Td>
                      </Tr>

                      {model.colors.map((color, index) => {
                        const colorIndex = index;
                        return (
                          <Tr key={index} style={{ width: '100%' }}>
                            <Td style={{ width: '25%' }}>
                              <Input
                                validityStyles={false}
                                type="text"
                                name="colorName"
                                value={color.colorName}
                                style={{ width: '90%' }}
                                maxLength={20}
                                autoComplete="off"
                                onChange={(e) =>
                                  _handleColorNameChange(e, modelIndex, index)
                                }
                              />
                            </Td>
                            <Td style={{ width: '35%' }}>
                              {color.colorTelecoms.map((telecom, index) => (
                                <Checkbox
                                  key={index}
                                  value={telecom.code}
                                  checked={telecom.checked}
                                  onChange={(e) =>
                                    _handleColorTelecomChange(
                                      e,
                                      modelIndex,
                                      colorIndex,
                                      index,
                                    )
                                  }
                                >
                                  <label
                                    style={{
                                      width: '40px',
                                      fontSize: '0.9em',
                                      marginLeft: '5px',
                                    }}
                                  >
                                    {telecom.name}
                                  </label>
                                </Checkbox>
                              ))}
                            </Td>
                            <Td style={{ width: '20%' }}>
                              <ComboBox
                                style={{
                                  width: '90%',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name="colorYn"
                                value={color.colorYn}
                                data={state.useOptions}
                                dataItemKey="code"
                                textField="name"
                                onChange={(e) =>
                                  _handleColorYnChange(e, modelIndex, index)
                                }
                                required={false}
                              />
                            </Td>
                            <Td style={{ width: '5%' }}>
                              <RemoveIcon
                                style={{ cursor: 'pointer', marginTop: '5px' }}
                                onClick={(e) =>
                                  _handleColorRemove(modelIndex, index)
                                }
                              />
                            </Td>
                          </Tr>
                        );
                      })}
                    </Table>
                  </Td>
                </Tr>

                {/* 모델의 기본요금제 */}
                <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
                  <Td width={'100%'}>
                    <Table style={{ width: '100%' }}>
                      <Tr style={{ width: '100%', background: '#f5f5f5' }}>
                        <Td
                          style={{
                            width: '25%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          통신사
                        </Td>
                        <Td
                          style={{
                            width: '35%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          요금제
                        </Td>
                        <Td
                          style={{
                            width: '20%',
                            height: '25px',
                            fontSize: '1em',
                            paddingLeft: '10px',
                            borderBottom: '1px solid #dcdcdc',
                          }}
                        >
                          사용여부
                        </Td>
                        <Td
                          style={{
                            width: '5%',
                            height: '20px',
                            marginLeft: '2px',
                          }}
                        >
                          <AddIcon
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => _handleChargeAdd(e, modelIndex)}
                          />
                        </Td>
                      </Tr>

                      {model.charges.map((charge, index) => {
                        return (
                          <Tr key={index} style={{ width: '100%' }}>
                            <Td style={{ width: '25%' }}>
                              <ComboBox
                                style={{
                                  width: '90%',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name="chargeTelecom"
                                value={charge.chargeTelecom}
                                data={state.telecomOptions}
                                dataItemKey="code"
                                textField="name"
                                onChange={(e) =>
                                  _handleChargeInputChange(e, modelIndex, index)
                                }
                                required={false}
                              />
                            </Td>
                            <Td style={{ width: '35%' }}>
                              <ComboBox
                                style={{
                                  width: '90%',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name="chargeCode"
                                value={charge.chargeCode}
                                data={state.chargeOptions}
                                dataItemKey="code"
                                textField="name"
                                onChange={(e) =>
                                  _handleChargeInputChange(e, modelIndex, index)
                                }
                                required={false}
                              />
                            </Td>
                            <Td style={{ width: '20%' }}>
                              <ComboBox
                                style={{
                                  width: '90%',
                                  borderBottom: '1px solid #dcdcdc',
                                }}
                                name="chargeYn"
                                value={charge.chargeYn}
                                data={state.useOptions}
                                dataItemKey="code"
                                textField="name"
                                onChange={(e) =>
                                  _handleChargeInputChange(e, modelIndex, index)
                                }
                                required={false}
                              />
                            </Td>
                            <Td style={{ width: '5%' }}>
                              <RemoveIcon
                                style={{ cursor: 'pointer', marginTop: '5px' }}
                                onClick={() =>
                                  _handleChargeRemove(modelIndex, index)
                                }
                              />
                            </Td>
                          </Tr>
                        );
                      })}
                    </Table>
                  </Td>
                </Tr>
              </Table>
            );
          })}

          <Table width={'100%'} style={{ marginTop: '5px' }}>
            <Tr width={'100%'} direction={direction} background={'#fff'}>
              <Td width={'100%'} align={'center'} style={{ fontWeight: '600' }}>
                <span style={{ width: '60px' }}>모델 추가</span>
                <AddIcon
                  style={{ cursor: 'pointer' }}
                  onClick={(e) => _handleModelAdd(e)}
                />
              </Td>
            </Tr>
          </Table>

          <Title style={{ marginTop: '20px' }}>대표 썸네일</Title>
          <Table width={'100%'}>
            <Tr width={'100%'} direction={'row'} background={'#fff'}>
              <Td width={'20%'}>
                <GoodsImgUpload state={state} setState={setState} />
                {/* {state.thumbnail !== '' && (
                  <img src={state.thumbnail} width={'60px'} alt="" />
                )} */}
              </Td>
            </Tr>
          </Table>

          <Title style={{ marginTop: '20px' }}>상품설명</Title>
          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={'100%'}>
                <TextArea
                  name="content"
                  value={state.content}
                  style={{ width: '100%', height: '300px' }}
                  autoSize={true}
                  onChange={_handleTextarea}
                />
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            {state.accurateStatus !== '9' && (
              <Button
                primary={true}
                type={'submit'}
                onClick={() => setState({ ...state, isSave: true })}
              >
                저장하기
              </Button>
            )}

            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default GoodsInfo;
