import React from 'react';
import { Switch } from 'react-router-dom';
// 개발자 Components
import _isMobile from '../utils/isMobile';
import AuthRoute from '../components/common/AuthRoute'; // 로그인 필요 체크

import HpShareLink from '../components/partnership/hpRequest/SharedLink'; // 휴대폰신청 > 링크신청
import InsuShareLink from '../components/insu/ShareLink'; // 다이렉트보험 > 링크신청

import Home from './mypage/Home'; // 홈 Route
import Member from './mypage/Member'; // 회원 Route
import Point from './mypage/Point'; // 포인트 Route
import HpRequest from './mypage/HpRequest'; // 휴대폰신청 Route
import InsuRequest from './mypage/InsuRequest'; // 다이렉트보험 Route
import MyInfoRoute from './mypage/MyInfoRoute'; // 내정보 Route

export default function MypageRoute() {
  return (
    <Switch>
      <AuthRoute
        path="/mypage/home"
        render={(props) => <Home {...props} />}
      />
      <AuthRoute
        path="/mypage/member"
        render={(props) => <Member {...props} />}
      />
      <AuthRoute
        path="/mypage/point"
        render={(props) => <Point {...props} />}
      />      
      <AuthRoute
        path="/mypage/hpRequest"
        render={(props) => <HpRequest {...props} />}
      />
      <AuthRoute
        path="/mypage/insuRequest"
        render={(props) => <InsuRequest {...props} />}
      />
      <AuthRoute
        path="/mypage/myinfo"
        render={(props) => <MyInfoRoute {...props} />}
      />
      <AuthRoute
        path="/mypage/hpShareLink"
        render={(props) => <HpShareLink {...props} />}
      />
      <AuthRoute
        path="/mypage/insuShareLink"
        render={(props) => <InsuShareLink {...props} />}
      />       
    </Switch>
  );
}
