/* **********************************************************************
  관리자페이지 > 환경설정 > 휴대폰신청
  - 2021.05.19. 박태수
  - 2021.05.28. 양민석 : 스파이더2 리뉴얼작업
 *********************************************************************** */
import React, { useState, useEffect } from 'react';
import { Input } from '@progress/kendo-react-inputs';
import { Button } from '@progress/kendo-react-buttons';
import { ComboBox } from '@progress/kendo-react-dropdowns';
import { Dialog } from '@progress/kendo-react-dialogs';
import { useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
// 개발자 라이브러리
import _axios from '../../../utils/axios';
// css
import {
  ButtonWrap,
  Container,
  Label,
  Table,
  Td,
  Title,
  Tr,
  Wrap,
} from '../../../css/list';
import '../../../css/kendo.css';

const RequestInfo = (props) => {
  const authStatus = useSelector((store) => store.auth.authStatus);
  const [state, setState] = useState({
    isLoading: true,
    idx: props.location.idx,
    mode: props.location.mode,
    adminId: authStatus.userId,
    custName: '',
    hpNo: '',
    useTelecom: '',
    changeTelecom: '',
    telecomOptions: [],
    requestAssort: '',
    assortOptions: [],
    modelCode: '',
    modelOptions: [],
    chargeName: '',

    installment: '',
    discountType: '',
    discountTypeOptions: [],

    cardName: '',
    benefitAssort: '',
    benefitOptions: [],
    commission: '',
    requestStatus: '',
    statusOptions: [],
    discountData: [],
    cashBackData: [],
    comment: '',
    statusMemo: '',
    adminMemo: '',
    wdate: '',
    logData: [],

    success: false,
    message: null,
    visibleDialog: false,
    confirmDialog: false,
  });

  const _getData = async () => {
    const url = '/api/member/hp/get_request_info.php';
    const params = {
      idx: state.idx,
    };

    const { data } = await _axios(url, params);

    setState({
      ...state,
      isLoading: false,
      isSave: false,

      memId: data.memId,
      memName: data.memName,
      hpNo: data.hpNo,
      custName: data.custName,
      useTelecom: { code: data.useTelecom, name: data.useTelecomName },
      changeTelecom: { code: data.changeTelecom, name: data.changeTelecomName },
      telecomOptions: data.telecomOptions,

      requestAssort: { code: data.requestAssort, name: data.assortName },
      assortOptions: data.assortOptions,

      modelCode: { code: data.modelCode, name: data.modelName },
      modelOptions: data.modelOptions,

      colorCode: { code: data.colorCode, name: data.colorName },
      colorOptions: data.colorOptions,
      capacityCode: { code: data.capacityCode, name: data.capacityName },
      capacityOptions: data.capacityOptions,

      chargeName: data.chargeName,
      installment: data.installment,

      discountType: { code: data.discountType, name: data.discountTypeName },
      discountTypeOptions: data.discountTypeOptions,

      cardName: data.cardName,
      benefitAssort: { code: data.benefitAssort, name: data.benefitName },
      benefitOptions: data.benefitOptions,
      commission: data.commission,

      discountData: data.discountData,
      cashBackData: data.cashBackData,

      requestStatus: { code: data.requestStatus, name: data.statusName },
      statusOptions: data.statusOptions,
      comment: data.comment,
      accountName: data.accountName,
      adminMemo: data.adminMemo,
      wdate: data.wdate,
    });
  };

  const _handleSubmit = (e) => {
    e.preventDefault();

    setTimeout(() => {
      setState({ ...state, success: false });
    }, 1000);
  };

  const _handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  const _toggleDialog = () => {
    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
      confirmDialog: !state.confirmDialog,
    });
  };

  useEffect(() => {
    _getData();
  }, []);

  const cashBackData = state.cashBackData;
  const direction = isMobile ? 'column' : 'row';
  const conWidth = isMobile ? '100%' : '650px';
  const tdWidth = isMobile ? '100%' : '50%';
  const labelWidth = '25%';

  return (
    <Container width={conWidth}>
      <form onSubmit={_handleSubmit}>
        <Wrap width={'100%'}>
          <Title>휴대폰신청 〉 신청목록 〉 상세정보</Title>
          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              <Td width={tdWidth}>
                <Label width={labelWidth}>고객명</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="custName"
                  value={state.custName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>휴대폰번호</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="hpNo"
                  value={state.hpNo}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>현재통신사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="useTelecom"
                  value={state.useTelecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>희망통신사</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="changeTelecom"
                  value={state.changeTelecom}
                  data={state.telecomOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>모델명</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="modelCode"
                  value={state.modelCode}
                  data={state.modelOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>용량</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="capacityCode"
                  value={state.capacityCode}
                  data={state.capacityOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>색상</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="colorCode"
                  value={state.colorCode}
                  data={state.colorOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>요금제</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="chargeName"
                  value={state.chargeName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인구분</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="discountType"
                  value={state.discountType}
                  data={state.discountTypeOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>제휴카드</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="cardName"
                  value={state.cardName}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할인혜택</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="benefitAssort"
                  value={state.benefitAssort}
                  data={state.benefitOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>할부개월</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="installment"
                  value={state.installment}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>수수료</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="commission"
                  value={state.commission}
                  style={{ width: '74%' }}
                  maxLength={20}
                  required={false}
                  onChange={_handleInputChange}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청구분</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="requestAssort"
                  value={state.requestAssort}
                  data={state.assortOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청상태</Label>
                <ComboBox
                  style={{ width: '74%', borderBottom: '1px solid #dcdcdc' }}
                  name="requestStatus"
                  value={state.requestStatus}
                  data={state.statusOptions}
                  dataItemKey="code"
                  textField="name"
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
              <Td width={tdWidth}>
                <Label width={labelWidth}>신청일자</Label>
                <Input
                  validityStyles={false}
                  type="text"
                  name="wdate"
                  value={state.wdate}
                  style={{ width: '74%' }}
                  maxLength={20}
                  onChange={_handleInputChange}
                  required={false}
                />
              </Td>
            </Tr>
          </Table>

          <Title>할인정보</Title>
          <Table width={'100%'}>
            <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
              {state.discountData &&
                state.discountData.map((discount) => (
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>{discount.discountName}</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="discountPrice"
                      value={discount.discountPrice}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={false}
                    />
                  </Td>
                ))}
            </Tr>
          </Table>

          {state.benefitAssort.code === 'C' && (
            <>
              <Title>캐시백정보</Title>
              <Table width={'100%'}>
                <Tr width={'100%'} direction={direction} background={'#dcdcdc'}>
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>은행명</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="bankName"
                      value={cashBackData.bankName}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={false}
                    />
                  </Td>
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>예금주</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="accountName"
                      value={cashBackData.accountName}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={false}
                    />
                  </Td>
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>계좌번호</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="accountNo"
                      value={cashBackData.accountNo}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={false}
                    />
                  </Td>
                  <Td width={tdWidth}>
                    <Label width={labelWidth}>캐시</Label>
                    <Input
                      validityStyles={false}
                      type="text"
                      name="cash"
                      value={cashBackData.cash}
                      style={{ width: '74%' }}
                      maxLength={20}
                      required={false}
                    />
                  </Td>
                </Tr>
              </Table>
            </>
          )}

          <Title>신청자 메모</Title>
          <Table>
            <Tr>
              <Td width={'100%'}>
                <div
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #dcdcdc',
                  }}
                >
                  {state.comment &&
                    state.comment.split('\n').map((line, index) => {
                      return (
                        <span key={index}>
                          {line}
                          <br />
                        </span>
                      );
                    })}
                </div>
              </Td>
            </Tr>
          </Table>

          <ButtonWrap>
            <Button type={'button'} onClick={() => props.history.goBack()}>
              이전화면
            </Button>
          </ButtonWrap>
        </Wrap>
      </form>

      {state.success && state.visibleDialog && (
        <Dialog title={'알림'} onClose={_toggleDialog}>
          <p style={{ margin: '25px', textAlign: 'center' }}>{state.message}</p>
        </Dialog>
      )}
    </Container>
  );
};

export default RequestInfo;
