import React, { useState } from 'react';
import styled from 'styled-components';
// css
import theme from '../../../../styles/theme';

const PointHomeChart = ({
  percent,
  index,
  monthIndex,
  setMonthIndex,
  rental,
}) => {
  const [month, setMonth] = useState(false);
  const handleMonthCheck = () => {
    setMonth(!month);
    setMonthIndex(index);
  };
  if (rental) {
    return (
      <StyledUl
        onClick={handleMonthCheck}
        className={month && 'active'}
        key={index}
      >
        <li
          style={{
            backgroundColor: `${theme.colors.murstard}`,
            height: `${percent.depthPercent}%`,
          }}
        ></li>
        <li
          style={{
            backgroundColor: `${theme.colors.royalblue}`,
            height: `${percent.directPercent}%`,
          }}
        ></li>
      </StyledUl>
    );
  }
  return (
    <StyledLi
      onClick={handleMonthCheck}
      className={month && 'active'}
      style={
        monthIndex == index
          ? {
              backgroundColor: `${theme.colors.murstard}`,
              height: `${percent}%`,
            }
          : { height: `${percent}%` }
      }
      key={index}
    ></StyledLi>
  );
};

export const StyledUl = styled.ul`
  background-color: white;
  width: 15px;
  height: 100%;
`;

export const StyledLi = styled.li``;

export default PointHomeChart;
