/*
  Board형태의 List
  2021.05.19. 박태수, 페이징
  2021.05.28. 양민석 : 조건문 마우스point 옵션
*/
import React, { useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { Pager } from '@progress/kendo-react-data-tools';
import {
  ExcelExport,
  ExcelExportColumn,
} from '@progress/kendo-react-excel-export';
// 개발자 라이브러리
import { setBoard } from '../../../redux/actions/board';
// css
import { Container, Table, Tr, Th, Td } from '../../../css/list';

const ListView = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const state = props.state;
  const setState = props.setState;
  const headers = props.headers;
  const datas = props.state.loadData;
  const excelHeader = props.excelHeader;
  const excelData = props.state.excelData;
  const infoPath = props.infoPath;

  // Checkbox toggle
  const _handleCheckbox = (e) => {
    const newData =
      e.target.name === 'checkAll'
        ? state.loadData.map((item) => ({
            ...item,
            isChecked: e.target.checked,
          }))
        : state.loadData.map((item) => {
            return item.idx === e.target.value
              ? { ...item, isChecked: e.target.checked }
              : item;
          });
    setState({
      ...state,
      loadData: newData,
    });
  };
  // 상세페이지 click
  const _handleInfoClick = (idx) => {
    dispatch(setBoard(state.searchAble));

    history.push({
      pathname: infoPath,
      idx: idx,
      mode: 'update',
    });
  };
  
  // Paging click
  const _handlePageChange = (e) => {
    const { searchAble } = state;
    const { skip, take } = e;
    
    setState({
      ...state,
      isLoading: true,
      skip: skip,
      searchAble: {
        ...searchAble,
        page: skip / take + 1,
        pageSize: take,
      },
    });
  };

  // 엑셀 다운로드
  const _exporter = useRef();
  const _export = () => {
    _exporter.current.save();
    setState({
      ...state,
      isExport: false,
    });
  };
console.log(state);
  return (
    <Container style={{ marginTop: '5px' }}>
      <Table width={'100%'}>
        <Tr width={'100%'} height={'35px'} background={'#dcdcdc'}>
          {headers.map((header, index) =>
            header.type === 'checkbox' ? (
              <Td key={index} width={header.width} align={'center'}>
                <input
                  type="checkbox"
                  name="checkAll"
                  onChange={_handleCheckbox}
                />
              </Td>
            ) : (
              <Th key={index} width={header.width} align={'center'}>
                {header.title}
              </Th>
            ),
          )}
        </Tr>

        {datas.map((row, index) => (
          <Tr
            key={index}
            width={'100%'}
            height={'30px'}
            style={{ background: index % 2 == 0 ? '#fff' : '#f5f5f5' }}
          >
            {headers.map((header, index2) =>
              header.type === 'checkbox' ? (
                <Td key={index2} width={header.width} align={header.align}>
                  <input
                    key={index2}
                    type="checkbox"
                    value={row.idx}
                    checked={row.isChecked}
                    onChange={_handleCheckbox}
                  />
                </Td>
              ) : (
                <Td
                  key={index2}
                  width={header.width}
                  align={header.align}
                  onClick={() => _handleInfoClick(row.idx)}
                  style={props.user ? {} : { cursor: 'pointer' }}
                >
                  {row[header.name]}
                </Td>
              ),
            )}
          </Tr>
        ))}
      </Table>

      <div style={{ width: '100%', height: '5px' }}></div>

      <Pager
        skip={state.skip}
        take={state.searchAble.pageSize}
        total={state.rowTotal}
        buttonCount={state.pageable.buttonCount}
        info={state.pageable.info}
        type={state.pageable.type}
        pageSizes={
          state.searchAble.pageSizes ? state.searchAble.pageSizes : undefined
        }
        previousNext={state.pageable.previousNext}
        onPageChange={_handlePageChange}
      />

      {state.isExport && (
        <ExcelExport
          data={state.excelData}
          fileName="cash_request.xlsx"
          ref={_exporter}
        >
          {state.excelData.map((row) => {
            excelHeader.map((header, index) => (
              <ExcelExportColumn 
                key={index}
                field={header.field}
                title={header.title}
                locked={header.lock}
                width={header.width}
              />
            ))
          })}
        </ExcelExport>
      )}
    </Container>
  );
};

export default ListView;
