import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
// 개발자 라이브러리
import * as actions from '../../../redux/actions/hp';
import _axios from '../../../utils/axios';
// css
import {
  BigLabel,
  Container,
  ParentWrap,
  SquareButton,
} from '../../../css/common-component';

export default function BanefitType({ history }) {
  const dispatch = useDispatch();
  const {
    changeTelecom,
    assort,
    modelCode,
    capacityCode,
    chargeCode,
    discountType,
    cardCode,
    marginPrice,
  } = useSelector((store) => store.hp);

  const _selectBanefitType = async (banefitAssort) => {
    const buyPrce = await _getBuyPrice(cardCode);
    if (parseInt(buyPrce) === 0) {
      dispatch(actions.selectBanefitAssort(banefitAssort));
      history.push('/partnership/hp/guideTable');
    } else {
      dispatch(actions.selectBanefitAssort(banefitAssort));
      history.push('/partnership/hp/installment');
    }
  };

  const _getBuyPrice = async (cardCode) => {
    const url = '/api/hp/get_factory_info.php';
    const params = {
      changeTelecom,
      assort,
      modelCode,
      capacityCode,
      chargeCode,
      discountType,
      cardCode,
      marginPrice,
    };
    const data = await _axios(url, params);

    return data.buyPrice;
  };

  return (
    <Container className="page">
      <ParentWrap
        padding="0px 39px 242px"
        style={{ backgroundColor: 'transparent' }}
      >
        <BigLabel
          textAlign="left"
          margin="0 auto 111px auto"
          width="100%"
          bold="normal"
        >
          추가지원 선택
        </BigLabel>
        {/* <SmallLabel width="100%" height="16px" textAlign="center" margin={"61px auto 55px auto"} bold="normal">
          혜택 : {benefitPrice}원
        </SmallLabel> */}

        <div
          style={{
            display: 'flex',
            marginTop: '55px',
            marginBottom: '55px',
            justifyContent: 'space-between',
          }}
        >
          <SquareButton
            width="118px"
            height="118px"
            margin="0 0px 0px 0"
            border="solid 1px #e2e2e2"
            backgroundColor="#000f34"
            color="#ffffff"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectBanefitType('M')}
          >
            <pre
              style={{
                color: '#fff',
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
              }}
            >{`단말기 할인`}</pre>
          </SquareButton>
          <SquareButton
            width="118px"
            height="118px"
            margin="0 0 0px 0px"
            border="solid 1px #e2e2e2"
            backgroundColor="#ffffff"
            color="#000f34"
            boxShadow="0 3px 6px 0 rgba(0, 0, 0, 0.16)"
            onClick={() => _selectBanefitType('C')}
          >
            <pre
              style={{
                color: '#000f34',
                margin: '0',
                fontSize: '15px',
                lineHeight: '17px',
              }}
            >{`캐시백`}</pre>
          </SquareButton>
        </div>
      </ParentWrap>
    </Container>
  );
}
